/*
 * 1260 - Horizontal Tabbed component
 *
 */

 .stay-money {
     &__bottom-wrapper {
         display: none;

         &.active {
             display: block;
         }
     }
    &__horizontal-tab-main-wrapper {
        width: calc(100% - 30px);
        position: relative;     
        background-color: $brand-white;
        

        ul {
            display: flex;
            
            padding-left: 0;
            position: relative;
            margin-bottom: 0;
           
            li {
                font-family: $pfhandbookpro-regular;
                font-size: $font16;
                text-align: center;
                color: rgba(0, 0, 0, 0.5) !important;
                padding: 14px 16px;
                white-space: nowrap;
                display: inline-block;
                position: relative;
                cursor: pointer;

                &::before {
                    display: none;
                }

                &:last-child {
                    &:after {
                        display: none;
                    }
                }
                
                &.active {
                    font-family: $pfhandbookpro-bold;
                    z-index: 1;
                    color: $brand-black !important;

                    &:before {
                        display: none;
                    }

                    // Color Theme
                    @include themifySelf($themes) {
                        background-color: themed('backgroundColor');  
                    }
                }
                @include mQ(991px, 768px){
                    font-size: $font14;
                    padding: 14px 24px;
                }
                @include mQ(767px, 576px){
                    font-size: $font16;
                    padding: 14px 24px;
                }
                @include mQ(575px, 360px){
                    font-size: $font11;
                    padding: 14px 12px;
                }

            }
        }   
    }
    &__horizontal-tab-scroll-wrapper {
        width: 100%;
        overflow-x: auto;
        transition: all 0.2s;
        will-change: transform;
        user-select: none;
        -ms-overflow-style: none;
        &::-webkit-scrollbar {
            display: none;
        }
    }
    &__horizontal-tab-scroll-cta {
        position: absolute;
        border: 1px solid $brand-capital-red !important;
        width: 24px;
        height: 24px;
        margin: 0;
        padding: 0;
        border: 0 none;
        color: $brand-capital-red;
        border-radius: 50%;
        top: 14px;
        z-index: 1;
        text-align: center;
        top: 50%;
        transform: translateY(-50%);
        background-color: $brand-white;

        @include mQ(574px, 360px){
            width: 16px;
            height: 16px;
        }

        .icon {
            display: block;
            font-size: $font12;
            line-height: 22px;
            @include mQ(574px, 360px){
                line-height: 0;
                font-size: $font10;
            }
        }
        &.scroll-left {
            left: 0;
            .icon {
                margin: 1px 1px 0 0;
                display: flex;
                align-items: center;
                justify-content: center;

            }

            @include mQ(991px) {
                left: -5px;
            }
        }
        &.scroll-right {
            right: 0;
            .icon {
                margin: 1px 0 0 1px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
            @include mQ(991px) {
                right: -5px;
            }
        }
    }
}
