.datepicker {
  color: inherit;
  background: $form-input-bg;
  border-radius: 4px;
  width: 278px;
  padding: 4px;
  margin-top: 1px;
  top: 100px;
  left: 20px;
  box-shadow: 4px 4px 16px 0 rgba(0, 0, 0, 0.13);
  margin-top: 3px;
}

//Date block
.datepicker {
  td,
  th {
    width: 50px !important;
    height: 50px !important;
    font-size: $font16;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    font-family: $lato-regular;
    border-radius: 0;
    border: 1px solid $calender-border;
  }
}

.datepicker {
  table {
    width: 100%;
    tr {
      td {
        span {
          width: 25% !important;
          border-radius: 0 !important;
          border: 1px solid $calender-border;
          padding: 0 !important;
          margin: 0 !important;
        }
      }
    }
  }
}

//active date
.datepicker table tr td.active:hover,
.datepicker table tr td.active:hover:hover,
.datepicker table tr td.active.disabled:hover,
.datepicker table tr td.active.disabled:hover:hover,
.datepicker table tr td.active:active,
.datepicker table tr td.active:hover:active,
.datepicker table tr td.active.disabled:active,
.datepicker table tr td.active.disabled:hover:active,
.datepicker table tr td.active.active,
.datepicker table tr td.active:hover.active,
.datepicker table tr td.active.disabled.active,
.datepicker table tr td.active.disabled:hover.active,
.datepicker table tr td.active.disabled,
.datepicker table tr td.active:hover.disabled,
.datepicker table tr td.active.disabled.disabled,
.datepicker table tr td.active.disabled:hover.disabled,
.datepicker table tr td.active[disabled],
.datepicker table tr td.active:hover[disabled],
.datepicker table tr td.active.disabled[disabled],
.datepicker table tr td.active.disabled:hover[disabled] {
  background: $calendar-active;
}

//remove left top dropdown icon
.datepicker-dropdown {
  border: 0;
  padding: 0 !important;
  &:before {
    all: unset;
  }
  &:after {
    all: unset;
  }
}

//Datepicker months
.datepicker-dropdown {
  div {
    &:not(:first-child) {
      table tr td span.active:hover,
      table tr td span.active:hover:hover,
      table tr td span.active.disabled:hover,
      table tr td span.active.disabled:hover:hover,
      table tr td span.active:active,
      table tr td span.active:hover:active,
      table tr td span.active.disabled:active,
      table tr td span.active.disabled:hover:active,
      table tr td span.active.active,
      table tr td span.active:hover.active,
      table tr td span.active.disabled.active,
      table tr td span.active.disabled:hover.active,
      table tr td span.active.disabled,
      table tr td span.active:hover.disabled,
      table tr td span.active.disabled.disabled,
      table tr td span.active.disabled:hover.disabled,
      table tr td span.active[disabled],
      table tr td span.active:hover[disabled],
      table tr td span.active.disabled[disabled],
      table tr td span.active.disabled:hover[disabled] {
        background: $calendar-active;
      }
    }
  }
}

.datepicker-months table,
.datepicker-year table {
  width: 100%;
  border-radius: 0;
  border: 1px solid $calender-border;
}

.datepicker-dropdown {
  z-index: 10000 !important;
  //width: 344px;
}
