//cs-banner
.cs-banner {
    max-width: 73.6rem;
    &__h1 {
        font-size: $font24;
        line-height: $line-height-26;
        color: $csb-text-clr;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 0.4rem;
        }
    }
    &__title {
        font-size: $font44;
        line-height: $line-height-46;
        color: $csb-text-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
            margin-bottom: 0.6rem;
        }
    }
    &__p {
        font-size: $font24;
        line-height: $line-height-26;
        color: $csb-text-clr;
        font-family: $pfhandbookpro-thin;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__highlight {
        display: block;
        margin: 2rem 0 1rem 0;
        font-size: $font44;
        line-height: $line-height-46;
        font-family: $pfhandbookpro-bold;
        color: $csb-hightlight-clr;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
            margin: 1.6rem 0 0.6rem 0;
        }
    }
}