/*------Banner <start>--------*/
.banner-component {
  // margin-bottom: 28px;
  margin-bottom: 45px;
  float: left;
  width: 100%;
  @include mQ(1020px) {
    margin-bottom: 22px;
  }
  @include mQ(767px) {
    margin-bottom: 22px;
  }
}
.banner-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  min-height: 565px;
  z-index: -1;

  &:after {
    @include linearGradient(rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0));
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 50%;
  }

  .banner-image {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto;
    height: auto;
    max-height: none;
    max-width: none;
    min-height: 100%;
    min-width: 100%;
    transform: translate(-50%, -50%);
    @include mQ(1020px) {
      /* position: relative;
            top: 0%;
            left: 50%;
            width: 100%;
            min-height: auto;
            min-width: auto;
            transform: translate(-50%, 0%); */
    }
  }

  @include mQ(1020px) {
    min-height: unset !important;
    height: inherit !important;
    position: relative;
    &:after {
      display: none;
    }
  }
}
.banner-content-wrapper {
  position: relative;
  padding-top: 41px;
  float: left;
  width: 100%;
  padding-bottom: 12px;

  @include mQ(1020px) {
    padding-top: 11px;
    padding-bottom: 2px !important;
    position: absolute;
    top: 147px;
    padding-left: 5px;
    padding-right: 5px;
  }
  @include mQ(767px) {
    top: 122px;
  }

  .banner-title {
    font-size: 4rem;
    line-height: 46px;
    color: $white;
    padding-bottom: 10px;
    @include mQ(1199px) {
      font-size: 3.2rem;
      line-height: 36px;
      padding-bottom: 6px;
    }
    @include mQ(767px) {
      font-family: $pfhandbookpro-bold;
      font-size: 1.6rem;
      line-height: 18px;
    }
  }
  .banner-subtitle {
    font-family: $pfhandbookpro-bold;
    font-size: 3.4rem;
    line-height: 39px;
    color: $white;
    margin-bottom: 28px;
    padding-right: 45px;
    a {
      font-family: $pfhandbookpro-bold;
      font-size: 3.4rem;
      line-height: 39px;
      color: $white;
      margin-bottom: 8px;
      padding-bottom: 0;
      @include mQ(1200px) {
        font-size: 3rem;
        line-height: 34px;
      }
      @include mQ(1020px) {
        font-size: 2.4rem;
        line-height: 27px;
        margin-bottom: 4px;
      }
      @include mQ(767px) {
        font-size: 2.8rem;
        line-height: 31px;
      }
      &:hover,
      &:active,
      &:focus {
        //border-bottom: none !important;
        border-bottom: 1px solid $white;
      }
    }
    @include mQ(1200px) {
      font-size: 3rem;
      line-height: 34px;
      padding-right: 0px;
    }
    @include mQ(1020px) {
      font-size: 2.4rem;
      line-height: 27px;
      margin-bottom: 4px;
    }
    @include mQ(767px) {
      font-size: 2.8rem;
      line-height: 31px;
    }
  }

  .banner-category-tag {
    display: inline-block;
    padding: 4px 11px 6px 11px;
    background-color: $capital-red;
    text-transform: uppercase;
    border-radius: 2px;
    margin-bottom: 8px;

    &.pink {
      background-color: #da9089;
    }
    &.red {
      background-color: $capital-red;
    }
    &.green {
      background-color: #969f6e;
    }
    &.yellow {
      background-color: #d4ae69;
    }

    a {
      font-family: $pfhandbookpro-bold;
      font-size: 1.4rem;
      color: $white;
      border-bottom: 1px solid $white;
      // &:hover {
      //   border-bottom: none;
      // }
    }

    @include mQ(767px) {
      padding: 0px 6px 6px 6px;
      a {
        font-family: $pfhandbookpro-bold;
        font-size: 1.2rem;
        line-height: 14px;
      }
    }
  }

  .banner-content {
    margin-bottom: 17px;
    padding-right: 45px;
    p {
      color: $white;
      @include mQ(1199px) {
        font-size: 1.5rem;
        line-height: 17px;
      }
    }
    @include mQ(1200px) {
      padding-right: 30px;
    }
  }
  .banner-utility-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding-right: 65px;
    .home-banner-share {
      .like-component {
        .icon,
        .count-wrapper {
          color: $white;
        }
      }
      .share-component {
        .count-wrapper,
        .icon {
          color: $white;
        }
      }
    }
    p {
      font-size: 1.9rem;
      line-height: 20px;
      color: $white;
      margin-bottom: 0;
      @include mQ(1199px) {
        font-size: 1.2rem;
        line-height: 17px;
      }
    }
    @include mQ(767px) {
      .home-banner-share {
        display: none;
      }
    }
  }
}

.category-cta {
  margin-top: 4px;
}
/*------dark-banner <start>--------*/
.without-banner {
  .dark-banner {
    .banner-content-wrapper {
      * {
        color: $black !important;
      }
      .banner-category-tag {
        a {
          color: $white !important;
        }
      }
    }
  }
}
.dark-banner {
  &.banner-component {
    margin-bottom: 5px;
  }
  .banner-content-wrapper {
    * {
      color: $white !important;
    }
    padding-top: 70px;
    padding-bottom: 12px;

    .content-banner-share {
      background-color: $white;
      display: inline-block;
      padding: 11px 20px;
      position: absolute;
      right: 15px;
      bottom: 15px;
      border-radius: 5px;
      left: 255px;
      width: 300px;
      
      * {
        color: $black !important;
      }
      .icon-downvote {
        color: $capital-red !important;
      }
      .share-component {
        margin-left: 10px;
      }
      @include mQ(1200px) {
        padding: 10px 15px;
        bottom: 15px;
      }
      @include mQ(1020px) {
        padding: 1px 11px 3px;
      }
      @include mQ(767px) {
        position: relative;
        //margin-right: 15px;
        //margin-left: 15px;
        margin-bottom: 40px;
        bottom: 0;
        left: 0;
      }
    }

    .banner-subtitle {
      font-family: $pfhandbookpro-light;
      letter-spacing: -0.1px;
    }
    @include mQ(1020px) {
      padding-top: 37px;
      padding-bottom: 12px;
      .banner-category-tag {
        margin-bottom: 13px;
      }
    }
    @include mQ(767px) {
      top: 147px;
    }
  }
  .banner-wrapper {
    min-height: 460px;
    &:after {
      @include linearGradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.2));
      height: 100%;
    }
    @include mQ(1020px) {
      min-height: 195px;
      height: inherit !important;
      &:after {
        display: none;
      }
    }
    @include mQ(767px) {
      min-height: 245px;
      top: -10px;
    }

    .banner-image {
      @include mQ(1020px) {
        position: relative;
        top: 0%;
        left: 50%;
        width: 100%;
        min-height: auto;
        min-width: auto;
        transform: translate(-50%, 0%);
      }
      @include mQ(767px) {
        position: absolute;
        top: 50%;
        left: 50%;
        width: auto;
        min-height: 100%;
        min-width: 100%;
        transform: translate(-50%, -50%);
      }
    }

    @include mQ(767px) {
      position: absolute;
      top: 122px;
    }
  }
  @include mQ(1020px, 768px) {
    .content-banner {
      margin-top: -70px;
      position: relative;
      display: inherit;
    }
  }

  @include mQ(1020px) {
    &.banner-component {
    }
  }
  @include mQ(767px) {
    .banner-subtitle {
      font-family: $pfhandbookpro-bold !important;
      margin-bottom: 10px;
    }
  }
  .banner-utility-wrapper {
    display: block;
    margin-bottom: 10px;
    @include mQ(767px) {
      display: none;
    }
  }
}
.without-banner {
  .top-navigation {
    ul {
      li {
        a {
          color: $black;
          &:hover,
          &:focus {
            color: $black;
            border-bottom: 1px solid $black;
          }
        }
        &::after {
          border-right-color: $black;
        }
      }
    }
  }
  .breadcrumb-component {
    margin-top: 16px;
  }
  .breadcrumb {
    margin-bottom: 9px;
  }
}
/*------dark-banner <end>--------*/

//Banner on Corp
.banner-component--v2 {
  margin-top: 0 !important;
  height: 100% !important;
  //Banner Title - Link
  .banner {
    &-content-wrapper {
      position: relative !important;
      float: left !important;
      width: 100% !important;
      padding-top: 19px;
      padding-bottom: 12px !important;
      top: 0 !important;
      @include for-md-desktop {
        padding-top: 20px;
      }
      @include for-tablet {
        padding-top: 12px;
      }
      @include for-phone {
        margin-top: 0;
      }
    }
    &-title {
      color: $black;
      font-size: $font24;
      letter-spacing: -0.44px;
      line-height: normal;
      font-family: $pfhandbookpro-regular;
      @include for-md-desktop {
        font-size: $font20;
        letter-spacing: -0.37px;
      }
      @include for-tablet {
        font-size: $font16;
        letter-spacing: -0.3px;
      }
    }
    &-category-tag {
      @include for-md-desktop {
        padding: 0px 8px 6px 8px;
      }
      @include for-tablet {
        padding: 0px 6px 2px 6px;
        font-size: $font8;
        letter-spacing: -0.2px;
        margin-bottom: 0 !important;
      }
      a {
        @include for-md-desktop {
          font-size: $font12;
          letter-spacing: -0.25px;
          line-height: normal;
        }
        @include for-tablet {
          font-size: $font8;
          line-height: normal;
        }
        @include for-phone() {
          font-size: $font9;
        }
      }
    }
  }
  //trending widget
  .trending-widget-banner-wrapper {
    margin-top: 0;
  }
  //article
  .article {
    margin-bottom: 0;
    @include for-tablet {
      flex-direction: column;
      padding: 0 0 10px 0;
    }
    @include for-phone {
      margin-bottom: 20px;
    }
    &-img {
      height: 259px;
      @include for-md-desktop {
        height: 228px;
      }
      @include for-tablet {
        height: 228px;
        width: 100%;
      }
      @include for-phone {
        height: 140px;
      }
    }
    &-desc {
      h3 {
        font-family: $pfhandbookpro-bold;
        font-size: $font30;
        line-height: $line-height-34;
        @include for-md-desktop {
          font-size: $font23;
          line-height: $line-height-29;
        }
      }
    }
    &-details {
      padding-left: 15px;
    }
    &-updates {
      margin-top: 22px;
    }
  }

  //trending article
  .trending-widget-banner-wrapper {
    .trending-item {
      width: calc(50% - 15px);
      margin-bottom: 69px;
      @include for-md-desktop {
        margin-bottom: 59px;
      }
      @include for-tablet {
        margin-bottom: 44px;
      }
      @include for-phone {
        width: calc(50% - 8px);
      }
      .article {
        &-details {
          padding: 10px 17px 10px 0;
          @include for-md-desktop {
            padding: 10px 17px 19px 0;
          }
          @include for-tablet {
            padding: 8px 17px 6px 0;
          }
        }
        &-desc {
          padding-top: 15px;
          h3 {
            @include for-md-desktop {
              font-size: $font12;
              line-height: $line-height-14;
            }
          }
        }
      }
    }
  }
  //cta-wrapper
  &__cta-wrapper {
    // display: flex;
    // flex-direction: row;
    // flex-wrap: wrap;
    p {
      font-size: $font24;
      line-height: normal;
      margin-left: 29px;
      display: inline-block;
      max-width: 330px;
      margin-bottom: 0;
      vertical-align: bottom;
      @include for-md-desktop {
        font-size: $font21;
        margin-left: 28px;
      }
      @include for-tablet {
        max-width: 227px;
        font-size: $font16;
      }
      @include for-phone {
        max-width: 50%;
        margin-left: 10px;
        margin-top: 0;
        display: block;
      }
    }

    @include for-phone { 
      display: flex;
      align-items: center;
      .big-cta {
        padding: 6px 39px 6px 10px !important;
        max-width: 132px;
        width: 100%;
      }
    }

  }
  //Big Cta
  .big-cta {
    padding: 6px 59px 6px 10px; 
    &__small-text {
      @include for-md-desktop {
        font-size: $font12;
        line-height: $line-height-20;
      }
      @include for-tablet {
        font-size: $font9;
        line-height: $line-height-16;
      }
    }
    &__large-text {
      font-size: $font20;
      line-height: $line-height-20;
      @include mQ(1399px) {
        font-size: $font18;
        line-height: $line-height-20;
      }
      @include for-tablet {
        font-size: $font14;
        line-height: $line-height-16;
      }
    }
    &:after {
      line-height: $line-height-20;      
      @include for-tablet {
        line-height: $line-height-16;
      }
    }    
  }
}
/*------Banner <end>--------*/
