.media-mentions-wrap {
    // display: flex;
    // justify-content: space-between;
    position: relative;
    display: inline-block;
    width: 100%;
    .ad-banner {
        max-width: 26.6%;
        width: 100%;
        position: relative;
        display: inline-block;
        float: right;
        @include for-tablet {
            display: none;
        }
    }
}

.media-mentions {
    padding: 4rem 3.2rem 4rem 0;
    max-width: 58.5%;
    display: inline-block;
    @include for-md-desktop {
        max-width: 55%;
    }
    @include for-tablet {
        max-width: 100%;
    }
    @include for-phone {
        padding-right: 0;
    }
    &__list {
        display: flex;
        margin-top: 3.6rem;
        @include for-phone {
            justify-content: space-between;
        }
    }
    &__item {
        margin-right: 3.2rem;
        text-align: center;
        max-width: 110px;
        overflow-wrap: break-word;
        @include for-phone {
            margin-right: 0;
        }
    }
    &__icon-wrap {
        border-radius: 50%;
        background: $media-men-icon-wrap;
        width: 8rem;
        height: 8rem;
        box-shadow: 0 0 3px 0 rgba($media-men-icon-shadow, 0.24);
        margin: 0 auto 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        @include for-phone {
            width: 5.6rem;
            height: 5.6rem;
        }
    }
    &__icon {
        font-size: $font30;
        line-height: $line-height-80;
        max-width: 3.2rem;
        @include for-phone {
            font-size: $font24;
            line-height: $line-height-56;
            max-width: 2.4rem;
        }
    }
    &__name {
        font-size: $font20;
        line-height: $line-height-1;
        @include for-phone {
            font-size: $font16;
        }
    }
    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}