/*
 * 1263 - Vertical Tabbed component
 *
 */

.dashboard {
    &__vertical-tab-main-wrapper {
        ul {
            padding-left: 0px;
            @include mQ(767px){
                overflow: visible;
            }
            li {
                position: relative;
                cursor: pointer;
                margin: 0;
                padding: 0;
                
                & > span,
                & > a {
                    font-family: $pfhandbookpro-medium;
                    font-size: $font14;
                    color: $raven-gray;
                    margin: 0px;
                    padding-top: 8px;
                    padding-bottom: 8px;
                    padding-left: 70px;
                    padding-right: 10px;
                    position: relative;
                    display: block; 
                    cursor: pointer;   
                    line-height: 22px;      
                    
                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
                        padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                        padding-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                        padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 6.0rem);
                        padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.0rem);
                        line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.2rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.4vw;
                        padding-top: 0.8vw;
                        padding-bottom: 0.8vw;
                        padding-left: 6.0vw;
                        padding-right: 1.0vw;
                        line-height: 2.2vw; 
                    }
                    // @include mQmin($xxl) {
                    //     font-size: 17px;
                    //     padding-top: 9px;
                    //     padding-bottom: 10px;
                    //     padding-left: 79px;
                    //     padding-right: 10px;
                    //     line-height: 26px;
                    // } 
                }
                & > a:hover,
                & > a:focus,
                & > a:active
                 {
                    border-bottom: 0 none;
                }                
                .data-info {
                    .icon-info {
                        &+span {
                            display: none;
                        }
                    }
                    &.active {
                        .icon-info {
                            width: 12px;
                            height: 12px;
                            position: absolute;
                            border: 1px solid #6a4742;
                            border-radius: 50%;
                            color: #6a4742;
                            margin-left: 5px;
                            margin-top: 4px;
                            z-index: 9;
                            @include fontScaling(width,1.2);
                            @include fontScaling(height,1.2);
                            @include fontScaling(border-width,1.2);
                            @include fontScaling(margin-left,0.5);
                            @include fontScaling(margin-top,0.4);

                            &:after {
                                content: "i";
                                font-style: normal;
                                font-size: 10px;
                                width: 12px;
                                height: 12px;
                                position: absolute;
                                top: -6px;
                                left: 4px;

                                @include fontScaling(font-size,1.0);
                                @include fontScaling(width,1.2);
                                @include fontScaling(height,1.2);
                                @include fontScaling(top,-0.6);
                                @include fontScaling(left,0.4);
                            }
                            &:hover {
                                &+span {
                                    display: block;
                                    position: absolute;
                                    font-size: 1.0rem;
                                    @include fontScaling(font-size,1.0);
                                    line-height: 14px;
                                    @include fontScaling(line-height,1.4);
                                    width: 100%;
                                    top: 10px;
                                    @include fontScaling(top,1.0);
                                    max-width: 82px;
                                    @include fontScaling(max-width,8.2);
                                    left: 75%;
                                    transform: translateX(-50%);
                                    text-align: center;
                                    border-radius: 0.3px;
                                    @include fontScaling(border-radius,0.3);
                                    box-shadow: 4px 4px 16px 0 rgba(0, 0, 0, 0.08);
                                    background-color: #ffffff;
                                    color: #000;
                                    z-index: 99;
                                    font-family: "pfhandbookpro-regular", sans-serif;
                                    padding: 3px;
                                    @include fontScaling(padding,0.3);
                                    white-space: break-spaces;
                                }
                            }
                        }
                    }
                }
                &.has-child {
                    &>span {
                        .icon {
                            position: absolute;
                            line-height: 22px;
                            font-size: $font7;                            
                            margin-top: 0;
                            margin-bottom: 0;
                            margin-left: 12px;
                            transform: rotate(-90deg);
                            
                            @media screen and (min-width: $custom-screen-max-width)  {
                                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font9});
                                line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font22});
                                margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                            }
                            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                font-size: 0.9vw;
                                line-height: 2.2vw;
                                margin-left: 1.2vw;
                            }
                            // @include mQmin($xxl) {
                            //     font-size: 8px;
                            //     line-height: 27px;
                            //     margin-left: 7px;
                            // }

                            @include mQ(991px) {
                                font-size: 7px;
                            }
                        }
                    }
                    &.is-lob > span {
                        .icon {
                            top: 9px;
                            transform: rotate(0deg);
                            right: 13px;
                            @include fontScaling(top,1.0);
                            @include fontScaling(right,0.6);
                        }
                    }
                    &.active {
                        &>span {
                            .icon {
                                margin-top: 0;
                                margin-bottom: 0;
                                margin-left: 7px;
                                transform: rotate(0deg);
                                @media screen and (min-width: $custom-screen-max-width) {
                                    margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font7});
                                }
                                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                    margin-left: 0.7vw;
                                }
                                // Color Theme
                                @include themify($themes) {
                                    color: themed("textColorForLeftMainTitle");
                                }
                            }
                        }
                        &.is-lob > span {
                            .icon {
                                top: 10px;
                                @include fontScaling(top,1.0);
                            }
                        }
                    }
                }                
                >a {
                    .icon {
                        position: absolute;
                        right: 20px;
                        top: 8px;
                        line-height: 22px;
                        font-size: 9px;
                        margin-top: 0;
                        margin-bottom: 0;
                        margin-left: 12px;

                        @media screen and (min-width: $custom-screen-max-width) {
                            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font9});
                            right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
                            top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8});
                            line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font22});
                            margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            font-size: 0.9vw;
                            right: 2.0vw;
                            top: 0.8vw;
                            line-height: 2.2vw;
                            margin-left: 1.2vw;
                        }

                        @include mQ(991px) {
                            font-size: 7px;
                        }
                    }
                }
                &.active {
                    &>span {
                        font-family: $pfhandbookpro-bold;
                        position: relative;
                        padding-right: 5px;
                        font-size: $font18;

                        @media screen and (min-width: $custom-screen-max-width) {
                            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
                            padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem);
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            font-size: 1.8vw;
                            padding-right: 0.5vw;
                        }
                        // @include mQmin($xxl) {
                        //     font-size: 21px;
                        //     padding-right: 7px;
                        // }

                        // Color Theme
                        @include themify($themes) {
                            //background-color: themed('backgroundColor');
                            color: themed("textColorForLeftMainTitle");
                        }
                    }
                    &:before {
                        // Color Theme
                        @include themify($themes) {
                            color: themed("textColorForLeftMainTitle");
                        }
                    }
                }
                &:before {
                    color: $raven-gray;
                    width: 20px;
                    height: auto;
                    font-size: $font20;
                    position: absolute;
                    left: 48px;
                    top: 7px;
                    z-index: 9;
                    text-align: center;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
                        width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
                        left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.8rem);
                        top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 2.0vw;
                        width: 2.0vw;
                        left: 4.8vw;
                        top: 0.7vw;
                    }
                }
                &:not(.icon) {
                    &:before {
                        content: "";
                    }
                    &>span {
                        &:after {
                            content: "" !important;
                        }
                    }
                }
                &.active.has-child {
                    &.level-1 {
                        ul {
                            display: block;
                        }
                    }
                }
                &.has-child {
                    &.level-1 {
                        // Level 1 Style
                        ul {
                            display: none;
                            position: relative;
                            margin-top: 0px;
                            padding-top: 0;
                            &:before {
                                border-left: solid 1px rgba($ford-gray, 0.3);
                                content: "";
                                display: block;
                                left: 40px;
                                position: absolute;
                                height: calc(100% - 10px);
                                top: 50%;
                                transform: translateY(-50%);
                                margin-top: 5px;

                                @media screen and (min-width: $custom-screen-max-width) {
                                    left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.0rem);
                                    border-left: solid calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem) rgba($ford-gray, 0.3);
                                    height: calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.0rem));
                                    margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem);
                                }
                                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                    left: 4.0vw;
                                    border-left: solid 0.1vw rgba($ford-gray, 0.3);
                                    height: calc(100% - 1.0vw);
                                    margin-top: 0.5vw;
                                }
                            }
                            li {
                                a {
                                    font-size: $font12;
                                    margin-left: 78px;
                                    padding-bottom: 5px;
                                    padding-top: 5px;
                                    font-family: $pfhandbookpro-regular;
                                    padding-right: 16px;
                                    color: $raven-gray;

                                    @media screen and (min-width: $custom-screen-max-width) {
                                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                                        margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 7.8rem);
                                        padding-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
                                        padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
                                        padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                                    }
                                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                        font-size: 1.2vw;
                                        margin-left: 7.8vw;
                                        padding-bottom: 0.5vw;
                                        padding-top: 0.5vw;
                                        padding-right: 1.6vw;
                                    }


                                    &:hover, &:focus {
                                        border: none;
                                    }
                                }
                                &>span {
                                    font-size: $font12;
                                    padding-left: 78px;
                                    padding-bottom: 5px;
                                    padding-top: 5px;
                                    font-family: $pfhandbookpro-regular;
                                    padding-right: 16px;
                                    word-break: break-word;
                                    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
                                        word-break: break-all;
                                    }

                                    @media screen and (min-width: $custom-screen-max-width) {
                                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                                        padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 7.8rem);
                                        padding-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
                                        padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
                                        padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                                    }
                                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                        font-size: 1.2vw;
                                        padding-left: 7.8vw;
                                        padding-bottom: 0.5vw;
                                        padding-top: 0.5vw;
                                        padding-right: 1.6vw;
                                    }

                                    .pill {
                                        padding: 1px 8px;
                                        border-radius: 12px;
                                        margin-left: -8px;
                                        display: inline-block;

                                        @media screen and (min-width: $custom-screen-max-width) {
                                            padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                                            border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                                            margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.8rem);
                                        }
                                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                            padding: 0.1vw 0.8vw;
                                            border-radius: 1.2vw;
                                            margin-left: -0.8vw;
                                        }

                                        @include themify($themes) {
                                            background-color: themed("brandPillBackgroundColor");
                                        }
                                    }
                                }                                
                                &:first-child {
                                    &>span {
                                        padding-top: 10px;
                                        @media screen and (min-width: $custom-screen-max-width) {
                                            padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
                                        }
                                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                            padding-top: 1.0vw;
                                        }
                                    }
                                }
                                &:last-child {
                                    &>span {
                                        padding-bottom: 2px;
                                        margin-bottom: 8px;
                                    }
                                }
                                &.active {
                                    &>span {
                                        font-family: $pfhandbookpro-bold;
                                        background-color: transparent;
                                        font-size: $font16;

                                        @media screen and (min-width: $custom-screen-max-width) {
                                            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                                        }
                                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                            font-size: 1.6vw;
                                        }
                                        // Color Theme
                                        @include themify($themes) {
                                            color: themed("textColorForLeftMainTitle");
                                        }
                                    }
                                }
                            }
                        }
                        // Level 2 Style
                        .level-2 {
                            &.active {
                                ul {
                                    display: block;
                                }
                            }
                            ul {
                                display: none;
                                &:before {
                                    display: none;
                                }
                                li {
                                    a {
                                        font-size: $font12;
                                        margin-left: 85px;
                                        padding-bottom: 5px;
                                        padding-top: 5px;
                                        font-family: $pfhandbookpro-regular;
                                        padding-right: 16px;
                                        color: $raven-gray;
                                        &:hover, &:focus {
                                            border: none;
                                        }
                                    }
                                    &>span {
                                        font-size: $font12;
                                        font-family: $pfhandbookpro-regular;
                                        padding-left: 85px;
                                        padding-bottom: 3px;
                                        padding-top: 5px;
                                        padding-right: 7px;
                                        @include mQ(991px) {
                                            font-size: $font18 !important;
                                            padding-top: 10px !important;
                                        }
                                    }
                                    &:first-child {
                                        &>span {
                                            padding-top: 5px;
                                        }
                                    }
                                    &:last-child {
                                        &>span {
                                            padding-bottom: 0px;
                                            margin-bottom: 4px;
                                        }
                                        &:after {
                                            border-bottom: solid 1px rgba($ford-gray, 0.1);
                                            content: "";
                                            width: calc(100% - 70px);
                                            height: 1px;
                                            display: block;
                                            margin-bottom: 5px;
                                            margin-top: 7px;
                                            position: relative;
                                            left: 30%;
                                            top: 0;
                                        }
                                    }
                                    &.active {
                                        &>span {
                                            font-size: $font14;
                                            font-family: $pfhandbookpro-bold;
                                            @include mQ(991px) {
                                                font-size: $font20 !important;
                                                padding-top: 10px;
                                            }
                                        }
                                    }                                    
                                }
                            }
                        }                        
                    }
                }
                &.is-external {
                    a {
                        padding-right: 40px;
                        @media screen and (min-width: $custom-screen-max-width) {
                            padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.0rem);
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            padding-right: 4.0vw;
                        }
                    }
                }
                // Tablet View Vertical Tab
                @include mQ(991px) {
                    display: inline-block;
                    margin-left: 17px;
                    margin-right: 17px;
                    white-space: nowrap;
                    &.level-1:first-child {
                        margin-left: 21px;
                    }
                    &.level-1:last-child {
                        padding-right: 21px;
                    }
                    &.active {
                        &>span {
                            background-color: transparent !important;
                            padding-left: 10px;
                            padding-right: 10px;
                            // Color Theme
                            @include themify($themes) {
                                border-bottom: themed( "verticalTabResponsiveBottomSelectedBorder");
                            }
                        }
                    }
                    &.has-child {
                        &>span {
                            .icon {
                                margin-top: 11px;
                                margin-left: 7px;
                                top: 11px;                                                                
                            }
                        }
                        &.level-1 {
                            .tab-back,
                            .icon-left-arrow-mob {
                                display: none;
                            }
                        }
                        &.active {
                            &>span {
                                .icon {
                                    margin-top: 11px;
                                    margin-left: 7px;
                                    transform: rotate(-90deg);
                                    @include for-phone {
                                        transform: rotate(0deg);
                                    }
                                }
                            }
                            &.level-1 {
                                display: flex;
                                position: absolute;
                                width: 100%;
                                z-index: 9;
                                align-items: flex-end;
                                overflow-x: auto;
                                // Color Theme
                                @include themify($themes) {
                                    background-color: themed( "backgroundColorWithOpacityForResponsive");
                                }
                                .tab-back,
                                .icon-left-arrow-mob {
                                    display: block;
                                    font-size: 15px;
                                    @include themify($themes) {
                                        color: themed( "textColorForLeftMainTitle");
                                    }
                                    position: relative;
                                    z-index: 101;
                                    padding-right: 25px;
                                    margin-bottom: 18px;
                                }
                                >ul {
                                    display: flex;
                                    align-items: flex-end;
                                    padding-left: 28px;
                                    margin-left: 10px;
                                    padding-bottom: 0;
                                    &:before {
                                        margin-top: 5px;
                                        height: 33px;
                                        left: 25px;
                                    }
                                    li {
                                        &>span {
                                            font-size: $font20;
                                            border-bottom: none;
                                            padding-top: 20.5px;
                                            padding-bottom: 23.5px;
                                            margin-bottom: 0;
                                            padding-left: 17px;
                                        }
                                        &:first-child {
                                            &>span {}
                                        }
                                    }
                                }
                                .level-2 {
                                    margin-left: 10px;
                                    margin-right: 10px;
                                    &:last-child {
                                        padding-right: 50px;
                                    }
                                    &.active {
                                        &>span {
                                            &:after {
                                                content: $icon-down-arrow-selected !important;
                                                display: block !important;
                                                -moz-osx-font-smoothing: grayscale;
                                                -webkit-font-smoothing: antialiased;
                                                font-family: "font-icons";
                                                font-style: normal;
                                                font-variant: normal;
                                                font-weight: normal;
                                                line-height: 1;
                                                speak: none;
                                                text-transform: none;
                                                position: absolute;
                                                left: 50%;
                                                transform: translateX(-50%);
                                                font-size: 10px;
                                                top: 45px;
                                            }
                                        }
                                        .icon-info {
                                            display: none;
                                        }
                                        ul {
                                            position: fixed;
                                            height: 48px;
                                            display: flex;
                                            align-items: center;
                                            width: 100vw;
                                            top: 66px;
                                            left: 0;
                                            z-index: 99;
                                            // Color Theme
                                            @include themify($themes) {
                                                background-color: themed( "backgroundColorWithOpacityForLeftVerticalResponsive");
                                            }
                                        }
                                    }
                                    ul {
                                        li {
                                            margin-left: 20px;
                                            margin-right: 5px;
                                            &.level-3 {
                                                & > span {
                                                    padding-top: 13px !important;
                                                    padding-bottom: 13px;
                                                    margin-bottom: 0;
                                                }                                        
                                            }
                                            &:last-child {
                                                &:after {
                                                    display: none;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    &>span,
                    &>a {
                        padding-left: 10px;
                        padding-right: 10px;
                        font-size: $font20;
                        padding-bottom: 13px;
                        border-bottom: 3px solid transparent;
                        @include mQ(991px) {
                            padding-top: 20.5px;
                            padding-bottom: 20.5px;
                            font-family: $pfhandbookpro-regular;
                            line-height: 22px;
                        } 
                        &:after {
                            display: none !important;
                        }
                    }
                    &:before {
                        display: none;
                    }
                    &.is-external {
                        a {
                            padding-right: 10px;
                        }
                    }
                }
            }
            // Only for First level LI Style
            &>li {
                &.active {
                    &>span {
                        // Color Theme
                        @include themify($themes) {
                            background-color: themed("backgroundColorWithOpacity");
                        }
                    }
                    &:before {
                        top: 7px;
                        @media screen and (min-width: $custom-screen-max-width) {
                            top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem);
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            top: 0.7vw;
                        }
                    }
                    &.level-1 {
                        &>span:after {
                            transform: translateY(-50%) rotate(45deg);
                            width: 8px;
                            height: 8px;
                            top: 50%;
                            right: -3px;
                            position: absolute;
                            border-radius: 2px;
                            content: "";
                            display: block;
                            z-index: 9;
                            // Color Theme
                            @include themify($themes) {
                                background-color: themed("backgroundColorWithOpacityArrow");
                            }

                            @media screen and (min-width: $custom-screen-max-width) {
                                width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8});
                                height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8});
                                right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.3rem);
                                border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem);
                            }
                            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                width: 0.8vw;
                                height: 0.8vw;
                                right: -0.3vw;
                                border-radius: 0.2vw;
                            }
                        }                        
                    }
                }
                &.level-1 {
                    &:not(.active) {
                        & > span {
                            .pill {
                                padding: 1px 30px 2px 50px;
                                border-radius: 12px;
                                margin-left: -50px;
                                display: inline-block;
                                margin-top: -1px;
                                margin-bottom: -2px;

                                @media screen and (min-width: $custom-screen-max-width) {
                                    padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.0rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 5.0rem);
                                    border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                                    margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -5.0rem);
                                    margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.1rem);
                                    margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem);
                                }
                                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                    padding: 0.1vw 3.0vw 0.2vw 5.0vw;
                                    border-radius: 1.2vw;
                                    margin-left: -5.0vw;
                                    margin-top: -0.1vw;
                                    margin-bottom: -0.2vw;
                                }

                                @include themify($themes) {
                                    background-color: themed("brandPillBackgroundColor");
                                    color: themed("textColorForLeftMainTitle");                                    
                                }
                            }
                        }
                    }
                }
            }
            
            @include mQ(991px) {                
                display: flex;
                margin-bottom: 0;
                height: 66px;
                align-items: flex-end;
                & > li {                    
                    &.level-1 {
                        &:not(.active),
                        &.active {
                            & > span {
                                .pill {
                                    padding: 5px 15px;
                                    border-radius: 25px;
                                    margin-left: 0;
                                    margin: -5px -15px;
                                    @include themify($themes) {
                                        background-color: themed("brandPillBackgroundColor");
                                        color: themed("textColorForLeftMainTitle");                                    
                                    }
                                }
                            }                        
                        }
                        & > a {
                            .icon {
                                display: none;
                            }
                        }
                    }
                }
                &.is-type-lob {
                    li {
                        &.has-child {          
                            &.active {
                                &.level-1 {
                                    //position: relative;
                                    position: unset;
                                    width: auto;
                                    flex-shrink: 0; 
                                    @include mQ(575px){
                                        position: unset;
                                    }  
                                    @include mQ(767px){
                                        position: unset;
                                    }                                  
                                    & > ul {
                                        //position: fixed;
                                        position: absolute;
                                        top: 66px;
                                        left: 0;
                                        padding-left: 0;
                                        margin-left: 0;
                                        padding-bottom: 0;
                                        height: auto;
                                        width: 100%;
                                        overflow-x: auto;
                                        &:before {
                                            display: none;
                                        }                                      
                                        @include themify($themes) {
                                            background-color: themed("backgroundColorWithOpacityForLeftVerticalResponsive");
                                        }
                                        li.level-2 {
                                            & > span {
                                                line-height: 35px;
                                                font-size: 20px;
                                                font-family: $pfhandbookpro-medium;
                                                padding-top: 6px;
                                                padding-bottom: 6px;
                                                &:after {
                                                    display: none !important;
                                                }
                                                .pill {
                                                    padding: 0 20px;                                                    
                                                    border-radius: 25px;
                                                }
                                            }
                                            &.active {
                                                & > span {
                                                    font-family: $pfhandbookpro-bold;                                                                                                        
                                                }
                                            }
                                        }
                                    }
                                    & > span {
                                        font-family: $pfhandbookpro-bold;

                                    }
                                    .tab-back {
                                        display: none;
                                    }
                                }
                            }
                        }
                        &.level-1 {                            
                            & > span,
                            & > a {
                                padding-top: 20.5px;
                                padding-bottom: 20.5px;
                                font-family: $pfhandbookpro-regular;
                                line-height: 22px;
                                .icon {
                                    display: none;
                                }
                            }                            
                        }
                        
                    }
                }

            }
            @include mQ(575px) {
                height: 42px;
                & > li {
                    margin-left: 7px;
                    margin-right: 7px;
                    &.level-1 {
                        &:not(.active),
                        &.active {
                            & > span,
                            & > a {
                                line-height: 22px;
                                font-size: 13px;
                                padding: 8.5px 10px;
                                
                                .pill {
                                    padding: 0 9px;
                                    border-radius: 11px;
                                    margin: 0;
                                }
                                .icon {
                                    top: -1px;
                                }
                            }                        
                        }
                        & > a {
                            .icon {
                                display: none;
                            }
                        }                        
                    }
                    &.has-child {                        
                        &.active {                            
                            &.level-1 {  
                                .icon-left-arrow-mob{
                                    margin-bottom: 11px;
                                    padding-right: 10px;
                                }
                                >ul {  
                                    margin-left: 0;
                                    &:before {
                                        margin-top: 0;
                                        height: 27px;                                        
                                    }                                  
                                    li {
                                        &>span {
                                            font-size: $font13;
                                            padding: 8.5px 10px;
                                            border-bottom: 3px solid $transparent;
                                            @include mQ(575px) {
                                                border-bottom: 0;
                                            }
                                        }                                        
                                        &.level-2 {
                                            &.active {
                                                & > span:after {
                                                    font-size: 7px;
                                                    top: 28px;
                                                }
                                                ul {
                                                    top: 42px;
                                                    height: 30px;
                                                    li {
                                                        &.level-3 {
                                                            margin-left: 5px;
                                                            margin-right: 5px;
                                                            & > span {
                                                                font-size: $font13 !important;
                                                                padding-top: 4px !important;
                                                                padding-bottom: 4px;
                                                                border-bottom: 0 none;
                                                            }
                                                        }
                                                        &:first-child {
                                                            margin-left: 20px;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.is-type-lob {
                    height: 42px;
                    li {   
                        &.has-child {          
                            &.active {
                                &.level-1 {
                                    & > ul {
                                        top: 42px;
                                        padding-left: 20px;
                                        li.level-2 {
                                            margin-left: 0;
                                            margin-right: 0; 
                                            &:last-child {
                                                padding-right: 0;
                                            }                                           
                                            & > span {
                                                line-height: 22px;
                                                font-size: 11px;
                                                padding: 4px 11px;                                                
                                                .pill {
                                                    padding: 0 13px;                                                    
                                                    border-radius: 11px;
                                                }
                                            }                                            
                                        }
                                    }                                    
                                }
                            }
                        }                     
                        &.level-1 {
                            margin-right: 0;                         
                            & > span,
                            & > a {
                                padding-top: 8.5px;
                                padding-bottom: 8.5px;
                                font-size: 13px;                                
                            }                            
                        }
                    }
                }
            }
        }
        @include mQ(991px) {
            overflow-x: hidden;
            transition: all 0.2s;
            will-change: transform;
            user-select: none;
            -ms-overflow-style: none;
            //height: 75px;

            >ul {
                overflow-x: auto;
                @include mQ(576px){
                    overflow: visible;
                position: relative;
                }
                
                // Color Theme
                @include themify($themes) {
                    background-color: themed("backgroundColorWithOpacityForResponsive");
                }
            }
        }
        .level1-mobile-scroll-cta {
            position: absolute;            
            width: 24px;
            height: 24px;
            margin: 0;
            padding: 0;
            border: 0 none;
            color: $white;
            border-radius: 50%;
            top: 14px;
            z-index: 11;
            text-align: center;
            @include themify($themes) {
                background-color: themed('backgroundColor');
            }
            @include mQ(991px) {
                top: 20px;
            }
            @include mQ(575px) {
                top: 8px;
            }
            .icon {
                display: block;
                font-size: 12px;
                line-height: 22px;
            }
            &.scroll-left {
                left: -12px;
                .icon {
                    margin: 1px 1px 0 0;
                }
    
                @include mQ(991px) {
                    left: 0;
                }
            }
            &.scroll-right {
                right: -12px;
                .icon {
                    margin: 1px 0 0 1px;
                }
                @include mQ(991px) {
                    right: 0;
                }
            }
        }
    }
}

.dashboard__bottom-wrapper[data-id="personal-finance"] .dashboard__vertical-tab-main-wrapper ul {
    display: none;
}

.lOBQuickLinks ul.link-list__list--column li:nth-child(odd) {
    @include for-phone{
        clear: both;
    }
}
.dashboard__bottom-wrapper[data-id="insurance-advisory"] .dashboard__vertical-tab-main-wrapper, .dashboard__bottom-wrapper[data-id="motor-insurance"] .dashboard__vertical-tab-main-wrapper {
    @include mQ(991px){
        height: 115px;
    }
    @include mQ(575px){
        height: 72px;
    }
}
.dashboard__bottom-wrapper[data-id="insurance-advisory"] .dashboard__vertical-tab-main-wrapper ul.is-type-lob li.has-child.active.level-1 > ul, .dashboard__bottom-wrapper[data-id="motor-insurance"] .dashboard__vertical-tab-main-wrapper ul.is-type-lob li.has-child.active.level-1 > ul {
    @include for-phone{
        position: absolute;
        top: 66px;
    }
    @include mQ(575px){
        position: absolute;
        top: 42px;
    }
} 
