.product-card {
    border-radius: 2px;
    box-shadow: 0 0 14px 0 rgba($pro-card-shadow, 0.24);
    border: solid 1px $pro-card-border;
    position: relative;
    &--ad {
        max-height: 45rem;
    }
    &__top {
        padding: 4.8rem 2rem 1rem;
    }
    &__mid {
        padding: 1.6rem 2rem;
        height: 166px;
        overflow: hidden;
    }
    &__bottom {
        padding: 0 2rem 1.6rem;
        min-height: 13.4rem;
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
        .button {
            width: 100%;
            position: relative;
            z-index: 1;
            &:last-child {
                margin-top: 2rem;
                @include for-phone {
                    margin-top: 1rem;
                }
            }
        }
    }
    &__title {
        font-size: $font24;
        line-height: $line-height-26;
        color: $term-card-title;
        text-transform: capitalize;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__desc {
        font-size: $font20;
        line-height: $line-height-22;
        color: $term-card-title;
        margin: 0;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__tag {
        position: absolute;
        top: 1.5rem;
        left: 0;
        background: $card-tag-bg;
        color: $card-tag-color;
        font-size: $font14;
        line-height: $line-height-14;
        padding: 0 1rem 0 2.4rem;
        display: flex;
        align-items: center;
        height: 2.3rem;
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-12;
            height: 2rem;
        }
    }
    &__icon {
        font-size: $font16;
        line-height: $line-height-16;
        margin: 0 0.6rem 0 0;
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-12;
        }
    }
    &__punch-line {
        font-size: $font20;
        line-height: $line-height-1;
        margin-bottom: 2rem;
        @include for-phone {
            margin-bottom: 1.2rem;
        }
    }
    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}

.features {
    display: flex;
    align-items: flex-start;
    padding: 0;
    margin: 0;
    justify-content: space-evenly;
    &__item {
        // width: 33%;
        padding-right: 1rem;
    }
    &__text {
        color: $card-feature;
        font-size: $font18;
        line-height: $line-height-20;
        display: block;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__icon {
        font-size: $font20;
        display: block;
        max-width: 3.6rem;
        @include for-phone {
            font-size: $font18;
            max-width: 3.2rem;
        }
    }
}

.term-products {
    .slick-slide {
        margin: 0 1.6rem;
    }
    .slick-list {
        margin: 0 -1.6px;
        @include for-tablet {
            padding: 0 10% 0 0;
            margin: 0;
        }
    }
    @include for-md-desktop {
        .slick-nav--product {
            display: none;
        }
    }
}

.product-list--calc {
    .slick-track {
        margin-left: 0;
        margin-right: 0;
    }
}