.wb-login-wrap {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    .ad-banner {
        max-width: 45rem;
        @include for-tablet {
            display: none;
        }
    }
}

.wb-login {
    flex-grow: 1;
    max-width: calc(100% - 45rem);
    padding-right: 7rem;
    @include for-tablet {
        max-width: 100%;
        padding-right: 0;
        margin-bottom: 2rem;
    }
    &__box {
        border-radius: 0.4rem;
        box-shadow: 0 0 1.4rem 0 rgba($wb-login-box-shadow, 0.24);
        border: 0.1rem solid $wb-login-box-border;
        background-color: $wb-login-box-bg;
        max-width: 70rem;
        min-height: 40rem;
        position: relative;
    }
    &__head {
        padding: 2.4rem;
        background-color: $wb-login-box-head-bg;
        color: $wb-login-box-head-color;
    }
    &__body {
        padding: 2.4rem;
    }
    .button-link {
        padding-left: 0;
    }
    .btn-wrap {
        position: absolute;
        bottom: 2.4rem;
        right: 2.4rem;
        @include mQ(1024px) {
            position: static;
        }
    }
    .abc-form {
        .form-group {
            &--otp {
                flex-wrap: wrap;
                display: flex;
                align-items: flex-end;
                .form-group__label {
                    width: 100%;
                }
                .input-group {
                    width: auto;
                    margin-right: 5rem;
                    @include for-md-desktop {
                        margin: 0 0 1rem 0;
                    }
                    @include for-tablet {
                        margin: 0 1rem 0 0;
                    }
                    @include for-phone {
                        margin: 0 0 1rem 0;
                    }
                }
                .otp-timer {
                    font-size: $font18;
                    line-height: $line-height-28;
                }
            }
        }
        .form-control {
            &--otp {
                max-width: 5rem;
                line-height: 5rem;
                height: 5rem;
                margin-right: 1rem;
                text-align: center;
                font-size: $font16;
            }
        }
    }
}