//benefits-ulip
.benefits-ulip {
    &__title-wrap {
        margin-bottom: 3.5rem;
    }
    &__list {
        max-width: inherit;
    }
    &__desc {
        margin-bottom: 1rem;
    }
    &__item{
        padding-bottom: 2.5rem;
        margin-bottom: 2.5rem;
        border-bottom: 1px solid $benefit-border;
        &:last-child{
            padding-bottom: 2.5rem;
        margin-bottom: 2.5rem;
            border-bottom: 1px solid $benefit-border;
        }
        @include for-phone{
            padding-bottom: 1.6rem;
            margin-bottom: 1.6rem;
            &:last-child{
                margin-bottom: 3rem;
                padding-bottom: 1.6rem;
                border-bottom: 1px solid $benefit-border;
            }
        }
        &:nth-child(-n + 3) {
            display: block;
            &.benefits--mobile-v2{
                @include for-phone{
                    display: flex;
                }
            }
        }
        
    }
    &__button-link{
        padding-left: 0;
    }
}