.feedback-wrap {
    width: 100%;
    .login-success {
        display: none;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 2px;
        box-shadow: 0 0 14px 0 rgba($success-pop-shadow, 0.24);
        border: solid 1px $success-pop-border;
        background-color: $success-pop-bg;
        width: 100%;
        max-width: 32.8rem;
        text-align: center;
        padding: 2.4rem;
        z-index: 10003;
        &--active {
            display: block;
        }
        &__icon {
            font-size: $font64;
            line-height: $line-height-1;
            color: $success-icon;
        }
        &__title {
            font-size: $font32;
            line-height: $line-height-1;
        }
        &__msg {
            font-size: $font24;
            line-height: $line-height-26;
            font-family: $pfhandbookpro-thin;
        }
    }
    &__overlay {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: $video-overlay;
        z-index: 10002;
        display: none;
        &__close {
            position: absolute;
            right: 2rem;
            top: 2rem;
        }
        &__icon {
            font-size: $font20;
            color: $video-close-icon;
        }
    }
    .tabs {
        &__anchor {
            &--v2 {
                color: $feedback-tab-link-v2;
                background: $feedback-tab-link-v2-bg;
                &:hover,
                &:active,
                &:focus {
                    color: $feedback-tab-link-v2;
                }
            }
        }
        &__link {
            &--v2 {
                border-bottom: 1px solid $feedback-tab-link-border;
            }
            &--active {
                .tabs__anchor {
                    &--v2 {
                        color: $feedback-tab-link-protect;
                        background: $feedback-tab-link-v2-bg;
                        border-bottom: 2px solid $feedback-tab-link-protect;
                        &:hover,
                        &:active,
                        &:focus {
                            color: $feedback-tab-link-protect;
                        }
                    }
                }
            }
        }
    }
}