//video-blog
.video-blog {
    width: 100%;
    &--v2 {
        display: flex;
        @include for-tablet {
            flex-wrap: wrap;
        }
        .video-blog__icon {
            @include for-tablet {
                font-size: $font60;
            }
        }
        .video-blog__desc {
            height: auto;
        }
    }
    &__img-wrap {
        margin-bottom: 2.4rem;
        position: relative;
        width: 100%;
        &--novideo {
            .video-blog__icon {
                display: none;
            }
        }
        &:hover {
            .video-blog__icon {
                color: $video-icon-hover;
            }
        }
        @include for-tablet {
            margin-bottom: 1.5rem;
        }
        &--v2 {
            margin-bottom: 0;
            width: 50%;
            @include for-tablet {
                width: 100%;
                .video-blog__iframe {
                    height: 36rem;
                }
            }
        }
    }
    &__iframe {
        width: 100%;
        height: 36rem;
        display: none;
        @include for-tablet {
            height: 24rem;
        }
    }
    &__icon {
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        top: 50%;
        transform: translateY(-50%);
        color: $video-icon;
        text-align: center;
        font-size: $font64;
        @include for-tablet {
            font-size: $font32;
        }
    }
    &__img {
        width: 100%;
    }
    &__title {
        a {
            font-size: $font32;
            line-height: $line-height-34;
            color: $video-title;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            &:hover {
                color: $video-title-hover;
            }
            @include for-tablet {
                font-size: $font22;
                line-height: $line-height-24;
            }
        }
        &--v2 {
            width: 70%;
            a {
                font-size: $font44;
                line-height: $line-height-44;
                white-space: normal;
                @include for-tablet {
                    font-size: $font32;
                    line-height: $line-height-32;
                }
            }
            @include for-tablet {
                width: 100%;
            }
        }
    }
    &__desc {
        height: 5.6rem;
        p {
            font-size: $font22;
            line-height: $line-height-28;
            font-family: $pfhandbookpro-thin;
            color: $video-title;
            @include for-tablet {
                font-size: $font18;
                line-height: $line-height-20;
            }
        }
        @include for-tablet {
            margin-bottom: 2rem;
            height: 4rem;
        }
        &--v2 {
            p {
                font-size: $font24;
                line-height: $line-height-26;
                @include for-tablet {
                    font-size: $font18;
                    line-height: $line-height-20;
                }
            }
            @include for-tablet {
                margin-bottom: 2rem;
            }
        }
    }
    &__link {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }
    &__info-wrap {
        &--v2 {
            width: 50%;
            padding-right: 3.2rem;
            @include for-tablet {
                padding-right: 0;
                width: 100%;
            }
        }
    }
}

.video-blog-wrap {
    &--v2 {
        padding: 3rem 0;
        @include for-tablet {
            padding: 2rem 0;
        }
    }
    .media-tile__likes {
        position: relative;
        right: auto;
        bottom: auto;
        margin-top: 2.4rem;
        @include for-tablet {
            margin: 1.5rem 0;
        }
    }
}