//disclaimer
.disclaimer {
    &__info,
    &__label {
        font-size: $font14;
        line-height: $line-height-16;
        color: $disclaimer-info-clr;
        font-family: $pfencoresanspro-book;
    }
    &__info {
        margin-bottom: 1.5rem;
    }
    &__label {
        text-transform: uppercase;
    }
    &__title-wrap {
        margin-bottom: 1rem;
    }
}