.blog-form-wrapper {
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.13);
  border: solid 1px #979797;
  background-color: $white;
  padding: 18px 20px;
  position: relative;
  .blog-form-text {
    padding-bottom: 15px;
    p {
      font-size: 20px;
      line-height: 29px;
      font-family: $pfhandbookpro-medium;
      color: $black;
      margin-bottom: 10px;
    }
    .brand-highlight {
      color: $capital-red;
    }
  }

  .blog-form-failure-message {
    text-align: center;
    display: none;
    p {
      color: $error-code;
      margin-bottom: 10px;
    }
  }

  #blogPost {
    p {
      margin-bottom: 10px;
    }
    .btn-wrapper {
      display: block;
      padding-top: 5px;
      padding-bottom: 25px;
      @include mQ(1020px) {
        display: none;
      }
    }
    label {
      display: block;

      opacity: 0.7;
      margin-bottom: 8px;
    }
    input {
      padding: 10px 15px 12px;
      border-radius: 4px;
      border: solid 1px #cdcece;
      width: 100%;
      color: $black;
      &::-webkit-input-placeholder {
        /* Edge */
        opacity: 0.25;
        color: $black;
      }
      @include for-phone {
        padding: 7px 10px 7px;
        font-size: $font14;
      }

      &:-ms-input-placeholder {
        /* Internet Explorer */
        opacity: 0.25;
        color: $black;
      }

      &::placeholder {
        opacity: 0.25;
        color: $black;
      }
      &.error {
        &::-webkit-input-placeholder {
          /* Edge */
          opacity: 1;
          color: $error-code;
        }

        &:-ms-input-placeholder {
          /* Internet Explorer */
          opacity: 1;
          color: $error-code;
        }

        &::placeholder {
          opacity: 1;
          color: $error-code;
        }
      }
    }
    .cityfield,
    .statefield {
      display: inline-block;
      width: 44.5%;
      margin-bottom: 25px;
      input {
        //opacity: 0.25;
      }
      @include mQ(1020px) {
        display: none;
      }
    }
    .statefield {
      float: right;
    }
    .form-btn {
      background-color: $capital-red;
      color: $white;
      -webkit-appearance: none;
      font-family: $pfhandbookpro-bold;
      text-transform: uppercase;
      font-size: 20px;
      border: 0;
      border-radius: 2px;
      padding: 12px 32px;
      &.disabled {
        background-color: #9dafbd;
      }
      @include for-phone {
        font-size: 16px;
        padding: 8px 27px;
      }
    }
    .fieldgroup {
      // @include mQ(1020px) {
      //   display: none;
      // }
      textarea {
        padding: 10px 15px 12px;
        resize: none;
        height: 65px;
        width: 100%;
        border: solid 1px #cdcece;
        border-radius: 4px;
        &::-webkit-input-placeholder {
          /* Edge */
          opacity: 0.25;
          color: $black;
        }

        &:-ms-input-placeholder {
          /* Internet Explorer */
          opacity: 0.25;
          color: $black;
        }

        &::placeholder {
          opacity: 0.25;
          color: $black;
        }
        &.error {
          &::-webkit-input-placeholder {
            /* Edge */
            opacity: 1;
            color: $error-code;
          }

          &:-ms-input-placeholder {
            /* Internet Explorer */
            opacity: 1;
            color: $error-code;
          }

          &::placeholder {
            opacity: 1;
            color: $error-code;
          }
        }
      }
      span.error {
        background: transparent !important;
        border: none !important;
        font-size: 12.6px;
        font-family: $pfencoresanspro-book;
        color: $capital-red;
      }
      margin-bottom: 20px;
      @include for-phone {
        margin-bottom: 10px;
      }
      &.otp-field {
        position: relative;
        .otp-txt {
          position: absolute;
          display: none;
          right: 30px;
          width: 215.1px;
          padding: 7px 9px;
          font-size: 16px;
          line-height: 20px;
          color: $black;
          box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.16);
          border: solid 1px #dfdfdf;
          background-color: $white;
          border-radius: 3px;
          margin-right: 6px;
          top: -9px;
          z-index: 9;
          &:after,
          &:before {
            // content: '';
            // top: 0;
            // z-index: 3;
            // right: -8px;

            // position: absolute;
            // height: 0;
            // border-top: 8px solid transparent;
            // border-bottom: 8px solid transparent;
            // border-left: 8px solid $white;
            content: "";
            display: block;
            position: absolute;
            left: 100%;
            width: 0;
            height: 0;
            border-style: solid;
          }
          &:after {
            top: 11px;
            border-color: transparent transparent transparent #fff;
            border-width: 7px;
          }
          &:before {
            top: 10px;
            border-color: transparent transparent transparent #dfdfdf;
            border-width: 8px;
          }
        }
        .otp-label {
          width: auto;
          display: inline-block;
        }
        .icon-help {
          float: right;
          font-size: 8px;
          border: 1px solid #cdcece;
          border-radius: 50%;
          padding: 5px 5px 3px 5px;
          line-height: normal;
          margin: 0;
          color: $black;
          cursor: pointer;
        }
      }
      &.phone-field {
        position: relative;
        input {
          width: 73%;
          display: inline-block;
          float: left;
          border-right: 0 !important;
          border-right-style: none !important;
          border-top-right-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
        }
        .otp-button {
          @include mQ(1120px) {
            padding: 12px 3px;
          }
          @include for-phone {
            padding: 8px 3px;
            font-size: $font14;
          }
          cursor: pointer;
          background-color: $capital-red;
          padding: 12px 8px;
          float: right;
          text-align: center;
          color: $white;
          width: 27%;
          border-radius: 4px;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          border: 0 none;
          &.disabled {
            background-color: #9dafbd;
          }
        }
      }
      &.terms-conditions {
        height: auto !important;
        margin-bottom: 7px;

        p {
          font-family: $pfhandbookpro-medium;
          color: $black;
          margin-bottom: 0;
          @include for-phone {
            font-size: $font14;
          }
        }
        a {
          text-decoration: underline !important;
          border-bottom: none !important;
          font-family: $pfhandbookpro-medium;
          @include for-phone {
            font-size: $font14;
          }
        }
        input:checked ~ .checkbox-label:before {
          border: 2px solid #000;
        }
        input:checked ~ .checkbox-label:after {
          z-index: 1;
          content: "";
          position: absolute;
          left: 7px;
          top: 2px;
          width: 6px;
          height: 9px;
          border-width: 0 2px 2px 0 !important;
          -ms-transform: rotate(45deg);
          transform: rotate(45deg);
          border: solid #c7222a;
          @include for-phone {
            left: 6px;
            top: 5px;
            width: 5px;
            height: 8px;
          }
        }
        .checkbox-label {
          position: relative;
          padding-left: 35px;
          display: block;
          @include for-phone {
            padding-left: 25px;
          }
          &:before {
            content: "";
            top: -2px;
            display: block;
            height: 20px;
            left: 0px;
            position: absolute;
            width: 20px;
            z-index: 1;
            border: solid 2px #cdcece;
            border-radius: 3px;
            @include for-phone {
              top: 2px;
              height: 16px;
              width: 16px;
            }
          }
        }
        input {
          margin-right: 15px;
          float: left;
          height: 20px;
          width: 20px;
          z-index: 10;
          position: absolute;
          opacity: 0;
          cursor: pointer;
        }
      }
      &.zip-field {
        input {
          width: 73%;
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
        .pin-search-button {
          background-color: $capital-red;
          cursor: pointer;
          padding: 12px 8px;
          float: right;
          text-align: center;
          color: $white;
          border-radius: 4px;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          width: 27%;
          border: 0 none;
          &.disabled {
            background-color: #9dafbd;
          }
          @include for-phone {
            padding: 8px 8px;
            font-size: $font14;
          }
        }
      }
      &.otp-error {
        //OTP Error
        #otp-error {
          color: $capital-red;
          //color: $battelship-grey;
          margin-top: 5px;
          display: block;
          position: relative;
          //padding-left: 27px;
          // &:before {
          //   position: absolute;
          //   left: 0;
          //   display: inline-block;
          //   content: "i";
          //   width: 20px;
          //   height: 20px;
          //   color: $white;
          //   font-family: $pfhandbookpro-black;
          //   background: $orange-yellow;
          //   border-radius: 50%;
          //   text-align: center;
          //   font-size: 15px;
          // }
        }
      }
    }
    .mandatory-div {
      @include mQ(1020px) {
        display: none;
      }
      p {
        font-family: $pfhandbookpro-lightitalic;
        color: $black;
      }
    }
  }
  .submit-btn {
    background-color: $capital-red;
    color: $white;
    -webkit-appearance: none;
    font-family: $pfhandbookpro-bold;
    text-transform: uppercase;
    font-size: 20px;
    border: 0;
    padding: 12px 32px;
    display: none;
    cursor: pointer;
    @include mQ(1020px) {
      display: block;
    }
  }
  .thankyou-msg {
    position: absolute;
    top: 50%;
    display: none;
    left: 50%;
    width: 70%;
    margin-left: -35%;
    transform: translate(0%, -50%);
  }
  .thankyou-txt {
    font-size: 20px;
    line-height: 25px;
    font-family: $pfhandbookpro-medium;
    color: $black;
    text-align: center;
  }
  @include for-phone {
    &.thankyou-msg-wrapper {
      height: 350px;
    }
  } 
}
.lead-form-title {
  display: none;
}
//Blog Form as card
.blog-post-expanded {
  .lead-form-title--expaned {
    display: block;
  }
  #blogPost {
    .fieldgroup,
    .btn-wrapper,
    .mandatory-div {
      display: block;
    }
    .cityfield,
    .statefield {
      @include for-tablet {
        display: inline-block;
      }
    }
  }

  #submit-click {
    display: none;
  }

  &.blog-post-full-width-expanded {
    .blog-form-text {
      padding-left: 5px;
      padding-right: 5px;
      @include mQ(767px) {
        padding-left: 0px;
        padding-right: 0px;
      }
    }
    #blogPost {
      margin-left: -20px;
      margin-right: -20px;
      @include mQ(767px) {
        margin-left: 0px;
        margin-right: 0px;
      }
      .fieldgroup {
        float: left;
        width: calc(50% - 50px);
        margin-left: 25px;
        margin-right: 25px;
        height: 50px;
        @include mQ(767px) {
          width: 100%;
          margin-left: 0;
          margin-right: 0;
          height: auto !important;
        }
        &.captchafield {
          height: 120px;
        }
        &.captchafield,
        &.clearfield {
          clear: both;
        }
      }
      .btn-wrapper {
        float: left;
        width: calc(50% - 50px);
        margin-left: 25px;
        margin-right: 25px;
        @include mQ(767px) {
          width: 100%;
          margin-left: 0;
          margin-right: 0;
          height: auto !important;
        }
      }

      .mandatory-div {
        margin-top: 5px;
        p {
          font-family: "pfhandbookpro-lightitalic", sans-serif;
        }
      }
      .blog-checkbox,
      .blog-radio {
        width: calc(100% - 50px);
        margin-bottom: 10px;
      }
      .cityfield,
      .statefield {
        @include for-tablet {
          //display: inline-block;
        }
      }
      @include for-phone {
        .cityfield,
        .statefield {
          width: calc(50% - 9px);
        }
        .cityfield {
          margin-left: 9px;
        }
        .statefield {
          margin-right: 9px;
        }
      }
    }
  }
}
.blog-post-collapsed {
  .lead-form-title--collapsed {
    display: block;
  }
  #blogPost {
    .fieldgroup,
    .cityfield,
    .statefield,
    .btn-wrapper,
    .mandatory-div {
      display: none;
    }
  }

  #submit-click {
    display: block;
  }
}

.blog-post-expanded--mobile {
  .lead-form-title--expanded {
    @include for-tablet {
      display: block;
    }
  }
  #blogPost {
    .fieldgroup,
    .btn-wrapper,
    .mandatory-div {
      @include for-tablet {
        display: block;
      }
    }
    .cityfield,
    .statefield {
      @include for-tablet {
        display: inline-block;
      }
    }
  }

  #submit-click {
    @include for-tablet {
      display: none;
    }
  }
}
.blog-post-collapsed--mobile {
  .lead-form-title--collapsed {
    @include for-tablet {
      display: block;
    }
  }
  .lead-form-title--expaned {
    @include for-tablet {
      display: none;
    }
  }
  #blogPost {
    .fieldgroup,
    .cityfield,
    .statefield,
    .btn-wrapper,
    .mandatory-div {
      @include for-tablet {
        display: none;
      }
    }
  }

  #submit-click {
    @include for-tablet {
      display: block;
    }
  }
}

.blog-post-card {
  #blogPost {
    .fieldgroup,
    .cityfield,
    .statefield,
    .btn-wrapper,
    .mandatory-div {
      display: none;
    }
  }

  #submit-click {
    display: block;
  }
}
.terms-conditions-div {
  display: none;
  position: fixed;
  background: transparent;
  z-index: 99999;
  color: #000000;
  display: none;
  border: 0px solid #525252;
  text-align: left;
  top: 50%;
  left: 50%;
  background-color: #fff;
  width: 820px;
  padding: 30px 30px 30px;
  left: 50%;
  max-height: 550px;
  overflow-y: scroll;
  @include mQ(1020px) {
    width: 712px;
  }
  @include mQ(767px) {
    width: 80%;
    height: 93%;
    overflow: auto;
  }
}
.terms-overlay {
  position: fixed;
  z-index: -1;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  opacity: 0.75;
}
.popup {
  height: 100%;
  overflow: hidden;
  width: 100%;
  position: fixed;
  .terms-overlay {
    position: fixed;
    z-index: 10;
    background-color: #000;
  }
  .terms-conditions-title {
    font-size: 20px;
    line-height: 29px;
    color: $capital-red;
    font-family: $pfhandbookpro-medium;
    margin-bottom: 20px;
  }
  .terms-conditions-content {
    p {
      font-size: $font14;
      margin-bottom: 44px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .popup-close {
    position: absolute;
    right: 0px;
    top: 0px;
    width: 52px;
    height: 52px;
    background-color: #5a5a5a;
    &:hover {
      background-color: $capital-red;
    }
    &:before,
    &:after {
      position: absolute;
      left: 50%;
      content: " ";
      height: 33px;
      width: 2px;
      background-color: #fff;
      top: 10px;
    }
    &:before {
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
  }
}
.popover-body {
  font-family: $pfhandbookpro-regular;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: normal;
  color: $black;
  padding: 9px 14px;
}

//thank you message
.check-ok {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-bottom: 30px;
}
.check-ok-svg {
  width: 212px;
  height: 212px;
  display: block;
}

/*
* Blog Form
*
*/

.blog-checkbox {
  height: auto !important;
  input:checked ~ .checkbox-label:before {
    border: 2px solid #000;
  }
  input:checked ~ .checkbox-label:after {
    z-index: 1;
    content: "";
    position: absolute;
    left: 7px;
    top: 4px;
    width: 6px;
    height: 9px;
    border-width: 0 2px 2px 0 !important;
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    border: solid #c7222a;
    @include for-phone {
      left: 6px;
      top: 5px;
      width: 5px;
      height: 8px;
    }
  }
  .checkbox-label {
    position: relative;
    padding-left: 30px;
    display: block;
    @include for-phone {
      padding-left: 25px;
      p {
        font-size: 1.4rem;
      }
    }
    &:before {
      content: "";
      top: 0px;
      display: block;
      height: 20px;
      left: 0px;
      position: absolute;
      width: 20px;
      z-index: 1;
      border: solid 2px #cdcece;
      border-radius: 3px;
      @include for-phone {
        top: 2px;
        height: 16px;
        width: 16px;
      }
    }
  }
  input {
    margin-right: 15px;
    float: left;
    height: 20px;
    width: 20px;
    z-index: 10;
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }
  &--label-container {
    label:first-child {
      margin-bottom: 15px !important;
      @include for-phone {
        margin-bottom: 20px !important;
        font-size: $font14;
      }
    }
  }
}

.blog-radio {
  height: auto !important;
  ul {
    &.full-width {
      width: 60%;
      display: flex;
      flex: 1;
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 100%;
      margin-bottom: 0;
      @include mQ(767px) {
        column-count: 1;
        padding-top: 0;
        width: 100%;
      }
      &--rhs {
        width: 100%;
        li {
          width: 33%;
        }
      }
    }
    padding-top: 10px;
    padding-left: 0;
    li {
      font-size: 18px;
      font-family: $pfhandbookpro-regular;
      color: $black;
      padding-bottom: 0;
      padding-top: 0;
      margin-bottom: 15px;
      width: 33%;
      &:before {
        display: none;
      }

      span {
        opacity: 0.7;
      }
      @include mQ(767px) {
        margin-bottom: 21px;
        width: 33%;
      }
    }
  }
  .form-check {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 0px;
    padding-top: 1px;
    cursor: pointer;
    font-family: $pfhandbookpro-regular;
    font-size: $font18;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: $form-label;

    user-select: none;

    @include for-phone {
      font-size: $font14;
      padding-left: 25px;
    }
    .radio {
      opacity: 1 !important;
      position: absolute;
      top: 0;
      left: 0;
      height: 24px;
      width: 24px;
      background-color: $form-input-bg;
      border-radius: 50%;
      border: 2px solid $form-input-border;
      &:after {
        content: "";
        position: absolute;
        display: none;
        top: 5px;
        left: 5px;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        @include for-phone {
          top: 2px;
          left: 2px;
          width: 8px;
          height: 8px;
        }
      }
      @include for-phone {
        height: 16px;
        width: 16px;
      }
    }
    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      margin: 0;
      z-index: 1;
      &:checked ~ .checkmark {
        border-radius: 4px;
        box-shadow: 0 0 6px 0 $form-input-shadow;
        background-color: $form-input-bg;
        border: 2px solid $form-input-border;
        outline: none;
      }
      &:checked ~ .checkmark:after {
        display: block;
      }
      &:checked ~ .radio {
        background-color: $form-input-bg;
        border: 2px solid $form-input-border;
        box-shadow: 0 0 6px 0 $form-input-shadow;
      }
      &:checked ~ .radio:after {
        display: block;
      }
    }
  }
}
//Blog Migration
.two-columns{
  .blog-form-wrapper{
    margin-bottom: 25px;
  }
}