.wb-footer {
    background: $wb-footer-bg;
    padding: 6rem 0;
    @include for-phone {
        padding: 2rem 0 4rem;
    }
    .foot-link-wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        border-bottom: 1px solid $wb-foot-border;
        @include for-phone {
            margin: 0 -15px;
            border-bottom: 0;
        }
        &__col {
            width: 25%;
            @include for-tablet {
                width: 50%;
            }
            @include for-phone {
                width: 100%;
            }
        }
    }
    .img-link-block {
        display: flex;
        &__img {
            max-width: 5.5rem;
            height: 3.6rem;
            margin-right: 1rem;
        }
    }
}

.foot-link-set {
    padding-right: 5rem;
    @include for-phone {
        border-bottom: 1px solid $wb-foot-border;
        padding: 0 15px;
    }
    &__title-link {
        font-family: $pfencoresanspro-bold;
        font-size: $font18;
        line-height: $line-height-26;
        color: $wb-footer-title;
        margin-bottom: 0.8rem;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        &:hover,
        &:active,
        &:focus {
            color: $wb-foot-link-hover;
        }
        .icon {
            display: none;
        }
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-20;
            padding: 2rem 3.5rem 2rem 0;
            margin-bottom: 0;
            position: relative;
            br {
                display: none;
            }
            .icon {
                display: block;
                position: absolute;
                right: 0rem;
                top: 1.2rem;
                font-family: font-icons;
                font-size: $font14;
                width: 3rem;
                height: 3rem;
                line-height: $line-height-30;
                text-align: center;
                z-index: 1;
                transition: transform 0.3s ease-out;
            }
        }
    }
    &--active {
        .foot-link-set__title-link {
            @include for-phone {
                .icon {
                    transform: rotate(180deg);
                }
            }
        }
    }
    &__sub-title {
        font-family: $pfencoresanspro-bold;
        font-size: $font14;
        line-height: $line-height-22;
        color: $wb-foot-subtitle;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    &__link {
        font-family: $pfencoresanspro-book;
        font-size: $font14;
        line-height: $line-height-26;
        color: $wb-footer-title;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        &--bold {
            font-family: $pfencoresanspro-bold;
            line-height: $line-height-22;
        }
        &:hover,
        &:active,
        &:focus {
            color: $wb-foot-link-hover;
        }
    }
    &__list {
        padding-left: 0;
        margin-bottom: 4rem;
        &--sub {
            margin-bottom: 2rem;
        }
        @include for-phone {
            margin-bottom: 2rem;
        }
        &--social {
            display: flex;
            justify-content: flex-start;
            .foot-link-set__item {
                margin-right: 1.2rem;
                @include for-phone {
                    margin-right: 4rem;
                }
                &::last-child {
                    @include for-phone {
                        margin-right: 0;
                    }
                }
            }
            .foot-link-set__icon {
                font-size: $font22;
                color: $wb-footer-title;
                @include for-phone {
                    font-size: $font28;
                }
            }
        }
        &--tax {
            max-width: 21rem;
            @include for-phone {
                max-width: 100%;
            }
            .foot-link-set__item {
                padding: 1.8rem 0;
                border-bottom: 1px solid $wb-foot-border;
                &:first-child {
                    padding-top: 0;
                }
                &:last-child {
                    padding-bottom: 0;
                    border-bottom: 0;
                }
            }
            .foot-link-set__link {
                line-height: 2.1rem;
                white-space: normal;
            }
        }
    }
    &__cont {
        @include for-phone {
            display: none;
        }
    }
}

.aware-block {
    padding: 2rem 0;
    border-bottom: 1px solid $wb-foot-border;
    margin-bottom: 3rem;
    &__title {
        font-family: $pfencoresanspro-bold;
        font-size: $font18;
        line-height: $line-height-26;
        color: $wb-footer-title;
        margin-bottom: 0.8rem;
    }
    &__desc {
        font-family: $pfencoresanspro-book;
        font-size: $font14;
        line-height: $line-height-20;
        color: $wb-footer-title;
        margin: 0;
    }
}

.reg-office {
    padding: 2rem 0;
    border-bottom: 1px solid $wb-foot-border;
    @include for-phone {
        border-top: 1px solid $wb-foot-border;
    }
    &__title {
        font-family: $pfencoresanspro-bold;
        font-size: $font14;
        line-height: $line-height-22;
        color: $wb-footer-title;
        display: block;
        margin-bottom: 2rem;
        &:nth-child(2) {
            margin-bottom: 0.8rem;
        }
    }
    &__desc {
        font-family: $pfencoresanspro-book;
        font-size: $font14;
        line-height: $line-height-20;
        color: $wb-footer-title;
        margin-bottom: 2rem;
    }
}

.foot-link-socialset {
    @include for-phone {
        padding: 0 15px;
    }
}