.stay-money-wise {
    width: 100%;
    padding: 15px;
   
    .title {
        line-height: 1.25;
        font-size: $font24;
        margin: 0 0 16px;
        font-family: $pfhandbookpro-bold;
        @include for-phone {
          margin: 0 0 29px;
        }

        @include mQ(575px) {
          margin: 0 0 8px;
          font-size: 1.5rem;
        }

    }
    &__ul {
        margin: 0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        li {
            margin: 0 14px 14px 0;
            padding: 0; 
            width: calc(33.333% - 10px);
            @include mQ(991px,768px){
              margin: 0 11px 11px 0;
              width: calc(33.333% - 8px);
            }
            &::before {
                content: '';
                display: none;
            }
            &:nth-child(3n + 3) {
                margin-right:  0;
            }
            &.information-box {
                background-color: $brand-white;
                border-radius: 5px;
                box-shadow: 3px 7px 13px 0 rgba(0, 0, 0, 0.08);
                a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    border: 0;
                    padding: 9px 7px;

                    @include mQ(991px,768px){
                      padding: 4px 5px;
                    }

                    @include for-phone {
                      padding: 16px 10px;
                    }

                    @include mQ(575px) {
                      padding: 4px 4px;
                    }

                    & > .icon {

                        &::before {
                            line-height: inherit;
                        }
                        @include mQ(991px,768px){
                          font-size: $font20;
                        }
                        @include mQ(767px,576px){
                          font-size: 3.1rem;
                        }
                        @include mQ(575px, 360px){
                          font-size: $font16;
                        }
                    }
                }
                .sub-title {
                    font-size: $font12;
                    margin-bottom: 3px;
                    color: $range-house-gradient-end;
                    line-height: normal;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    @include mQ(991px,768px){
                      font-size: $font10;
                    }
                    @include mQ(767px,576px){
                      font-size: $font15;
                      margin-top: 5px;
                    }
                    @include mQ(575px, 360px){
                      font-size: 1.2rem;
                      line-height: 1.08;
                    }
                    .icon {
                        font-size: $font14;
                        margin: 0 0 8px 0;
                        color: $range-house-gradient-end;

                        @include mQ(991px,768px){
                          font-size: $font8;
                        }

                        @include mQ(767px,575px){
                          font-size: $font7;
                        }
                        @include mQ(575px, 360px){
                          font-size: $font20;
                        }
                    }
                    &__text {
                        display: block;
                        margin-right: 15px;
                        flex: 1;
                        font-size: $font12;

                        @include mQ(991px,768px){
                          font-size: $font10;
                          margin-right: 0px;
                        }
                        @include mQ(767px, 576px){
                          font-size: $font15;
                        }
                        @include mQ(575px, 360px){
                          font-size: $font9;
                        }
                    }
                   
                    .icon-right-arrow-thin {
                        @include mQ(767px, 576px){
                            font-size: $font16;
                        }
                        @include mQ(575px, 360px){
                          font-size: $font10;
                      }
                    }          
                }
                .info {
                    font-size: $font11;
                    margin: 0;
                    line-height: normal;
                    @include mQ(767px){
                        font-size: $font13;
                    } 
                    @include mQ(575px, 360px){
                      font-size: 1.1rem;
                    }
                }
                &.mb-hide {
                  @include mQ(767px){
                    display: none;
                  } 
                }
            }
            @include mQ(768px, 576px){
              width: calc(33.333% - 11px);
              margin: 0 16px 16px 0;
            }
            @include mQ(575px, 360px){
              width: calc(33.333% - 3px);
              margin: 0 4px 4px 0;
            }
        }
          @include mQ(767px){
            display: flex;
          }
    }
    .view-more-less {
        display:  none;
        justify-content: center;
        .btn-view-more {
            background-color: $range-house-gradient-end;
            color: $brand-white;
            font-size: $font20;
            min-width: 120px;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            margin: 10px 0 16px 0;
            flex-direction: column;
            padding: 5px;
            border-radius: 2px;
            border: 0;
            .icon {
                font-size: $font10;
                display: inline;
                margin: 0;
                line-height: 0;
            }
            @include mQ(575px){
                font-size: $font20;
            }
        }
        @include mQ(767px){
            display: flex;
            position: relative;
            &::before,  &::after {
              content: '';
              display: block;
              height: 1px;
              background-color: $capital-red;
              position: absolute;
              bottom: 0;
              width: calc(50% + 15px);
            }
            &::after {
              right: -15px;
            }
            &::before {
             left: -15px;
            }
        }
        &.arrow-up {
            position: relative;
            .icon-arrow {
                &::before{
                    display: inline-block;
                    transform: rotate(180deg);
                }
            }
        }
    }
    // Money Solutions Pillar
    .money-solutions-piller {
        display: none;
       @include mQ(767px){
            display: block;
       }
       //Pillar CSS
       .mos-pillarcolumn {
        &__heading {
          font-style: normal;
          font-stretch: normal;
          letter-spacing: normal;
          text-align: center;
          color: $card-heading;
          margin: 3px 0;
          h1 {
            font-family: $pfhandbookpro-regular;
            font-size: $font30;
            font-weight: bold;
            line-height: normal;
            @include mQ(575px){
              margin: 24px 0;
              font-size: $font24;
            }
            @include mQ(360px){
              margin: 26px 0 15px 0;
              font-size: $font15;
            }
          }
        }
        &__container {
          width: 100%;
          
          .scLooseFrameZone {
            width: 100%;
            .mos-pillarcolumn__box {
              width: 100%;
            }
          }
        }
        &__box {
          
          display: none;
          &:last-child {
            margin-right: 0;
          }
          &.active {
              display: block;
              .mos-pillarcolumn__card-container{
                display: flex;
                flex-wrap: wrap;
              }
          }
        }
        &__title {
          width: 100%;
          padding-top: 6px;
          padding-bottom: 6px;
          display: block;
          h4 {
            font-size: $font26;
            color: $box-main-title;
            text-align: center;
            font-family: $pfhandbookpro-bold;
          }
          
        }
        &__card {
          background: $card-background;
          padding: 11px;
          box-shadow: 1px -2px 8px 0 rgba($black, 0.08);
          position: relative;
          margin-top: 18px;
          
          .icon-link {
            margin-right: 18px;
            margin-top: 16px;
            text-align: right;
            float: right;
            display: flex;
            color: $card-arrow;
          }
          .icon {
            font-size: $font15;
            margin: 0;
            line-height: normal;
          }
          
          @include mQ(767px) {
            width: calc(33.333% - 12px);
            border-radius: 3px;
            padding: 8px;
            display: flex;
            flex-direction: column;

            &:nth-child(3n + 2) {
              margin-left: 18px;
              margin-right: 18px;
            }
          }
          @include mQ(360px) {
            margin-top: 10px;
            width: calc(33.333% - 7px);
            padding: 4px;

            &:nth-child(3n + 2) {
              margin-left: 10px;
              margin-right: 10px;
            }
          }
        }
        &__card-title {
          font-family: $pfhandbookpro-medium;
          letter-spacing: normal;
          text-transform: uppercase;

          @include mQ(767px) {
            flex: 1;
          }

          @include mQ(575px) {
            font-size: $font14;
          }
          @include mQ(360px) {
            font-size: $font11;
          }
          span {
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: $card-title-1;

            @include mQ(575px) {
              font-size: $font14;
            }
            @include mQ(360px) {
              font-size: $font11;
            }
            .icon-right-arrow-thin {
              @include mQ(575px) {
                font-size: $font9;
                margin-left: 8px;
              }
              @include mQ(360px) {
                margin-left: 2px;
              }
            }
            &.card-txt {
              color: $brand-black;
              @include mQ(575px) {
                font-size: $font24;
                margin: 8px 0 15px 0;
                max-width: 100px;
              }
              @include mQ(575px, 360px) {
                font-size: $font15;
                margin: 4px 0 7px 0;
                line-height: 1;
                max-width: 86px;
              }
            }
          }
        }
        &__list {
          ul {
            padding: 0;
            li {
              display: flex;
              justify-content: space-between;
              width: 100%;
              padding: 0 23px;
              margin-top: 24px;
              line-height: $line-height-24;
              &::before {
                content: " ";
                all: unset;
                display: none;
              }
             
              a {
                color: $card-qs-link;
              }
              .icon {
                color: $card-qs-arrow;
              }
            }
          }
        }
        //Mobile Start
        &__tabcontainer {
          width: 100vw;
          position: relative;
          left: calc(-1 * (100vw - 100%) / 2);
         
          ul {
            width: 100%;
            padding-left: 0;
            display: flex;
            flex-wrap: inherit;

            li {
             flex: 1;
              padding: 0;
              &::before {
                content: "";
                all: unset;
                display: none;
              }
              a {
                font-family: $pfhandbookpro-regular;
                font-size: $font16;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: $line-height-1;
                letter-spacing: normal;
                text-align: center;
                color: $card-tab-link;
                padding: 10px 15px;
                display: flex;
                position: relative;
                height: 100%;
                align-items: center;
                justify-content: center;
                border: 0;
                
                &.active {
                  color: $card-tab-link-active;
                  font-weight: bold;
                }
                @include mQ(767px, 576px) {
                  font-size: $font24;
                  min-height: 66px;
                }
                @include mQ(575px, 360px) {
                  font-size: $font15;
                  min-height: 40px;
                }
              }
            }
          }
        }
        //Protecting
        &__protecting {
          .mos-pillarcolumn,
          &.mos-pillarcolumn {
            &__title {
              background: $protecting;
              border: 1px solid $protecting;
            }
            &__card {
              border-left: 3.6px solid $protecting;
              .btn {
                background: $protecting;
                color: $card-btn-hover;
                border: 1px solid $protecting;
                text-transform: uppercase;
              }
            }
            &__qslink {
              &:hover {
                color: $protecting;
                text-decoration: underline;
              }
            }
            &__item {
              border-bottom: 6px solid $protecting;
              .active {
                background: $protecting;
                padding-bottom: 11px;
              }
              @include mQ(360px) {
                border-bottom: 4px solid $protecting;
              }
            }
          }
        }
        //Investing
        &__investing {
          .mos-pillarcolumn,
          &.mos-pillarcolumn {
            &__title {
              background: $investing;
              border: 1px solid $investing;
            }
            &__card {
              border-left: 3.6px solid $investing;
              .btn {
                text-transform: uppercase;
                background: $investing;
                color: $card-btn-hover;
                border: 1px solid $investing;
              }
            }
           
            &__qslink {
              &:hover {
                color: $investing;
                text-decoration: underline;
              }
            }
            &__item {
              border-bottom: 6px solid $investing;
              .active {
                background: $investing;
                padding-bottom: 11px;
              }
              @include mQ(360px) {
                border-bottom: 4px solid $investing;
              }
            }
          }
        }
        //Financing
        &__financing {
          .mos-pillarcolumn,
          &.mos-pillarcolumn {
            &__title {
              background: $financing;
              border: 1px solid $financing;
            }
            &__card {
              border-left: 3.6px solid $financing;
              .btn {
                background: $financing;
                color: $card-btn-hover;
                border: 1px solid $financing;
              }
            }
            
            &__qslink {
              &:hover {
                color: $financing;
                text-decoration: underline;
              }
            }
            &__item {
              border-bottom: 6px solid $financing;
              .active {
                background: $financing;
                padding-bottom: 11px;
              }
              @include mQ(360px) {
                border-bottom: 4px solid $financing;
              }
            }
          }
        }
        //Quick Services
        &__qs {
          .mos-pillarcolumn,
          &.mos-pillarcolumn {
            &__title {
              background: $capital-red;
              border: 1px solid $capital-red;
            }
            &__card {
              border-left: 3.6px solid $capital-red;
              .btn {
                background: $capital-red;
                color: $card-btn-hover;
                border: 1px solid $capital-red;
              }
            }
            &__item {
              border-bottom: 6px solid $capital-red;
              .active {
                background: $capital-red;
                padding-bottom: 11px;
              }
              @include mQ(360px) {
                border-bottom: 4px solid $capital-red;
              }
            }
          }
        }
        a {
          &:visited,
          &:hover,
          &:active,
          &:focus {
            border: none;
          }
        }
        //Button
        .btn {
          min-height: 24px;
          border-radius: 2px;
          background-color: $card-btn-bg;
          font-family: $pfhandbookpro-bold;
          font-size: $font11;
          text-align: center;
          display: block;
          width: 100%;
          max-width: 115px;
          font-size: $font11;
          @include mQ(575px) {
            max-width: none;
            text-transform: initial !important;
          }
        }
      }
    }
    &.desktop-view {
      display: none;
      .stay-money-wise__ul {
        li {
            width: 100%;
            max-width: 142px;
            margin-right: 24px;
        }
      }
      @include mQ(767px) {
        display: none !important;
      }
    }
    @include mQ(767px) {
      display: block !important;
    }
}
.sub-wrapper {
  display: none;

    .tabs-flex-wrapper {
      display: flex;
      align-items: center;
      .tab-back-sm {
        font-size: $font24;
        margin: 0 14px 0 0;
        border: 0;
          @include mQ(575px, 360px){
            font-size: $font12;
          }
      }
    }
    
    /*Common Calculator Card*/
    .calculator-card {
      .calc-title {
          margin: 0 0 5px 0;
          font-size: $font16;
          color: rgba(0, 0, 0, 0.6);
          &:first-of-type {
            margin-top: 10px;
          }
      }
      .tiles {
        display: block;
        border-left: 2px solid;
        border-radius: 3px;
        background-color: $brand-white;
        padding: 12px 10px;
        box-shadow: 0 22px 44px 0 rgba(0, 0, 0, 0.1);

        @include mQ(1920px, 993px){
          width: calc(50% - 8px);
          margin-bottom: 16px;

          &:nth-child(2n + 2) {
            margin-left: 16px;
          }
        }
        @include mQ(992px, 769px){
          width: calc(50% - 10px);
          margin-bottom: 20px;
          max-width: 205px;

          &:nth-child(2n + 2) {
            margin-left: 20px;
          }
        }
        @include mQ(768px, 575px){
          width: calc(50% - 8px);
          margin-bottom: 16px;
          max-width: 205px;

          &:nth-child(2n + 2) {
            margin-left: 16px;
          }
        }
        @include mQ(575px, 320px){
          width: calc(50% - 11px);
          margin-bottom: 22px;
          
          &:nth-child(2n + 2) {
            margin-left: 22px;
          }
        }

        &:hover {
          border-bottom: 0;
        }
         // Color Theme
         @include themifySelf($themes) {
          border-color: themed('backgroundColor');  
        }

        .badges {
          display: block;
          font-size: $font10;
          max-width: 66px;
          width: 100%;
          text-align: center;
          margin-bottom: 10px;
          color: $brand-white;
          border-radius: 2px;
          text-transform: uppercase;
          line-height: 15px;
          // Color Theme
          @include themifySelf($themes) {
            background-color: themed('backgroundColor');  
          }
          @include mQ(575px, 360px){
            margin-bottom: 5px;
          }
          &__red {
            background-color: $capital-red;
          }
        }
       
        .arrow-box {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .heading-sm {
            display: block;
            font-size: $font18;
            max-width: 160px;
            color: $brand-black;
            line-height: initial;

            @include mQ(991px, 768px){
              font-size: $font14;
            }
            @include mQ(767px, 576px){
              font-size: $font18;
            }
            @include mQ(575px, 320px){
              font-size: $font11;
            }
          }

          .icon-right-arrow-thin {
            font-size: $font13;
            color: $capital-red;
            margin: 0;
            line-height: initial;
          }
        }
       
        
    }
    .calculator-card-row {
      display: flex;
      flex-wrap: wrap;
    }

    @include mQ(992px, 360px){
      width: 100%;
      max-width: 430px;
    }
  }
  
  &.sm-blogs {

    .calculator-card {

      @include mQ(768px, 575px){
        width: 100%;
        max-width: none;
      }

      .tiles {

          margin-top: 20px;
          margin-bottom: 0;
          min-height: 96px;

          @include mQ(992px, 769px){
            min-height: 77px;
            font-size: $font10;
          }
  
          @include mQ(768px, 575px){
            font-size: $font18;
            min-height: 130px;
            margin: 20px 0 0 0;
            width: 100%;
            max-width: none;
          }
  
          @include mQ(575px, 360px){
            font-size: $font12;
            min-height: 80px;
            margin-top: 22px;
            margin-bottom: 0;
          }

          .badges {
            font-size: $font12;

            @include mQ(768px, 575px){
              max-width: 122px;
              line-height: 24px;
            }
    
            @include mQ(575px, 360px){
              max-width: 70px;
              line-height: 15px;
            }

          }
          .arrow-box {

            .heading {
              max-width: none;
              font-size: $font16;

              @include mQ(768px, 575px){
                font-size: $font18;
              }
      
              @include mQ(575px, 360px){
                font-size: $font12;
              }
            }
          }
      }
    }   
    .read-all-blogs {
      max-width: 240px;
      min-height: 64px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: $font23;
      border: 2px solid $capital-red;
      background-color: $brand-white;
      margin: 22px auto;
      border-radius: 3px;

      @include mQ(575px, 360px){
        max-width: 150px;
        min-height: 40px;
        font-size: $font14;
      }

    }
  }
  &.sm-podcats {
    .calculator-card {

      .podcast-tiles {
       border: 0;
       margin-top: 20px;

        .podcast-heading {
          display: flex;
          justify-content: space-between;
          font-size: $font20;

          .podcast-icon {
            margin-right: 20px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 35px;
            height: 35px;
            background-color: $brand-white;
            border-radius: 50%;
            box-shadow: 3px 5px 13px 0 rgba(0, 0, 0, 0.08);

            @include mQ(575px, 360px){
              width: 22px;
              height: 22px;
            }

              &::before {
                content: '';
                display: inline-block;
                width: 0; 
                height: 0; 
                border-top: 8px solid transparent;
                border-bottom: 8px solid transparent;
                border-left: 12px solid $capital-red;

                @include mQ(575px, 360px){
                  border-top: 6px solid transparent;
                  border-bottom: 6px solid transparent;
                  border-left: 6px solid $capital-red;
                }

              }
          }

          .podcast-data {
            flex: 1;
          }

          p {
              font-size: $font11;
              margin: 0;
              color: $brand-black;
              line-height: normal;

              .podcast-title {
                display: block;
                font-size: $font20;
                margin-bottom: 4px;

                @include mQ(768px, 575px){
                  font-size: $font16;
                }
                @include mQ(575px, 360px){
                  font-size: $font12;
                } 
              }

              @include mQ(768px, 575px){
                font-size: $font9;
              }
              @include mQ(575px, 360px){
                font-size: $font11;
              }
          }
        }
        .podcast-date {
          font-size: $font14;
          display: flex;
          align-items: center;
          justify-content: space-between;
          color: $brand-black;
          margin-top: 4px;

          @include mQ(768px, 575px){
            font-size: $font12;
          }
          .badges {
            color: $brand-white;
            min-width: 83px;
            line-height: 19px;
            font-size: $font12;
            text-transform: uppercase;
            border-radius: 2px;
            text-align: center;
            // Color Theme
            @include themifySelf($themes) {
              background-color: themed('backgroundColor');  
            }
          }

          .bullet {
            color: #c1c7d0;
          }
        }
      }
    }   
  }
}

/*******  Repeated User Stay money wise *******/
.repeated-user-title {
  display: none;
}
.repeate-user-stay-money-wise {
  .login-right-col {
      display: block;
      @include mQ(768px){
        width: 105%;
      }
      @include mQ(576px){
        width: 100%;
      }
      @include mQ(360px){
        width: 100%;
      }
      &::before {
          content: none;
      }
      @include mQ(767px){
          display: block;
          padding: 0;
      }
      .stay-money-wise {

          @include mQ(767px){
              padding-left: 0;
              padding-right: 0;
          }
          .sub-title__text {
              // @include mQ(991px){
              //     font-size: $font10;
              // }
              @include mQ(575px){
                  font-size: $font15;
              }
              @include mQ(575px, 360px){
                  margin-right: 24px;
                  font-size: $font12;
                  word-break: break-word;
              }   
              
          }
         
      }
      .dashboard__nbo-cards-wrapper {
          height: auto !important;
          position: static;
         
          .dashboard__nbo-cards-innerwrapper {
              padding-left: 16px;
              padding-right: 16px;
              overflow: hidden;
              .owl-carousel{
                .owl-stage-outer{
                  overflow: visible !important;
                  // .owl-item{
                  //   max-width: 290px;
                  // }
                }
                .owl-stage{
                  padding-left: 0 !important;
                }
                .owl-nav.disabled{
                  display: block !important;
                  color: #c7222a;
                  margin-top: 10px;
                }
                button.owl-next{
                  position: absolute;
                  right: 0;
                }
                button.owl-prev, button.owl-next {
                  border: 1px solid red !important;
                  border-radius: 35px;
                  padding: 4px !important;
                  width: 15px;
                  height: 15px;
                  span{
                    position: relative;
                    bottom: 10px;
                    font-size: 15px;
                    text-align: center;
                  }
              }
            } 
              @include mQ(767px, 576px){
                  padding: 0;
              }
              @include mQ(575px, 360px){
                  margin-top: 0;
                  padding: 0;
                  width: 100%;
              }   

              .dashboard__nbo-recommended {
                  display: none;
              }
              .dashboard__nbo-card-item-wrapper {
                  justify-content: space-between;
  
                  .dashboard__nbo-info-icon {
                      margin: 0;
                      //width: calc(85% - 0px);
                      
                      width: calc(50% - 15px);
                      border: 0;
                      max-width: none;
                      border-radius: 2px;
                      box-shadow: 0 6px 20px -9px rgba(0, 0, 0, 0.22);
                     
                      @include mQ(768px){
                        width: calc(94% - 15px);
                      }
                      @include mQ(576px){
                        width: calc(50% - 15px);
                      }
                      @include mQ(540px){
                        width: calc(50% - 15px);
                      }
                      
                      .dashboard__nbo--label {
                          padding: 6px;
                          height: 100%;
                          max-height: 60px;
                          font-size: 1.6rem;
                          margin-bottom: 7px;
                          min-height: 40px;
                          
                          .cta-label {
                              font-size: $font16;
                              min-height: 32px;
                              display: inline-flex;
                              align-items: center;
                              margin-right: 5px;
                              line-height: 1.6rem;
                              max-width: 150px;

                              @include mQ(991px, 768px){
                                  font-size: $font11;
                                  line-height: normal;
                              }
                              @include mQ(870px, 768px){
                                 min-height: 40px;
                              } 
                              @include mQ(767px, 576px){
                                  font-size: $font14;
                              }  
                              @include mQ(575px, 360px){
                                  font-size: $font10;
                                  line-height: normal;
                              }  
                          }
                          
                          .btn-mini {
                              font-size: $font12;
                              min-width: 76px;
                              display: block;
                              padding: 0 4px;
                              line-height: 20px;

                              @include mQ(910px, 576px){
                                min-width: 62px;
                                font-size: $font8;
                              }  
                              @include for-phone {
                                min-width: 60px;
                              }
                              @include mQ(575px, 360px){
                                  height: auto;
                                  font-size: $font8;
                              } 
                          }

                      }
                      .dashboard__nbo--title {
                          font-size: 2rem;
                          &-cta {
                            font-size: 2rem;
                            line-height: 20px;
                            padding: 0 10px;
                          }
                          .curve-lbl {
                              font-size: $font15;
                              display: block;
                              border-radius: 12px;
                              padding: 2px 10px;
                              line-height: 20px;
                              
                              @include mQ(991px, 768px){
                                  font-size: $font12;
                              }
                              @include mQ(767px, 576px){
                                  font-size: $font11;
                              }
                              @include mQ(575px, 360px){
                                  font-size: $font11;
                              } 
                          }
                          .initial-bg {
                              display: none;
                          }
                          .txt-mobile {
                              display: none;
                          }
                      }
                      .dashboard__nbo--content {
                          font-size: $font16;
                          margin: 5px 0 4px 0;
                          max-height: 100px;
                          min-height: 78px;
                          padding: 0 10px;
                          
                          @include mQ(991px, 768px){
                              font-size: $font12;
                          }
                      }
                      .dashboard__nbo--button {
                          text-align: right;
                          margin-bottom: 10px;
                          padding: 0 15px 0 10px;

                          @include mQ(767px, 360px){
                              display: block;
                              padding: 0;
                          }

                          .btn-orange {
                              min-width: 78px;
                              min-height: 26px;
                              background-color: #f7a500;
                              color: $brand-black;
                              font-size: $font14;
                              padding: 0 2px;

                              @include mQ(767px, 360px){
                                  display: none;
                              }
                          }
                         
                      }
                      .foot-notes {
                          display: flex;
                          font-size: $font16;
                          border-radius: 0; 
                          max-height: 40px;
                          height: 100%;
                          align-items: center;
                          padding: 5px 10px;
                          line-height: 1;

                          @include mQ(991px, 768px){
                              font-size: $font8;
                              line-height: normal;
                          }
                          @include mQ(575px, 360px){
                              font-size: $font9;
                              line-height: normal;
                          }   
                      }
                      
                  }
              }
          }
          @include mQ(767px) {
            display: block !important;
          }
      }
      .repeated-user-title {
          font-size: $font24;
          line-height: 30px;
          letter-spacing: -0.27px;
          margin: 0;
          padding: 8px 16px 20px 16px;
          font-weight: bold;
          display: block;

          @include mQ(991px, 768px){
              font-size: $font18;
          }
          @include mQ(767px, 576px){
           font-size: $font24;
           padding: 0;
          }
          @include mQ(575px, 360px){
           font-size: $font15;
           padding: 0;
          }   
      }
  }
   .stay-money-wise:not(.stay-money-wise.desktop-view) {
       & > .title, .stay-money-wise__ul, .view-more-less {
          display: none !important;
          @include mQ(767px){
              display: flex !important;
          }
       }
       & > .title.hide, .stay-money-wise__ul.hide, .view-more-less.hide {
        @include mQ(767px){
            display: none !important;
        }
     }
   }
  .stay-money-wise.desktop-view {
      display: block !important;
      @include mQ(767px){
          display: none !important;
      }
      .stay-money-wise__ul {
          li {
              @include mQ(991px, 768px){
                  max-width: 124px;
                  margin-right: 16px;
              }
          }
      } 
  }
  .mb-bg {
    @include mQ(767px){
      background-color: #f8fcff;
    }
    .dashboard__nbo-cards-wrapper {
      @include mQ(767px){
        background-color: transparent;
      }
    }
  }
}