.ad-banner {
    position: relative;
    $this: &;
    @include for-tablet {
        margin: 0 -15px;
    }
    &__img {
        @include for-phone {
            display: none;
        }
        &--mob {
            display: none;
            @include for-phone {
                display: block;
                width: 100%;
            }
        }
    }
    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
    &__title {
        font-size: $font44;
        line-height: $line-height-46;
        color: $adb-text-clr;
        margin-bottom: 3rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
            margin-bottom: 1.6rem;
            padding-left: 1.5rem;
        }
        @include for-tablet {
            padding: 0 1.5rem;
        }
    }
    &--slider {
        #{$this}__img-wrap {
            position: relative;
        }
        .slick-slide {
            img {
                &.ad-banner__img {
                    @include for-phone {
                        display: none;
                    }
                }
                &.ad-banner__img--mob {
                    display: none;
                    @include for-phone {
                        display: block;
                        width: 100%;
                    }
                }
            }
        }
        .slick-nav--ad-banner {
            @include mQ(1025px) {
                display: none;
            }
        }
    }
}