.content__policy-summary-main-wrapper {
  padding-bottom: 0px;
  width: 100%;

  .summary-list-item {
    border-bottom: 1px solid #dee2e6;
    @include fontScaling(border-bottom-width,0.1);
    padding: 9px 18px 0px 18px;
    @include fontScaling(padding-top,0.9);
    @include fontScaling(padding-right,1.8);
    @include fontScaling(padding-bottom,0.0);
    @include fontScaling(padding-left,1.8);
    
    
    overflow: hidden;

    @include for-phone {
      padding-top: 15px;
      padding-bottom: 15px;
    }

    h3 {
      @include themify($themes) {
        color: themed('backgroundColor');
      }

      font-family: $pfhandbookpro-bold;
      font-size: $font24;
      @include fontScaling(font-size,2.4);
      line-height: 1.2;
      margin-bottom: 10px;
      @include fontScaling(margin-bottom,1.0);
    
      @include for-phone{
        font-size: $font15;
      }
      @include mQ(575px) {
        font-size: 1.5rem;
      }
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0px;
      
      display: flex;
      flex-wrap: wrap;

      li {
        display: flex;
        list-style: none;
        width: 33.33%;

        @include for-phone {
          width: 50% !important;
          padding: 19px 25px !important;
        }

        padding: 6px 0px 8px 0px;
        @include fontScaling(padding-top,0.6);
        @include fontScaling(padding-right,0);
        @include fontScaling(padding-bottom,0.8);
        @include fontScaling(padding-left,0);
    

        &::before {
          content: none;
        }

        .summary-content {
          display: flex;
          flex-direction: column;

          .colored {
            &::first-letter{
              text-transform: uppercase;
            }
            text-transform: lowercase;
            @include themify($themes) {
              color: themed('backgroundColor') !important;
            }
          }
        }

        span {
          font-size: $font14;
          @include fontScaling(font-size,1.4);
          font-family: $pfhandbookpro-light;
          line-height: 1.15;
          margin-bottom: 2px;
          @include fontScaling(margin-bottom,0.2);
          color: $brand-black;

          @include mQ(767px, 576px) {
            font-size: 2.0rem;
          }

          @include mQ(575px) {
            font-size: 1.3rem;
          }

          &.title-tag {
            position: relative;
            top: -5px;
            @include fontScaling(top,-0.5);

            @include mQ(575px) {
              top: -3px;
            }
          }
        }

        p, span.pascal-case {
          margin-bottom: 15px;
          @include fontScaling(margin-bottom,1.5);
          font-size: $font26;
          @include fontScaling(font-size,2.6);
          color: $brand-black;
          font-family: $pfhandbookpro-medium;
          line-height: 1.2;
          word-break: break-word;
          @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            word-break: break-all;
          }

          @include mQ(767px, 576px) {
            font-size: 3.0rem;
          }

          @include mQ(575px) {
            font-size: 1.9rem;
          }
        }

        &:nth-child(3n+1) {
          width: 40%;
          padding-right: 35px;
          @include fontScaling(padding-right,3.5);
        }

        &:nth-child(3n+2) {
          width: 30%;
        }

        &:nth-child(3n+3) {
          width: 30%;
        }
      }

      &.striped {
        @include mQ(0, 768px, true) {
          li:nth-child(6n - 2) {
            position: relative;

            &::before {
              content: "";
              background-color: rgba(152, 166, 184, 0.1);
              position: absolute;
              top: 0;
              left: -18px;
              right: -18px;
              height: 100%;
              width: 9999px;
            }
          }
        }

        @include mQ(767px) {
          li:nth-child(4n - 1) {
            position: relative;

            &::before {
              content: "";
              background-color: rgba(152, 166, 184, 0.1);
              position: absolute;
              top: 0;
              left: -18px;
              right: -18px;
              height: 100%;
              width: 9999px;
            }
          }
        }
      }

      &.striped {
        li {
          p {
            margin-bottom: 0px;
            
          } 
        }
      }
    }

    &:last-child {
      border: none;
    }

    &.equal_width {
      ul {
        li {
          padding-right: 10px;
          @include fontScaling(padding-right,1.0);
          width: 33.33%;
        }
      }
    }

    .dropdown-lbl {
      font-size: 1.4rem;
      line-height: 1.15;
      margin-bottom: 8px;
      @include fontScaling(margin-bottom,0.8);
    }

    .ellipses-txt {
      max-width: 200px;
      @include fontScaling(max-width,20.0);
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      vertical-align: middle;
      margin: 0 8px 0 0;
      @include fontScaling(margin-right,0.8);
      
      @include for-phone {
        //white-space: normal;
        white-space: nowrap;
        max-width: 150px;
      }

      +.btn-tooltip {
        display: inline-block;
        width: 12px;
        @include fontScaling(width,1.2);
        height: 12px;
        @include fontScaling(height,1.2);
        text-align: center;
        line-height: 15px;
        @include fontScaling(line-height,1.5);
        border-radius: 50%;
        border: 1px solid;
        @include fontScaling(border-width,0.1);
        line-height: 10px;
        @include fontScaling(line-height,1.0);
        font-size: 10px;
        @include fontScaling(font-size,1.0);
        vertical-align: middle;
        color: $protecting-shade;

      }
    }
  }

  .padding-low {
    .summary-list-item {
      padding: 0px;
      
      padding-left: 12px;
      @include fontScaling(padding-left,1.2);
    }
  }
}

.dashboard__summary-card-head-content {
  p {
    font-family: $pfhandbookpro-light;
    margin-bottom: 20px;
    @include fontScaling(margin-bottom,2.0);
    color: $brand-black;

    span {
      font-family: $pfhandbookpro-bold;
    }
  }
}

.no-padding {
  padding: 0px !important;

  &.dashboard__summary-card {
    padding: 0px;
  }
}

.margin-bottom-0 {
  margin-bottom: 0px;

  &.dashboard__summary-card {
    margin-bottom: 0px;
  }
}

.main-font {
  .fontsize {
    font-size: 1.4rem !important;
  }
}

.ellipse-box {
  .ellipses-txt {
    max-width: 200px;
    @include fontScaling(max-width,20.0);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    vertical-align: middle;
    margin: 0 8px 0 0;
    @include fontScaling(margin-right,0.8);

    @include for-phone {
      white-space: normal;
    }

    +.btn-tooltip {
      display: inline-block;
      width: 12px;
      @include fontScaling(width,1.2);
      height: 12px;
      @include fontScaling(height,1.2);
      text-align: center;
      line-height: 15px;
      @include fontScaling(line-height,1.5);
      border-radius: 50%;
      border: 1px solid;
      @include fontScaling(border-width,0.1);
      line-height: 10px;
      @include fontScaling(line-height,1.0);
      font-size: 10px;
      @include fontScaling(font-size,1.0);
      vertical-align: middle;
      color: $protecting-shade;

    }
  }
}