.progress-list {
    font-size: $font0;
    padding: 1.5rem 0 0 0;
    @include for-phone {
        padding-top: 2rem;
    }
    &__item {
        display: inline-block;
        vertical-align: top;
        width: 25%;
        position: relative;
        padding: 1.6rem 0 0;
        &:first-child {
            &:after {
                display: none;
            }
        }
        &:before {
            content: "";
            position: absolute;
            top: -0.7rem;
            left: 0;
            width: 1.4rem;
            height: 1.4rem;
            background: $progress-bullet;
            display: inline-block;
            border-radius: 50%;
            z-index: 1;
        }
        &:after {
            content: "";
            position: absolute;
            top: 0;
            right: 100%;
            width: 100%;
            height: 2px;
            background: $progress-bar;
        }
        &--active {
            &:after {
                background-color: $progress-bar-active;
            }
            &:before {
                content: "";
                background-color: $progress-bullet-active;
            }
            .progress-list__icon,
            .progress-list__step {
                color: $progress-icon-active;
            }
        }
    }
    &__icon {
        font-size: $font24;
        color: $progress-icon;
        @include for-phone {
            font-size: $font20;
        }
    }
    &__step {
        font-size: $font18;
        line-height: $line-height-20;
        color: $progress-text;
        margin-top: 0.5rem;
        display: block;
        width: 50%;
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-14;
            margin-top: 0;
        }
    }
}

.plan-info {
    text-align: center;
    background: $plan-info-bg;
    padding: 2.4rem;
    display: flex;
    justify-content: center;
    margin: 4rem 0 2rem;
    @include for-phone {
        padding: 1.6rem;
        margin-top: 2.4rem;
    }
    &__name,
    &__amt {
        font-size: $font24;
        font-family: $pfhandbookpro-bold;
        width: 50%;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-22;
            text-align: left;
        }
        .text {
            font-size: $font20;
            line-height: $line-height-26;
            font-family: $pfhandbookpro-regular;
            display: block;
            @include for-phone {
                font-size: $font16;
                line-height: $line-height-16;
            }
        }
    }
    &__name {
        border-right: 1px solid $progress-border;
        @include for-phone {
            width: calc(50% - 1.5rem);
        }
    }
    &__amt {
        @include for-phone {
            padding-left: 1.5rem;
        }
    }
}