//why-buy
.why-buy {
    // background: $wb-bg-color;
    padding: 5rem 4rem;
    @include for-phone {
        padding: 3rem 1.5rem;
        margin: 0 -1.5rem;
    }
    &__title-wrap {
        margin-bottom: 4rem;
    }
    &__list {
        max-width: inherit;
    }
    &__item {
        padding-bottom: 0;
        border-bottom: none;
        margin-bottom: 4rem;
        &:last-child {
            margin-bottom: 0;
        }
        &:nth-child(-n + 3) {
            display: block;
            &.benefits--mobile-v2{
                @include for-phone{
                    display: flex;
                }
            }
        }
    }
    &__button-link{
        margin-top: 1.7rem;
        margin-left: 6.5rem;
        @include for-phone{
            margin-top: 4rem;
            margin-left: 0;
            padding-left: 0;
        }
    }
    &__button-link{
        margin-top: 1.7rem;
        margin-left: 6.5rem;
        @include for-phone{
            margin-top: 4rem;
    margin-left: 0;
    padding-left: 0;
        }
    }
}