.searchContentWrapper {
    margin-bottom: 25px;
}
.main-content.HideOverflow{
    overflow: hidden;
}
.search-page-heading {
    display: flex;
    align-items: baseline;
    .col-12{
        flex:0 0 auto; 
    }
}
.show-result {
    font-size: 2rem;
    color: #9DAFBD;
    margin-left: 18px;
    @include mQ(1020px){
        display: none;
    }
}
.search-bottom-section {
    margin-top: 22px;
    margin-bottom: 16px;
    @include mQ(1020px){
        margin-top: 18px;
        margin-bottom: 0px;
    }
}
.alt {
    .search-bottom-section {
        margin-bottom: 9px;
    }
}
.searchContentItem {
    padding-top: 25px;
    padding-bottom: 25px;
    &:first-child {
        padding-top: 0px;
    }
    &.alt {
        position: relative;
        background-color: $light-white-grey;
        &:before, &:after {
            background-color: $light-white-grey;
            content: "";
            position: absolute;
            width: 100vw;
            height: 100%;
            top: 0;
            z-index: -1;
        }
        &:before {
            left: -100vw;
        }
        &:after {
            left: 100%;
        }
    }
    .search-content-section {
        a {
            color: $black;
            &:hover,&:active,&:focus {
                color: $black;
                border-bottom: 1px solid $black !important;
            }
        }
        h2 {
            margin-bottom: 15px;
            font-family: $pfhandbookpro-light;
            letter-spacing: -0.1px;
            @include mQ(1020px){
                margin-bottom: 7px;
            }
        }
        p {
            color: $dark-grey;
            @include mQ(1020px){
                font-size: 1.6rem;
                line-height: 18px;
            }
        }
    }
    .search-top-section {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 11px;
        @include mQ(767px){
            margin-bottom: 5px;
            display: block;
        }
        .search-date {
            font-size: 1.4rem;
            line-height: 16px;
            color: #9DAFBD;
            letter-spacing: -0.32px;
            @include mQ(1020px){
                font-size: 1.3rem;
                color: $black;
            }
        }

        .search-tag {
            background-color: rgba(199,34,42,1);
            @include mQ(767px){
                display: inline-block;
                margin-bottom: 14px;
            }
            a {
                font-family: $pfhandbookpro-medium;
                font-size: 1.4rem;
                line-height: 20px;
                padding: 10px 22px 9px 22px;
                color: $black;
                border-bottom: none;
                display: inline-block;
                &:hover, &:active, &:focus {
                    border-bottom: none;
                }
            }

            &.pink {
                background-color: rgba(218,144,137,1);
            }
            &.green {
                background-color: rgba(150,159,110,1);
            }
            &.yellow {
                background-color: rgba(212,174,105,1);
            }

            @include mQ(1020px){
                background-color: $capital-red;

                &.pink {
                    background-color: $pink;
                }
                &.green {
                    background-color: $green-link;
                }
                &.yellow {
                    background-color: $yellow;
                }

                a {
                    font-size: 1.2rem;
                    line-height: 20px;
                    color: $white;
                    padding: 6px 11px 5px 11px;
                }
            }
        }

    }
}