//default button style
.button {
    line-height: $line-height-28;
    text-align: left;
    border: none;
    border-radius: 4px;
    padding: 0;
    white-space: normal;
    text-transform: uppercase;
    opacity: 1;
    min-width: 184px;
    display: inline-block;
}

//Button Primary
.button-primary {
    background: $button-primary-background-color;
    color: $button-primary-color;
    font-family: $pfhandbookpro-medium;
    font-size: $font24;
    font-weight: 500;
    padding: 18px 45px 18px 16px;
    position: relative;
    &:hover,
    &:active,
    &:focus {
        background: $button-primary-active-color;
        color: $button-primary-color;
        border: none;
    }
    &:after {
        content: $icon-right-arrow;
        top: 50%;
        transform: translate(-50%, -50%);
        font-family: "font-icons";
        color: $button-primary-color;
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        text-transform: none;
        position: absolute;
        font-size: $button-primary-icon-size;
        opacity: 0.6;
        vertical-align: baseline;
        right: 8px;
    }
}

//Button Secondary
.button-secondary {
    color: $button-secondary-color;
    font-family: $pfhandbookpro-medium;
    font-size: $font24;
    transition: background-color 0.2s ease;
    border-radius: 2px;
    border: 2px solid $button-secondary-border-color !important;
    font-weight: 500;
    padding: 18px 45px 18px 16px;
    position: relative;
    &:hover,
    &:active,
    &:focus {
        background: $button-secondary-active-color;
        color: $button-secondary-color;
        border: 2px solid $button-secondary-hover-border-color !important;
    }
    &:after {
        content: $icon-right-arrow;
        top: 50%;
        transform: translate(-50%, -50%);
        font-family: "font-icons";
        color: $button-secondary-color;
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        text-transform: none;
        position: absolute;
        font-size: $button-secondary-icon-size;
        vertical-align: baseline;
        right: 8px;
    }
    &--capital-red {
        background: $button-capital-red-bg;
        border: 0;
        border-radius: 0;
        padding: 7px 16px !important;
        &:hover,
        &:active,
        &:focus {
            background: $button-capital-red-hover;
            border: none;
        }
    }
}

//Quick Services Button
.qsbutton {
    min-width: 109px;
    padding: 15px 27px;
    font-family: $pfhandbookpro-regular;
    font-size: $font20;
    border-radius: 2px;
    border-color: transparent;
    border: none;
    outline: none;
    background-color: transparent;
    &__mf {
        background-color: $mutualfund-button-bg;
        color: $mutualfund-button-color;
        &--inverse {
            color: $mutalfund-button-inverse-color;
            border: 1px solid $mutualfund-button-border;
        }
        &:hover,
        &:active,
        &:focus {
            background: $mutualfund-button-bg;
            color: $mutualfund-button-color;
            border: none;
            outline: none;
        }
    }
    &__li {
        background-color: $lifeinsurance-button-bg;
        color: $lifeinsurance-button-color;
        &--inverse {
            color: $lifeinsurance-button-inverse-color;
            border: 1px solid $lifeinsurance-button-border;
        }
        &:hover,
        &:active,
        &:focus {
            background: $lifeinsurance-button-bg;
            color: $lifeinsurance-button-color;
            border: none;
            outline: none;
        }
    }
    &__fin {
        background-color: $fin-button-bg;
        color: $fin-button-color;
        &:hover,
        &:active,
        &:focus {
            background: $fin-button-bg;
            color: $fin-button-color;
            border: none;
            outline: none;
        }
    }
}

//Promotion CTA
.promotion-cta {
    border-radius: 2px;
    padding: 10px 48px 10px 18px;
    font-family: $pfhandbookpro-bold;
    font-size: $font20;
    color: $promotion-cta;
    display: inline-block;
    position: relative;
    text-transform: uppercase;
    &:after {
        content: $icon-right-arrow;
        top: 50%;
        transform: translate(-50%, -50%);
        font-family: "font-icons";
        color: $promotion-cta-icon-color;
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        text-transform: none;
        position: absolute;
        font-size: $promotion-cta-icon-font;
        vertical-align: baseline;
        right: 8px;
    }
    &:hover,
    &:active,
    &:focus {
        color: $promotion-cta-icon-color;
        border: none;
    }
}

//Big CTA
.big-cta {
    display: inline-block;
    padding: 10px 59px 6px 10px;
    position: relative;
    &__small-text {
        font-size: $font14;
        line-height: $line-height-20;
        text-transform: uppercase;
    }
    &__big-text {
        font-size: $font20;
        line-height: $line-height-20;
    }
    span {
        color: $big-cta-color;
        display: block;
    }
    &:after {
        color: $big-cta-icon-color;
        font-family: "font-icons";
        content: $icon-right-arrow;
        font-size: $font9;
        position: absolute;
        right: 15px;
        top: 6px;
    }
    &:hover,
    &:active,
    &:focus {
        border: none;
    }
}

//primary cta for website
.button-brown {
    color: $btn-brown-color;
    text-align: center;
    font-size: $font20;
    line-height: $line-height-20;
    text-transform: capitalize;
    padding: 1.2rem;
    min-width: 15.4rem;
    font-family: $pfhandbookpro-bold;
    border-radius: 2px;
    &:hover,
    &:active,
    &:focus {
        color: $btn-brown-color-hover;
        box-shadow: 0 0 6px 0 $btn-brown-hover-shadow;
        border: none;
    }
}

.button-link {
    text-align: center;
    min-width: 0;
    padding: 0 2rem;
    color: $btn-link-color;
    font-size: $font20;
    font-family: $pfhandbookpro-bold;
    background: transparent;
    text-decoration: underline;
    text-transform: capitalize;
    &--icon {
        text-decoration: none;
    }
    .icon {
        font-size: $font18;
        margin-right: 1rem;
        display: inline-block;
        vertical-align: middle;
    }
    &:hover,
    &:active,
    &:focus {
        border: none;
        text-decoration: underline;
    }
}

.button-border {
    text-align: center;
    font-size: $font20;
    line-height: $line-height-20;
    background: transparent;
    border: 1px solid $btn-border-border;
    padding: 1.1rem 2rem;
    font-family: $pfhandbookpro-bold;
    text-transform: capitalize;
    &:hover,
    &:active,
    &:focus {
        border: 1px solid $btn-border-border;
        background: transparent;
    }
}


// HFL Buttons
.hfl-btn {
  text-decoration: none;
  outline: none;
  border: none;
  box-sizing: border-box;
  border-radius: 3px;
  font-family: $pfhandbookpro-regular;
  min-width: 120px;
  padding: 12px 24px;
  font-size: $font18;
  display: inline-block;



  &.btn-primary {
    background: $button-primary-background-color;
    color: $button-primary-color;
    border: 1px solid $button-primary-background-color;

    &:active,
    &:focus {
      background: $button-primary-active-color !important;
      color: $button-primary-color !important;
      border: 1px solid $button-primary-background-color !important;
    }
  }

  &.btn-secondary {
    color: $button-secondary-color;
    /* border: 1px solid $button-secondary-border-color;*/
    background: transparent;
    border: 1px solid;

    &:active,
    &:focus {
      background: transparent !important;
      color: $button-secondary-color !important;
    }
  }

  &.cta {
    font-family: $pfhandbookpro-bold;
  }

  &.cta-big {
    font-size: $font20;
    padding: 14px 26px;
    border-radius: 8px;
  }

  &.icon-btn {
    padding-right: 50px;
    position: relative;

    &:after {
      content: $icon-plus;
      top: 45%;
      transform: translate(-50%, -50%);
      font-family: "font-icons";
      color: $button-primary-color;
      font-style: normal;
      font-variant: normal;
      font-weight: normal;
      line-height: 1;
      text-transform: none;
      position: absolute;
      font-size: $button-primary-icon-size;
      vertical-align: baseline;
      right: 8px;
    }
  }

  &.btn-forward {
    position: relative;
    text-align: left;

    &:active,
    &:focus {
      background: $button-primary-active-color !important;
      color: $button-primary-color !important;
      border: 1px solid $button-primary-background-color !important;
    }

    &:after {
      content: $icon-next;
      top: 45%;
      transform: translate(-50%, -50%);
      font-family: "font-icons";
      color: $button-primary-color;
      font-style: normal;
      font-variant: normal;
      font-weight: normal;
      line-height: 1;
      text-transform: none;
      position: absolute;
      font-size: $button-primary-icon-size;
      vertical-align: baseline;
      right: 0px;
    }
  }

  &.btn-disabled {
    background: $button-disabled-color;
    color: $button-secondary-color;
    border: 1px solid $button-disabled-color;

    &:after {
      color: $button-secondary-color;
    }
  }

  &.clear-btn {
    color: $button-secondary-color;
    padding: 0px;
    min-width: 5px;
    display: block;

    &:hover {
      color: $button-secondary-color;
    }
  }

  &.whitebg-btn {
    background: $white;
    color: $black;
    box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.3);
  }
}

.buttonGoBack {
  line-height: $line-height-28;
  text-align: left;
  border: none;
  border-radius: 4px;
  padding: 0;
  white-space: normal;
  opacity: 1;
  min-width: 184px;
  display: inline-block;
}
//Button Primary
.button-buttonGoBack {
