.dashboard {
    &__greetings-component-wrapper {
        margin-top: 17px;

        @media screen and (min-width: $custom-screen-max-width) {
            margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font17});
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            margin-top: 1.7vw;
        }
    }
    &__link-solutions-component {        
        margin-left: auto;
        padding: 8px 24px;
        border: 1px solid $brand-capital-red;
        border-radius: 3px;
        display: flex;
        flex-shrink: 0;
        flex-grow: 0;
        align-self: flex-start;    
        
        @media screen and (min-width: $custom-screen-max-width) {
            padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font24});
            border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.3rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            padding: 0.8vw 2.4vw;
            border-radius: 0.3vw;
        }
        // @include mQmin($xxl) {
        //     padding: 9px 25px 6px;
        //     border-radius: 3px;
        // }
        // @include mQmin($xxxl){
        //     padding: 14px 40px;
        //     border-radius: 3px;
        //     margin-right: 5px;
        // }


        @include mQ(767px) { 
            margin-left: 0;
            width: 100%;
            padding: 13px 24px;
        }
        @include mQ(575px) {
            padding: 8px 10px;
        }
        .dashboard {
            &__link-solutions-copy {
                @include mQ(767px) { 
                    margin-right: 24px;
                }
                @include mQ(575px) { 
                    margin-right: 20px;
                }
                p {
                    color: $black;
                    font-size: $font14;         
                    line-height: 16px;
                    margin-bottom: 0;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
                        line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.4vw;
                        line-height: 1.6vw;
                    }

                    // @include mQmin($xxl) {
                    //     font-size: 17px;
                    //     line-height: 17px;
                    // }
                    // @include mQmin($xxxl) {
                    //     font-size: 24px;
                    //     line-height: 21px;
                    // }

                    @include mQ(767px) { 
                        font-size: $font24;
                        line-height: 24px;
                        margin-bottom: 5px;         
                    }
                    @include mQ(575px) {
                        font-size: $font15;
                        line-height: 15px;
                        margin-bottom: 0;         
                    }
                }
                .cta__flat {
                    font-size: $font12;
                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.2vw;
                    }

                    // @include mQmin($xxl){
                    //     font-size: 14px;
                    // }
                    // @include mQmin($xxxl) {
                    //     font-size: 20px;
                    // }

                    &:hover,
                    &:focus,
                    &:active {
                        border-bottom: 0 none;
                    }
                    @include mQ(767px) { 
                        font-size: $font16;
                    }
                    @include mQ(575px) {
                        font-size: $font11;
                    }   
                }
                .btn-tooltip {
                    background-color: $brand-support-yellow;
                    color: $black;
                    width: 14px;
                    height: 14px;
                    text-align: center;
                    display: inline-block;
                    margin-left: 8px;
                    font-size: $font12;
                    line-height: 14px;
                    border-radius: 50%;
                    cursor: pointer;   
                    
                    @media screen and (min-width: $custom-screen-max-width){
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                        width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
                        height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
                        margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8});
                        line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        width: 1.4vw;
                        height: 1.4vw;
                        margin-left: 0.8vw;
                        font-size: 1.2vw;
                        line-height: 1.4vw;
                    }
                    // @include mQmin($xxl) {
                    //     width: 14px;
                    //     height: 14px;
                    //     margin-left: 15px;
                    //     font-size: 12px;
                    //     line-height: 14px;
                    // }


                    &:hover,
                    &:focus,
                    &:active {
                        border-bottom: 0 none;
                    }
                    @include mQ(575px) {
                        width: 12px;
                        height: 12px;
                        margin-left: 5px;
                        font-size: $font10;
                        line-height: 12px;
                    }
                }
            }            
            &__link-solutions-cta-wrapper {
                margin-left: 24px;
                @media screen and (min-width: $custom-screen-max-width) {
                    margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font24});
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    margin-left: 2.4vw;
                }
                // @include mQmin($xxxl){
                //     margin-left: 44px;
                // }
                @include mQ(767px) { 
                    margin-left: auto;
                }
                .btn-orange {
                    background-color: #f7a500;
                    padding: 5px 23px 5px;
                    font-size: 14.4px;
                    font-family: "pfhandbookpro-bold", sans-serif;
                    border-bottom: none;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
                        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem) 
                        calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.3rem) 
                        calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem)
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.4vw;
                        padding: 0.5vw 2.3vw 0.5vw;
                    }
                    
                    // @include mQmin($xxl){
                    //     font-size: 19px;
                    //     padding: 5px 40px;
                    // }
                    // @include mQmin($xxxl) {
                    //     font-size: 2.7rem;
                    //     padding: 6px 55px;
                    // }

                    &:hover, &:active, &:focus {
                        color: $black;
                    }
                    @include mQ(767px,576px) {
                        font-size: 2.2rem;
                        padding: 8px 23px 8px;
                    }
                    @include mQ(575px) {
                        font-size: 1.2rem;
                        padding: 8px 20px;
                    }
                }                
            }
        }
    }
}