.free-text {
  position: relative;
  padding: 48px 0;
  text-align: center;
  @include for-phone {
    text-align: left;
  }
  &__subtext {
    margin-top: 24px;
    .icon {
      font-size: $font22;
      margin-right: 6px;
    }
    span {
      &:nth-last-child(1) {
        @include for-phone {
          display: block;
          margin: 12px 0 8px 0;
        }
      }
    }
  }
}
