//income-info
.income-info {
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $ii-black-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1rem;
    }
    &__p {
        font-family: $pfhandbookpro-thin;
        color: $ii-black-clr;
        font-size: $font24;
        line-height: $line-height-26;
        margin-bottom: 1.8rem;
    }
    &__close {
        font-size: $font18;
        color: $ii-black-clr;
        position: absolute;
        top: 2rem;
        right: 2rem;
    }
}