.company-info {
    padding: 0 4.8rem 0 12rem;
    max-width: 1400px;
    @include for-tablet {
        padding: 0 1.5rem;
    }
    &__desc {
        color: $company-color;
        font-size: $font18;
        line-height: $line-height-23;
        @include for-phone {
            font-size: $font14;
        }
    }
}