//data-value
.data-value{
    padding: 2rem 0;
    @include for-tablet{
        padding: 2rem 0;
    }
    &__wrap{
        padding: 0;
        margin-bottom: 0;
    }
    &__item{
        display: inline-block;
        vertical-align: middle;
        margin-right: 8rem;
        @include for-tablet{
            margin-bottom: 0;
            margin-right: 4rem;
        }
        @include for-phone{
            margin-bottom: 2rem;
            margin-right: 4rem;
        }
        &:last-child{
            margin-right: 0;
        }
        &:last-child,&:nth-last-child(2){
            @include for-tablet{
                margin-bottom: 0;
            }
        }
    }
    &__label{
        display:block;
        font-size: $font20;
        line-height: $line-height-1-1;
        color: $dv-label-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 0.6rem;
        @include for-tablet{
            font-size: $font16;
            line-height: $line-height-18;
            margin-bottom: 0.2rem;
        }
    }
    &__value{
        font-size: $font24;
        line-height: $line-height-26;
        color: $dv-value-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 0;
        @include for-tablet{
            font-size: $font20;
            line-height: $line-height-22;
        }
    }
}