
.download-filter{
    .form-row--download{
        .col-xs-6{
            @include for-tablet{
                flex: 0 0 50%;
                max-width: 50%;
            }
            @include for-phone{
                flex: 0 0 50%;
                max-width: 50%;
            }
            
        }
        .col-xs-12{
            @include for-tablet{
                flex: 0 0 100%;
                max-width: 100%;
            }
            @include for-phone{
                flex: 0 0 100%;
                max-width: 100%;
            }
        }
    }
    .abc-form--download-filter .row{
        @include for-tablet{
            width: 100%;
            margin: 0;
        }
    }
    .abc-form--download-filter {
        .selectize-input {
            .item ~ .item {
                display: none;
            }
        }
    }
    &__second-field{
        margin-top: 4rem;
        @include for-tablet{
            margin-top: 1rem;
        }

        @include for-phone{
            margin-top: 1rem;
        }

    }
}