//card-info
.card-info {
    padding: 3rem 4.5rem;
    background: $ci-white-clr;
    border: 1px solid $ci-border-clr;
    box-shadow: 0 0 14px 0 rgba(106, 71, 66, 0.24);
    border-radius: 0.2rem;
    @include for-phone {
        padding: 2rem;
    }
    &__title {
        font-size: $font44;
        line-height: $line-height-46;
        color: $ci-black-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1.2rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
            margin-bottom: 1.6rem;
        }
    }
    &__p {
        font-size: $font24;
        line-height: $line-height-26;
        color: $ci-black-clr;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 1rem;
        &:last-child {
            margin-bottom: 0;
        }
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
        a {
            font-size: $font24;
            line-height: $line-height-26;
            color: $ci-black-clr;
            font-family: $pfhandbookpro-thin;
            @include for-phone {
                font-size: $font16;
                line-height: $line-height-18;
            }
        }
    }
    &__desc {
        margin-bottom: 2rem;
    }
    &__act-wrap {
        text-align: right;
    }
}