.chat-bot {
    $self: &;
    
    &__btn {
        background: $chat-btn-bg;
        width: 6.4rem;
        height: 6.4rem;
        line-height: $line-height-64;
        // padding: 1.7rem 1rem;
        border-radius: 50%;
        position: fixed;
        bottom: 10rem;
        right: 10rem;
        z-index: 10001;
        text-align: center;
        border-color: $chat-btn-bg;
        transition: all 0.3s ease;
        border: 0;
        @include for-phone {
            opacity: 0.6;
        }
        &--typ2 {
            background: $chat-btn-typ2-bg;
            border-color: $chat-btn-typ2-bg;
            #{$self}__icon {
                color: $chat-btn-typ2-color;
            }
        }
        &--opaque {
            opacity: 0.5;
        }
        &--chat {
            line-height: $line-height-72;
        }
        #{$self}__btn-label {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 125%;
            font-size: $font18;
            line-height: $line-height-1;
            color: $chat-btn-lbl;
            font-family: $pfhandbookpro-bold;
            width: 11rem;
            text-align: right;
        }
        &--child {
            position: relative;
            right: auto;
            bottom: auto;
        }
    }
    &__icon {
        color: $chat-btn-icon;
        font-size: $font24;
        @include for-phone {
            font-size: $font14;
        }
    }
    &__desktop {
        @include for-md-desktop {
            display: none;
        }
    }
    &__mobile {
        display: none;
        #{$self}__btn {
            bottom: 5rem;
            right: 2.4rem;
            &--child {
                bottom: auto;
                right: auto;
            }
        }
        @include for-md-desktop {
            display: block;
        }
        &--active {
            #{$self}__btn {
                background: $chat-mob-active;
                border-color: $chat-mob-active;
                @include for-phone {
                    opacity: 1;
                }
                #{$self}__icon {
                    color: $chat-mob-active-color;
                }
                &--rotate {
                    transform: rotate(45deg);
                }
            }
            #{$self}__action-wrap {
                visibility: visible;
                opacity: 1;
                #{$self}__item {
                    // &:first-child {
                    //     #{$self}__btn {
                    //         bottom: 24rem;
                    //         @include for-phone {
                    //             bottom: 21rem;
                    //         }
                    //     }
                    // }
                    // &:last-child {
                    //     #{$self}__btn {
                    //         bottom: 17rem;
                    //         @include for-phone {
                    //             bottom: 13rem;
                    //         }
                    //     }
                    // }
                }
            }
        }
    }
    &__action-wrap {
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease;
        margin: 0;
        position: fixed;
        right: 10rem;
        bottom: 16rem;
        z-index: 10001;
        @include for-md-desktop {
            right: 2.5rem;
            bottom: 12rem;
        }
        @include for-phone {
            .chat-bot__icon {
                font-size: $font24;
            }
        }
    }
    &__item {
        margin-bottom: 2rem;
    }
}

.overlay {
    // display: none;
    background: $chat-overlay;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    &--active {
        opacity: 1;
        visibility: visible;
    }
    @include for-tablet {
        display: block;
    }
}