.login-left-col,
.login-right-col {
    //padding-top: 35px;
    padding-top: 25px;
    padding-bottom: 20px;
}
.login-right-col {
    position: relative;
    display: flex;
    align-items: stretch;
    height: 100%;
    background-color: #f8fcff;
    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: -100%;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-color: inherit;
    }
    @include for-tablet {
        &::before {
            top: 0;
            left: 100%;
        }
    }
    @include for-phone {
        &::before {
            top: 0;
            left: -15px;
            width: 15px;
        }
        &::after {
            content: "";
            position: absolute;
            right: -15px;
            top: 0;
            width: 15px;
            height: 100%;
            z-index: -1;
            background-color: inherit;
        }
    }
    @include mQ(767px){
        padding-top: 0;
    }
}
// Form Elements
.abc-form {
    &.dropdown-sms-form {
        .form-group {
            display: block !important;
        }
        .account-statement-sms-content-wrapper {
            .form-group {
                display: flex !important;
            }
        }
    }
    &.abc-post-login-form {
        .login-left-col &{
            padding-left: 40px;
            padding-right: 40px;

            @include mQ(767px) {
                padding-left: 5px;
                padding-right: 5px;
            }
        }
        h2 {
            font-family: $pfhandbookpro-bold;
            font-size: $font24;
            line-height: 1.25;
            width: 100%;
            margin-bottom: 8px;
            @include mQ(767px, 576px) {
                font-size: 3.52rem;
            }
        }
        .form-group {
            margin-top: 10px;
            display: flex;
            flex-wrap: wrap;
            &__label {
                font-size: $font20;
                opacity: 0.7;
                color: $black;
                margin-bottom: 0;
            }
            .has-btn-overlay {
                .form-control {
                    padding-right: 70px;
                }
            }
        }
        .form-group__placeholder,
        .input-group__placeholder {
            font-size: $font20;
            padding: 0.375rem 1.8rem;
            height: 50px;
        }
        .form-input-wrapper {
            width: 100%;
            position: relative;
            display: flex;
            flex-wrap: wrap;
        }
        .radio-button-collection {
            display: flex;
            .radio-input-wrapper {
                position: relative;
                label {
                    position: relative;
                    padding-left: 30px;
                    padding-right: 30px;
                    &::before {
                        content: "";
                        width: 16px;
                        height: 16px;
                        position: absolute;
                        top: 2px;
                        left: 0;
                        border: 1px solid $form-input-border;
                        border-radius: 50%;
                    }
                }   
                input[type="radio"] {
                    position: absolute;
                    opacity: 0;
                    height: 0;
                    width: 0;
                    cursor: pointer;
                    &:checked + label {
                        &::after {
                            content: "";
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            top: 5px;
                            left: 3px;
                            background-color: $capital-red;
                            border-radius: 50%;
                        }
                    }
                } 
            }
        }
        span.error {
            font-family: $pfencoresanspro-reg;
            font-size: $font12;
            color: #6c7174;
            border: none !important;
            background-color: transparent !important;
            margin-top: 7px;
            display: block;
            position: relative;
            padding-left: 28px;
            &:before {
                content: "\f06a";
                width: 20px;
                height: 20px;
                border-radius: 50%;
                position: absolute;
                left: 0px;
                top: 0;
                color: #F7A500;
                font-size: 23px;
                text-align: center;
                font-family: FontAwesome;
            }
        }
        .selectize-dropdown [data-selectable].option {
            box-shadow: inset 0 -1px 0 0 $brand-white-grey;
        }
        .form-inline-link {
            margin-left: auto;
            padding-bottom: 8px;
            font-size: $font16;
            color: $black;
            &:hover,
            &:focus,
            &:active {
                border-bottom: 0 none;
            }
            @include mQ(767px,576px) {
                font-size: 2.3rem;
                padding-top: 8px;
                padding-bottom: 0px; 
            }
        }
        .btn-tooltip {
            background-color: $brand-support-yellow;
            width: 16px;
            height: 16px;
            text-align: center;
            display: inline-block;
            margin-left: 8px;
            margin-top: 3px;
            font-size: $font12;
            line-height: 16px;
            border-radius: 50%;
            cursor: pointer;            
            &:hover,
            &:focus,
            &:active {
                border-bottom: 0 none;
            }
        }        
        .btn {
            font-weight: bold;
            &:disabled {
                opacity: 1;
                background-color: $raven-gray;
            }
            &__full {
                width: 100%;
            }
            &__overlay {
                font-size: 1.6rem;
                margin: 0;
                padding: 0;
                background: none;
                border: 0 none;
                position: absolute;
                right: 12px;
                top: 50%;
                transform: translateY(-50%);
                color: $capital-red;
                @include mQ(767px,576px) {
                   
                    font-size: 2.3rem;
                }
                &.use-otp-cta {
                    top: 25px;
                    @include for-phone {
                        top: 35px;
                    }
                    @include mQ(575px) {
                        top: 22px;
                    } 
                }
            }
        }
        .form-checkbox-wrapper {
            position: relative;
            input[type="checkbox"] {
                visibility: hidden;
                position: absolute;
                width: 1px;
                height: 1px;
                &:checked + label::after {
                    z-index: 1;
                    content: "";
                    position: absolute;
                    left: 7px;
                    top: 4px;
                    width: 6px;
                    height: 9px;
                    border-width: 0 2px 2px 0 !important;
                    -ms-transform: rotate(45deg);
                    transform: rotate(45deg);
                    border: solid $capital-red;
                }
            }
            label {
                padding-left: 28px;
                &::before {
                    content: "";
                    position: absolute;
                    border: 1px solid $grey-shade-1;
                    width: 19px;
                    height: 19px;
                    left: 0;
                    top: 0;
                }            
            }
        }        
        .btn__border {
            width: 100%;
            border-radius: 2px;
            line-height: 1.5;
            background-color: $white;
            box-shadow: 1px 0 8px 0 rgba($black, 0.08);
            border-left: 6px solid $capital-red;
            color: $black;
            font-weight: bold;
            padding-left: 14px;
            padding-right: 55px;
            font-size: $font17;
            position: relative;
            padding-top: 10px;
            padding-bottom: 10px; 
            &:hover,
            &:focus,
            &:active {
                border-bottom: 0 none;
            }
            &:after {
                content: $icon-right-arrow-thin;
                top: 50%;
                transform: translate(-50%, -50%);
                font-family: "font-icons";
                color: $black;
                font-style: normal;
                font-variant: normal;
                font-weight: normal;
                line-height: 1;
                text-transform: none;
                position: absolute;
                font-size: $button-primary-icon-size;
                opacity: 0.6;
                vertical-align: baseline;
                right: 5px;
            }
            @include mQ(768px){
                font-size: $font13;
            }
            @include mQ(360px){
                font-size: $font15;
            }
        }
        .cta__flat {
            font-family: $pfhandbookpro-medium;
            text-decoration: underline;
            display: inline-block;
            font-size: 1.8rem;
            line-height: 2rem;
            &:hover,
            &:focus,
            &:active {
                border-bottom: 0 none;
            }

            @include mQ(768px){
                font-size: $font13;
            }
            @include mQ(576px){
                font-size: $font17;
            }
            @include mQ(360px){
                font-size: $font11;
            }
        }
        &.pf-my-docs-soa-form,
        &.sme-my-docs-soa-form,
        &.mortgage-my-docs-soa-form,
        &.las-my-docs-soa-form,
        &.pf-my-docs-interest-certificate-form {
            span.error {
                width: 100%;
            }                        
        }       
    }
}

// Modal
#repeatUser {
    padding-right: 0 !important;
} 
.abc-form {
    &.abc-post-login-form-modal {
        h2 {
            font-family: $pfhandbookpro-regular;
            color: $black;
            padding-right: 30px;
            margin-bottom: 10px;
            @include mQ(767px) {
                margin-bottom: 25px;
            }
        } 
        .form-group {
            .has-btn-overlay {
                .form-control {
                    padding-right: 110px;
                    @include mQ(767px) {
                        padding-right: 140px;
                    }
                }
            }
            .act-btn {
                margin-top: 80px;
                margin-bottom: 17px;
                @include mQ(767px) {
                    margin-top: 50px;
                }
            }
        }
        .use-otp-toggle-password {
            .use-otp-cta-modal {
                right: 55px !important;
                @include mQ(767px) {
                    top:35px !important;
                }
                @include mQ(575px) {
                    top:22px !important;
                }
            }
            .toggle-password {
                top:25px;

                .icon {
                    font-size: 2.2rem;
                }
                @include mQ(767px) {
                    top:35px;
                    .icon {
                        font-size: 2.6rem;
                    }
                }
                @include mQ(575px) {
                    top:22px;
                    .icon {
                        font-size: 2.2rem;
                    }
                }
            }
        }
    }
}