.column-grid {
  display: flex;
}

.category {
  &.pink {
    .category-cta {
      border-color: $pink;
      color: $pink;

      &:hover,
      &:focus,
      &:active {
        background-color: $pink;
        color: $white;
        text-decoration: none;
        border-left: 9px solid $pink;
      }
    }

    .article {
      border-color: $pink;
    }

    .tag {
      background: $pink;
    }
  }

  &.yellow {
    .category-cta {
      border-color: $yellow;
      color: $yellow;

      &:hover,
      &:focus,
      &:active {
        background-color: $yellow;
        color: $white;
        text-decoration: none;
        border-left: 9px solid $yellow;
      }
    }

    .article {
      border-color: $yellow;
    }

    .tag {
      background: $yellow;
    }
  }

  &.green {
    .category-cta {
      border-color: $green;
      color: $green;

      &:hover,
      &:focus,
      &:active {
        background-color: $green;
        color: $white;
        text-decoration: none;
        border-left: 9px solid $green;
      }
    }

    .article {
      border-color: $green;
    }

    .tag {
      background: $green;
    }
  }
}

.article-title-link {
  pointer-events: none;
  cursor: default;
  text-decoration: none;
  color: black !important;
  border: none !important;

  @include mQ(1020px) {
    pointer-events: unset;
    cursor: pointer;

    &:hover {
      color: $black;
      border-bottom: none;
    }
  }
}

.article {
  margin-bottom: 45px;
  border-left: solid 4px $capital-red;
  @include box-shadow(
    $x-axis: 1px,
    $y-axis: -2px,
    $blur: 8px,
    $color: rgba(0, 0, 0, 0.08)
  );
  border-radius: 4px;
  width: 100%;
  position: relative;
  &-cta {
    display: none;
    position: absolute;
    top: 0;
    height: 100%;
    z-index: 11;
    width: 100%;
    cursor: pointer;
    border: 0 none !important;
  }

  &-img {
    position: relative;
    height: 180px;
    overflow: hidden;
    border-radius: 0 4px 0 0;

    @include mQ(1020px) {
      height: 130px;
      width: 130px;
    }

    picture {
      /*top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      position: absolute;
      max-width: inherit;*/

      @include mQ(1020px) {
        position: relative;
        top: auto;
        left: auto;
        transform: translate(0%, 0%);
        max-width: 100%;
      }
    }

    img {
      width: auto;
      border-radius: 0 4px 0 0;
      margin-bottom: 0;
      min-height: 180px;
      max-width: inherit;

      @include mQ(1020px) {
        min-height: auto;
      }
    }
  }

  &-details {
    padding: 12px 15px 13px 30px;

    .tag {
      display: none;
    }

    @include mQ(1200px) {
      padding: 12px 15px 13px 15px;
    }
  }

  &-updates {
    display: flex;
    justify-content: space-between;
  }

  &-desc {
    h3,
    .article-title-link {
      font-size: 2rem;
      font-family: $pfhandbookpro-medium;
      line-height: 25px;
      margin-bottom: 20px;
    }

    .read-more {
      margin-bottom: 20px;
      &:hover {
        text-decoration: none;
        color: $white !important;
      }
    }
  }

  &-posted {
    font-size: 1.4rem;
    font-family: $pfhandbookpro-medium;
    line-height: 20px;

    @include mQ(1200px) {
      font-size: 1.2rem;
    }
  }
}

.no-image {
  // margin-top: 2px;
  .tag {
    position: relative;
  }
}

.tag {
  background: $capital-red;
  color: $white;
  position: absolute;
  bottom: 0;
  font-size: 1.4rem;
  font-family: $pfhandbookpro-medium;
  line-height: 20px;
  padding: 5px 30px;
  left: 0;

  &.tag-hide {
    opacity: 0;
  }

  @include mQ(1200px) {
    padding: 5px 15px;
  }
}

@include mQ($screen-sm) {
  .column-grid {
    .article {
      position: relative;

      &-updates {
        position: absolute;
        bottom: 10px;

        .like-component .increment .icon {
          font-size: 1.5rem;
        }

        .like-component .count-wrapper {
          font-size: 1rem;
          margin-left: 10px;
        }

        .like-shares .share-component {
          margin-left: 29px;
        }

        .share-component .share-wrapper .icon {
          font-size: 1.5rem;
        }

        .share-component .count-wrapper {
          font-size: 1rem;
          margin-left: 4px;
        }
      }
    }
  }

  .article {
    display: flex;
    flex-flow: row-reverse;
    padding: 10px;
    margin-bottom: 40px;

    &-details {
      padding: 0 10px 0 0;
      flex: 1;

      .tag {
        display: inline-block;
        position: static;
        font-size: 1.2rem;
        margin-left: -10px;
        padding: 1px 6px;
        margin-bottom: 5px;
      }
    }

    &-updates {
      justify-content: flex-start;
      flex-direction: column-reverse;
    }

    &-posted {
      font-size: 1.3rem;
      line-height: 15px;
    }

    &-desc {
      h3,
      .article-title-link {
        font-size: 1.6rem;
        line-height: 19px;
        margin-bottom: 7px;
      }

      .read-more {
        display: none;
      }
    }

    &-img {
      img {
        width: auto;
        border-radius: 4px;
      }

      .tag {
        display: none;
      }
    }
  }
}

@include mQ(767px) {
  .column-grid {
    .article {
      &-img {
        width: 120px;
        height: 120px;
      }
    }
  }
  .like-shares {
    display: none;
  }

  .article {
    margin-bottom: 20px;

    &-desc {
      h3 {
        font-size: 1.4rem;
        line-height: 17px;
      }

      .article-title-link {
        font-size: 1.6rem;
        line-height: 19px;
      }
    }

    &-posted {
      font-size: 1.2rem;
      line-height: 14px;
    }
  }
}

.category {
  .column-grid {
    .article {
      min-height: 360px;

      @include mQ(1020px) {
        min-height: unset;
      }
    }
  }
}

@include mQ(767px) {
  .article-cta {
    display: block;
  }
}
