//icon-card
.calc-icon-card {
    $this: &;
    text-align: center;
    padding: 2rem 1rem;
    background: $ir-card-bg;
    border: 1px solid $ir-border-clr;
    box-shadow: 0 0 19px 0 rgba(106, 71, 66, 0.24);
    position: relative;
    height: 14.9rem;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
    @include for-phone {
        height: 12.5rem;
    }
    &__icon {
        font-size: $font50;
        width: 5rem;
        height: 5rem;
        display: block;
        margin: 0 auto 1rem;
        color: $ir-clr;
        transition: all 0.3s ease;
        @include for-phone {
            font-size: $font30;
            width: 3rem;
            height: 3rem;
        }
    }
    &__text {
        font-size: $font20;
        line-height: $line-height-22;
        color: $ir-clr;
        font-family: $pfhandbookpro-regular;
        transition: all 0.3s ease;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__check-box {
        width: 2.4rem;
        height: 2.4rem;
        border: 2px solid $ir-active-clr;
        border-radius: 0.4rem;
        background: $ir-card-bg;
        position: absolute;
        top: -1rem;
        left: 0;
        right: 0;
        margin: 0 auto;
        transition: all 0.3s ease;
        @include for-phone {
            width: 2rem;
            height: 2rem;
            border-width: 1px;
        }
        #{$this}__check {
            color: $ir-card-bg;
            font-size: $font12;
            position: absolute;
            top: 40%;
            left: 35%;
            transform: translate(-50%, -50%);
            @include for-phone {
                font-size: $font10;
                top: 39%;
                left: 29%;
            }
        }
    }
}