/*
 * 1260 - Horizontal Tabbed component
 *
 */

.dashboard {
    &__horizontal-tab-main-wrapper {
        width: 77.814%;
        position: relative;        
        @include mQ(991px) {
            width: 100%;
        }

        ul {
            display: flex;
            background-color: $brand-white-grey;
            padding-left: 0;
            position: relative;
            margin-bottom: 0;
            &:before {
                content: "";
                background-color: $white;
                width: 100%;
                position: absolute;
                left: 0;
                top: 0;
                height: 8px;
                display: block;
                margin-left: 0;

                @media screen and (min-width: $custom-screen-max-width) {
                    height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    height: 0.8vw;
                }

                @include mQ(991px) {
                    height: 10px;
                }
                @include mQ(575px) {
                    height: 6px;
                }
            }
            li {
                font-family: $pfhandbookpro-regular;
                font-size: $font18;
                text-align: center;
                background-color: $brand-white-grey;
                color: rgba(0, 0, 0, 0.5) !important;
                padding-left: 0;
                white-space: nowrap;
                padding: 14px 16px 0px 16px;
                height: 44px;
                display: inline-block;
                position: relative;
                cursor: pointer;

                @media screen and (min-width: $custom-screen-max-width) {
                    font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
                    padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16}) 0px calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                    height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.4rem);
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    font-size: 1.8vw;
                    padding: 1.4vw 1.6vw 0 1.6vw;
                    height: 4.4vw;
                }
                // @include mQmin($xxl){
                //     font-size: 22px;
                //     padding: 18px 16px 0 13px;
                //     height: 53px;
                // }


                &:last-child {
                    &:after {
                        display: none;
                    }
                }
                &:before {
                    content: "";
                    background-color: $white;
                    width: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                    height: 8px;
                    display: block;
                    margin-left: 0;

                    @media screen and (min-width: $custom-screen-max-width) {
                        height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        height: 0.8vw;
                    }
                    
                    @include mQ(991px) {
                        height: 10px;
                    }

                    @include mQ(575px) {
                        height: 6px;
                    }
                }
                &:after {
                    content: "";
                    height: 22px;
                    width: 1px;
                    display: block;
                    background-color: $ford-gray;
                    opacity: 0.4;
                    position: absolute;
                    right: -1px;
                    top: 50%;
                    margin-top: 5px;
                    z-index: 2;
                    transform: translateY(-50%);

                    @media screen and (min-width: $custom-screen-max-width) {
                        height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font22});
                        width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem);
                        right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.1rem);
                        margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        height: 2.2vw;
                        width: 0.1vw;
                        right: -0.1vw;
                        margin-top: 0.5vw;
                    }
                }
                &.active {
                    font-family: $pfhandbookpro-bold;
                    color: $white !important;
                    z-index: 1;
                    padding-top: 11px;
                    padding-left: 24px;
                    padding-right: 24px;

                    @media screen and (min-width: $custom-screen-max-width) {
                        padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
                        padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.4rem);
                        padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.4rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        padding-top: 1.1vw;
                        padding-left: 2.4vw;
                        padding-right: 2.4vw;
                    }


                    
                    &:before {
                        display: none;
                    }
                    &:after {
                        margin-top: 4px;
                        @media screen and (min-width: $custom-screen-max-width) {
                            margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem);
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            margin-top: 0.4vw;
                        }
                    }

                    // Color Theme
                    @include themifySelf($themes) {
                        background-color: themed('backgroundColor');  
                    }
                }

                @include mQ(991px) {
                    font-size: $font21;
                    line-height: 1.31;
                    height: 64px;
                    padding: 22px 32px 0px 32px;
                    &.active {
                        padding-left: 16px;
                        padding-right: 16px;
                        padding-top: 18px;
                        box-shadow: -10px -8px 42px 0 rgba(0, 0, 0, 0.05);
                    }
                }

                @include mQ(575px) {
                    font-size: $font13;
                    height: 40px;
                    padding: 16px 23px 0px 23px;
                    &.active {
                        padding-top: 13px;
                    }
                }

                // For You Tab
                &.for-you-tab {
                    &.active {
                        background-color: $brand-capital-red;
                    }                    
                }
            }
        }   
    }
    &__horizontal-tab-scroll-wrapper {
        width: 100%;
        overflow-x: auto;
        transition: all 0.2s;
        will-change: transform;
        user-select: none;
        -ms-overflow-style: none;
        &::-webkit-scrollbar {
            display: none;
        }
    }
    &__horizontal-tab-scroll-cta {
        position: absolute;
        background-color: $brand-capital-red;
        width: 24px;
        height: 24px;
        margin: 0;
        padding: 0;
        border: 0 none;
        color: $white;
        border-radius: 50%;
        top: 14px;
        z-index: 1;
        text-align: center;

        @media screen and (min-width: $custom-screen-max-width) {
            width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.4rem);
            height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.4rem);
            top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.4rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            width: 2.4vw;
            height: 2.4vw;
            top: 1.4vw;
        }

        @include mQ(991px) {
            top: 25px;
        }
        @include mQ(575px) {
            top: 10px;
        }
        .icon {
            display: block;
            font-size: $font12;
            line-height: 22px;

            @media screen and (min-width: $custom-screen-max-width) {
                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font22});
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                font-size: 1.2vw;
                line-height: 2.2vw;
            }
        }
        &.scroll-left {
            left: -12px;
            @media screen and (min-width: $custom-screen-max-width) {
                left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.2rem);
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                left: -1.2vw;
            }
            .icon {
                margin: 1px 1px 0 0;

                @media screen and (min-width: $custom-screen-max-width) {
                    margin: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem) 0 0;
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    margin: 0.1vw 0.1vw 0 0;
                }

            }

            @include mQ(991px) {
                left: 5px;
            }
        }
        &.scroll-right {
            right: -12px;

            @media screen and (min-width: $custom-screen-max-width) {
                right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.2rem);
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                right: -1.2vw;
            }

            .icon {
                margin: 1px 0 0 1px;

                @media screen and (min-width: $custom-screen-max-width) {
                    margin: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem) 0 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem);
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    margin: 0.1vw 0 0 0.1vw;
                }

            }
            @include mQ(991px) {
                right: 5px;
            }
        }
    }
}
