//Utility Links Start
.top-right-wrapper {
  margin-bottom: 20px;
  @include mQ(1020px) {
    position: absolute;
    right: 95px;
    top: 26px;
  }
  @include mQ(767px) {
    right: 65px;
  }
}
.loggedin-user {
  position: relative;
  vertical-align: middle;
  &:before {
    content: none;
  }
  .divicon {
    border-radius: 50px;
    padding: 3px 5px;
    border: 1px solid $utility-links-logged-in-border;
    margin-left: 0px;
    margin-top: 2px;
    background-color: $utility-links-logged-in-background;
    line-height: 0.89;
    font-size: 15px;
    width: 25px;
    height: 25px;
    text-transform: uppercase;
    font-family: $pfhandbookpro-regular;
    margin-left: 2px;
    &:hover {
      border: 1px solid $utility-links-logged-in-hover-border;
    }
  }
}
.dashboard-list {
  position: relative;
  display: inline-block;
  padding-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  vertical-align: top;
  top: 2px;
  @include mQ(1020px) {
    top: 0;
  }
  .divicon {
    border-radius: 50px;
    border: 1px solid $utility-links-logged-in-border;
    position: relative;
    background-color: $utility-links-logged-in-background;
    line-height: 18px;
    font-size: 9.8px;
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    @include mQ(767px) {
      height: 31px;
      width: 31px;
      font-size: 18.6px;
      line-height: 29px;
    }
    &:hover {
      border: 1px solid $utility-links-logged-in-hover-border;
    }    
  }
  .icon {
    font-size: $font12;
    line-height: 23px;
    @include mQ(1020px) {
      display: none;
    }
  }
  .loggedin-item {
    padding-top: 0;
    a {      
      &:hover,
      &:focus {
        border-bottom: none;
      }      
    }
    &.show {
      .login-dropdowm {
        display: block;
        width: 204px !important;
        margin-top: 15px !important;    
        right: -32px !important;
        padding-top: 5px !important;
        padding-bottom: 5px !important;
      }
    }
  }
  li:before {
    content: none;
  }
  .login-dropdowm {
    float: right;
    margin-top: 15px;
    height: auto;
    min-width: 204px;    
    background-color: $white;
    box-shadow: 0 6px 12px 0
      rgba($utility-links-logged-in-dropdown-shadow, 0.18);
    right: -32px;
    position: absolute;
    z-index: 999;
    padding: 0;
    display: none;
    padding-top: 5px;
    padding-bottom: 5px;
    li {
      padding-bottom: 5px;
      margin-left: 6px;
      margin-right: 6px;
      padding-top: 5px;
      padding-left: 10px;
      padding-right: 10px;
      a {
        font-size: 16px;
        line-height: 20px;
        color: $black;        
      }
      &.last_child {
        border-top: 1px solid $utility-links-dropdown-border;
        margin-top: 10px;
      }
    }
  }
  .arrow_up {
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid $utility-icon-arrow-color;
    position: absolute;
    right: 35px;
    top: -10px;
    @include mQ(1020px) {
      margin-right: 15px;
    }
  }
}
.top-right-item {
  margin-left: 15px;
  text-transform: uppercase;
  display: inline-block;
  a {
    &:hover {
      border-color: $utility-links-hover-border-color;
    }
  }
  .iconText {
    color: $utility-links-color;
    @include mQ(1280px, 1201px) {
      font-size: $font16;
    }
  }
  .icon {
    color: $utility-links-icon-color;
  }
  &.login-link {
    position: relative;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
  }
  &.dashboard-link {
    margin-right: 10px;
    a{
      background-color: $white;
      padding: 0 6px;
      border-radius: 10px;
      .iconText {
        vertical-align: top;
        color: $utility-links-logged-in-background;
        line-height: 20px;
      }
    }
  }
  @include mQ(1350px, 1020px) {
    &.phone-link {
      margin-left: 0;
    }
  }
  @include mQ(1199px) {
    margin-left: 5px;
  }
  span {
    &::before {
      font-size: 1.3rem;
      @include mQ(1200px) {
        font-size: 1.1rem;
      }
    }
  }
  .icon {
    margin-right: 0;
    margin-left: 2px;
  }
  a {
    @include mQ(1200px) {
      font-size: 1.6rem;
    }
  }
}
@include for-tablet {
  .utility-links {
   /* padding-right: 42px;
    padding-left: 21px;*/
    padding-top: 17px;
    padding-bottom: 19px;
    display: table;
    width: 100%;
    .icon,
    .loggedin-name {
      font-size: 2.5rem;
      display: table-cell;
      color: $utility-links-icon-color;
      text-decoration: none;
      border-bottom: none !important;
      padding-bottom: 0;
      text-align: center;
    }
    .icon-phone {
      text-align: center;
      border-left: 1px solid rgba($utility-links-mobile-icon-left-border, 0.2);
      border-right: 1px solid rgba($utility-links-mobile-icon-left-border, 0.2);
      padding-right: 6px;
      padding-left: 6px;
    }
    .icon-login {
      //text-align: right;
    }
  }
}
#mobi-navigation {
  @include for-tablet {
    .dashboard-list,
    .loggedin-name {
      padding: 0;
      vertical-align: text-top;
    }
  }
}
//Utility Links Ends
