 body {
  @include font-smoothing;
  font-family: $pfhandbookpro-regular;
  font-size: $font18;
  line-height: 20px;
  overflow-x: hidden;
  color: $black;
}
html {
  font-size: $font10;
  overflow-x: hidden;
}
$icon-font-path: "../fonts/";

/*
 *  Font Icons example
 *
 */

.social-icons a,
.social-icons a:hover {
  text-decoration: none;
}

.icon {
  font-size: 32px;
  margin: 4px;
  &.icon-instagram {
    color: $dark-grey;
  }
  &.icon-facebook {
    color: $facebookBlue;
  }
  &.icon-twitter {
    color: $twitterBlue;
  }
}

.icon .screenreader {
  display: none;
}

/*
 *  SVG Sprite example
 *
 */

.sprite {
  display: inline-block;
  fill: currentColor;
  margin: 8px;
  width: 32px;
  &.sprite-instagram {
    fill: $dark-grey;
  }
  &.sprite-facebook {
    fill: $facebookBlue;
  }
  &.sprite-twitter {
    fill: $twitterBlue;
  }
}
