.icon-card {
    &__list {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        padding: 0;
        margin-bottom: 0;
    }
    &__item {
        text-align: center;
        width: calc(20% - 3.2rem);
        border-radius: 2px;
        box-shadow: 0 0 14px 0 rgba($icon-card-shadow, 0.24);
        border: solid 1px $icon-card-border;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        padding: 3rem 2rem 2rem;
        margin-right: 3.2rem;
        margin-bottom: 2rem;
        position: relative;
        &:last-child {
            padding-bottom: 1rem;
        }
        @include for-tablet {
            width: calc(20% - 0.8rem);
        }
        @include for-phone {
            width: calc(50% - 0.8rem);
            margin-bottom: 1.6rem;
            padding: 2rem 1.2rem 1rem;
            &:nth-child(odd) {
                margin-right: 0.8rem;
            }
            &:nth-child(even) {
                margin-right: 0;
            }
        }
    }
    &__icon-wrap {
        margin-bottom: 1.5rem;
    }
    &__icon {
        font-size: $font70;
        line-height: $line-height-1;
        margin-bottom: 1rem;
        max-width: 7rem;
        @include for-phone {
            font-size: $font50;
            max-width: 5rem;
        }
    }
    &__name {
        font-size: $font20;
        line-height: $line-height-20;
        color: $icon-card-name;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}

// on-page-editor

.on-page-editor {
    .icon-card {
        &__list {
            & > div {
                width: calc(20% - 3.2rem);
                margin-right: 3.2rem;
                margin-bottom: 2rem;
            }
        }
        &__item {
            width: 100%;
            margin-right: 0;
            margin-bottom: 0;
        }
    }
}