.company-profile {
    padding: 4rem 0;
    @include for-phone {
        padding: 2rem 0;
    }
    .cat-pro-banner {
        &__desc {
            margin-bottom: 3rem;
        }
    }
    .tips__title {
        font-size: $font24;
        line-height: $line-height-26;
        margin-bottom: 2rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    .tips__item {
        padding-left: 1.5rem;
    }
}