//calc-form
.calc-form {
    $this: &;
    max-width: 73.6rem;
    margin: 0 auto;
    &__inline {
        display: inline-block;
        vertical-align: baseline;
        margin-right: 1rem;
        &:last-child {
            margin-right: 0;
        }
    }
    &__title {
        font-size: $font24;
        line-height: $line-height-26;
        color: $cf-text-clr;
        font-family: $pfhandbookpro-regular;
        text-transform: capitalize;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__header {
        margin-bottom: 1.5rem;
        @include for-phone {
            margin-bottom: 1.2rem;
        }
    }
    &__footer {
        background: $cf-footer-bg;
        border: 1px solid $cf-border-clr;
        box-shadow: 0 0 14px 0 rgba(106, 71, 66, 0.24);
        position: relative;
        height: 8rem;
        padding: 0;
        margin: 0;
        #{$this}__footer-item {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            &:first-child {
                left: 2rem;
                right: auto;
                @include for-phone {
                    left: 0.5rem;
                }
            }
            &:last-child {
                right: 2rem;
                left: auto;
            }
        }
        #{$this}__note {
            position: absolute;
            left: 0;
            top: -3rem;
            @include for-phone {
                top: -5rem;
                left: 1.5rem;
                right: 1.5rem;
            }
        }
    }
    &__form-item {
        margin-bottom: 4rem;
        @include for-phone {
            margin-bottom: 2rem;
            transition: all 0.3s ease;
        }
        &--flex {
            display: flex;
            align-items: center;
            justify-content: space-between;
            #{$this}__form-control {
                text-align: right;
                width: 100%;
            }
            #{$this}__title {
                width: 55%;
            }
        }
        &--fade-out {
            @include for-phone {
                opacity: 0;
            }
        }
    }
    &__head-wrap {
        margin-bottom: 3rem;
    }
    &__form-label {
        font-size: $font32;
        line-height: $line-height-34;
        color: $cf-text-clr;
        font-family: $pfhandbookpro-regular;
        display: block;
        margin-bottom: 0;
        max-width: 60%;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
            max-width: inherit;
        }
        #{$this}__form-control {
            display: inline-block;
            margin: 0 1rem;
            width: 6rem;
            text-align: center;
            vertical-align: middle;
        }
        &--text-grey {
            color: $cf-grey-text;
        }
        &--full {
            max-width: 100%;
            @include for-phone {
                max-width: inherit;
            }
        }
    }
    &__form-control {
        font-size: $font44;
        line-height: $line-height-46;
        color: $cf-text-clr;
        font-family: $pfhandbookpro-bold;
        border: none;
        border-radius: 0;
        border-bottom: 1px solid $cf-border-clr;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
        }
        &--md-width {
            width: 25.5rem;
        }
        &--other-field {
            margin-top: 5rem;
        }
    }
    &__overlay {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba($calc-overlay, 0.6);
        z-index: 10001;
        display: none;
    }
    &__loader-icon {
        font-size: $font64;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        @include for-phone {
            font-size: $font44;
        }
        &.icon-wealth-13 {
            left: 46%;
            top: 44%;
        }
    }
    &__modal {
        display: none;
        position: fixed;
        left: 50%;
        top: 50%;
        width: 100%;
        background: $calc-modal;
        transform: translate(-50%, -50%);
        padding: 4rem 3rem;
        max-width: 38.8rem;
        z-index: 10002;
        text-align: center;
        border-radius: 2px;
        box-shadow: 0 0 14px 0 rgba($calc-modal-shadow, 0.24);
        border: solid 1px $calc-modal-border;
        @include for-phone {
            max-width: 28rem;
            padding: 2.4rem 1.6rem;
        }
        &-img-wrap {
            width: 16rem;
            height: 16rem;
            margin: 0 auto 2rem;
            background: url('../images/calc-loader.gif');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            position: relative;
            @include for-phone {
                width: 9.4rem;
                height: 9.4rem;
            }
        }
        &-title {
            font-size: $font32;
            line-height: $line-height-1;
            color: $calc-text;
            @include for-phone {
                font-size: $font22;
                margin-bottom: 0.5rem;
            }
        }
        &-desc {
            font-family: $pfhandbookpro-thin;
            font-size: $font24;
            line-height: $line-height-26;
            color: $calc-text;
            margin: 0;
            @include for-phone {
                font-size: $font18;
                line-height: $line-height-20;
            }
        }
        &--income {
            max-width: 60rem;
            padding: 2rem;
            text-align: left;
        }
    }
    &__input-wrap {
        position: relative;
        width: 52.5%;
    }
    &__dummy-inpt {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        display: inline-block;
        border: none;
        border-bottom: 1px solid $cf-border-clr;
        text-align: right;
        font-size: $font44;
        line-height: $line-height-46;
        font-family: $pfhandbookpro-bold;
        color: $cf-text-clr;
        padding: 0 1rem;
        background: $cf-footer-bg;
        cursor: pointer;
        &::placeholder {
            color: $rrs-placeholder-clr;
        }
        @include for-tablet {
            font-size: $font32;
            line-height: $line-height-40;
        }
    }
    &__note {
        font-size: $font14;
        line-height: $line-height-16;
        color: $cf-grey-text;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 0;
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-12;
        }
    }
    &__note-icon {
        font-size: $font14;
        display: inline-block;
        vertical-align: middle;
        margin-right: 0.5rem;
        @include for-phone {
            font-size: $font12;
        }
    }
    &__info-icon {
        font-size: $font14;
    }
    &__info-link {
        position: absolute;
        top: -5.7rem;
        right: 0;
        @include for-phone {
            top: -5.2rem;
        }
    }
    .input-dummy {
        &__icon,
        &__value {
            display: inline-block;
            vertical-align: top;
        }
        &__icon {
            font-size: $font18;
            margin: 6px -3px 0 0;
            @include for-tablet {
                font-size: $font12;
                margin: 8px -3px 0 0;
            }
        }
    }
    span.error {
        background-color: transparent !important;
        color: $capital-red;
        border: 0 !important;
    }
}

.act-wrap {
    $self: &;
    &__btn {
        margin-left: auto;
        background: transparent;
        border: none;
        display: inline-block;
        margin-right: 4rem;
        &:last-child {
            margin-right: 0;
        }
        #{$self}__icon {
            font-size: $font11;
            color: $cf-btn-link;
            display: inline-block;
            vertical-align: middle;
            margin-top: 1rem;
        }
        #{$self}__link {
            color: $cf-btn-link;
            font-size: $font18;
            line-height: $line-height-20;
            font-family: $pfhandbookpro-bold;
            display: inline-block;
            vertical-align: middle;
            text-decoration: underline;
        }
    }
}