.range-slider-wrap {
    $this: &;
    max-width: 73.6rem;
    margin: 0 auto;
    &--img {
        padding: 8rem 0 5rem;
        max-width: 72.6rem;
        @include for-phone {
            padding: 7rem 0 5rem;
        }
        .noUi-target {
            background: $range-house-bar;
            border-radius: 9px;
            box-shadow: none;
            border: 0;
            margin-bottom: 8rem;
        }
        .noUi-connect {
            background: $range-house-bar-active;
        }
        .noUi-horizontal {
            height: 3px;
            .noUi-handle {
                width: 17px;
                height: 17px;
                border-radius: 24px;
                right: -5px;
                box-shadow: 0 0 16px 0 rgba($range-house-shadow, 0.49);
                background-image: linear-gradient( 69deg, $range-house-gradient-start 15%, $range-house-gradient-end 83%);
                cursor: pointer;
                &:before,
                &:after {
                    display: none;
                }
            }
            @include for-tablet {
                margin: 0 15px;
            }
        }
        .noUi-marker-horizontal {
            &.noUi-marker-large {
                width: 0;
            }
        }
        .noUi-value {
            font-size: $font20;
            line-height: $line-height-22;
            @include for-tablet {
                font-size: $font14;
                line-height: $line-height-16;
            }
        }
        .noUi-pips-horizontal {
            padding: 5px 0;
            width: calc(100% - 30px);
            left: 50%;
            transform: translateX(-50%);
        }
        .noUi-tooltip {
            box-shadow: none;
            border: none;
            background: transparent;
            .icon {
                font-size: $font60;
                color: $protecting-shade;
                @include for-tablet {
                    font-size: $font40;
                }
            }
        }
        [disabled] {
            .noUi-connect {
                background: $range-house-bar;
            }
            .noUi-tooltip {
                display: none;
            }
            &.noUi-horizontal {
                .noUi-handle {
                    background: $range-house-bar;
                    box-shadow: none;
                }
            }
        }
        #{$this}__title-wrap {
            @include for-tablet {
                margin-bottom: 0;
                margin-top: 5.5rem;
            }
        }
        .range-slider-wrap__title-wrap{
            margin-bottom: 0;
        }
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $rrs-text-clr;
        font-family: $pfhandbookpro-regular;
        @include for-tablet {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__input,
    &__dummy-inpt {
        display: inline-block;
        border: none;
        border-bottom: 1px solid $rrs-border-clr;
        text-align: right;
        font-size: $font44;
        line-height: $line-height-46;
        font-family: $pfhandbookpro-bold;
        color: $rrs-text-clr;
        // min-height: 5.2rem;
        padding: 0 1rem;
        width: 80%;
        &::placeholder {
            color: $rrs-placeholder-clr;
        }
        @include for-tablet {
            font-size: $font32;
            line-height: $line-height-40;
        }
    }
    &__input {
        display: none;
        &::-ms-clear {
            width : 0;
            height: 0;
        }
        &.error{
            &+.range-slider-wrap__dummy-inpt{
                border: solid 1px #bd2b2b !important;
                background: #bd2b2b1f !important;
            }
        }
    }
    &__input-text {
        font-size: $font18;
        line-height: $line-height-20;
        font-family: $pfhandbookpro-regular;
        color: $rrs-grey-text;
        position: absolute;
        right: 0;
        bottom: -2.2rem;
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-14;
            bottom: -1.8rem;
        }
    }
    .input-dummy {
        &__icon,
        &__value {
            display: inline-block;
            vertical-align: top;
        }
        &__icon {
            font-size: $font18;
            margin: 6px -3px 0 0;
            @include for-tablet {
                font-size: $font12;
                margin: 8px -3px 0 0;
            }
        }
    }
    &__title-wrap {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 5.5rem;
    }
    &__wrap-item {
        width: 50%;
        position: relative;
        &:first-child {
            width: 48%;
        }
        &:last-child {
            text-align: right;
        }
        &--opac {
            opacity: 0;
        }
    }
    &--ruler {
        padding: 0 0 7rem;
        .noUi-target {
            padding: 0 3rem;
            position: relative;
            @include for-phone{
            margin: 0 -15px;}
            &:before {
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                height: 6.7rem;
                background: $rrs-bg-color;
                opacity: 0.3;
            }
            &.noUi-horizontal {
                height: 0;
                border: none;
            }
        }
        .noUi-marker-horizontal {
            &.noUi-marker {
                background: $rrs-text-color;
                width: 1px;
                height: 1.6rem;
                &.noUi-marker-large {
                    height: 3rem;
                }
            }
        }
        .noUi-value-large {
            top: 40%;
            color: $rrs-pipe-color;
            font-size: $font16;
            line-height: $line-height-18;
            font-family: $pfhandbookpro-regular;
            @include for-tablet {
                font-size: $font14;
                line-height: $line-height-16;
            }
        }
        .noUi-pips-horizontal {
            padding-top: 0;
            width: auto;
            right: 0;
            margin: 0 3rem auto;
            // @include for-tablet {
            //     margin: 0 1.5rem auto;
            // }
        }
        .noUi-tooltip {
            transform: translate(-50%, -43%);
            background: $rrs-gradient-top-color;
            background: -moz-linear-gradient( top, $rrs-gradient-top-color 0%, $rrs-gradient-bottom-color 100%);
            background: -webkit-gradient( left top, left bottom, color-stop(0%, $rrs-gradient-top-color), color-stop(100%, $rrs-gradient-bottom-color));
            background: -webkit-linear-gradient( top, $rrs-gradient-top-color 0%, $rrs-gradient-bottom-color 100%);
            background: -o-linear-gradient( top, $rrs-gradient-top-color 0%, $rrs-gradient-bottom-color 100%);
            background: -ms-linear-gradient( top, $rrs-gradient-top-color 0%, $rrs-gradient-bottom-color 100%);
            background: linear-gradient( to bottom, $rrs-gradient-top-color 0%, $rrs-gradient-bottom-color 100%);
            filter: "progid:DXImageTransform.Microsoft.gradient( startColorstr=" $rrs-gradient-top-color ", endColorstr=" $rrs-gradient-bottom-color ", GradientType=0 )";
            font-size: $font16;
            line-height: $line-height-18;
            color: $rrs-tooltip-text-clr;
            font-family: $pfhandbookpro-regular;
            padding: 1rem 0rem;
            border-radius: 0.2rem;
            border: none;
            text-align: center;
            width: 7rem;
            min-height: 3.8rem;
            @include for-tablet {
                width: 4.5rem;
                padding: 0.4rem 0;
            }
            &:after {
                content: "";
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 8px 9.5px 0 9.5px;
                border-color: $rrs-gradient-bottom-color transparent transparent transparent;
                bottom: -8px;
                @include for-tablet {
                    border-width: 6px 7.5px 0 7.5px;
                    bottom: -6px;
                }
            }
        }
        .noUi-handle {
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 8px 8.5px 0 8.5px;
            border-color: $rrs-tooltip-text-clr transparent transparent transparent;
            background: none;
            box-shadow: none;
            right: -8px;
            top: 0;
            cursor: pointer;
            &:after,
            &:before {
                content: none;
            }
        }
    }
    &--drag {
        padding: 0 0 4rem;
        .range-slider-wrap__title-wrap {
            margin-bottom: 3rem;
        }
        .noUi-pips-horizontal {
            padding: 0;
            width: calc(100% - 30px);
            left: 50%;
            transform: translateX(-50%);
            .noUi-marker {
                display: none;
            }
        }
        .noUi-horizontal {
            height: 3px;
            .noUi-handle {
                cursor: pointer;
                top: -9px;
                right: -20px;
                width: 40px;
                height: 21px;
                border-radius: 6px;
                box-shadow: 0 0 16px 0 rgba($range-drag-shadow, 0.49);
                background-image: linear-gradient( 69deg, $range-drag-gradient-start 15%, $range-drag-gradient-end 83%);
                &:before,
                &:after {
                    font-family: font-icons;
                    font-size: 8px;
                    color: $range-drag-handle;
                    background: transparent;
                    width: auto;
                    line-height: 1;
                }
                &:before {
                    content: $icon-prev;
                    left: 10px;
                }
                &:after {
                    content: $icon-next;
                    left: 20px;
                }
            }
        }
        .noUi-target {
            background: $range-drag-bar;
            border-radius: 9px;
            box-shadow: none;
            border: 0;
        }
        .noUi-tooltip {
            box-shadow: none;
            border: none;
            background-color: transparent;
            bottom: auto;
            top: 120%;
            font-size: $font20;
            font-family: $pfhandbookpro-bold;
            color: $range-drag-tooltip;
            @include for-tablet {
                font-size: $font18;
            }
        }
        .noUi-value {
            font-size: $font20;
            line-height: $line-height-22;
            @include for-tablet {
                font-size: $font14;
                line-height: $line-height-16;
                left: -1rem;
            }
        }
        .noUi-base {
            width: calc(100% - 39px);
            left: 20px;
        }
    }
    &--wealth {
        .noUi-tooltip {
            &__img {
                min-width: 2rem;
                &--sm {
                    min-width: 3.6rem;
                }
                &--md {
                    min-width: 4rem;
                }
                &--lg {
                    min-width: 5.3rem;
                }
            }
        }
    }
    &--percent {
        .range-slider-wrap__wrap-item{
            &:last-child{
                &::after{
                    content: '%';
                    position: absolute;
                    right: 0;
                    top:0;
                    font-size: $font36;
                    line-height: $line-height-46;
                    font-family: $pfhandbookpro-bold;
                    padding-right: 2px;
                    @include for-tablet{
                        font-size: $font27;
                        top: -10%;
                        right: 3%;
                    
        }
                }
            }
        }
        .range-slider-wrap__input {
            display: inline-block;
            padding-right: 36px;
        }
        .noUi-horizontal {
            height: 42px;
            background: $range-cent-bg;
            border-radius: 21px;
            border: none;
            box-shadow: none;
            @include for-tablet {
                height: 32px;
            }
            .noUi-handle {
                top: -2px;
                width: 47px;
                height: 47px;
                border-radius: 50%;
                box-shadow: 0 0 16px 0 rgba($range-cent-shadow, 0.49);
                background-image: linear-gradient( 69deg, $range-cent-gradient-start 15%, $range-cent-gradient-end 83%);
                cursor: pointer;
                &:before,
                &:after {
                    display: none;
                }
                @include for-tablet {
                    width: 36px;
                    height: 36px;
                }
            }
        }
        .noUi-tooltip {
            background: transparent;
            box-shadow: none;
            border: 0;
            bottom: 16%;
            font-size: $font18;
            line-height: $line-height-20;
            color: $range-cent-handle;
            @include for-tablet {
                font-size: $font12;
                line-height: $line-height-12;
            }
        }
        .noUi-pips-horizontal {
            top: 0;
            left: 2%;
            width: 92%;
            @include for-tablet {
                top: -3px;
                left: 3%;
                width: 88%;
            }
        }
        .noUi-marker-horizontal {
            &.noUi-marker-large {
                display: none;
            }
            &.noUi-marker-normal {
                height: 12px;
                top: 16px;
                background-color: $range-cent-mark;
                @include for-tablet {
                    top: 13px;
                    opacity: 0.3;
                }
            }
        }
        .noUi-value-horizontal {
            transform: none;
            color: $range-cent-mark;
            @include for-tablet {
                font-size: $font14;
                line-height: $line-height-16;
            }
        }
        .noUi-base {
            width: 95%;
            margin: 0 auto;
        }
    }
}