/* website mobile quick actions menu strat */

.mobile-quick-actions {
    .action-list {
        display: none;
        @include for-phone {
            display: flex !important;
            justify-content: space-around;
            border-bottom: 1px solid $quick-action-border;
        }
    }
    .action {
        padding: 1.3rem 0;
        display: inherit;
        align-items: center;
        &__icon {
            color: $quick-action-color;
            font-size: $font18;
            margin-right: 0.4rem;
        }
        &__name {
            color: $quick-action-color;
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
}


/* website mobile quick actions menu end */