.buy-login-wrap {
    display: flex;
    width: 100%;
    
    flex-wrap: wrap;

   
   
    // .ad-banner {
    //     max-width: 45rem;
    //     @include for-tablet {
    //         display: none;
    //     }
    // }
}

.buy-login {
    // background-color: aqua;
    flex-grow: 1;
    width:100%;
    
    //margin:auto;
  //  max-width: calc(100% - 40rem);//changes made for migrations
    // padding-right: 7rem; 
    
    @include for-tablet {
        //max-width: 100%;
         padding: 1rem;
        margin-bottom: 2rem;
    }
    &__box {
        border-radius: 0.4rem;
        height:auto;
       
        // max-width:70rem;//added for migrations
        box-shadow: 0 0 1.4rem 0 rgba($wb-login-box-shadow, 0.24);
        border: 0.1rem solid $wb-login-box-border;
        background-color: $wb-login-box-bg;
        // max-width: 80rem;
        min-height: 40rem;
        position: relative;

        // @include for-tablet{
        //     height: fit-content;//added now
            
        // }

        //added now 
        //height:950px;
        
    }
    &__head {
        padding: 2.4rem;
        background-color: $wb-login-box-head-bg;
        color: $wb-login-box-head-color;
    }

    &__head-text{
        @include for-tablet{
            font-size: 1.8rem !important;//added now
            
            
        }
    }

    &__body {
        padding: 2.4rem;
    }
    .button-link {
        padding-left: 0;
    }
    .btn-wrap {
        // position: static;
        //margin-bottom:400px;
        // added now padding-top:50px;
        bottom: 2.4rem;
        right: 2.4rem;
        //below line added now for height
        //height:105px;
        @include mQ(1024px) {
            position: static;
        }
    }
    .abc-form {
        .form-group {
            &--otp {
                flex-wrap: wrap;
                display: flex;
                align-items: flex-end;
                .form-group__label {
                    width: 100%;
                }
                .input-group {
                    width: auto;
                    margin-right: 5rem;
                    @include for-md-desktop {
                        margin: 0 0 1rem 0;
                    }
                    @include for-tablet {
                        margin: 0 1rem 0 0;
                    }
                    @include for-phone {
                        margin: 0 0 1rem 0;
                    }
                }
                .otp-timer {
                    font-size: $font18;
                    line-height: $line-height-28;
                }
            }
        }
        .form-control {
            &--otp {
                max-width: 5rem;
                line-height: 5rem;
                height: 5rem;
                margin-right: 1rem;
                text-align: center;
                font-size: $font16;
            }
        }
    }
}

.buy-online__thanks-msg{
    display: flex;
    justify-content: center;
    text-align: center;
    &__container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      h3{
        font-size: 2.5rem;
        color: #6a4742;
        text-align: center;
      }
      h2{
        font-size: 3rem;
        color: #c7222a;
        text-align: center;
      }
      .s_advise{
          h3{
            border-top: 1px solid #da9089;
            border-bottom: 1px solid #da9089;
            padding: 1rem 0;
          }
      }
    }
}

