.full-width-ctas {
    &-wrapper {
      padding-top: 40px;
  
      @include mQ(767px) {
        padding-top: 30px;
      }
  
      @include mQ(575px) {
        padding-top: 10px;
      }
    }
  
    &-close-cta {
      display: flex;
  
      .close-cta {
        margin-left: auto;
        margin-right: 40px;
        color: $black;
        padding: 5px;
  
        @include mQ(767px) {
          margin-right: 0;
        }
  
        @include mQ(575px) {
          margin-right: 0;
        }
  
        .icon {
          font-size: $font25;
          line-height: 25px;
  
          @include mQ(991px) {
            font-size: $font22;
            line-height: 22px;
          }
  
          @include mQ(575px) {
            font-size: $font14;
            line-height: 14px;
          }
        }
  
        &:hover,
        &:focus,
        &:active {
          border-bottom: 0 none;
        }
      }
    }
  
    &-component {
      margin: 0 auto;
      max-width: 450px;
  
      @include mQ(991px) {
        max-width: 350px;
      }
  
      @include mQ(575px) {
        max-width: 220px;
      }
    }
  
    &-header {
      margin: 0 20px;
  
      @include mQ(991px) {
        margin: 0 10px;
      }
  
      h2 {
        text-align: center;
        font-size: $font30;
        line-height: 1.25;
        font-family: $pfhandbookpro-bold;
  
        @include mQ(991px) {
          font-size: $font24;
        }
  
        @include mQ(575px) {
          font-size: $font15;
        }
      }
    }
  
    &-list {
      padding-top: 30px;
      padding-bottom: 30px;
  
      @include mQ(767px) {
        padding-top: 20px;
        padding-bottom: 20px;
      }
  
      @include mQ(575px) {
        padding-top: 5px;
        padding-bottom: 5px;
      }
  
      ul {
        columns: 2;
        margin: 0 -25px;
        padding: 0;
        list-style: none;
  
        @include mQ(767px) {
          columns: 1;
          margin: 0 auto;
          max-width: 185px;
        }
  
        @include mQ(575px) {
          max-width: 115px;
        }
  
        li {
          padding: 10px 25px;
  
          @include mQ(767px) {
            padding: 10px 0;
          }
  
          @include mQ(575px) {
            padding: 5px 0;
          }
  
          &::before {
            content: none;
          }
  
          a {
            color: $black;
            display: block;
            position: relative;
            padding: 10px 20px 10px 0;
            font-size: $font22;
            line-height: 1.11111111;
  
            @include mQ(991px) {
              font-size: $font18;
            }
  
            @include mQ(767px) {
              font-size: $font24;
            }
  
            @include mQ(575px) {
              font-size: $font15;
            }
  
            &::after {
              content: $icon-right-arrow;
              font-family: "font-icons";
              font-size: $font16;
              position: absolute;
              right: 0;
              color: $brand-capital-red;
              top: 50%;
              transform: translateY(-50%);
  
              @include mQ(991px) {
                font-size: $font13;
              }
  
              @include mQ(767px) {
                font-size: $font15;
              }
  
              @include mQ(575px) {
                font-size: $font9;
              }
            }
  
            &:hover,
            &:focus,
            &:active {
              border-bottom: 0 none;
              color: $brand-capital-red;
            }
          }
        }
      }
    }
  
    &-list-bottom-wrapper {
      margin-top: 53px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 25px;
    }
  
    &-list-bottom-label {
      font-size: $font14;
      letter-spacing: -0.19px;
      color: $black;
      font-family: "pfhandbookpro-thin", sans-serif;
    }
  
    &-list-bottom-cta {
      a {
        font-size: $font18;
        letter-spacing: -0.24px;
        color: $black;
        font-family: "pfhandbookpro-bold", sans-serif;
        position: relative;
        margin-top: 7px;
        padding-right: 33px;
        display: inline-block;
  
        &:hover,
        &:active,
        &:focus {
          border: none;
        }
  
        &:before {
          right: 0;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
        }
      }
  
    }
  }
  
  /*-- Create One ID CTAs --*/
  .full-width-one-id-cta-wrapper {
    .icon-one-id-cta {
      display: block;
      font-size: 8.4rem;
      margin-bottom: 27px;
      color: #c7222a;
  
      @include mQ(767px, 576px) {
        font-size: 14.7rem;
      }
    }
  
    .img-one-id-cta {
      width: 70px;
      height: 70px;
      margin-left: auto;
      margin-right: auto;
  
      @include mQ(767px, 576px) {
        width: 147px;
        height: 147px;
      }
  
      img {
        width: 100%;
        margin-bottom: 0;
      }
    }
  
    .one-id-cta-info-content-wrapper {
      margin: 0 40px;
  
      h2 {
        @include mQ(767px, 576px) {
          font-size: $font40;
        }
      }
  
      p,
      a {
        font-family: "pfhandbookpro-regular", sans-serif;
        font-size: $font12;
        line-height: normal;
        color: #000000;
        margin-top: 15px;
        text-align: center;
  
        @include mQ(767px, 576px) {
          font-size: $font24;
        }
      }
  
      a {
        font-family: "pfhandbookpro-bold", sans-serif;
  
        &:hover {
          border-color: #000000;
        }
      }
  
      &+.abc-form-wrapper {
        margin-top: 27px;
      }
    }
  }
  
  .abc-form.abc-post-login-form {
    .form-checkbox-wrapper {
      &.terms-and-conditions-checkbox-wrapper {
        label {
          font-size: 1.5rem;
          display: block;
          color: rgba(0, 0, 0, 0.7);
  
          a {
            font-size: 1.5rem;
            color: rgba(199, 34, 42, 0.7)
          }
        }
      }
  
      &+span.error {
        float: left;
        width: 100%;
      }
    }
  
    .username-icon {
      width: 24px;
      height: 24px;
      display: block;
      position: absolute;
      right: 20px;
      top: 13px;
  
      img {
        width: 100%;
      }
  
      @include mQ(991px, 768px) {
        right: 13px;
        top: 8px;
      }
  
      @include mQ(767px, 576px) {
        right: 20px;
        top: 18px;
        width: 34px;
        height: 34px;
      }
  
      @include mQ(575px) {
        right: 10px;
        top: 11px;
      }
    }
  
    .available,
    .unavailable {
      display: block;
      margin-top: 7px;
      font-size: 1.6rem;
  
      @include mQ(767px, 576px) {
        margin-top: 11px;
        font-size: 2.4rem;
        margin-bottom: 5px;
      }
    }
  
    .available {
      color: #3bb54a;
    }
  
    .unavailable {
      color: #c0c0c0;
    }
  }