.benefits-list {
    max-width: 725px;
    margin: 0 auto;
    padding: 0;
    &__item {
        padding-bottom: 2.5rem;
        margin-bottom: 2.5rem;
        border-bottom: 1px solid $benefit-border;
        @include for-phone {
            padding-bottom: 1.6rem;
            margin-bottom: 1.6rem;
        }
        &:last-child {
            border-bottom: 0;
        }
    }
}

.benefits {
    $self: &;
    padding-left: 8.5rem;
    position: relative;
    @include for-phone {
        padding-left: 3.5rem;
    }
    &__icon {
        position: absolute;
        top: 0;
        left: 0;
        font-size: $font44;
        color: $benefit-icon;
        max-width: 5.5rem;
        @include for-phone {
            font-size: $font22;
            max-width: 2.2rem;
        }
    }
    &__title {
        display: block;
        font-size: $font32;
        line-height: $line-height-1;
        color: $benefit-title;
        @include for-phone {
            font-size: $font22;
        }
        &--typ-sm {
            font-size: $font24;
            line-height: $line-height-26;
            margin-top: 2.5rem;
            margin-bottom: 0.8rem;
            display: block;
            color: $benefit-title;
        }
    }
    &__desc {
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $benefit-desc;
        display: block;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__sublist {
        list-style: disc;
        @include for-phone {
            padding-top: 0;
            padding-bottom: 1.5rem;
        }
    }
    &__sublist-item {
        font-size: $font24;
        line-height: $line-height-32;
        font-family: $pfhandbookpro-thin;
        color: $benefit-desc !important;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &--mobile-v2 {
        @include for-phone {
            padding-left: 0;
            display: flex;
            flex-flow: row wrap;
            align-items: center;
            #{$self}__icon {
                position: relative;
                top: auto;
                left: auto;
                font-size: $font50;
                max-width: 5rem;
                width: 30%;
                margin-right: 1.5rem;
                margin-bottom: 1.5rem;
            }
            #{$self}__title--typ2 {
                width: 75%;
                margin-bottom: 1.5rem;
            }
            #{$self}__desc {
                width: 100%;
            }
        }
    }
}