//article-detail
.article-detail {
    $this: &;
    &__button {
        padding: 0;
        text-decoration: none;
    }
    &__item-list {
        display: flex;
        flex-flow: row wrap;
        align-items: flex-start;
        max-width: 100%;
        margin-bottom: 4rem;
        @include for-phone {
            left: auto;
        }
    }
    &__item-tile {
        width: 33.33%;
        padding: 0 1.5rem;
        margin-bottom: 2rem;
        margin-top: 0;
        @include for-tablet {
            width: 50%;
        }
        @include for-phone {
            width: 100%;
            padding: 0;
        }
        &:nth-last-child(-n + 2) {
            @include for-tablet {
                margin-bottom: 0;
            }
        }
        &:nth-last-child(-n + 3) {
            margin-bottom: 0;
            @include for-tablet {
                margin-bottom: 2rem;
            }
            @include for-phone {
                margin-bottom: 2rem;
            }
        }
        &:last-child {
            @include for-phone {
                margin-bottom: 0;
            }
        }
        #{$this}__wrapper--blog {
            @include for-phone {
                min-height: 28rem;
            }
        }
        #{$this}__media--blog {
            @include for-phone {
                height: 18rem;
            }
        }
    }
    &__title-wrap {
        margin-bottom: 4rem;
        @include for-phone {
            margin-bottom: 2rem;
        }
    }
}

.highlighted {
    display: inline;
    position: relative;
    background: $highlight-tooltip-bg;
    &--active {
        background: $highlited-text;
        cursor: pointer;
        &:hover {
            background: $highlight-tooltip-bg-hover;
        }
    }
    &__tooltip {
        position: absolute;
        top: -7.5rem;
        right: -3rem;
        padding: 1.6rem;
        background: $highlight-clear-bg;
        box-shadow: 0 3px 4px 0 rgba($highlight-tooltip-shadow, 0.16);
        border: 1px solid $highlight-tooltip-border;
        display: block;
        cursor: pointer;
        &:hover,
        &:active {
            background: $highlight-tooltip-bg-active;
            &:after {
                border-top-color: $highlight-tooltip-bg-active;
            }
        }
        &:after {
            content: "";
            position: absolute;
            bottom: -1.5rem;
            left: 50%;
            transform: translateX(-50%);
            width: 0;
            height: 0;
            border-left: 1.5rem solid transparent;
            border-right: 1.5rem solid transparent;
            border-top: 1.5rem solid $highlight-clear-bg;
        }
        &--clear {
            display: none;
            font-size: $font18;
            line-height: $line-height-20;
            color: $highlight-tooltip-clr;
            font-family: $pfhandbookpro-regular;
            top: -5.5rem;
            width: 13rem;
            &:after {
                border-top-color: $highlight-clear-bg;
            }
        }
    }
    &__icon {
        font-size: $font28;
        line-height: $line-height-1;
        color: $highlight-tooltip-clr;
    }
}