.promotion-text {
  width: 100%;
  &__container {
    box-shadow: 0 2px 8px rgba($black, 0.08);
    margin-bottom: 8px;
    padding: 0;
    height: 190px;
    border-radius: 4px;
    @include mQ(991px, 768px) {
      width: calc(50% - 10px);
      float: left;
      margin-right: 20px;
      &:nth-child(even) {
        margin-right: 0;
      }
    }
    @include mQ(767px) {
      max-width: 330px;
      margin-left: auto;
      margin-right: auto;
      height: 126px;
      font-size: 16px;
      line-height: normal;
    }
  }
  &__cta-container {
    box-shadow: 0 2px 8px rgba($black, 0.08);
    padding: 12px 0;
    border-radius: 4px;
    max-width: 858px;
    @include for-md-desktop {
      max-width: 736px;
    }
    @include for-tablet {
      max-width: 744px;
    }
    margin: 0 auto;
    .promotion-text {
      &__cta {
        margin: 0;
        @include for-phone {
          padding-left: 22px;
        }
        a {
          @include for-phone {
            font-size: $font16;
          }
          &:hover,
          &:active,
          &:focus {
            border: none !important;
            text-decoration: none !important;
            color: $promotion-cta-link !important;
          }
        }
      }
      &__title {
        display: flex;
        align-items: center;
        @include for-phone {
          flex-basis: 45%;
          flex-grow: 0;
        }

        span {
          font-size: $font30;
          color: $promotion-cta-title;
          @include for-md-desktop {
            font-size: $font26;
          }
          @include for-phone {
            font-size: $font18;
          }
        }
      }
    }
  }
  &__wrapper {
    padding: 32px 24px;
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;
    border-radius: 4px;
    justify-content: center;
    @include for-phone {
      padding-top: 10px;
      padding-bottom: 10px;
    }
    &--media {
      //background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), $black);
    }
  }
  &__cta-wrapper {
    display: flex;
    flex-direction: row;
    /*for ie*/
    justify-content: space-around;
    /*for moz & chrome*/
    -webkit-justify-content: space-evenly !important;
    @include for-phone {
      padding-right: 20px;
      padding-left: 20px;
    }
  }
  &__media {
    img {
      margin: 0;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100% !important;
      -o-object-fit: cover;
      object-fit: cover;
      z-index: 0;
      width: 100%;
      height: 190px;
      z-index: -1;
      border-radius: 4px;
      @include for-phone {
        height: 126px;
        object-fit: unset;
      }
    }
  }
  &__title {
    span {
      font-size: $font30;
      line-height: normal;
      color: $promotion-title;
      @include for-md-desktop {
        font-size: $font24;
      }
      @include for-phone {
        font-size: $font18;
      }
    }
  }
  &__cta {
    margin-top: 15px;
    @include for-phone {
      margin-top: 20px;
    }
  }
  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &:hover,
    &:active,
    &:visited {
      border: none;
      text-decoration: none;
    }
  }
}
//Blog Migration
.two-columns{
  .promotion-text{
    margin-bottom: 25px;
  }
}