//sme-scenario
.sme-scenario{
    &__title{
        font-size: $font44;
        line-height: $line-height-46;
        color: $scenario-text-color;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 0.8rem;
        @include for-phone{
            font-size: $font32;
            line-height: $line-height-34;
        }
    }
    &__content{
        display: flex;
        justify-content: space-between;
        padding: 0;
        @include for-phone{
            display:block;
            justify-content: inherit;
        }
    }
    &__item{
        width: 48%;
        @include for-phone{
            width: 100%;
            margin-bottom: 2rem;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    &__desc{
        font-size: $font24;
        line-height: $line-height-26;
        color: $scenario-text-color;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 4rem;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 2rem;
        }
    }
    &__img-wrap{
        max-height: 27.6rem;
    }
    &__img{
        width: 100%;
    }
}