 /*
  * ABC Post Login - Theme definitions
  *
  */
 
 $light-investing: #a1ac7b;$recomm-product-title: $protecting-shade;
 $recomm-investing-color: #2d370c;
 
 $themes: (
   investing: (
     backgroundColor: $brand-investing,
     verticalTabResponsiveBottomSelectedBorder: 3px solid $investing-shade,
     backgroundColorWithOpacity: rgba($brand-investing, 0.3),
     backgroundColorWithOpacityForResponsive: #fafbf8,
     backgroundColorWithOpacityArrow: #d7dac9,
     contentCardLeftBorderColor: 3px solid $brand-investing,
     contentCardWithBackgroundOpacity: rgba($brand-investing, 0.3),
     backgroundColorWithOpacityForLeftVertical: rgba($investing-pallete-01, 0.3),
     backgroundColorWithOpacityForLeftVerticalResponsive:
       rgba($brand-investing, 0.21),
     textColorForLeftMainTitle: $investing-shade,
     contentHorizontalTabActiveBorder: 3px solid $light-investing,
     contentHorizontalTabArrowColor: $light-investing,
     contentHorizontalTabArrowBorder: solid 0.8px $light-investing,
     brandPillBackgroundColor: rgba($investing-pallete-02, 0.26),
     recommProductTitle: $recomm-investing-color,
     recommProductTitleBg: rgba($brand-investing, 0.25),
     recommfoteNotesBg: rgba($brand-investing, 0.5),
   ),
   protecting: (
     backgroundColor: $brand-protecting,
     verticalTabResponsiveBottomSelectedBorder: 3px solid $protecting-shade,
     backgroundColorWithOpacity: rgba($protecting-pallete-02, 1),
     backgroundColorWithOpacityForResponsive: #fdf9f9,
     backgroundColorWithOpacityArrow: $protecting-pallete-02,
     contentCardLeftBorderColor: 3px solid $brand-protecting,
     contentCardWithBackgroundOpacity: rgba($brand-protecting, 0.3),
     backgroundColorWithOpacityForLeftVertical: rgba($protecting-pallete-01, 0.4),
     backgroundColorWithOpacityForLeftVerticalResponsive:
       rgba($brand-protecting, 0.21),
     textColorForLeftMainTitle: $protecting-shade,
     contentHorizontalTabActiveBorder: 3px solid $brand-protecting,
     contentHorizontalTabArrowColor: $brand-protecting,
     contentHorizontalTabArrowBorder: solid 0.8px $brand-protecting,
     brandPillBackgroundColor: rgba($protecting-pallete-02, 0.26),
     recommProductTitle: $recomm-product-title,
     recommProductTitleBg: rgba($brand-protecting, 0.25),
     recommfoteNotesBg: rgba($brand-protecting, 0.5),
   ),
   financing: (
     backgroundColor: $brand-financing,
     verticalTabResponsiveBottomSelectedBorder: 3px solid $financing-shade,
     backgroundColorWithOpacity: rgba($brand-financing, 1),
     backgroundColorWithOpacityForResponsive: #fdfbf7,
     backgroundColorWithOpacityArrow: $brand-financing,
     contentCardLeftBorderColor: 3px solid $brand-financing,
     contentCardWithBackgroundOpacity: rgba($brand-financing, 0.3),
     backgroundColorWithOpacityForLeftVertical: rgba($financing-pellete-01, 0.5),
     backgroundColorWithOpacityForLeftVerticalResponsive:
       rgba($brand-financing, 0.21),
     textColorForLeftMainTitle: $financing-shade,
     contentHorizontalTabActiveBorder: 3px solid $brand-financing,
     contentHorizontalTabArrowColor: $brand-financing,
     contentHorizontalTabArrowBorder: solid 0.8px $brand-financing,
     brandPillBackgroundColor: rgba($financing-pellete-02, 0.26),
     recommProductTitle: $recomm-product-title,
     recommProductTitleBg: rgba($brand-financing, 0.25),
     recommfoteNotesBg: rgba($brand-financing, 0.5),
   ),
 );
