.tabs {
    clear: both;
    width: 100%;
    &--vertical {
        display: flex;
        align-items: flex-start;
        @include for-phone {
            flex-wrap: wrap;
        }
    }
    
    &__links {        
        display: flex;
        justify-content: center;
        padding-left: 0;
        @include for-phone {
            flex-wrap: wrap;
        }
        &--v2 {
            justify-content: start;
            padding: 0;
            @include for-phone {
                justify-content: center;
            }
        }
        &--center {
            justify-content: center;
        }
        &--v3 {
            justify-content: flex-start;
                    }
        &--vertical {
            flex-wrap: wrap;
            flex-direction: column;
            max-width: 288px;
            width: 25%;
            @include for-phone {
                max-width: 100%;
                width: 100%;
                flex-direction: row;
                margin-bottom: 3rem;
                .slick-list {
                    margin: 0 -15px;
                    padding-left: 15px;
                }
                .slick-slide {
                    margin: 0 4px;
                }
            }
        }
        &--droplist {
            background: $filter-drop-list;
            border: 1px solid $filter-drop-border;
            box-shadow: 0 2px 4px 0 rgba($filter-drop-shadow, 0.25);
            position: absolute;
            top: 45px;
            left: 0;
            width: 100%;
            z-index: 1;
            max-height: 22rem;
            overflow: hidden;
            display: none;
            .tabs__item {
                margin-bottom: 0;
            }
            .tabs__link {
                font-size: $font16;
                line-height: $line-height-20;
                background: transparent;
                color: $filter-drop-selected;
                border-bottom: 1px solid $filter-drop-border;
                width: 100%;
                margin-bottom: 0;
                &--active {
                    .tabs__anchor {
                        background: transparent;
                        color: $filter-drop-selected;
                        &:hover {
                            color: $filter-drop-selected;
                        }
                    }
                }
            }
            .tabs__anchor {
                font-size: $font20;
                line-height: $line-height-1-1;
                font-family: $pfhandbookpro-regular;
                padding: 1rem 1.6rem;
                border-radius: 2px;
                transition: all 0.5s ease;
                display: block;
                background: transparent;
                color: $filter-drop-selected;
                @include for-phone {
                    display: inline-block;
                    text-align: left;
                }
                &--active,
                &:hover {
                    color: $filter-drop-selected;
                    background: $filter-drop-item-active;
                }
                &:active,
                &:focus-within {
                    color: $filter-drop-selected;
                    background: $filter-drop-item-active;
                }
            }
        }
    }
    &__anchor {
        color: $tab-link;
        font-size: $font24;
        padding: 1.8rem 5rem;
        display: inline-block;
        background-color: $tab-link-bg;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 26rem;
        display: block;
        @include for-phone {
            max-width: 11rem;
        }
        &--v2 {
            color: $tab-link-v2;
            font-size: $font18;
            line-height: $line-height-20;
            text-transform: uppercase;
            padding: 1.5rem 1.7rem;
            background: transparent;
            overflow: initial;
            &:hover,
            &:active,
            &:focus {
                color: $tab-link-v2;
            }
            @include for-phone {
                text-transform: capitalize;
                max-width: 20rem;
            }
        }
        &--v3 {
            padding: 1.2rem 1.6rem;
            border-radius: 0.4rem;
            font-size: $font20;
            line-height: $line-height-1;
            color: $tab-link-v3;
            @include for-phone {
                max-width: 20rem;
            }
        }
        &--vertical {
            width: 100%;
            @include for-phone {
                text-align: center;
                max-width: none;
            }
        }
        &:hover,
        &:active,
        &:focus {
            color: $tab-link;
        }
        @include for-phone {
            font-size: $font16;
            padding: 1.2rem 2rem;
        }
    }
    &__link {
        position: relative;
        &--active {
            .tabs__anchor {
                background: $tab-link-protect;
                color: $tab-link-white;
                &:hover,
                &:active,
                &:focus {
                    color: $tab-anchor-hover;
                }
                &--v2 {
                    color: $tab-link-protect;
                    background: transparent;
                    border-bottom: 2px solid $tab-link-protect;
                    &:hover,
                    &:active,
                    &:focus {
                        color: $tab-link-protect;
                    }
                }
                &--v3 {
                    background: $tab-link-v3-active;
                }
            }
        }
        &:first-child {
            .tabs__anchor {
                border-top-left-radius: 0.4rem;
                border-bottom-left-radius: 0.4rem;
            }
        }
        &:last-child {
            .tabs__anchor {
                border-top-right-radius: 0.4rem;
                border-bottom-right-radius: 0.4rem;
            }
        }
        &:not(:last-child) {
            &:after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 1px;
                height: 2.4rem;
                background: $tab-link-seperater;
            }
        }
        &.tabs__link--active {
            &:after {
                background: $tab-link-protect;
            }
        }
        &--v2 {
            border-bottom: 1px solid $tab-link-seperater;
            &:after {
                display: none;
            }
            &:first-child {
                .tabs__anchor {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                }
            }
            &:last-child {
                .tabs__anchor {
                    border-top-right-radius: 0;
                    border-bottom-right-radius: 0;
                }
            }
        }
        &--v3 {
            margin-right: 2rem;
            @include for-phone {
                margin-right: 0;
            }
            &:after {
                display: none;
            }
        }
        &--noborder {
            border: 0;
            .tabs__anchor {
                border: 0;
            }
        }
        &--vertical {
            margin: 0 3.2rem 1.6rem 0;
            @include for-phone {
                margin-bottom: 0;
            }
        }
    }
    &__contents {
        &--vertical {
            width: 100%;
            max-width: 736px;
        }
    }
    &__content {
        display: none;
        padding: 0 0 4rem;
        &--active {
            display: block;
        }
        &--v2 {
            padding: 2.4rem 0;
        }
        &--nopadding {
            padding: 0;
        }
        @include for-phone {
            padding: 0.4rem 0 2.4rem;
        }
    }
    &__title {
        font-size: $font44;
        color: $tab-title;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
            text-align: center;
        }
    }
    &__desc {
        font-size: $font32;
        line-height: $line-height-32;
        color: $tab-title;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 3rem;
        padding-right: 10rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-18;
            margin-bottom: 1.6rem;
            text-align: center;
            padding-right: 0;
            display: none;
            br {
                display: none;
            }
        }
    }
    &__sub-title {
        font-size: $font32;
        line-height: $line-height-32;
        color: $tab-desc;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 3rem;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-22;
            margin-bottom: 1.6rem;
        }
    }
    .btn-wrap {
        padding-top: 4rem;
        .button {
            &:nth-child(2) {
                margin-left: 2rem;
            }
        }
        @include for-phone {
            width: 100%;
            text-align: center;
        }
    }
    .abc-form--feedback {
        .btn-wrap {
            text-align: right;
            padding-top: 0;
        }
    }
    &--dropdown {
        position: relative;
        @include for-phone {
            margin: 0 0 2rem;
        }
        .mCSB_inside>.mCSB_container {
            margin-right: 0;
        }
    }
    &__dropdown {
        width: 100%;
        border: 1px solid $filter-drop-border;
        max-width: 288px;
        margin-right: 3.2rem;
        position: relative;
        padding: 1.2rem 1.8rem;
        border-radius: 4px;
        cursor: pointer;
        @include for-phone {
            max-width: 100%;
            margin: 0 0 2rem 0;
        }
    }
    &__selected {
        font-size: $font18;
        line-height: $line-height-1;
    }
    &__drop-icon {
        font-size: $font14;
        line-height: $line-height-18;
        color: $filter-drop-icon;
        position: absolute;
        right: 1rem;
        top: 1.2rem;
    }
    @include for-phone {
        .three-col-list.link-list {
            &__container {
                ul {
                    li {
                        flex: 0 0 50%;
                        max-width: 50%;
                        &:last-child {
                            a {
                                border-bottom: 1px solid $increment-border;
                            }
                        }
                    }
                }
                .link-list__list {
                    &--icon .icon {
                        display: none;
                    }
                    li {
                        a {
                            padding: 9px 30px 9px 0;
                            &:after {
                                @include for-phone {
                                    top: unset;
                                    bottom: 10px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.on-page-editor {
    .tabs__anchor {
        min-width: 26rem;
    }
}