//icon-radio
.icon-radio {
  $this: &;
  display: flex;
  margin-top: 2.5rem;

  @include for-phone {
    flex-flow: row wrap;
    width: 100%;
  }

  &__wrap {
    position: relative;
    margin-right: 2rem;
    width: 13.1rem;

    @include for-phone {
      margin-right: 1rem;
      margin-bottom: 2.5rem;
      width: 10rem;
    }

    @include for-small-phone {
      width: 9rem;
    }
  }

  &__input:not(old) {
    width: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    position: absolute;
    //display: none;
    ~ #{$this}__label {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      cursor: pointer;
      margin-bottom: 0;
    }

    &:hover {
      ~ #{$this}__label {
        + .calc-icon-card {
          .calc-icon-card__icon,
          .calc-icon-card__text {
            color: $ir-active-clr;
          }
        }
      }
    }

    &:checked {
      ~ #{$this}__label {
        + .calc-icon-card {
          .calc-icon-card__icon,
          .calc-icon-card__text {
            color: $ir-active-clr;
          }

          .calc-icon-card__check-box {
            background: $ir-active-clr;
          }
        }
      }
    }
  }

  &--strip {
    @include for-phone {
      width: 100%;
      justify-content: flex-start;
    }

    #{$this}__wrap {
      margin-right: 0;
      border-right: 1px solid $ir-border-clr;
      border-bottom: 1px solid $ir-border-clr;

      @include for-phone {
        margin-bottom: 0;
      }

      @include for-md-desktop {
        border-right: 1px solid $ir-border-clr;
        border-bottom: 1px solid $ir-border-clr;

        &:nth-child(3n + 3) {
          border-right: 1px solid $ir-border-clr;
        }

        &:nth-last-child(-n + 3) {
          border-bottom: 1px solid $ir-border-clr;
        }
      }

      @include for-phone {
        &:nth-child(3n + 3) {
          border-right: none;
        }

        &:nth-last-child(-n + 3) {
          border-bottom: none;
        }
      }

      &:last-child {
        border-right: none;
      }
    }

    #{$this}__card {
      box-shadow: none;
      border: none;
      height: auto;
    }

    #{$this}__input:not(old) {
      ~ #{$this}__label {
        + .calc-icon-card {
          .calc-icon-card__text {
            font-family: $pfhandbookpro-bold;
          }
        }
      }

      &:checked {
        ~ #{$this}__label {
          + .calc-icon-card {
            background: $ir-active-clr;

            .calc-icon-card__icon,
            .calc-icon-card__text {
              color: $ir-card-bg;
            }
          }
        }
      }
    }
  }

  &--no-border {
    #{$this}__wrap {
      margin-right: 5rem;

      @include for-small-phone {
        margin-right: 0;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    #{$this}__card {
      box-shadow: none;
      border: none;
      height: auto;
    }

    #{$this}__input:not(old) {
      ~ #{$this}__label {
        + .calc-icon-card {
          padding: 2rem 0;

          .calc-icon-card__text {
            font-family: $pfhandbookpro-bold;
          }
        }
      }
    }
  }

  &--streched {
    #{$this}__card {
      min-height: 13rem;
      height: 100%;
      justify-content: flex-start;
    }
  }

  &--btn {
    @include for-phone {
      width: 100%;
    }

    #{$this}__wrap {
      width: auto;
      margin-right: 3rem;
    }

    #{$this}__card {
      height: auto;
      padding: 1rem 2.5rem;
      border: none;
      box-shadow: none;
      background: $ir-light-bg;
      border-radius: 0.4rem;

      @include for-phone {
        padding: 1rem 1.5rem;
      }

      .calc-icon-card__text {
        color: $ir-light-text-color;
      }
    }

    #{$this}__input:not(old) {
      &:hover {
        ~ #{$this}__label {
          + .calc-icon-card {
            .calc-icon-card__text {
              color: $ir-btn-active-clr;
            }
          }
        }
      }

      &:checked {
        ~ #{$this}__label {
          + .calc-icon-card {
            background: $ir-btn-active-clr;

            .calc-icon-card__text {
              color: $ir-card-bg;
            }
          }
        }
      }
    }
  }

  &--btn-health {
    margin-bottom: 1rem;

    #{$this}__main-label {
      margin-right: 1rem;
    }
  }

  &--default {
    margin-top: 1.5rem;

    .calc-icon-card {
      display: flex;
      align-content: center;
      justify-content: space-between;
      max-width: 21rem;
      text-align: left;

      @include for-phone {
        width: 14rem;
        max-width: inherit;
      }

      &__text {
        font-size: $font32;

        @include for-phone {
          font-size: $font18;
        }
      }

      &__check-box {
        top: 50%;
        right: auto;
        transform: translateY(-50%);
      }
    }

    #{$this}__wrap {
      width: auto;
      margin-right: 0;
    }

    #{$this}__card {
      height: auto;
      padding: 0 0 0 4rem;
      border: none;
      box-shadow: none;
      background: transparent;
      border-radius: 0;

      @include for-phone {
        padding: 0 0 0 3rem;
      }

      .calc-icon-card__text {
        color: $ir-black-clr;
      }
    }

    #{$this}__input:not(old) {
      &:hover {
        ~ #{$this}__label {
          + .calc-icon-card {
            .calc-icon-card__text {
              color: $ir-black-clr;
            }
          }
        }
      }

      &:checked {
        ~ #{$this}__label {
          + .calc-icon-card {
            .calc-icon-card__check-box {
              background: $ir-active-clr;
            }
          }
        }
      }
    }
  }

  &--two-ele {
    @include for-phone {
      max-width: 25rem;

      #{$this}__wrap {
        width: 12rem;
        border-right: 1px solid $ir-border-clr;
        border-bottom: 1px solid $ir-border-clr;

        &:nth-last-child(-n + 3) {
          border-bottom: 1px solid $ir-border-clr;
        }

        &:nth-child(3n + 3) {
          border-right: 1px solid $ir-border-clr;
        }

        &:nth-child(even) {
          border-right: none;
        }

        &--last {
          border-bottom: none !important;
        }

        &--last-child-border {
          border-right: 1px solid $ir-border-clr !important;
        }
      }
    }
  }

  &--circle {
    justify-content: center;

    #{$this}__wrap {
      position: relative;
      margin-right: 3rem;
      width: 7.1rem;
    }

    #{$this}__card {
      box-shadow: none;
      height: 7.1rem;
    }

    #{$this}__input:not(old) {
      ~ #{$this}__label {
        + .calc-icon-card {
          justify-content: space-evenly;
          align-items: center;
          border-radius: 50%;

          .calc-icon-card__icon,
          .calc-icon-card__text {
            color: $ir-light-text-color;
            padding: 0px;
            margin: 0px;
          }

          .calc-icon-card__icon {
            font-size: $font32;
            margin-bottom: 4px;
          }
        }
      }

      &:checked {
        ~ #{$this}__label {
          + .calc-icon-card {
            background: $ir-active-bg-color;

            .calc-icon-card__icon,
            .calc-icon-card__text {
              color: $ir-active-clr;
            }
          }
        }
      }
    }
  }

  &--separated {
    justify-content: center;
    flex-flow: row wrap;

    #{$this}__wrap {
      position: relative;
      margin-right: 4rem;
      margin-bottom: 1rem;
      width: 25.1rem;
    }

    #{$this}__card {
      box-shadow: none;
      height: 9.9rem;
    }

    #{$this}__input:not(old) {
      ~ #{$this}__label {
        z-index: 1;
        cursor: pointer;
        margin-bottom: 0;

        + .calc-icon-card {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: flex-start;
          padding: 0 32px;
          border-radius: 5px;

          .calc-icon-card__icon,
          .calc-icon-card__text {
            color: $ir-light-text-color;
            padding: 0px;
            margin: 0px;
          }

          .calc-icon-card__icon {
            margin-right: 16px;
            margin-top: -20px;
          }

          .calc-icon-card__text {
            text-align: left;
          }
        }
      }

      &:hover {
        ~ #{$this}__label {
          + .calc-icon-card {
            background: $grey-002;

            .calc-icon-card__icon,
            .calc-icon-card__text {
              color: $ir-light-text-color;
            }
          }
        }
      }

      &:checked {
        ~ #{$this}__label {
          + .calc-icon-card {
            background: $ir-active-bg-color;
            box-shadow: 0px 5px 10px $ir-active-bg-color;
            border: 1px solid $ir-active-clr;

            .calc-icon-card__icon,
            .calc-icon-card__text {
              color: $ir-active-clr;
            }

            .calc-icon-card__check-box {
              background: $ir-active-clr;
            }
          }
        }
      }
    }

    @include for-phone {
      margin-right: 0px;
      width: 100%;
      justify-content: space-evenly;

      #{$this}__wrap {
        margin-right: 0px;
        width: 17.1rem;
      }

      #{$this}__input:not(old) {
        ~ #{$this}__label {
          + .calc-icon-card {
            padding: 0 12px;
          }
        }
      }
    }
  }
}
