.poll-component {
    float: left;
    width: 100%;
    position: relative;
    &.top-margin {
        margin-top: 75px;
    }
}
.poll-item {
    @include box-shadow(0, 2px, 8px, rgba(0,0,0,0.13));
    margin-bottom: 20px;
    padding: 1px;
    text-align: center;
    border-radius: 4px;
}
.poll-content-wrapper {
    border: 1px solid #979797;
    padding: 20px;
    border-radius: 4px;
    min-height: 361px;
}
.poll-title {
    font-family: $pfhandbookpro-bold;
    color: #D0021B;
    font-size: 2rem;
    line-height: 23px;
    text-transform: uppercase;
    margin-bottom: 18px;
    letter-spacing: -0.45px;
}
.poll-question {
    font-family: $pfhandbookpro-medium;
    color: $black;
    font-size: 2rem;
    line-height: 25px;
    margin-bottom: 30px;
}
.poll-submit {
    .read-more {
        &.disabled{
            background-color: #565F66;
            border-color: #565F66;
        }
        background-color: $capital-red;
        border-color: $capital-red;
        color: $white;
        width: 160px;
    }
    margin-bottom: 5px;
}

.poll-answer-wrapper, .result-answer-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 45px;
}
.poll-msg, .poll-no-result-wrapper {
    p {
        font-family: $pfhandbookpro-bold;
    }
}
.poll-result-wrapper {
    .poll-answer {
        .poll-answer-title {
            position: relative;
            bottom: 0px;
            font-size: 1.2rem;
            line-height: 16px;
        }
        .result-number, .result-percent {
            font-family: $pfhandbookpro-bold;
            font-size: 1.6rem;
            line-height: 20px;
            color: $black;
            display: none;
        }
        
    }
    .poll-no-result-wrapper {
        display: none;
    }
    &.number {
        .result-number {
            display: block !important;
        }
    }
    &.percent {
        .result-percent {
            display: block !important;
        }
    }
    &.no-result {
        .result-answer-wrapper, &+ .poll-msg {
            display: none !important;
        }
        .poll-no-result-wrapper {
            display: block !important;
            margin-top: 40px;
            float: left;
            width: 100%;
        }
    }
}

.poll-result-wrapper.display {
    .poll-answer {
        min-width: 85px;
    }
}

.poll-answer {
    border: 1px solid #E1E2E3;
    background-color: #FEFEFF;
    border-radius: 9px;
    margin: 0 21px;
    min-height: 104px;
    min-width:76px;
    padding: 10px;
    position: relative;
    overflow: hidden;
    @include mQ(1299px){
        margin: 0 5px;
    }
    @include mQ(991px,768px){
        margin: 0 20px;
    }
    @include mQ(767px,360px){
        margin: 0 10px;
    }

    &.active {
        /*@include box-shadow(-4px, 12px, 13px, rgba(0,0,0,0.13));*/

        .radio {
            border: solid 1px $capital-red !important;
        }

        > span { 
            color: $capital-red;
        }
        .poll-answer-title {
            color: $black;
        }
        .poll-progressbar {
            &:after{ border-bottom: 3px solid $capital-red; }
        }
    }
    &.selected {
        /*@include box-shadow(-4px, 12px, 13px, rgba(0,0,0,0.13));*/

        .radio {
            border: solid 1px $capital-red !important;
            background-color: $white !important;
            &:after {
                top: 1px;
                left: 1px;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background: $capital-red;
                display: block;
            }
        }

        
        .poll-answer-title {
            color: $black;
        }
        .poll-progressbar {
            background-color: $capital-red;
            &:after{ border-bottom: 3px solid $capital-red; }
        }

        &.white {
            .poll-answer-title {
                color: $white;
                transition: color 0.4s linear 1.5s;
            }
            > span { 
                color: $white;
                transition: color 0.4s linear 1.5s;
            }
            .radio {
                border: solid 1px $white !important;
                background-color: $capital-red !important;
                &:after {
                    background: $white;
                }
                transition: all 0.4s linear 1.5s;
            }
        }

    }
    .radio-wrapper {
        margin: 0 auto;
    }

    > span {
        display: block;
        width: 100%;
        color: #6A4742;
        font-size: 1.2rem;
        position: relative;
    }

    .poll-answer-title {
        font-family: $pfhandbookpro-bold;
        font-size: 1.7rem;
        line-height: 18px;
        color: $black;
        margin-top: 12px;
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
    }
    .poll-progressbar {
        height: 0%;
        position: absolute;
        left: 0;
        bottom: 3px;
        width: 100%;
        background-color: rgba(68, 73, 81, 0.2);
        border-radius: 4px 4px 0px 0px;
        transition: height 2s linear 0.5s;
        &:after {
            border-bottom: 3px solid rgba(68, 73, 81, 0.2);
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            bottom: -3px;
            content: "";
        }
    }
}

.multipollwrap {
    &.pollhide {
        display: none;
    }
}

.poll-action {
    display: flex;
    justify-content: flex-end;
    // position: absolute;
    right: 0;
    // top: -71px;
    // @include mQ(1200px){
    //     top: -65px;
    // }
    // @include mQ(1020px){
    //     top: -50px;
    // }
}

.pollPrev, .pollNext {
    margin-left: 10px;
    margin-bottom: 25px;
    > a {
        position: relative;
        display: inline-block;
        cursor: pointer;
        &:after {
            -moz-osx-font-smoothing: grayscale;
            -webkit-font-smoothing: antialiased;
            content: $icon-pagination-arrow;
            font-family: "font-icons";
            font-style: normal;
            font-variant: normal;
            font-weight: normal;
            line-height: 1;
            speak: none;
            text-transform: none;
            position: absolute;
            top: 11px;
            left: 10px;
            color: $white;
        }
    }
}

.pollPrev.disabled {
    > a  {
        border-radius: 2px;
        color: $white;
        background-color: darken(desaturate(adjust-hue($capital-red, -160), 67.68), 1.96);
        cursor: not-allowed;
    }
}

.pollPrev {
    > a  {
        width: 38px;
        height: 38px;
        border-radius: 2px;
        color: $white;
        background-color: $capital-red;
        @include mQ(1200px){
            width: 35px;
            height: 35px;
            font-size: 1.4rem;
        }
        &:hover, &:active, &:focus {
            border-color: transparent;
        }
    }
}

.pollNext.disabled {
    > a {
        border-radius: 2px;
        color: $white;
        background-color: darken(desaturate(adjust-hue($capital-red, -160), 67.68), 1.96);
        cursor: not-allowed;
        &:after {
            transform: rotateY(180deg);
        }
    }
}
.pollNext {
    > a {
        width: 38px;
        height: 38px;
        border-radius: 2px;
        color: $white;
        background-color: $capital-red;
        @include mQ(1200px){
            width: 35px;
            height: 35px;
            font-size: 1.4rem;
        }
        &:hover, &:active, &:focus {
            border-color: transparent;
        }
        &:after {
            transform: rotateY(180deg);
        }
    }
}