.contact {
    $this:&;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 0;
    @include for-phone {
        margin: 0 -15px;
    }
    &__head {
        background: $contact-head;
        padding: 1.6rem;
        display: flex;
        align-items: center;
        @include for-phone {
            padding: 1.6rem 15px;
            position: relative;
            background: transparent;
            cursor: pointer;
        }
    }
    &__body {
        padding: 1.6rem 0;
        @include for-phone {
            padding: 1.6rem 15px;
            margin-bottom: 2rem;
            border-bottom: 1px solid $contact-border;
            display: none;
        }
        &--center {
            @include for-phone {
                text-align: center;
            }
        }
    }
    &__item {
        width: calc(50% - 1.6rem);
        border-bottom: 1px solid $contact-border;
        margin-bottom: 3.2rem;
        @include for-phone {
            width: 100%;
            margin-bottom: 0rem;
            &:nth-child(1),
            &:nth-child(2) {
                margin-bottom: 0rem;
            }
        }
        &--active {
            @include for-phone {
                .contact__head {
                    background: $contact-head;
                }
            }
        }
    }
    &__title {
        font-size: $font24;
        line-height: $line-height-26;
        color: $contact-text;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-20;
        }
    }
    &__icon {
        font-size: $font18;
        line-height: $line-height-1;
        max-width: 2rem;
        margin-right: 1.6rem;
        @include for-phone {
            font-size: $font16;
            margin-right: 2rem;
        }
    }
    &__sublist {
        display: flex;
        justify-content: space-between;
        padding: 0;
        @include for-phone {
            display: block;
        }
        &--row{
            flex-flow: row wrap;
            #{$this}__subitem{
                width: 50%;
                margin-bottom: 1rem;
                &:last-child,&:nth-last-child(2){
                    margin-bottom: 0;
                    @include for-phone{
                        margin-bottom: 1.5rem;
                    }
                }
                @include for-phone{
                    width: 100%;
                    margin-bottom: 1.5rem;
                    
                }
            }
        }
    }
    &__subitem {
        @include for-phone {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 1.5rem;
        }
    }
    &__desc {
        display: block;
        font-size: $font20;
        line-height: $line-height-22;
        &--bold {
            color: $contact-text;
            display: inline-block;
        }
        &--v2 {
            margin-top: 1.6rem;
        }
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-18;
        }
    }
    a[href^="tel:"],
    &__link {
        color: $contact-link !important;
        font-size: $font20;
        line-height: $line-height-22;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-18;
        }
    }
    &__meta {
        display: none;
        font-size: $font12;
        line-height: $line-height-14;
        color: $contact-desc;
        text-align: right;
        @include for-phone {
            display: block;
        }
    }
}

.contact-us-wrap {
    &.accordian--v2 {
        .accordian__item {
            .accordian__title {
                background: $contact-acc-trigger-panel-2;
            }
            &--active {
                .accordian__title {
                    background: $contact-acc-trigger-panel-active;
                    border-color: $contact-acc-trigger-panel-active;
                }
            }
        }
    }
    .accordian__icon {
        color: $contact-acc-icon;
    }
}