.banner-component {
  &--v2 {
    background: $grey-001;
    // @include for-phone() {
    //   background: none;
    // }
    padding: 57px 0 33px;
    position: relative;
    margin-bottom: 0 !important;
    &:before() {
      content: "";
      position: absolute;
      right: 100%;
      top: 0;
      width: calc(50vw);
      height: 100%;
      background: $grey-001;
      // @include for-phone() {
      //   background: none;
      // }
    }
    &:after() {
      content: "";
      top: 0;
      position: absolute;
      left: 100%;
      width: calc(50vw);
      height: 100%;
      background: $grey-001;
      // @include for-phone() {
      //   background: none;
      // }
    }
    
    @include for-phone {
      padding: 50px 0 10px;
      margin-bottom: 0 !important;
      .banner-component--v2__cta-wrapper {
        margin-bottom: 0px;
      }
    }
  }
}

//Blog Block
.blog-block {
  position: relative;
  display: flex;
  flex: 1;
  width: 100%;
  @include for-phone() {
    flex-direction: column;
  }
  &__left {
    box-shadow: 1px -2px 8px 0 rgba($blog-block-shadow, 0.08);
    position: relative;
    border-left: 5px solid;
    background: $white;
    cursor:pointer;
    max-height: 384px;
    max-width: 590px;
    @include mQ(1399px) {
      max-height: 332px;
      max-width: 519px;
    }
    @include mQ(1199px) {
      max-height: 225px;
      max-width: 330px;
    }
    @include mQ(1199px,1024px) {
      max-height: 270px;
      max-width: 410px;
    }
    @include for-phone() {
      border-left: 3px solid;
      max-height: 100%;
      max-width: 100%;
    }
    display: flex;
    &--advicing {
      border-color: $brand-advising;
    }
    &--protecting {
      border-color: $brand-protecting;
    }
    &--investing {
      border-color: $brand-investing;
    }
    &--financing {
      border-color: $brand-financing;
    }
    
    .blog-block__article-img {
      max-height: 259px;
      @include mQ(1399px) {
        max-height: 228px;
      }
      @include for-phone() {
        max-height: 100%;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  &__article-img {
    img {
      max-height: 259px;

      @include for-phone() {
        margin-bottom: 0;
        max-height: 100%;
      }
    }
  }
  &__article-title {
    font-size: $font30;
    line-height: $line-height-34;
    letter-spacing: -0.9px;
    color: $blog-block-left-article;
    margin: 14px 15px 12px 15px;
    height: 6.8rem;
    @include mQ(1399px){ 
      height: 5.8rem;
      margin: 8px 15px 11px 15px;
    }
    @include mQ(1199px){
      height: 3.6rem;
    }
    a {
      font-size: $font30;
      line-height: $line-height-34;
      letter-spacing: -0.9px;
      color: $blog-block-left-article;
      display: block;
      @include mQ(1399px){
        font-size: $font23;
        line-height: $line-height-29;
        letter-spacing: -0.07px;
      }
      @include mQ(1199px){
        font-size: $font15;
        line-height: $line-height-18;
        letter-spacing: -0.04px;
      }
      &:hover,
      &:active,
      &:focus {
        border: none;
      }
    }

    @include for-md-desktop() {
      font-size: $font23;
      line-height: $line-height-29;
      letter-spacing: -0.7px;
    }
    @include mQ(1199px) {
      font-size: $font15;
      line-height: $line-height-18;
      letter-spacing: -0.04px;
      margin: 0 9px 13px 9px;
    }
    @include for-phone() {
      font-size: $font14;
      line-height: $line-height-17;
      margin: 7px 7px 11px 7px;
    }
  }
  &__article-date {
    font-family: $pfhandbookpro-regular;
    font-size: $font14;
    line-height: $line-height-20;
    color: $blog-block-date;
    margin: 0 0 10px 15px;
    @include mQ(1199px) {
      font-size: $font10;
      line-height: 1.2rem;
      margin: 0 9px 10px 9px;
    }
    @include for-phone() {
      font-size: $font12;
      margin-left: 7px;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include mQ(1399px) {
      align-self: center;
    }
    @include for-phone() {
      width: 100%;
    }
  }
  &__article-wrapper {
    display: flex;
    flex-direction: row;
    margin-left: 30px;
    @include mQ(1399px) {
      margin-left: 47px;
    }
    @include mQ(1199px) {
      margin-left: 21px;
    }
    @include for-phone() {
      flex-direction: column;
      margin-left: 0;
    }
    &:nth-child(1) {
      margin-bottom: 76px;
      @include mQ(1399px) {
        margin-bottom: 30px;
      }
      @include mQ(1199px) {
        margin-bottom: 15px;
      }
      @include mQ(1024px) {
        margin-bottom: 14px;
      }
      @include for-phone() {
        margin-bottom: 0;
      }
    }
    &:nth-child(2) {
      .blog-block__article-card {
        @include for-phone() {
          margin-top: 0;
        }
      }
    }
  }
  &__article-card {
    display: flex;
    flex-direction: row;
    background: $blog-block-card-bg;
    width: 328px;
    cursor:pointer;
    max-height: 150px;
    box-shadow: 1px -1px 8px 0 rgba($blog-block-shadow, 0.08);
    justify-content: space-between;
    border-radius: 2px;
    @include mQ(1399px) {
      max-height: 126px;
      width: 270px;
    }
    @include mQ(1199px) {
      max-height: 92px;
      width: 195px;
    }
    @include mQ(1199px,1024px) {
      max-height: 92px;
      width: 250px;
    }
    @include for-phone() {
      max-height: 150px;
      width: 100%;
    }
    &:first-child() {
      @include for-phone() {
        margin-top: 35px;
        margin-bottom: 35px;
      }
    }
    &:nth-child(2) {
      margin-left: 30px;
      @include mQ(1399px) {
        margin-left: 18px;
      }
      @include mQ(1199px) {
        margin-left: 14px;
      }
      @include for-phone() {
        margin-left: 0;
      }
    }
    @include for-phone() {
      max-width: 100%;
      margin-bottom: 35px;
    }
    &:last-child() {
      //margin-bottom: 10px;
      @include for-phone() {
        margin-bottom: 35px;
      }
    }
  }
  &__card {
    border-left: 3px solid;
    display: flex;
    flex-direction: column;
    border-radius: 2px 0 0 2px;
    //max-width: 168px;
    @include mQ(1024px) {
      max-width: 100%;
    }
    @include for-phone() {
      max-width: 56%;
    }
    &--advicing {
      border-color: $brand-advising;
    }
    &--protecting {
      border-color: $brand-protecting;
    }
    &--investing {
      border-color: $brand-investing;
    }
    &--financing {
      border-color: $brand-financing;
    }
  }
  &__article-badge {
    font-family: $pfhandbookpro-medium;
    font-size: $font12;
    line-height: $line-height-20;
    text-align: center;
    display: inline-block;
    max-width: 132px;
    margin-top: 10px;
    color: $blog-block-badge-text;
    text-transform: uppercase;
    padding-top: 2px;
    padding-bottom: 2px;
    @include mQ(1399px) {
      font-size: 1rem;
      line-height: 1.8rem;
      text-align: left;
      max-width: 108px;
      margin-top: 8px;
      padding-top: 1px;
      padding-bottom: 0;
      padding-left: 8px;
    }
    @include for-md-desktop() {
      font-size: $font10;
      letter-spacing: 0;
      text-align: left;
      max-width: 108px;
      padding-left: 8px;
    }
    @include mQ(1024px) {
      max-width: 110px;
      letter-spacing: -0.23px;
    }
    @include for-phone() {
      font-family: $pfhandbookpro-medium;
      font-size: $font12;
      line-height: $line-height-20;
      text-align: center;
      display: inline-block;
      max-width: 132px;
      padding: 2px 0;
    }
  }
  &__article-title-card {
    margin: 19px 10px 19px 15px;
    height: 6rem;
    position: static;
    @include mQ(1399px) {
      margin: 12px 10px 12px 5px;
      height: 4.5rem;
      position: static;
      line-height: 13px;
    }
    @include mQ(1199px) {
      margin: 8px 10px 6px 8px;
    }
    @include for-phone() {
      margin: 19px 0 22px 15px;
      height: 5.8rem;
    }
    a {
      font-family: $pfhandbookpro-medium;
      font-size: $font14;
      line-height: $line-height-16;
      letter-spacing: -0.39px;
      color: $blog-block-cart-title;
      @include mQ(1399px) {
        font-size: $font12;
        line-height: $line-height-14;
        letter-spacing: -0.33px;
      }
      @include mQ(1024px) {
        line-height: $line-height-17;
      }
      @include for-phone() {
        font-size: $font14;
        line-height: $line-height-16;
        letter-spacing: -0.39px;
      }
      &:visited,
      &:hover,
      &:active {
        border: none;
      }
    }
  }
  &__article-date-card {
    font-family: $pfhandbookpro-regular;
    font-size: $font12;
    line-height: normal;
    letter-spacing: -0.27px;
    margin: 0 0 11px 14px;
    display: flex;
    height: 100%;
    justify-content: flex-start;
    align-items: flex-end;
    @include mQ(1399px) {
      margin: 0 0 19px 5px;
    }
    @include mQ(1199px) {
      font-size: $font10;
      margin: 0 0 19px 8px;
    }
    @include for-phone() {
      margin: 0 0 11px 14px;
      height: auto;
    }
  }
  &__article-img-card {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 130px;
    height: 130px;
    margin-top: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    @include mQ(1399px) {
      max-width: 110px;
      height: 110px;
      margin-top: 8px;
      margin-right: 8px;
      margin-bottom: 8px;
    }
    @include mQ(1199px) {
      display: none;
    }
    @include for-phone() {
      display: flex;
      height: 130px;
      max-width: 130px;
      margin-top: 10px;
      margin-right: 10px;
      margin-bottom: 10px;
    }
    img {
      margin-bottom: 0px;
      @include for-md-desktop() {
        margin-right: 8px;
      }
      border-radius: 4px !important;
    }
  }
}
//Blog block
.banner-component--v2 .banner-content-wrapper {
  @include for-phone() {
    padding-left: 0;
  }
}
