//Quick Service Form
.qsform {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  &__container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  &__buttongroup {
    margin: 19px 0 0 0;
    text-align: center;
    width: 100%;
    @include for-phone {
      text-align: left;
    }
    button {
      margin-right: 20px;
    }
    &--left {
      text-align: left;
    }
  }
  &__content {
    margin-top: 60px;
    width: 100%;
    .table {
      thead {
        td {
          padding: 16px 0;
        }
      }
      tr {
        height: 60px;
      }
      td {
        padding: 30px 0;
        height: auto !important;
        vertical-align: middle !important;
      }
    }
  }
  &__table {
    max-width: 100% !important;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    border: 1px solid $heading-border;
    @include for-phone {
      width: 200%;
    }
    thead {
      th {
        background: $heading-bg;
        font-size: $font13;
        line-height: normal;
        padding: 16px 0;
        text-align: center;
        color: $heading-color !important;
        font-weight: normal;
        border-bottom: 0;
        vertical-align: inherit;
        font-family: $pfhandbookpro-bold;
        //white-space: nowrap;
      }
    }
    tbody {
      td {
        text-align: center;
        font-size: $font13;
        vertical-align: inherit;

        padding: 25px 0;
      }
    }
    &--bordernone {
      &.table {
        tr {
          height: 40px;
          td {
            border: none;
            padding-top: 0;
            padding-bottom: 20px;
          }
          &:first-child {
            td {
              padding-top: 15px;
            }
            &:last-child {
              td {
                padding-bottom: 12px;
              }
            }
          }
        }
      }
    }
  }
  &__subcontent {
    margin-top: 30px;
  }
  &__buttonandtext {
    text-align: center;
    display: block;
    a {
      display: inline-block;
    }
    span {
      margin-top: 18px;
      font-size: $font16;
      color: #6c7174;
      display: block;
    }
  }
  &__details {
    margin-top: 40px;
    &--title {
      color: #6c7174;
    }
    &--content {
      color: #6c7174;
      ul {
        li {
          &:before {
            color: #6c7174;
          }
          font-family: $pfhandbookpro-regular;
        }
      }
    }
  }
  &__note {
    font-size: $font16;
    line-height: $line-height-20;
    font-family: $pfhandbookpro-regular;
    color: $qs-note;
    margin-top: 20px;
    display: block;
    width: 100%;
  }
  .form-group {
    margin-bottom: 35px;
  }
  &__navdividend {
    &--select {
      .selectize-input {
        .item {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}

//Quick Service Modal
.qsmodal {
  z-index: 10000;
  &_dialog {
    max-width: 632px;
  }
  &__header {
    flex-direction: column;
    padding: 12px 25px 32px 25px;
    .close-btn {
      &:hover {
        cursor: pointer;
        border: none;
      }
      &:active {
        cursor: pointer;
        border: none;
      }
    }
    .popup-close {
      position: absolute;
      right: 0px;
      top: 0px;
      width: 40px;
      height: 40px;
      background-color: #5a5a5a;
      &:before,
      &:after {
        position: absolute;
        left: 50%;
        content: " ";
        height: 22px;
        width: 2px;
        background-color: #fff;
        top: 10px;
      }
      &:before {
        transform: rotate(45deg);
      }
      &:after {
        transform: rotate(-45deg);
      }
    }
  }
  &__subtitle {
    font-size: $font16;
    color: #6c7174;
    width: 90%;
  }
  .modal-body {
    padding-bottom: 30px;
  }
}
