.calc-speedometer {
    width: 300px;
    height: 300px;
    position: relative;
    .progress-vline {
        position: absolute;
        top: 5%;
        bottom: 50%;
        left: 50%;
        width: 1px;
        transform: rotate(-124deg) translateX(-50%);
        transform-origin: 0 100%;
    }
    // .progress-vline .progress-vline-handel {
    //     position: absolute;
    //     top: 0;
    //     height: 15%;
    //     width: 1px;
    //     background-color: $dark-grey;
    //     transform-origin: 0 670%;
    //     transform: rotate(-0deg);
    //     transition-duration: 1s;
    //     transition-timing-function: linear;
    // }
    .progress-vline .progress-vline-handel span {
        display: inline-block;
        position: absolute;
        top: 0;
        height: 15%;
        width: 1px;
        background-color: $dark-grey;
        -ms-transform-origin: 0 670%;
        transform-origin: 0 670%;
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
        transition-duration: 1s;
        transition-timing-function: linear;
        overflow: hidden;
    }
    .progress-graph {
        background-image: url(../images/speedometer.png);
        background-repeat: no-repeat;
        padding: 6.5%;
        background-position: center 3%;
        background-size: 84%;
        position: absolute;
    }
    canvas {
        transform: rotate(-36deg);
    }
    .start-value {
        position: absolute;
        left: 17%;
        top: 76%;
        font-size: $font18;
        line-height: $line-height-20;
        color: $dark-grey;
        @include for-phone {
            font-size: $font11;
            line-height: $line-height-12;
        }
    }
    .end-value {
        position: absolute;
        right: 17%;
        top: 76%;
        font-size: $font18;
        line-height: $line-height-20;
        color: $dark-grey;
        @include for-phone {
            font-size: $font11;
            line-height: $line-height-12;
        }
    }
    .percent-value-wrapper {
        position: absolute;
        left: 10%;
        right: 10%;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        .percent-value {
            font-family: $pfhandbookpro-bold;
            font-size: $font44;
            line-height: $line-height-1;
            color: $black;
            @include for-phone {
                font-size: $font32;
            }
        }
        .static-lbl {
            font-family: $pfhandbookpro-thin;
            font-size: $font24;
            line-height: $line-height-26;
            color: $black;
            @include for-phone {
                font-size: $font18;
                line-height: $line-height-20;
            }
        }
    }
    .percent-value-wrapper span {
        display: block;
    }
}

.calc-speedometer-half {
    width: 300px;
    height: 300px;
    position: relative;
    .progress-vline {
        position: absolute;
        top: 10%;
        bottom: 50%;
        left: 50%;
        width: 1px;
        transform-origin: 50% 100%;
        transform: translateX(-50%) rotate(-90deg);
        &:after {
            content: "";
            width: 20px;
            height: 20px;
            background-color: #000;
            border-radius: 50%;
            position: absolute;
            bottom: -10px;
            left: 50%;
            transform: translateX(-50%);
        }
        .progress-vline-handel {
            width: 1px;
            height: 1px;
            position: absolute;
            bottom: 0;
            left: 50%;
            span {
                display: inline-block;
                width: 1px;
                height: 1px;
                transform: translateX(-50%) rotate(0deg);
                transform-origin: 50% 100%;
                transition-duration: 1s;
                transition-timing-function: linear;
                border-style: solid;
                border-width: 0 3px 100px 3px;
                border-color: transparent transparent #000 transparent;
                position: absolute;
                bottom: 0;
                left: 50%;
                overflow:hidden;
              }
        }
    }
    .progress-graph {
        background-image: url(../images/half-speedometer.png);
        background-repeat: no-repeat;
        padding: 6.5%;
        background-position: center -81%;
        background-size: 84%;
        position: absolute;
    }
    canvas {
        transform: rotate(0deg);
    }
    .start-value {
        position: absolute;
        left: 13%;
        top: 53%;
        font-size: $font14;
        line-height: $line-height-1;
        color: $dark-grey;
        @include for-phone {
            font-size: $font11;
            line-height: $line-height-12;
        }
    }
    .end-value {
        position: absolute;
        right: 9%;
        top: 53%;
        font-size: $font14;
        line-height: $line-height-1;
        color: $dark-grey;
        @include for-phone {
            font-size: $font11;
            line-height: $line-height-12;
        }
    }
    .percent-value-wrapper {
        position: absolute;
        left: 10%;
        right: 10%;
        top: 62%;
        transform: translateY(-50%);
        text-align: center;
        .percent-value {
            font-family: $pfhandbookpro-bold;
            font-size: $font44;
            line-height: $line-height-1;
            color: $black;
            margin-top: 2rem;
            @include for-phone {
                font-size: $font32;
                margin-top: 4rem;
            }
        }
        .static-lbl {
            font-family: $pfhandbookpro-thin;
            font-size: $font24;
            line-height: $line-height-26;
            color: $black;
            @include for-phone {
                font-size: $font18;
                line-height: $line-height-20;
            }
        }
    }
    .percent-value-wrapper span {
        display: block;
    }
}

