/*---Download/Share PDF---*/
#pdfcontainer {
    #calculatorResultContainer {
        .calc-speedometer-half, .calc-speedometer {
            .progress-vline {
                .progress-vline-handel {
                    span {
                        transition-duration: unset;
                    }
                }
            }
        }
    }
}
