//download-band
.download-band{
    background: $df-bg-color;
    padding: 2rem;
    display: table;
    width: 100%;
    $self:&;
    &__label{
        font-size: $font20;
        line-height: $line-height-1-1;
        color: $df-text-color;
        font-family: $pfhandbookpro-regular;
    }
    &__icon{
        font-size: $font15;
        color: $df-icon-color;
    }
    &__title-wrap{
        text-align: left;
    }
    &__action-wrap{
        text-align: right;
        width: 30%;
        #{$self}__act-btn{
            display: inline-block;
            margin-left: 3rem;
            &:first-child{
                margin-left: 0;
            }
        }
    }
    &__title-wrap,&__action-wrap{
        display: table-cell;
        vertical-align: middle;
        padding: 0;
        margin: 0;
        @include for-phone{
            vertical-align: top;
        }
    }
    &__name{
        display: table-cell;
        padding-right: 8rem;
        &:last-child{
            padding-right: 0;
        }
        &:nth-child(2) {
            width: 110px;
            padding-right: 0;
            @include mQ(767px) {
                width: 45px;
            }
        }
        @include for-phone{
            padding-right: 1.5rem;
            vertical-align: top;
        }
    }
    &__label{
        margin-bottom: 0;
    }
}