//group-service
.group-service{
    $self:&;
    &__btn {
        margin-top: 1rem;
        margin-left: auto;
        background: transparent;
        border: none;
        display: inline-block;
        margin-right: 4rem;
        &:last-child{
            margin-right: 0;
        }
        #{$self}__icon {
            font-size: $font18;
            color: $gsl-link-clr;
            display: inline-block;
            vertical-align: middle;
            margin-right: 0.6rem;
        }
        #{$self}__link {
            color: $gsl-link-clr;
            font-size: $font18;
            line-height: $line-height-20;
            font-family: $pfhandbookpro-bold;
            display: inline-block;
            vertical-align: middle;
            text-decoration: underline;
        }
    }
}