.website-ticker {
    padding: 0.5rem 1.5rem;
    &__marquee {
        font-size: $font16;
        line-height: $line-height-1;
        /* header yellow ticker */
        color: $marquee-text;
        a {
            color: $marquee-text;
        }
        @include for-phone {
            font-size: $font14;
        }
    }
}