.full-width-forgot-password-wrapper {
    max-width: 380px;
    margin: 0 auto;
    padding-top: 24px;
    padding-bottom: 55px;

    .fp-info {
        font-size: 1.2rem;
    }

    .fp-margin-bottom {
        margin-bottom: 0px;
    }
    .font24 {
        letter-spacing: -0.27px;
    }
    @include mQ(991px) {
        padding-top: 11px;
        max-width: 296px;
        .font24 {
            font-size: 1.87rem;
            line-height: 1.25;
            letter-spacing: -0.21px;
        }
        .fp-margin-bottom {
            margin-bottom: 70px !important;
        }
    }
    @include mQ(767px) {
        padding-top: 11px;
        max-width: 100%;
        .font24 {
            font-size: 3.52rem;
            letter-spacing: normal;
        }
        .fp-margin-bottom {
            margin-bottom: 130px !important;
        }
    }
    @include mQ(575px) {
        .font24 {
            font-size: 2.2rem;
            letter-spacing: normal;
        }
        .fp-margin-bottom {
            margin-bottom: 70px !important;
        }
    }
}

.abc-form {
    &.abc-post-login-form {
        @include mQ(991px) {
            .form-group__label {
                font-size: 1.5rem;
            }
            .form-group {
                margin-bottom: 1.3rem;
            }
            .form-group__placeholder {
                font-size: 1.62rem;
                padding: 0.375rem 1.8rem;
            }
            .act-btn {
                font-size: 1.2rem;
                border-radius: 1.6px;
                height: 35px;
                padding: 5px 30px;
            }
        }
        @include mQ(767px) {
            .form-group__label {
                font-size: 2.9rem;
            }
            .form-group {
                margin-top: 0.7rem;
                margin-bottom: 0.3rem;
            }
            .form-group__placeholder {
                font-size: 2.8rem;
                padding: 0.375rem 1.8rem;
                height: 70px;
            }
            .otp-button {
                height: 70px;
            }
            .act-btn {
                font-size: 2.3rem;
                border-radius: 2.9px;
                height: 64px;
                padding: 5px 30px;
            }
        }
        @include mQ(575px) {
            .form-group__label {
                font-size: 1.81rem;
            }
            .form-group {
                margin-top: 1rem;
                margin-bottom: 1.3rem;
            }
            .form-group__placeholder {
                font-size: 1.8rem;
                padding: 0.375rem 1.8rem;
                height: 44px;
            }
            .act-btn {
                font-size: 1.44rem;
                line-height: normal;
                border-radius: 1.8px;
                height: 40px;
                padding: 5px 30px;
            }
            .otp-button {
                height: 44px;
                padding: 10px 30px;
            }
        }
    }
}
