.abc-form {
    &.abc-post-login-form {
        .otp-button {
            cursor: pointer;
            background-color: #c7222a;
            padding: 15px 30px;
            float: right;
            text-align: center;
            color: #ffffff;
            border-radius: 2.9px;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            border: 0 none;
            white-space: nowrap;
            &.disabled {
                background-color: #6d7278;
                cursor: not-allowed;
            }
            &.otp-oneid-button {
                width: 30%;
            }
        }
        .otp-text {
            flex: 1;
            display: inline-block;
            float: left;
            border-right: 0 !important;
            border-right-style: none !important;
            border-top-right-radius: 0 !important;
            border-bottom-right-radius: 0 !important;
            &.otp-oneid-text {
                width: 70%;
            }
        }
        .btn {
            &__overlay {
                &.btn__otp-counter {
                    font-weight: bold;
                    color: #6d7278;
                }
                &.btn__otp-counter-cta {
                    font-weight: bold;
                    color: #c7222a;
                }
            }
        }
        .form-otp-label {
            &.error {
                position: relative;
                background-color: transparent !important;
                border: transparent !important;
                padding-left: 28px;
                &:before {
                    content: "\f06a";
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    position: absolute;
                    left: 0px;
                    top: 0;
                    color: #F7A500;
                    font-size: 23px;
                    text-align: center;
                    font-family: FontAwesome;
                }
            }
        }
        .cta__flat-with-arrow {
            position: relative;
            margin-top: -10px;
            &:after {
                content: $icon-right-arrow;
                top: 50%;
                transform: translate(-0%, -50%);
                font-family: "font-icons";
                color: #c7222a;
                font-style: normal;
                font-variant: normal;
                font-weight: normal;
                line-height: 1;
                text-transform: none;
                position: absolute;
                font-size: $button-primary-icon-size;
                vertical-align: baseline;
                right: -25px;
            }
        }
    }
}

/*---Modal Login Repeat User------*/

.modal-body {
    .abc-form {
        &.abc-post-login-form {
            padding-left: 0;
            padding-right: 0;
            span {
                &.error {
                    margin-top: 12px;
                    &:before {
                        top: -6px;
                    }
                }
            }
            .btn__overlay {
                &.use-otp-cta-modal {
                    top: 25px;
                }
            }
        }
    }
}