//Pillar Tile Starts
//Inherit the List Block Component
.pillar-tile {
  position: relative;
  // padding: 48px 0;
  // @include for-tablet {
  //   padding: 24px 0;
  // }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    width: 100vw;
    background: $pillar-tile-background;
    @include for-tablet {
      background: none;
    }
  }
  &__wrapper {
    background: $pillar-tile-block-background;

    @include for-tablet {
      display: flex;
      flex-direction: column;
      flex-direction: row;
      padding: 24px 0;
    }
    @include for-phone {
      flex-direction: column;
      padding: 0;
    }
    height: 100%;
  }
  &__media-wrapper {
    position: relative;
    @include for-tablet {
      flex: 1;
      margin: 0;
    }
    @include for-phone {
      flex: unset;
      margin: 0;
    }
  }
  &__img {
    img {
      margin: 0;
    }
    @include for-phone {
      display: none;
    }
  }
  &__title {
    position: absolute;
    top: 16px;
    left: 16px;
    float: left;
    opacity: 0.9;
    width: 65%;
    color: $pillar-tile-brand-title-color;
    padding: 8px;
    font-size: $font32;
    line-height: $line-height-32;
    @include for-tablet {
      font-size: $font24;
      line-height: $line-height-24;
    }
    @include for-phone {
      font-size: $font28;
      line-height: $line-height-32;
      width: 100%;
      margin: 0;
      position: relative;
      left: 0;
      top: 0;
    }
  }
  &__content-wrapper {
    padding: 16px;
    .caption {
      p {
        font-size: $font16;
        line-height: $line-height-24;
        font-family: $pfencoresanspro-reg;
      }
    }
    @include for-tablet {
      flex: 1;
      padding: 0px 0px 0px 24px;
      .caption {
        margin-bottom: 16px;
        p {
          font-size: $font16;
          line-height: $line-height-24;
        }
      }
    }
    @include for-phone {
      flex: unset;
      padding: 24px 0px;
    }
  }
  &__list {
    .link-list__list {
      margin: 0;
      li {
        a {
          padding: 10px 30px 10px 0;
          font-size: $font22;
          line-height: $line-height-28;
        }
        //link with icon
        @include link-icon(
          $pl-link-color,
          $pl-link-color,
          $pl-link-color,
          $pl-link-color,
          $pl-icon-color,
          $pl-icon-hover-color,
          $icon-right-arrow,
          $pl-icon-size
        );
        &:nth-child(n) {
          a {
            border-bottom: $lc-border-size solid $lc-border-color;
          }
        }
        &:last-child {
          a {
            border: none;
          }
        }
      }
    }
  }
  //variant 2
  &--link-wrapper {
    position: relative;
  }
  &--link {
    padding: 0;
    span {
      display: block;
    }
    .brand-name {
      margin: 8px;
      text-transform: uppercase;
      @include for-md-desktop {
        font-size: $font24;
        line-height: $line-height-24;
      }
      @include for-tablet {
        font-size: $font24;
        line-height: $line-height-24;
      }
      @include for-phone {
        margin: 16px;
        font-size: $font28;
        line-height: $line-height-32;
      }
    }
    .click {
      font-size: $font18;
      line-height: $line-height-24;
      margin: 16px 16px 8px 8px;
      position: relative;
      @include for-phone {
        font-size: $font20;
        line-height: $line-height-24;
        margin: 24px 16px 16px 16px;
      }
      &::after {
        content: $icon-right-arrow;
        font-family: "font-icons";
        color: $pillar-tile-click-icon-color;
        font-style: normal;
        line-height: 1;
        font-size: $pillar-tile-click-icon-size;
        position: absolute;
        right: 0;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
      }
    }
  }
  &--link-list-container {
    .link-list__list {
      margin: 0;
      li {
        a {
          @include for-tablet {
            font-size: $font22;
            line-height: $line-height-28;
          }
        }
      }
    }
  }
  &__link-container {
    a {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      &:hover {
        border: none;
        text-decoration: none;
      }
    }
  }
}
//Pillar Tile Ends
