//our-funds-list
.our-funds-list{
    $this:&;
    padding: 0;
    counter-reset: section;
    &__item{
        margin-bottom: 8rem;
        @include for-phone{
            margin-bottom: 3.7rem;
        }
        &:last-child{
            margin-bottom: 0;
        }
        #{$this}__title{
            font-size: $font32;
            line-height: $line-height-34;
            font-family: $pfhandbookpro-regular;
            color: $of-text-clr;
            @include for-phone{
                font-size: $font22;
                line-height: $line-height-24;
            }
            &--bottom-space{
                margin-bottom: 2rem;
                @include for-phone{
                    margin-bottom: 0.9rem;
                }
            }
            &:before{
                counter-increment: section;
                content: counter(section, decimal-leading-zero);
                color: $of-count-text-color;
                font-size: $font32;
                line-height: $line-height-34;
                font-family: $pfhandbookpro-regular;
                display: inline-block;
                margin-right: 0.7rem;
                @include for-phone{
                    font-size: $font22;
                    line-height: $line-height-24;
                    margin-right: 0.25rem;
                }
            }
        }
    }
    &__p{
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $of-text-clr;
        margin-bottom: 2rem;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-22;
            margin-bottom: 1.5rem;
        }
    }
    &__highlight{
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-regular;
        color: $of-text-clr;
        display: inline-block;
        margin-right: 0.5rem;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
            margin-right: 0.2rem;
        }
    }
    &__desc{
        #{$this}__p{
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    &__caption{
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $of-text-clr;
        margin-bottom: 2rem;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 1rem;
        }
    }
    &__figure{
        margin-bottom: 4rem;
        @include for-phone{
            margin-bottom: 2rem;
        }
    }
    &__img{
        width: 100%;
    }
}