//progress-bar
.progress-bar {
    $this: &;
    display: block;
    height: 1px;
    background: $pb-back-bg;
    position: relative;
    @include for-phone {
        margin: 0 -1.5rem;
    }
    &__filled {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        height: 2px;
        background: $pb-filled-bg;
        transition: all 0.3s ease;
        width: 30%;
    }
    &__text {
        font-size: $font18;
        line-height: $line-height-20;
        color: $pb-grey-text;
        font-family: $pfhandbookpro-regular;
        @include for-md-desktop {
            font-size: $font16;
            line-height: $line-height-18;
        }
        @include for-tablet {
            font-size: $font14;
            line-height: $line-height-16;
        }
    }
    &__value {
        font-size: $font18;
        line-height: $line-height-20;
        color: $pb-black-text;
        font-family: $pfhandbookpro-regular;
        @include for-tablet {
            font-size: $font14;
            line-height: $line-height-16;
        }
        .icon {
            font-size: $font12;
            @include for-tablet {
                font-size: $font10;
            }
        }
    }
    &__text-wrap {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
    }
    &--typ2 {
        padding-bottom: 3rem;
        background: transparent;
        border-top: 1px solid $pb-back-bg;
        height: auto;
        overflow: inherit;
        #{$this}__text-wrap {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
        }
        #{$this}__filled {
            height: 0.6rem;
            border-radius: 0.3rem;
            top: -3px;
        }
        @include for-tablet {
            padding-bottom: 2.5rem;
        }
        @include for-phone {
            margin: 0;
        }
    }
}

.progres {
    &__item {
        margin-bottom: 2rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
}


//HFL

.progress-card {
  padding: 0px 40px;
  display: flex;
  align-items: center;
  font-family: $pfhandbookpro-bold;

  &__back {
    font-size: $font20;
   /* color: $financing-shade;*/
    width: 20%;
    display: flex;
    align-items: center;
    padding-left: 16px;
  }

  &__step-title {
    font-size: $font23;
    line-height: 1.4;
    color: $financing-shade;
    text-align: center;
    width: 54%;
  }

  &__details {
    width: 24%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: $font14;

    &__step {
      border: 1px solid $financing-shade;
      border-radius: 50%;
      padding: 7px 4px;
      margin-right: 8px;
      width: 36px;
      height: 36px;
      font-size: 11px;
   
  }

  &__progress-wrapper {
    display: block;
  }

  &__label {
    color: $dark-grey;
  }

  &__step-title {
    color: $financing-shade;
    font-size: $font18;
    display: none;
  }

  &__current-progress {
    &__value {
      color: $progress-card-accent-color;
    }
  }

  &__next-step {
    &__value {
      color: $financing-shade;
    }
  }
}

  @include for-phone {
    padding: 0px 0px;

    &__back {
      padding: 0px;

      &--text {
        display: none;
      }
    }

    &__step-title {
      display: none;
    }

    &__details {
      width: 100%;
      padding: 0px;
      justify-content: center;

      &__step {
        border: 1px solid $financing-shade;
        border-radius: 50%;
        padding: 10px 4px;
        margin-right: 8px;
      }

      &__progress-wrapper {
        display: block;
      }

      &__label {
        color: $dark-grey;
      }

      &__current-progress {
        display: none;
      }

      &__step-title {
        display: block;
      }

      &__next-step {
        &__value {
          color: $financing-shade;
        }
      }
    }
  }
}

.progress-cardbar {
  width: 100%;
  height: 6px;
  background: $progress-card-primary-color;
  margin: 8px 0;
  color: $black;
  position: relative;

  &__filled {
    width: 30%;
    height: 6px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: $progress-card-accent-color;
  }
}