.insurance-card-wrap {
    display: inline-block;
    perspective: 1000px;
    transform-style: preserve-3d;
    width: 35.2rem;
    height: 34.2rem;
    .insurance-card {
        cursor: pointer;
        transition: 0.6s;
        transform-style: preserve-3d;
        position: relative;
        &.is-flipped {
            .insurance-card {
                &__face {
                    &--front {
                        transform: rotateY(180deg);
                    }
                    &--back {
                        transform: rotateY(0deg);
                    }
                }
            }
        }
        &__face {
            position: absolute;
            width: 100%;
            height: 34.2rem;
            left: 0;
            top: 0;
            font-size: $font40;
            border-radius: 2px;
            box-shadow: 0 0 14px 0 rgba($ins-box-shadow, 0.24);
            border: solid 1px $ins-card-border;
            backface-visibility: hidden;
            transition: 0.6s;
            transform-style: preserve-3d;
            &--back {
                transform: rotateY(-180deg);
                padding: 2.4rem;
                .button-link {
                    position: absolute;
                    bottom: 2.4rem;
                    left: 2.4rem;
                    padding: 0;
                }
            }
            &--front {
                z-index: 2;
                transform: rotateY(0deg);
            }
        }
        &__icon-wrap {
            /*height: 18rem;*/ 
            padding: 2.4rem 2.4rem 0;
            background-image: linear-gradient( to bottom, rgba($ins-grad-top, 0), rgba($ins-grad-bottom, 0.16) 100%);
            @include for-tablet {
                height: 15rem;
                padding: 1.6rem 1.6rem 0;
            }
            .icon {
                font-size: $font110;
                color: $ins-card-icon;
                @include for-tablet {
                    font-size: $font98;
                }
            }
        }
        &__icon-img {
            max-width: 11rem;
            @include for-tablet {
                max-width: 9.8rem;
            }
        }
        &__title {
            font-size: $font24;
            line-height: $line-height-24;
            padding: 2.4rem 2.4rem 0;
            color: $ins-card-title;
            @include for-tablet {
                font-size: $font20;
                padding: 1.6rem 1.6rem 0;
            }
        }
        .btn-wrap {
            padding: 2.4rem 2.4rem;
            @include for-tablet {
                padding: 1.6rem;
            }
            .button-brown {
                min-width: 0;
            }
            .button-link {
                @include for-tablet {
                    padding: 1.6rem 0 0;
                }
            }
        }
    }
    .on-page-editor &{
        width: 100%;
    }
}

.plan-counter {
    text-align: right;
    &__count {
        font-size: $font32;
        line-height: $line-height-32;
        font-weight: 600;
        color: $ins-card-count;
        display: block;
        @include for-tablet {
            font-size: $font28;
            line-height: $line-height-28;
        }
    }
    &__name {
        font-size: $font16;
        line-height: $line-height-16;
        color: $ins-card-name;
        text-transform: uppercase;
        display: block;
        @include for-tablet {
            font-size: $font12;
            line-height: $line-height-12;
        }
    }
}

.plan-list {
    max-height: 19rem;
    overflow: auto;
    padding: 0;
    &__item {
        a {
            font-size: $font18;
            color: $ins-link-item;
            padding: 1rem 0;
            display: block;
        }
        position: relative;
        &:not(:last-child) {
            border-bottom: 1px solid $ins-link-border;
        }
        &:last-child {
            padding-bottom: 1rem;
        }
        &:after {
            content: $icon-right-arrow;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            font-family: "font-icons";
            font-size: $font18;
            color: $ins-plan-arrow;
        }
        &:hover {
            &:after {
                color: $ins-item-after;
            }
        }
    }
}

.insurance-list {
    @include for-tablet {
        margin: 0 -15px;
    }
    .slick-slide {
        margin: 0 1.6rem;
    }
    .slick-list {
        margin: 0 -1.6px;
        @include for-tablet {
            padding: 0 20% 0 0;
            margin: 0;
        }
    }
}

@include for-tablet {
    .slick-nav--insurance {
        display: none;
    }
}