.dashboard {
    &__greetings-copy {
        display: flex;
        margin-bottom: 3px;
        @media screen and (min-width: $custom-screen-max-width) {
            margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.3rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            margin-bottom: 0.3vw;
        }
        // @include mQmin($xxxl) {
        //     margin-bottom: 3px;
        // }
        // @include mQmin($xxl) {
        //     margin-bottom: 0px;
        // }

        p {
            font-family: $pfhandbookpro-bold;
            font-size: $font16;
            color: $black;
            margin-top: 3px;
            margin-bottom: 0;

            @media screen and (min-width: $custom-screen-max-width) {
                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.3rem);
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                font-size: 1.6vw;
                margin-top: 0.3vw;
            }

            // @include mQmin($xxl){
            //     font-size: 1.9rem;
            //     margin-top: 2px;
            // }
            // @include mQmin($xxxl){
            //     font-size: 27px;
            //     margin-top: 9px;
            // }

            @include mQ(767px) { 
                font-size: $font24;
            }
            @include mQ(575px) { 
                font-size: $font15;
                line-height: 15px;
            }
        }
        .dashboard {
            &__edit-dropdown {
                margin-right: 20px;

                @media screen and (min-width: $custom-screen-max-width) {
                    margin-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.0rem);
                    //margin-right: 20px;
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    margin-right: 2.0vw;
                }
                
                @include mQ(767px) {
                    margin-left: auto;
                    margin-right: 0;
                }
            }
            &__edit-dropdown-cta {
                border: 1px solid $brand-capital-red;
                background-color: $brand-white-grey;
                margin: 0 0 0 8px;
                padding: 0 8px;
                font-size: $font18;
                line-height: 24px;
                height: 26px;
                color: $brand-capital-red;
                border-radius: 12.5px;

                @media screen and (min-width: $custom-screen-max-width) {
                    font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
                    margin: 0 0 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                    padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                    line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.4rem);
                    height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.6rem);
                    border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.25rem);
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    font-size: 1.8vw;
                    margin: 0 0 0 0.8vw;
                    padding: 0 0.8vw;
                    line-height: 2.4vw;
                    height: 2.6vw;
                    border-radius: 1.25vw;
                }

                // @include mQmin($xxl){
                //     font-size: 22px;
                //     margin: 0 0 0 11px;
                //     padding: 0 10px;
                //     line-height: 28px;
                //     height: 31px;
                //     border-radius: 25px;
                // }
                // @include mQmin($xxxl){
                //     font-size: 31px;
                //     margin: 0 0 0 20px;
                //     padding: 4px 16px;
                //     line-height: 24px;
                //     height: 43px;
                //     border-radius: 25px;
                // }

                @include mQ(575px) { 
                    font-size: $font11;
                    line-height: 14px;
                    height: 16px;
                    border-radius: 7.8px;
                }
                .icon {
                    font-size: $font12;
                    margin: 0;
                    line-height: 24px;
                    padding-left: 10px;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                        line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font24});
                        padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.2vw;
                        line-height: 2.4vw;
                        padding-left: 1.0vw;
                    }

                    @include mQ(575px) { 
                        font-size: $font10;
                        line-height: 16px;
                        padding-left: 5px;
                    }
                }
            }            
            &__edit-dropdown-list {
                border: 1px solid $brand-capital-red !important;
                position: absolute;
                width: 204px !important;
                background: $white;
                z-index: 99;
                margin-top: 7px !important;
                margin-bottom: 0;
                list-style: none;
                left: auto !important;
                right: 0 !important;    
                padding: 6px 0 !important;

                @media screen and (min-width: $custom-screen-max-width) {
                    width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 20.4rem) !important;
                    margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem) !important;
                    padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) 0 !important;
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    width: 20.4vw !important;
                    margin-top: 0.7vw !important;
                    padding: 0.6vw 0 !important;
                }

                // @include mQmin($xxxl) {
                //     width: 245px !important;
                //     margin-top: 9px !important;
                //     padding: 8px 0 !important;
                // }
                // @include mQmin($xxl) {
                //     width: 245px !important;
                //     margin-top: 9px !important;
                //     padding: 8px 0 !important;
                // }


                @include mQ(575px) { 
                    width: 130px !important;
                    padding: 3px 0 !important;
                    margin-top: 6px !important;
                }            
                &::before {
                    content: "";
                    position: absolute;
                    top: -6px;
                    right: 35px;
                    width: 11px;
                    height: 11px;
                    border-left: 1px solid $brand-capital-red;
                    border-bottom: 1px solid $brand-capital-red;
                    background-color: $white;
                    transform: rotate(135deg);

                    @media screen and (min-width: $custom-screen-max-width) {
                        top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.6rem);
                        right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.5rem);
                        width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
                        height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        top: -0.6vw;
                        right: 3.5vw;
                        width: 1.1vw;
                        height: 1.1vw;
                    }

                    @include mQ(575px) { 
                        right: 25px;
                        width: 8px;
                        height: 8px;
                        top: -5px;
                    }
                }
                li {
                    margin: 0;
                    padding: 6px 0;

                    @media screen and (min-width: $custom-screen-max-width) {
                        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) 0;
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        padding: 0.6vw 0;
                    }
                    // @include mQmin($xxl){
                    //     padding: 7px 0;
                    // }
                    
                    @include mQ(575px) { 
                        padding: 3px 0;
                    }
                    &::before {
                        display: none;
                    }
                    a {
                        font-family: $pfhandbookpro-regular;
                        font-size: $font16;
                        color: $black;
                        display: block;
                        padding: 0 15px;

                        @media screen and (min-width: $custom-screen-max-width) {
                            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                            padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15});
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            font-size: 1.6vw;
                            padding: 0 1.5vw;
                        }
                        // @include mQmin($xxl) {
                        //     font-size: 19px;
                        //     padding: 0 18px;
                        // }

                        &:hover,
                        &:focus,
                        &:active {
                            border-bottom: 0 none;
                        }
                        @include mQ(575px) { 
                            font-size: $font10;
                            padding: 0 10px;
                            line-height: 12.5px;
                        }
                    }
                }
            }            
        }
    }
    &__greetings-component-wrapper {
        display: flex;
        @include mQ(767px) {
            flex-direction: column;
        }
        .dashboard {
            &__greetings-component{
                @include mQmin($xxl){
                    padding-left: 17px;
                }
                @include mQmin($xxxl){
                    padding-left: 28px;
                }
            }
            &__login-info {
                p {
                    font-size: $font16;
                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.6vw;
                    }
                    // @include mQmin($xxl){
                    //     font-size: 1.9rem;
                    //     margin-top: 13px;
                    //     margin-bottom: 17px;
                    // }
                    // @include mQmin($xxxl){
                    //     font-size: 2.7rem;
                    //     margin-top: 5px;
                    //     margin-bottom: 17px;
                    // }
                    @include mQ(767px) {
                        margin-bottom: 17px;
                    }
                    @include mQ(575px) { 
                        font-size: $font11;
                        line-height: 13px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }    
}