/*
 * Portfolio Content Horizontal Tab Style
 *
 */

.dashboard {
  &__content-horizontal-tab-main-wrapper {
    //width: calc(100% - 60px);
    margin-bottom: 20px;
    position: relative;
    //margin-left: 30px;

    @media screen and (min-width: $custom-screen-max-width) {
      margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      margin-bottom: 20px;
    }

    @include for-phone {
      display: none;
    }

    ul {
      display: flex;
      padding-left: 13px;
      padding-right: 13px;
      position: relative;
      margin-bottom: 0;
      border-radius: 2px;
      box-shadow: 2px 6px 12px 0 rgba(0, 0, 0, 0.08);
      background-color: $white;
      margin-left: 0px;
      margin-right: 0px;
      overflow-x: auto;
      -ms-overflow-style: -ms-autohiding-scrollbar;
      -webkit-overflow-scrolling: touch;
      transition: all 0.2s;
      will-change: transform;
      user-select: none;
      -ms-overflow-style: none;


      @media screen and (min-width: $custom-screen-max-width) {
        padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font13});
        padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font13});
        border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem);
        box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.2rem) 0 rgba(0, 0, 0, 0.08);
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        padding-left: 1.3vw;
        padding-right: 1.3vw;
        border-radius: 0.2vw;
        box-shadow: 0.2vw 0.6vw 1.2vw 0 rgba(0, 0, 0, 0.08);
      }


      &::-webkit-scrollbar {
        display: none;
      }

      li {
        font-family: $pfhandbookpro-regular;
        font-size: $font16;
        text-align: center;
        color: rgba(0, 0, 0, 0.4) !important;
        padding-left: 0;
        white-space: nowrap;
        padding: 0px 11px 0px 11px;
        height: 48px;
        display: flex;
        align-items: center;
        position: relative;
        cursor: pointer;
        margin-left: 19px;
        margin-right: 19px;
        border-bottom: 3px solid transparent;

        @media screen and (min-width: $custom-screen-max-width) {
            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
            padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
            height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.8rem);
            margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
            margin-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
            border-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.3rem) solid transparent;
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            font-size: 1.6vw;
            padding: 0vw 1.1vw 0vw 1.1vw;
            height: 4.8vw;
            margin-left: 1.9vw;
            margin-right: 1.9vw;
            border-bottom: 0.3vw solid transparent;
        }

        @include for-tablet {
          margin-left: 27px;
          margin-right: 27px;
        }

        &:last-child {
          &:after {
            display: none;
          }
        }

        &:before {
          content: "";
        }

        &:after {
          content: "";
          height: 22px;
          width: 1px;
          display: block;
          background-color: $ford-gray;
          opacity: 0.4;
          position: absolute;
          right: -19px;
          top: 50%;
          z-index: 2;
          transform: translateY(-50%);

          @media screen and (min-width: $custom-screen-max-width) {
              height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font22});
              width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.1rem);
              right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.9rem);
          }
          @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
              height: 2.2vw;
              width: 0.1vw;
              right: -1.9vw;
          }

          @include for-tablet {
            right: -27px;
          }
        }

        &.active {
          font-family: $pfhandbookpro-regular;
          color: $black !important;
          z-index: 1;
          padding-top: 0px;
          padding-left: 11px;
          padding-right: 11px;

          @media screen and (min-width: $custom-screen-max-width) {
              padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
              padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
          }
          @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            padding-left: 1.1vw;
            padding-right: 1.1vw;
          }

          // Color Theme
          @include themify($themes) {
            border-bottom: themed('contentHorizontalTabActiveBorder');
          }

          &:before {
            display: none;
          }
        }
      }
    }

    .arrow {
      background-color: #fff;
      border-radius: 50%;
      cursor: pointer;
      z-index: 9;
      display: none;

      // Color Theme
      @include themify($themes) {
        border: themed('contentHorizontalTabArrowBorder');
        color: themed('contentHorizontalTabArrowColor');
      }

      &:after {
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        content: $icon-arrow;
        font-family: "font-icons";
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
        font-size: $font8;
        position: absolute;
        top: 5px;
        left: 5px;

        @media screen and (min-width: $custom-screen-max-width) {
            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font8});
            top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
            left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            font-size: 0.8vw;
            top: 0.5vw;
            left: 0.5vw;
        }
      }
    }

    .nav-prev {
      width: 19px;
      height: 19px;
      position: absolute;
      left: -10px;
      margin-top: 0;
      top: 14px;

      @media screen and (min-width: $custom-screen-max-width) {
          width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
          height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
          left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.0rem);
          top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        width: 1.9vw;
        height: 1.9vw;
        left: -1.0vw;
        top: 1.4vw;
      }

      &:after {
        transform: rotate(90deg);
        left: 4px;

        @media screen and (min-width: $custom-screen-max-width) {
          left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          left: 0.4vw;
        }
      }
    }

    .nav-next {
      width: 19px;
      height: 19px;
      position: absolute;
      right: -10px;
      margin-top: 0;
      top: 14px;

      @media screen and (min-width: $custom-screen-max-width) {
        width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
        height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
        right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.0rem);
        top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        width: 1.9vw;
        height: 1.9vw;
        right: -1.0vw;
        top: 1.4vw;
      }

      &:after {
        transform: rotate(-90deg);
      }
    }
  }

  &__horizontal-inner-content-wrapper {
    display: none;

    @include for-phone {
      display: block;

      .dashboard__portfolio-inner-content-wrapper {
        display: none;

        &.active {
          display: block;
        }
      }
    }

    &.active {
      display: block;
    }

  }

  @include for-phone {
    &__portfolio-mobile-dropdown {
      display: block;
      height: 61px;
      box-shadow: 0 10px 32px -14px rgba(0, 0, 0, 0.22);
      background-color: $white;
      font-family: $pfhandbookpro-regular;
      font-size: $font26;
      color: $black;
      margin: 0 0px 10px;
      padding: 20px 17px 20px;
      position: relative;
      border: solid 1px #f1f3f6;

      &:after {
        content: $icon-arrow;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        font-family: "font-icons";
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        position: absolute;
        top: 50%;
        margin-left: 10px;
        font-size: $font14;
        right: 30px;
        transform: translateY(-50%) rotate(0deg);

        // Color Theme
        @include themify($themes) {
          color: themed('contentHorizontalTabArrowColor');
        }
      }

      &.active {
        &:after {
          transform: translateY(-50%) rotate(180deg);
        }
      }

      @include mQ(575px) { 
        font-size: $font16;
        padding: 9px 20px 10px 11px;
        height: 38px;
        margin: 12px 0px 0;
        box-shadow: 0 7px 20px -9px rgba(0, 0, 0, 0.22);

        &:after {
          font-size: 1.0rem;
          right: 20px;
        }
      }
    }
  }
}

.low-padding {
  .dashboard {
    &__content-horizontal-tab-main-wrapper {
      ul {
        width: 100%;
        margin: 0px;
        display: table;

        @include mQ(575px) {
          padding-left: 6px;
          padding-right: 6px;
        }

        li {
          display: table-cell;
          width: 50%;
          padding: 0;
          height: auto;
          border-bottom: none !important;

          &::before {
            display: none;
          }

          span {
            padding-left: 5px;
            padding-right: 5px;
            text-align: center;
            vertical-align: middle;
            position: relative;
            height: 45px;
            border: 3px solid transparent !important;
            width: auto;
            display: inline-block;
            padding-top: 10px;

            @media screen and (min-width: $custom-screen-max-width) {
              padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
              padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
              padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
              height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.5rem);
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
              padding-left: 0.5vw;
              padding-right: 0.5vw;
              padding-top: 1.0vw;
              height: 4.5vw;
            }

            @include for-phone {
              padding-top: 8px;
              font-size: 2rem;
              height: 48px;
            }

            &::after {
              right: 0px;
            }

            &::before {
              content: "";
              width: calc(100% + 40px);
              position: absolute;
              left: -20px;
              bottom: -3px;
              background-color: transparent;
              height: 3px;
              margin: 0px;
              display: block;
            }

            @include mQ(767px) {
              &::before {
                width: 100%;
                left: 0px;
              }
            }

            @include mQ(575px) {
              padding-top: 4px;
              font-size: 1.2rem;
              height: 30px;
              padding-left: 0px;
              padding-right: 0px;
              margin-left: 6px;
              margin-right: 6px;

              &::before {
                width: 100%;
                left: 0px;
              }
            }
          }

          &.active {
            span {
              font-family: $pfhandbookpro-regular;
              color: $black !important;
              z-index: 1;
              padding-left: 11px;
              padding-right: 11px;

              @media screen and (min-width: $custom-screen-max-width) {
                padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
                padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11});
              }
              @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                padding-left: 1.1vw;
                padding-right: 1.1vw;
              }

              @include mQ(767px) {
                padding-left: 5px;
                padding-right: 5px;
              }

              @include mQ(575px) {
                padding-left: 0px;
                padding-right: 0px;
                margin-left: 6px;
                margin-right: 6px;
              }

              // Color Theme
              @include themify($themes) {
                border-bottom: themed('contentHorizontalTabActiveBorder');
              }

              &::before {
                @include themify($themes) {
                  background-color: themed('backgroundColor');
                }
              }
            }
          }

          &:after {
            right: 0 !important;

            @include mQ(575px) {
              height: 14px;
            }
          }
        }
      }

      .nav-next {
        display: none !important;
      }

      @include for-phone {
        display: block;
      }
    }

    &__horizontal-inner-content-wrapper {
      display: none;

      &.active {
        display: block;
      }

      @include for-phone {
        .dashboard__portfolio-inner-content-wrapper {
          display: block;
        }
      }
    }

    &__portfolio-mobile-dropdown {
      @include for-phone {
        display: none;
      }
    }
  }
}