//know-funds
.know-funds {
    $this: &;
    &__title {
        font-size: $font44;
        line-height: $line-height-46;
        color: $kyf-text-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
        }
    }
    &__p {
        font-size: $font24;
        line-height: $line-height-1-1;
        color: $kyf-text-clr;
        font-family: $pfhandbookpro-thin;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__title-wrap {
        max-width: 73.6rem;
        margin-bottom: 2.4rem;
    }
    &__link-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0;
        margin: 0;
        @include for-phone {
            display: block;
            justify-content: initial;
            align-items: initial;
        }
    }
    &__link-item {
        width: 32%;
        @include for-phone {
            width: 100%;
            margin-bottom: 2rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    &__link {
        display: flex;
        align-items: center;
        padding: 2.6rem 3rem;
        // background: $kyf-link-bg;
        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.24);
        @include for-phone {
            border-right: 0.2rem;
            padding: 0.95rem 2rem;
            min-height: 5rem;
        }
        &:hover {
            #{$this}__icon--arrow {
                color: $kyf-link-hover-clr;
            }
        }
        &--fixed-width {
            max-width: 54.4rem;
        }
    }
    &__link-text {
        font-size: $font24;
        line-height: $line-height-26;
        color: $kyf-text-clr;
        font-family: $pfhandbookpro-regular;
        text-transform: capitalize;
        width: 94%;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-22;
            width: auto;
        }
    }
    &__icon {
        font-size: 3rem;
        width: 3rem;
        height: 3rem;
        color: $kyf-icon-color;
        display: inline-block;
        margin-right: 2rem;
        @include for-phone {
            font-size: 2.3rem;
            width: 2.3rem;
            height: 2.3rem;
            margin-right: 1.6rem;
        }
        &--arrow {
            font-size: 2rem;
            width: 2rem;
            height: 2rem;
            margin-left: auto;
            transition: all 0.3s ease-in;
            margin-right: 0;
            @include for-phone {
                font-size: 1.6rem;
                width: 1.6rem;
                height: 1.6rem;
            }
        }
    }
    &--container-sm {
        max-width: 73.6rem;
        margin: 0 auto;
    }
}