.selectize-control.single .selectize-input {
    background: none;
    box-shadow: none;
    border-radius: 4px;
    background-color: $form-input-bg;
    border: 1px solid $form-input-border;
}

.selectize-input>input {
    opacity: 0.7;
    font-family: $pfhandbookpro-regular;
    font-size: $font16;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: $form-label;
    padding-bottom: 8px;
}

.selectize-dropdown .active {
    color: $form-input;
    background: $form-select-option-hover;
}

//Select option font-size
.selectize-dropdown,
.selectize-input,
.selectize-input input {
    font-size: $font18;
}

//Select Input
.selectize-input {
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1;
    letter-spacing: normal;
    text-align: left;
    height: 44px;
    border-radius: 4px;
    background-color: $form-input-bg;
    border: 1px solid $form-input-border;
    color: $form-input;
    &:after {
        content: $icon-arrow !important;
        font-family: "font-icons";
        color: $form-input;
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1.5;
        text-transform: none;
        position: absolute;
        font-size: $font12 !important;
        right: 10px !important;
        top: 50% !important;
        transform: translate(-50%, -50%) !important;
        width: 12px !important;
        height: 12px !important;
        border: 0 !important;
        transform-origin: 50% 50% 0;
    }
}

.selectize-control.single .selectize-input.dropdown-active:after {
    content: $icon-arrow !important;
    font-family: "font-icons";
    color: $form-input;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    line-height: 1;
    text-transform: none;
    position: absolute;
    font-size: $font12 !important;
    right: 10px !important;
    top: 50% !important;
    transform: rotate(180deg) !important;
    width: 12px !important;
    height: 12px !important;
    border: 0 !important;
    transform-origin: 25% 25%;
}

//Option Styling
.selectize-dropdown .option,
.selectize-dropdown .optgroup-header {
    padding: 14px 16px;
    border-color: $form-input-border;
    &:hover {
        cursor: pointer;
    }
}

//Select Item
.selectize-input>* {
    vertical-align: middle;
}

//Select Dropdown container
.selectize-control.single .selectize-input,
.selectize-dropdown.single {
    border-color: $form-input-border;
}

//Dropdown Active
.dropdown-active,
.selectize-dropdown {
    border-radius: 0 !important;
}

