/* ABC website styling */

.select--website {
    &.selectize-control.single .selectize-input {
        border: 0;
        border-bottom: 1px solid $select-border;
        border-radius: 0;
        font-size: $font32;
        height: 4.4rem;
        text-align: center;
        padding-top: 0;
        padding-bottom: 0;
    }
    .selectize-dropdown.single {
        text-align: left;
    }
}

.selectize-control.single .item {
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 30px;
    white-space: nowrap;
    max-width: 95vw;
    width: 95%;
    display: flex;
    align-items: center;
    padding: 0;
}

.selectize-input, .selectize-control.single .selectize-input.input-active {
    display: inline-flex;
}
// Add class in form tag for add line in dropdown
.pipe-style {
    .selectize-control.single .selectize-input {
        &::after {
            border-left: 1px solid #cdcece !important;
            height: 100% !important;
            margin: 0 !important;
            min-width: 46px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            right: -24px !important;
            color: $raven-gray;
            background-color: $brand-white;
        }
        &.dropdown-active {
            &::after {
                border-left: 0 !important;
                border-right: 1px solid #cdcece !important;
                color: $raven-gray;
                background-color: $brand-white;
            }
        }
    }
}