.search-results {
    &__title {
        font-size: $font32;
        line-height: $line-height-1;
        font-family: $pfhandbookpro-thin;
        text-align: center;
        margin-bottom: 3rem;
        @include for-tablet {
            font-size: $font20;
            font-family: $pfhandbookpro-regular;
            margin-bottom: 2rem;
            text-align: left;
        }
    }
    &__error {
        font-size: $font14;
        line-height: $line-height-16;
        color: $result-error;
        margin-bottom: 2rem;
        text-align: center;
        @include for-tablet {
            font-size: $font12;
            line-height: $line-height-14;
            text-align: left;
        }
    }
    &__block-wrap {
        @include for-tablet {
            width: auto;
            margin: 0 -15px;
        }
    }
    &__block {
        display: inline-block;
        vertical-align: top;
        width: calc(50% - 1.8rem);
        margin-bottom: 2.2rem;
        &:nth-child(odd) {
            margin-right: 3.2rem;
        }
        &:nth-last-child(-n + 2) {
            //margin-bottom: 0;
        }
        @include for-tablet {
            width: 100%;
            margin-bottom: 0;
            &:nth-child(odd) {
                margin-right: 0;
            }
        }
    }
    &__head {
        font-size: $font24;
        line-height: $line-height-26;
        background: $result-title-clr;
        padding: 1.8rem 6.4rem;
        @include for-tablet {
            padding: 2rem 1.5rem;
            font-size: $font20;
            line-height: $line-height-1;
            color: $result-title-mob;
        }
    }
    &__list {
        padding: 0 6.4rem;
        @include for-tablet {
            padding: 0 1.5rem;
        }
    }
    &__item {
        border-bottom: 1px solid $result-item-border;
        position: relative;
        &:last-child {
            border-bottom: 0;
        }
    }
    &__link {
        font-size: $font24;
        line-height: $line-height-26;
        color: $result-item-clr;
        display: block;
        padding: 2.4rem 0;
        white-space: nowrap;
        width: 65%;
        overflow: hidden;
        text-overflow: ellipsis;
        &:hover,
        &:focus,
        &:active {
            color: $result-item-clr;
            .search-results__icon {
                color: $result-icon-hover;
            }
        }
        @include for-tablet {
            padding: 2rem 0;
            font-size: $font20;
            line-height: $line-height-1;
        }
    }
    &__icon {
        font-size: $font18;
        line-height: $line-height-1;
        color: $result-item-icon;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    .searchaccordian {
        &__item {
            display: none;
        }

        &__view-all, &__view-less{
            padding: 20px 6.4rem 10px;
            @include mQ(1020px) {
                padding: 20px 2.4rem 10px 15px;
            }
        }
    }
    &__head {
        position: relative;
        cursor: pointer;
        .accordian__icon {
            position: absolute;
            right: 20px;
            top: 50%;
            transform: translateY(-50%);
        }
        &.active {
            & > .accordian__icon {
                &:before {
                    content: $icon-increment;
                }
            }
        }
    }
}