.award {
    @include for-phone {
        text-align: center;
    }
    &__img {
        max-width: 18rem;
        margin-bottom: 3rem;
        @include for-phone {
            border-radius: 50%;
            border: 10px solid $award-border;
        }
    }
    &__title {
        font-size: $font32;
        color: $award-title;
        padding-right: 10px;
        @include for-phone {
            display: none;
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__desc {
        font-size: $font24;
        line-height: $line-height-28;
        color: $award-desc;
        font-family: $pfhandbookpro-thin;
        @include for-phone {
            display: none;
            font-size: $font18;
            line-height: $line-height-22;
        }
    }
    &__designation {
        font-size: $font24;
        line-height: $line-height-28;
        color: $award-desc;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 1.4rem;
        display: block;
        font-weight: 300;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-22;
        }
    }
}

.award-list {
    list-style: none;
    .slick-prev,
    .slick-next {
        position: relative;
    }
    @include for-phone {
        .slick-slide {
            margin: 0 -5%;
            position: relative;
            z-index: 100;
            .award {
                transform: scale(0.65);
                transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
                opacity: 0.3;
            }
        }
        .slick-center {
            z-index: 101;
            .award {
                transform: scale(1);
                opacity: 1;
                &__title {
                    display: block;
                }
                &__desc {
                    display: block;
                }
            }
        }
    }
}

@include for-phone {
    .slick-nav--award {
        display: none;
    }
}

.award-list-wrap--testimonials {
    .award {
        text-align: center;
        &__img {
            margin: 0 auto 3rem;
        }
        &__desc {
            text-align: center;
            margin: 0 -8rem;
            padding: 0 1.5rem;
            @include for-phone {
                margin: 0 -4rem;
            }
        }
    }
    .award-list {
        .slick-slide {
            opacity: 0.3;
            transition: all 0.3s ease;
            .award {
                &__title,
                &__desc,
                &__designation {
                    opacity: 0;
                    transition: all 0.3s ease;
                }
            }
            &.slick-center {
                opacity: 1;
                .award {
                    &__title,
                    &__desc,
                    &__designation {
                        opacity: 1;
                    }
                }
            }
        }
    }
}