.paypremium {
  width: 100%;
  display: flex;
  flex-direction: row;
  &__steps {
    ul {
      padding-left: 10%;
      li {
        h4 {
          font-family: $roboto-medium !important;
          font-size: $font10;
          line-height: normal;
          word-break: break-word;
        }
        &::before {
          all: unset;
          content: "";
        }
        a {
          &:hover,
          &:active,
          &:focus {
            border: none;
          }
        }
      }
    }
  }
  &__step-content {
    &.active {
      display: block;
    }
  }
  &__details {
    font-size: $font16;
    line-height: $line-height-20;
    color: $paypremium-details;
    font-family: $pfhandbookpro-regular;
    padding: 0 15px;
    margin: 60px 0 25px 0;
  }
  &__step2-modal {
    max-width: 649px;
  }
  &__step-2-form {
    margin-top: 25px;
  }
  &__step-2-info {
    margin-top: 40px;
    padding-left: 10px;
    .form-group {
      .form-check {
        font-size: $font14;
        color: $paypremium-step-2-info;
        padding-top: 0;
        .checkmark {
          width: 16px;
          height: 16px;
          border-width: 1px;
          border-radius: 0;
          &:after {
            left: 4px;
            top: 2px;
            width: 6px;
            height: 8px;
          }
        }
        input:checked ~ .checkmark {
          border-radius: 0;
          box-shadow: 0;
          border-width: 1px;
        }
      }
    }
    .qsform__buttongroup {
      button {
        padding: 12px 0;
        margin-right: 0;
      }
    }
  }
  &__step-2-details {
    padding-left: 35px;
    margin-bottom: 48px;
    p {
      font-size: $font14;
      color: $paypremium-step-2-info;
    }
  }
  //form group
  .form-group {
    &__label {
      font-size: $font14;
    }
    &__placeholder {
      font-size: $font14;
      padding-left: 12px;
      height: 35px;
    }
    &__datepicker-container {
      img {
        top: 2px;
      }
    }
  }
  .qsform {
    &__table {
      table-layout: fixed;
      th {
        font-size: $font11;
      }
      td {
        font-size: $font11;
        font-family: $pfhandbookpro-regular;
        padding: 11px 0;
      }
    }
    &__pp-table-total {
      margin-top: 20px;
      background: $heading-bg;
      height: 19px;
      td {
        font-family: $pfhandbookpro-bold;
        color: $paypremium-total;
        text-align: right;
        padding: 0;
        &:last-child {
          text-align: center;
        }
      }
    }
  }
}

.progress-tracker {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0px auto;
  padding: 0;
  list-style: none;
}
.progress-tracker:hover,
.progress-tracker:focus {
  color: transparent !important;
}
.progress-step {
  display: flex;
  flex-direction: column;
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 0%;
  flex: 1 1 0%;
  margin: 0;
  padding: 0;
  min-width: 28px;
}
.progress-step:hover,
.progress-step:focus {
  color: transparent !important;
}
.progress-step:last-child {
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
}

.progress-step:not(:last-child)::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -10;
  top: 10px;
  bottom: 10px;
  right: -14px;
  width: 100%;
  height: 4px;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
}

.progress-step.is-active .progress-title {
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: $brand-protecting;
}
.progress-step.is-complete .progress-title {
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: $brand-protecting;
}
.progress-step > a {
  display: block;
  height: 30px;
}
.progress-step > a:hover {
  color: transparent;
}
.progress-marker {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
  z-index: 20;
  width: 14px;
  height: 14px;
  padding-bottom: 2px;
  color: #fff;
  font-weight: 400;
  border: 2px solid transparent;
  border-radius: 50%;
  -webkit-transition: background-color, border-color;
  transition: background-color, border-color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  top: 6px;
}

.progress-text {
  display: block;
  padding: 8px 9.3333333333px 20px 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.progress-title {
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #353535;
  margin-top: 0px;
  margin-bottom: 10px;
}

.progress-step .progress-marker {
  color: #fff;
  background-color: #a5a5a5;
}

.progress-step::after {
  background-color: #b6b6b6;
}

.progress-step .progress-text,
.progress-step .progress-step > a .progress-text {
  color: #333333;
}
.progress-step.is-active .progress-marker {
  background-color: #ffffff;
  width: 24px;
  height: 24px;
  border: 2px solid $brand-protecting;
  top: 0px;
}
.progress-step.is-active .progress-marker::after {
  width: 11px;
  height: 11px;
  border-radius: 15px;
  top: 1px;
  left: 0px;
  position: relative;
  background-color: $brand-protecting;
  content: "";
  display: inline-block;
  visibility: visible;
}
.progress-step.is-complete .progress-marker {
  background-color: transparent;
  width: 24px;
  height: 24px;
  /*    border: 3px solid $brand-protecting;*/
  top: 0px;
}
.progress-step.is-complete .progress-marker::after {
  width: 11px;
  height: 11px;
  border-radius: 15px;
  top: 1px;
  left: 0px;
  position: relative;
  background-color: $brand-protecting;
  content: "";
  display: inline-block;
  visibility: visible;
}

.progress-step.is-complete::after {
  background-color: #868686;
}

.progress-step:hover .progress-marker {
  /*background-color: #56ADF5;*/
}

.progress-tracker--center .progress-step {
  text-align: center;
}

.progress-tracker--center .progress-step:last-child {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.progress-tracker--center .progress-step::after {
  right: -50%;
}

.progress-tracker--center .progress-marker {
  margin-left: auto;
  margin-right: auto;
}

.progress-tracker--right .progress-step {
  text-align: right;
}

.progress-tracker--right .progress-step:last-child {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.progress-tracker--right .progress-step::after {
  right: calc(-100% + 14px);
}

.progress-tracker--right .progress-marker {
  margin-left: auto;
}

.progress-tracker--border {
  padding: 5px;
  border: 2px solid #868686;
  border-radius: 38px;
}

.progress-tracker--spaced .progress-step::after {
  width: calc(100% - 48px);
  margin-right: 24px;
}

.progress-tracker--word {
  padding-right: 38.6666666667px;
  overflow: hidden;
}

.progress-tracker--word .progress-text {
  display: inline-block;
  white-space: nowrap;
}

.progress-tracker--word .progress-title {
  margin: 0;
}

.progress-tracker--word-center {
  /*padding-right: 38.6666666667px;*/
  /*padding-left: 38.6666666667px;*/
}

.progress-tracker--word-center .progress-text {
  padding-right: 0;
  padding-left: 0;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  margin-left: 14px;
}

.progress-tracker--word-right {
  padding-right: 0;
  padding-left: 38.6666666667px;
}

.progress-tracker--word-right .progress-text {
  padding-left: 0;
  -webkit-transform: translateX(calc(-100% + 28px));
  transform: translateX(calc(-100% + 28px));
}

.progress-tracker--text .progress-step:last-child {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.progress-tracker--text-top .progress-step::after {
  top: auto;
}

.progress-tracker--text-top .progress-text {
  height: 100%;
}

.progress-tracker--text-top .progress-marker {
  bottom: 28px;
}

.progress-tracker--text-inline .progress-step {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.progress-tracker--text-inline .progress-text {
  position: relative;
  z-index: 30;
  max-width: 70%;
  white-space: nowrap;
  padding-top: 0;
  padding-bottom: 0;
  background-color: #fff;
}

.progress-tracker--text-inline .progress-title {
  margin: 0;
}

.progress-tracker--square .progress-step {
  padding-top: 0;
}

.progress-tracker--square .progress-marker {
  -webkit-transform: scaleX(0.33) translateY(-12px);
  transform: scaleX(0.33) translateY(-12px);
  border-radius: 0;
}

@media (max-width: 399px) {
  .progress-tracker-mobile {
    overflow-x: auto;
  }
  .progress-tracker-mobile .progress-tracker {
    min-width: 200%;
  }
}

.progress-tracker--vertical {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.progress-tracker--vertical .progress-step {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

.progress-tracker--vertical .progress-step::after {
  right: auto;
  top: 14px;
  left: 12px;
  width: 4px;
  height: 100%;
}

.progress-tracker--vertical .progress-marker {
  position: absolute;
  left: 0;
}

.progress-tracker--vertical .progress-text {
  padding-top: 7px;
  padding-left: 42px;
}

.progress-tracker--vertical .progress-step:not(:last-child) .progress-text {
  padding-bottom: 42px;
}

.anim-ripple .progress-marker::before,
.anim-ripple-large .progress-marker::before,
.anim-ripple-splash .progress-marker::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 30;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  visibility: hidden;
}

.anim-ripple :not(:active) .progress-marker::before,
.anim-ripple-large :not(:active) .progress-marker::before,
.anim-ripple-splash :not(:active) .progress-marker::before {
  -webkit-animation: scale-up 0.3s ease-out;
  animation: scale-up 0.3s ease-out;
}

.anim-ripple :focus .progress-marker::before,
.anim-ripple-large :focus .progress-marker::before,
.anim-ripple-splash :focus .progress-marker::before {
  visibility: visible;
}

.anim-ripple-large .progress-marker::before {
  width: 200%;
  height: 200%;
}

.anim-ripple-splash .progress-marker::before {
  width: 200%;
  height: 200%;
  box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.35);
}

.anim-ripple-double .progress-marker::before,
.anim-ripple-double .progress-marker::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 30;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  visibility: hidden;
  background: none;
  border: 3px solid rgba(0, 0, 0, 0.3);
}

.anim-ripple-double :not(:active) .progress-marker::before,
.anim-ripple-double :not(:active) .progress-marker::after {
  -webkit-animation: scale-up 0.3s ease-out 0s;
  animation: scale-up 0.3s ease-out 0s;
}

.anim-ripple-double :not(:active) .progress-marker::after {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
}

.anim-ripple-double :focus .progress-marker::before,
.anim-ripple-double :focus .progress-marker::after {
  visibility: visible;
}

.anim-path .progress-step::after {
  background-image: -webkit-linear-gradient(
    left,
    #b6b6b6 50%,
    $brand-protecting 50%
  );
  background-image: linear-gradient(
    to right,
    #b6b6b6 50%,
    $brand-protecting 50%
  );
  background-size: 200% 100%;
  background-position: 0% 100%;
  -webkit-transition: background-position 0.3s ease-out;
  transition: background-position 0.3s ease-out;
  z-index: 1;
}

.anim-path .progress-step.is-complete::after {
  background-position: -100% 100%;
}
