.login-tooltip, .post-login-tooltip {
    &.tooltip.show {
        opacity: 1;
        z-index: 10001;
    }
    .tooltip-inner {
        background-color: $white;
        color: $black;
        font-size: $font16;
        line-height: 20px;
        padding: 10px 15px;
        font-family: $pfhandbookpro-regular;
        text-align: left;
        box-shadow: 1px 0 8px 0 rgba($black, 0.08);
        border: 1px solid #dfdfdf;

        // @include fontScaling(font-size,1.6);
        // @include fontScaling(line-height,2.0);
        // @include fontScaling(padding-top,1.0);
        // @include fontScaling(padding-right,1.5);
        // @include fontScaling(padding-bottom,1.0);
        // @include fontScaling(padding-left,1.5);

    }
    &.bs-tooltip-top, 
    &.bs-tooltip-right,
    &.bs-tooltip-bottom,
    &.bs-tooltip-left,
    &.bs-tooltip-auto[x-placement^="top"],
    &.bs-tooltip-auto[x-placement^="right"],
    &.bs-tooltip-auto[x-placement^="bottom"],
    &.bs-tooltip-auto[x-placement^="left"] {
        .arrow {
            &::before {
                z-index: 1;
                width: 10px;
                height: 10px;
                background-color: $white;
                border-left: 1px solid #dfdfdf;
                border-bottom: 1px solid #dfdfdf;
                border-right: 0 none;
                border-top: 0 none;
            }
        }
    }
    &.bs-tooltip-right .arrow, 
    &.bs-tooltip-auto[x-placement^="right"] .arrow {
        &::before {
            right: -4px;
            margin-right: -1px;
            transform: rotate(45deg);
        }
    }
    &.bs-tooltip-bottom .arrow, 
    &.bs-tooltip-auto[x-placement^="bottom"] .arrow {
        &::before {
            bottom: -4px;
            margin-bottom: -1px;
            transform: rotate(135deg);
        }
    }
    &.bs-tooltip-top .arrow, 
    &.bs-tooltip-auto[x-placement^="top"] .arrow {
        &::before {
            top: -4px;
            margin-top: -1px;
            transform: rotate(-45deg);
        }
    }
    &.bs-tooltip-top .arrow, 
    &.bs-tooltip-auto[x-placement^="left"] .arrow {
        &::before {
            right: 0;
            margin-left: -5px;
            transform: rotate(225deg);
        }
    }
}

.post-login-tooltip {
    .tooltip-inner { 
        font-size: $font12;
        padding: 5px 9px;

        @include fontScaling(font-size,1.2);
        @include fontScaling(line-height,2.0);
        @include fontScaling(min-width,15.0);
        @include fontScaling(padding-top,0.5);
        @include fontScaling(padding-right,0.9);
        @include fontScaling(padding-bottom,0.5);
        @include fontScaling(padding-left,0.9);

    }
}