.takeaway_block {
  width: 100%;
  height: auto;
  background: #fff;
  box-shadow: 1px - 2px 8px 0 rgba(0, 0, 0, .08);
  border-radius: 5px;
  position: relative;

  &.pink {
    border-left: 4px solid $top-menu-protecting;
    border-right: 1px solid $top-menu-protecting;
    border-top: 1px solid $top-menu-protecting;
    border-bottom: 1px solid $top-menu-protecting;
    padding-right: 25px;
    width: 100%;
    height: auto;
    background: #fff;
    box-shadow: 1px - 2px 8px 0 rgba(0, 0, 0, .08);
    border-radius: 5px;
    position: relative;

    h2 {
      background: rgba(218, 144, 137, .2);
    }
  }

  &.green {
    border-left: 4px solid $top-menu-investing;
    border-right: 1px solid $top-menu-investing;
    border-top: 1px solid $top-menu-investing;
    border-bottom: 1px solid $top-menu-investing;
    padding-right: 25px;
    width: 100%;
    height: auto;
    background: #fff;
    box-shadow: 1px - 2px 8px 0 rgba(0, 0, 0, .08);
    border-radius: 5px;
    position: relative;

    h2 {
      background: rgba(150, 159, 110, .2);
    }
  }

  &.red {
    border-left: 4px solid $top-menu-advicing;
    border-right: 1px solid $top-menu-advicing;
    border-top: 1px solid $top-menu-advicing;
    border-bottom: 1px solid $top-menu-advicing;
    padding-right: 25px;
    width: 100%;
    height: auto;
    background: #fff;
    box-shadow: 1px - 2px 8px 0 rgba(0, 0, 0, .08);
    border-radius: 5px;
    position: relative;

    h2 {
      background: rgba(199, 34, 42, .2);
    }
  }

  &.yellow {
    border-left: 4px solid $top-menu-financing;
    border-right: 1px solid $top-menu-financing;
    border-top: 1px solid $top-menu-financing;
    border-bottom: 1px solid $top-menu-financing;
    padding-right: 25px;
    width: 100%;
    height: auto;
    background: #fff;
    box-shadow: 1px - 2px 8px 0 rgba(0, 0, 0, .08);
    border-radius: 5px;
    position: relative;

    h2 {
      background: rgba(212, 174, 105, .2);
    }
  }

  h2 {
   
    font-size: 18px;
    font-weight: bold;
    padding-left: 15px;
    color: #000;
    width: 150px;
    font-family: "pfhandbookpro-medium", sans - serif;
    letter-spacing: normal;

    display: inline - block;
    text-transform: uppercase;
  }


  .takeaway_list {
    padding-left: 35px;

    li {
      &::before {
        color: #000 !important;
      }
    }
  }
}


.blog-article-tableofcontent {
  width: 100%;
  height: auto;
  background: #fff;
  box-shadow: 1px -2px 8px 0 rgba(0,0,0,.08);
  position: relative;
  border: 1px solid #c7222a;
  padding: 15px 25px 15px 15px;

  span {
    text-transform: uppercase;
    font-size: 2.3rem;
    line-height: 34px;
    font-weight: bold;
  }

  ul li {
    color: #c7222a !important;
    font-family: "pfhandbookpro-medium",sans-serif;
    font-size: 16px;
    font-weight: bold;
    line-height: 0.75;
    text-transform: capitalize;
  }
}


.dark-banner .banner-content-wrapper .content-banner-share {
  background-color: #ffffff;
  display: inline-block;
  padding: 11px 20px 11px 15px;
  position: absolute;
  left: 180px;
  bottom: 0px !important;
  border-radius: 5px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.13);
  width: 300px;


  .share-component .share-content {
    position: absolute;
    width: 200px;
    left: -20px;
    top: -6px;
    display: block;

    @include mQ(767px) {
      position: absolute;
      width: 200px;
      left: 0px !important;
      top: -9px;
      display: block;
    }
  }



  @include mQ(767px) {
    position: relative;
    left:0;
    bottom: 0;
    padding: 10px !important;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.13);
    width: 300px;
  }
}






  .intro-title {
