}

.podcasttimestamp_block .podcasttimestamp_list {
  padding-left: 0px;
}

.podcasttimestamp_block .podcasttimestamp_list li {
  color: #000 !important;
  font-size: 2.2rem;
}

@media (max-width: 767px) {
  .podcasttimestamp_block .podcasttimestamp_list li {
