
/* Form Progress */

.coapp-progress-tracker {
  margin: 20px auto;
  text-align: center;
  color: #000;
  width: 90%;

  .circle {
    display: inline-block;
    background: #ecddc0;
    width: 220px;
    height: 60px;
    vertical-align: top;
    padding: 10px;
    margin-top: 21px;

    .label {
      display: inline-flex;
      width: 190px;
      text-align: center;
      font-size: 17px;
      font-weight: bold;

      p {
        color: #000;
        margin: auto;
      }

      .front-icon {
        font-size: 2rem;
        padding: 5px;
      }

      .back-icon {
        padding: 5px;
        margin-left: auto;
        font-size: 2rem;
      }
    }

    .p-label {
      margin-top: 2px;
      width: 55%;
    }

    .sub-label {
      position: relative;
      top: -20px;
      font-weight: bold;
      font-size: 1.3rem;
    }

    .act {
      border: 1px solid #695934;
    }

    .title {
      color: #b5b5ba;
      line-height: 18px;
      margin-left: -30px;
      display: block;
      width: 100px;
      margin-top: 5px;
    }

    .done {
      background: #000;
    }
  }

  .active {
    width: 0;
    height: 0;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid #ecddc0;
    left: -115px;
    top: 45px;
    position: relative;
  }

  .bg-white {
    border: 1px dotted #000;
    padding: 15px;

    .round {
      border: 1px solid #000;
      width: 30px;
      height: 30px;
      border-radius: 50%;

      span {
        color: #000;
        position: relative;
      }
    }


    .label {
      background: #FFF;

      p {
        color: #000;
        margin: auto;
      }
    }
  }

  .bar {
    display: inline-block;
    background: #695934;
    width: 220px;
    height: 80px;
    vertical-align: top;
    position: relative;
    width: 50px;
    height: 2px;
    margin: 0 -5px 17px -5px;
    border-left: none;
    border-right: none;
    border-radius: 0;
    top: 51px;
    vertical-align: top;

    .done {
      background: #000;

      .label {
        color: #FFF;
        background: #ECDDC0 0% 0% no-repeat padding-box;
        opacity: 1;
        box-shadow: inset 0 0 2px rgba(0, 0, 0, .2);
      }

      .title {
        color: #444;
      }
    }

    .active {
      background: linear-gradient(to right, #EEE 40%, #FFF 60%);
    }
  }
}



  .coapp-progress-tracker .circle:before, .coapp-progress-tracker .circle:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 100%;
    width: 0;
    height: 0;
  }

  .coapp-progress-tracker .circle:before {
    left: 100px;
    border: 11px solid transparent;
    border-bottom-color: rgb(8, 8, 8);
  }

  .coapp-progress-tracker .circle:after {
    left: 100px;
    border: 10px solid transparent;
    border-bottom-color: rgb(7, 7, 7);
  }


  .app-progress-tracker .circle,
  .app-progress-tracker .bar {
    display: inline-block;
    background: white;
    width: 246px;
    height: 90px;
    vertical-align: top;
    border: 0.5px solid #1F874C;
  }

  .app-progress-tracker .bar {
    position: relative;
    width: 80px;
    height: 2px;
    margin: 0 -5px 17px -5px;
    border-left: none;
    border-right: none;
    border-radius: 0;
    top: 40px;
    vertical-align: top
  }

  .app-progress-tracker .circle .label {
    float: right;
    padding-right: 41px;
    padding-top: 13px;
  }

  .app-progress-tracker .circle .label .sub-label {
    font-size: 10px;
    /* text-align: left; */
    letter-spacing: 0px;
    color: #919191;
  }