//Introduction Tile
.banner {
  position: relative;
  &__intro-tile-wrapper {
    // padding-bottom: 40px;
    @include for-phone {
      padding-bottom: 10px;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;
      width: 100vw;
      background: $intro-tile-background;
      z-index: -1;
      @include for-tablet {
        background: none;
      }
    }
    &--v2 {
      padding-bottom: 0;
    }
  }
  &__intro-tile-container {
    background: $intro-tile-content-background;
    padding: 48px 0;
    margin-top: -88px;
    @include for-tablet {
      margin-top: 0;
    }
    @include for-phone {
      padding-bottom: 0;
      padding-top: 32px;
    }
  }
  &__intro-tile-title {
    color: $intro-tile-title-color;
    font-size: $font44;
    line-height: $line-height-48;
    margin-bottom: 16px;
    text-align: center;
    @include for-phone {
      font-size: $font36;
      line-height: $line-height-40;
      text-align: left;
    }
  }
  &__intro-tile-desc {
    color: $intro-tile-desc-color;
    text-align: center;
    @include for-phone {
      text-align: left;
    }
  }
}
