.breadcrum-comp {
  @include for-tablet {
    display: block !important;
  }
  &__link {
    font-size: $font14;
    line-height: $line-height-16;
    color: $breadcrum-link-color;
    text-transform: capitalize;
    position: relative;
    padding-right: 2.2rem;
    &:after {
      content: $icon-next;
      font-family: font-icons;
      position: absolute;
      left: -1.6rem;
      bottom: 0;
      font-size: $font10;
    }
    &:last-child {
      color: $breadcrum-active-color;
      cursor: default;
    }
    &:first-child {
      &:after {
        display: none;
      }
    }
  }
}
