.info-card {
    $self: &;
    padding: 3rem 0;
    @include for-phone {
        padding: 2rem 0;
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $ic-title;
        font-family: $pfhandbookpro-regular;
        padding-left: 7rem;
        margin-bottom: 1rem;
        min-height: 7rem;
        @include for-md-desktop {
            font-size: $font24;
            line-height: $line-height-24;
            min-height: 5rem;
        }
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
            min-height: 5rem;
        }
    }
    &__info {
        font-size: $font20;
        line-height: $line-height-22;
        color: $ic-info;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 1rem;
        &:last-child {
            margin-bottom: 0;
        }
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__icon {
        font-size: $font50;
        color: $ic-icon;
        position: absolute;
        top: 2rem;
        max-width: 5rem;
        @include for-phone {
            font-size: $font38;
            max-width: 3.8rem;
        }
    }
    &__tile {
        padding: 2.5rem 2rem;
        background: $ic-tile-bg;
        box-shadow: 0 0 14px 0 rgba($ic-box-shadow, 0.24);
        // height: 23.2rem;
        position: relative;
        border-radius: 2px;
        border: 1px solid $ic-border;
        @include for-tablet {
            height: 27rem;
        }
        @include for-phone {
            height: 21rem;
        }
        &--typ2 {
            height: 28rem;
            @include for-md-desktop {
                height: 32rem;
            }
        }
    }
    .slick-slide {
        margin: 0 1.6rem;
    }
    .slick-list {
        margin: 0 -1.6px;
        @include for-tablet {
            padding: 0 10% 0 0;
            margin: 0;
        }
    }
    &--v2 {
        padding: 0;
        #{$self}__tile {
            height: 42.4rem;
            @include for-md-desktop {
                height: 32rem;
            }
        }
    }
    &__wrap {
        @include for-phone {
            margin-right: -15px;
        }
        .slick-slide > div, .slick-slide .info-card__item, .slick-slide .info-card__tile {
            height: 100%;
        }
    }
    @include for-tablet {
        .slick-nav--info-card,
        .slick-nav--info-card2 {
            display: none;
        }
    }
}