.birthday-block {
    height: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 14px 0 rgba($birthday-shadow, 0.24);
    overflow: hidden;
    margin: -1.5rem 0 4rem;
    @include for-phone {
        box-shadow: none;
        margin: -15px -15px 15px;
    }
    &__icon {
        font-size: $font70;
        color: $birthday-icon;
        margin-bottom: -2rem;
        max-width: 7.5rem;
        @include for-phone {
            font-size: $font56;
            padding-left: 1rem;
            max-width: 6rem;
        }
    }
    &__wish-wrap {
        display: flex;
        @include for-phone {
            justify-content: start;
            flex-wrap: wrap;
        }
    }
    &__wish {
        font-size: $font32;
        text-transform: capitalize;
        margin: 0 1rem 0 2rem;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-20;
            width: 100%;
        }
    }
    &__from {
        font-size: $font20;
        font-family: $pfhandbookpro-thin;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-20;
            width: 100%;
            margin: 0 1rem 0 2rem;
        }
    }
}