.calc-steps {
    $this: &;
    &__links {
        display: flex;
        justify-content: flex-start;
        counter-reset: section;
        margin-bottom: 0;
        padding: 0;
    }
    &__anchor {
        font-size: $font18;
        line-height: $line-height-20;
        color: $tp-link-clr;
        font-family: $pfhandbookpro-regular;
        display: block;
        padding: 1rem 0 1.3rem 2.5rem;
        background: none;
        position: relative;
        transition: all 0.3s ease-in;
        min-height: 4rem;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
        &:before {
            counter-increment: section;
            content: counter(section, decimal-leading-zero);
            position: absolute;
            left: 0;
        }
    }
    &__anchor-text {
        display: none;
        white-space: nowrap;
    }
    &__link {
        transition: all 0.5s ease-in;
        width: 2.5rem;
        margin-right: 2rem;
        overflow: hidden;
        &:last-child {
            margin-right: 0;
        }
        &:after {
            content: none;
        }
        &--active {
            width: auto;
            #{$this}__anchor {
                color: $tp-link-active-clr;
            }
            #{$this}__anchor-text {
                display: inline-block;
            }
        }
        &:hover {
            #{$this}__anchor {
                color: $tp-link-active-clr;
            }
        }
    }
    &__head {
        margin-bottom: 3rem;
        @include for-phone {
            margin-bottom: 2rem;
        }
    }
    &__content {
        padding-bottom: 0;
        min-height: 45.9rem;
        padding-bottom: 8rem;
        position: relative;
        @include for-phone {
            min-height: 38.4rem;
        }
        .calc-form__footer {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            @include for-phone {
                left: -1.5rem;
                right: -1.5rem;
            }
        }
    }
    &__slide {
        display: none;
        padding: 0 0 4rem;
        &--active {
            display: block;
        }
    }
}