.abc-form--search {
    .form-group {
        margin-bottom: 0;
    }
    .select {
        width: 71.3%;
    }
    .selectize-dropdown {
        .option {
            color: $search-select-option;
            & a {
                color: $search-select-option;
                border-bottom: none;
            }
        }
        .optgroup-header {
            font-size: $font14;
            line-height: $line-height-16;
            background: $search-optgroup-bg;
            color: $search-optgroup-clr;
            padding: 0.8rem 1.6rem;
        }
    }
    .selectize-control.single {
        .item {
            color: $search-select-item;
            line-height: $line-height-32;
        }
        .selectize-input {
            border: 0;
            height: 46px;
            &:after {
                display: none;
            }
            input {
                height: 3.2rem;
            }
        }
    }
    .selectize-dropdown-content {
        background: $search-droplist-bg;
        width: 32.8rem;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        border: 1px solid $search-droplist-border;
        box-shadow: 0 3px 8px 0 rgba($search-droplist-shadow, 0.16), 0 0 1px 0 rgba($search-droplist-shadow, 0.08);
        @include for-tablet {
            width: calc(100vw - 3rem);
        }
    }
    .selectize-dropdown [data-selectable] .highlight {
        background: $search-droplist-highlight;
        color: $search-droplist-highlight-clr;
    }
    .input-group {
        &--search {
            border: 1px solid $search-intgroup-border;
            padding-left: 1px;
            background: $input-typ2-bg;
            border-radius: 4px;
            .input-group-addon {
                font-size: $font18;
                line-height: $line-height-24;
                color: $addon-typ2-color;
                font-family: $pfhandbookpro-regular;
                position: relative;
                padding: 0 1.5rem;
                cursor: pointer;
                &--clear {
                    display: none;
                }
                .icon {
                    font-size: $font18;
                    line-height: $line-height-46;
                    margin: 0;
                    color: $search-mice;
                    &-cancel {
                        font-size: $font14;
                    }
                }
            }
            .form-control {
                box-shadow: none;
                border: none;
                padding: 1rem;
                &::placeholder {
                    // color: $clr-placeholder;
                    color: $input-typ2-plcholder;
                    font-size: $font18;
                    line-height: $line-height-1;
                    font-family: $pfhandbookpro-regular;
                }
            }
            .input-group-btn {
                background: $input-grup-btn-bg;
                .btn {
                    padding: 1rem 2rem;
                    font-size: $font20;
                    line-height: $line-height-24;
                    color: $input-grup-btn-typ2;
                    font-family: $pfhandbookpro-bold;
                }
            }
            .form-control {
                padding: 8px;
                border-radius: 4px;
                height: 46px;
                &::placeholder { 
                    color: #495057;
                    opacity: 1; /* Firefox */
                  }
            }
            .input-group-btn {
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
            }
        }
    }
}
.optgroup-header {
    font-size: 1.4rem;
    line-height: 1.6rem;
    background: #f5f5f5;
    color: #6c7174;
    padding: 0.8rem 1.6rem;
    font-weight: bold;
    border-color: #cdcece;
}
.website-search-wrap {
    &--page-level {
        display: none;
        @include for-tablet {
            display: block;
        }
    }
}

@include mQ(1020px){
    ul#ui-id-2, ul#ui-id-3 {
        width: calc(100% - 34px) !important;
    }
}