//Banner Starts
.banner {
  width: 100%;
  display: block;

  &__wrapper {
    width: 100%;
    min-height: 600px;

    @include for-phone {
      min-height: 487px;
    }

    z-index: -9999;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;

    @include for-phone {
      &--v2 {
        min-height: calc(100vh - 94px);
        height: 80vh;
      }
    }
    //variant 3
    &--v3 {
      height: 350px;
      min-height: auto;

      @include for-tablet {
        height: 262px;
      }

      @include for-phone {
      }
    }
    //variant 6
    &--v6 {
      min-height: 375px;
      display: flex;
      height: 100%;
      width: 100%;
      flex: 1;
      height: 100%;
      padding-top: 124px;
      align-items: flex-end;

      @include for-tablet {
        padding-top: 48px;
        min-height: auto;
      }

      @include for-phone {
        padding-top: 0;
      }
    }

    &--iframe {
      max-height: 600px;
      //Hide scrollbar on chrome, firefox and other
      &::-webkit-scrollbar {
        display: none;
      }
      //Hide scrollbar for IE
      -ms-overflow-style: none;

      iframe {
        width: 100%;
        min-height: 600px;
        z-index: -9999;
        border: none;

        @include for-phone {
          min-height: 487px;
        }
      }

      @include for-phone {
        max-height: 487px;
      }
    }
  }

  &__content {
    padding-left: 46px;
    display: block;

    @include for-tablet {
      padding: 0 15px;
    }

    @include for-phone {
      padding: 0;
    }

    .button-primary {
      min-width: 250px;
      margin-top: 40px;

      @include for-tablet {
        margin: 40px 0 128px 0;
      }

      @include for-phone {
        width: 100%;
        margin: 24px 0;
      }
    }
    //Banner Variant 2
    &--v2 {
      padding-top: 196px;
      padding-bottom: 100px;
      position: relative;

      .button-primary {
        margin: 0;
        min-width: 184px;

        @include for-phone {
          width: 100%;
          position: absolute;
          bottom: 24px;
          left: 0;
        }
      }

      @include for-md-desktop {
        padding-left: 15px;
        padding-right: 15px;
      }

      @include for-tablet {
        padding-top: 126px;
      }

      @include for-phone {
        padding: 40px 0 0 0;
        height: 76.5vh;
      }
    }
    //Banner Variant 3
    &--v3 {
      padding-top: 145px;
      padding-left: 0;

      .button-secondary {
        width: 230px;
        padding: 5px 14px;
        font-size: $font20;
        margin: 15px 0 0 0;

        &:hover {
          padding: 5px 14px;
          font-size: $font20;
        }
      }

      @include for-tablet {
        padding-top: 24px;
      }

      @include for-phone {
      }
    }
    //Banner Variant 6
    &--v6 {
      width: 100%;
      max-width: 1400px;
      padding-right: 48px;
      padding-left: 48px;
      padding-bottom: 48px;
      padding-top: 48px;
      margin-right: auto;
      margin-left: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;

      @include for-phone {
        padding-bottom: 32px;
        padding-top: 32px;
        padding-left: 15px;
        padding-right: 0;
        align-items: flex-start;
      }

      h2 {
        color: $banner-6-title;

        span {
          color: $black !important;
        }
      }

      p {
        font-size: $font24;
        line-height: $line-height-28;
        color: $banner-6-role;
		margin-top: 10px;

        span {
          color: $black !important;
        }
      }
    }
  }

  &__label {
    padding: 4px 8px;
    text-transform: uppercase;
    color: $banner-v2-title;

    &--v3 {
      text-transform: none;
      display: block;
      padding: 0;

      @include for-tablet {
        font-size: $font14;
        line-height: normal;
      }

      @include for-phone {
        font-size: $font18;
      }
    }
  }

  &__title {
    padding-top: 246px;
    color: $banner-title;
    font-family: $pfhandbookpro-light;
    letter-spacing: -1.2px;
    font-size: $font90;
    line-height: $line-height-72;

    @include for-tablet {
      padding-top: 126px;
    }

    @include for-phone {
      padding-top: 207px;
      font-size: $font64;
      line-height: $line-height-56;
      letter-spacing: normal;
    }

    &--bold {
      padding: 8px 0 40px 0;
      font-family: $pfhandbookpro-bold;
      width: 65%;
      line-height: 82px;

      @include for-md-desktop {
        width: 75%;
      }

      @include for-tablet {
        width: 100%;
      }

      @include for-phone {
        font-size: $font64;
        line-height: $line-height-56;
        letter-spacing: normal;
      }
    }

    &--v3 {
      font-size: $font40;
      line-height: $line-height-1-1;
      padding: 0;
      width: 444px;
      letter-spacing: 1.5px;

      @include for-tablet {
        width: 365px;
        margin-top: 14px;
        font-size: $font28;
        line-height: $line-height-28;
      }

      @include for-phone {
        width: 290px;
        margin-top: 22px;
        font-size: $font19;
        font-family: $pfhandbookpro-regular;
        font-weight: bold;
        line-height: $line-height-1-1;
      }
    }
  }
}
@import "../introduction-tile/introduction-tile";
//Banner Ends
