/*
 * Variant - 3 Column
 *
 */

 .three-col-list.link-list__container {
    position: relative;
    width: 100%;
    ul {
        display: flex;
        flex-wrap: wrap;
        li {
            -ms-flex: 0 0 33.33333%;
            flex: 0 0 33.33333%;
            max-width: 33.33333%;
            padding-right: 15px;
            padding-left: 15px;
            @include for-md-desktop {
                -ms-flex: 0 0 33.33333%;
                flex: 0 0 33.33333%;
                max-width: 33.33333%;
                padding-right: 15px;
                padding-left: 15px;
            }
            @include for-tablet {
                -ms-flex: 0 0 33.33333%;
                flex: 0 0 33.33333%;
                max-width: 33.33333%;
                padding-right: 15px;
                padding-left: 15px;
            }
            @include for-phone {
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                padding-right: 0;
                padding-left: 0;
            }
            &:before {
                all: unset;
            }
            a {
                font-size: $font24;
                line-height: $line-height-28;
                border-color: $white-grey;
                @include for-phone {
                    font-size: $font22;
                    line-height: $line-height-28;
                }
            }
            @include for-phone {
                &:last-child() {
                    a {
                        border: none;
                    }
                }
            }
        }
    }
    .link-list__list--icon {
        .icon {
            font-size: $font24;
            display: inline-block;
            vertical-align: middle;
            color: $link-v2-icon;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            &--img {
                margin: 0;
                max-width: 2.4rem;
            }
            @include for-tablet {
                transform: none;
                top: 20px;
            }
        }
        li {
            a {
                padding: 18px 20px 18px 40px;
            }
        }
    }
    &--tab {
        ul {
            li {
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                a {
                    padding: 8px 20px 8px 0;
                }
                &:last-child {
                    a {
                        border-bottom: 0;
                    }
                }
                @include for-phone {
                    padding-right: 15px;
                    padding-left: 15px;
                    a {
                        padding: 8px 20px 8px 0;
                        font-size: $font20;
                        line-height: $line-height-24;
                    }
                }
            }
        }
    }
    ul.link-list__list--download {
        li {
            padding-left: 0;
            padding-right: 0;
        }
    }
    .on-page-editor & {
        ul {
            .scLooseFrameZone.scEnabledChrome {
                width: 33.33333%;
                li {
                    width: 100%;
                    max-width: 100%;
                }                
            }
        }
    }
}


/*
* Variant - 2 Column
*
*/

.two-col-list.link-list__container {
    position: relative;
    width: 100%;
    ul {
        display: flex;
        flex-wrap: wrap;
        margin-left: 14.666667%;
        margin-right: 14.666667%;
        @include for-phone {
            margin: 0;
        }
        li {
            -ms-flex: 0 0 50%;
            flex: 0 0 50%;
            max-width: 50%;
            padding-right: 15px;
            padding-left: 15px;
            @include for-md-desktop {
                -ms-flex: 0 0 50%;
                flex: 0 0 50%;
                max-width: 50%;
                padding-right: 15px;
                padding-left: 15px;
            }
            @include for-tablet {
                -ms-flex: 0 0 50%;
                flex: 0 0 50%;
                max-width: 50%;
                padding-right: 15px;
                padding-left: 15px;
            }
            @include for-phone {
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                padding-right: 15px;
                padding-left: 15px;
                margin: 0;
            }
            &:before {
                all: unset;
            }
            a {
                font-size: $font24;
                line-height: $line-height-28;
                @include for-phone {
                    font-size: $font24;
                    line-height: $line-height-28;
                }
            }
            @include for-phone {
                &:last-child() {
                    a {
                        border: none;
                    }
                }
            }
        }
    }
    // *Link Column Specific* To make list item in two column without left-right margin
    .link-list__list--column {
        margin-left: -15px;
        margin-right: -15px;
    }
}


/*
* Core Style
*
*/

.link-list {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    &__container {
        width: 100%;
        display: block;
    }
    &__brand-title {
        padding: 2px 12px 6px 12px;
        font-size: $font32;
        line-height: $line-height-40;
        @include for-phone {
            font-size: $font28;
            line-height: $line-height-32;
        }
    }
    //Link Column Starts
    &__brand-title-container {
        text-align: center;
        margin: 48px 0 32px 0;
        height: 100%;
        @include for-phone {
            text-align: left;
            margin: 32px 0 16px 0;
            height: auto !important;
        }
    }
    &__brand-title {
        color: $brand-title-color;
    }
    //Link Column Ends
    &__title {
        font-size: $font64;
        line-height: $line-height-72;
        padding: 0 15px;
        @include for-phone {
            font-size: $font44;
            line-height: $line-height-48;
        }
        padding-bottom: 64px;
        text-align: center;
        @include for-phone {
            text-align: left;
            padding-bottom: 40px;
        }
    }
    &__list {
        padding-left: 0;
        list-style: none;
        margin-bottom: 0;
        li {
            padding: 0;
            display: flex;
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                /* IE10+ CSS styles go here */
                display: table;
            }
            a {
                width: 100%;
                padding: 18px 20px 18px 0;
                &:after {
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    color: $link-v2-arrow;
                }
            }
            //link with icon
            @include link-icon( $lb-link-color, $lb-link-color, $lb-link-color, $lb-link-color, $lb-icon-color, $lb-icon-hover-color, $icon-right-arrow, $lb-icon-size);
            &:nth-child(n) {
                a {
                    border-bottom: $lb-border-size solid $lb-border-color;
                }
            }
            &:before {
                content: "";
            }
        }
        //Link Column Starts
        &--column {
            li {
                //link with icon
                @include link-icon( $lc-link-color, $lc-link-color, $lc-link-color, $lc-link-color, $lc-icon-color, $lc-icon-hover-color, $icon-right-arrow, $lc-icon-size);
                &:nth-child(n) {
                    a {
                        border-bottom: $lc-border-size solid $lc-border-color;
                    }
                }
                a {
                    font-size: $font24;
                    line-height: $line-height-28;
                }
                &:last-child {
                    a {
                        border: none;
                    }
                }
            }
        }
        //Link Column Ends
        &--download {
            li {
                @include link-icon( $lb-link-color, $lb-link-color, $lb-link-color, $lb-link-color, $lb-icon-color, $lb-icon-hover-color, $icon-square-download, $lb-icon-size);
                a {
                    padding: 18px 20px 18px 15px;
                    margin-right: 3rem;
                    @include for-tablet {
                        margin-right: 1.4rem;
                    }
                    @include for-phone {
                        padding: 18px 20px 18px 0;
                        margin-right: 0;
                    }
                    &:after {
                        right: 1.5rem;
                        @include for-phone {
                            right: 0;
                        }
                    }
                    &:hover {
                        border-bottom: 1px solid $lb-border-color;
                    }
                }
            }
        }
    }
    @include for-md-desktop {}
    @include for-tablet {}
    @include for-phone {}
}