.article-link {
  border-radius: 4px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.13);
  margin-bottom: 55px;
  clear: both;
  .article-link-wrapper {
    padding: 20px 20px 20px 20px;

    .article-link-title {
      font-family: $pfhandbookpro-bold;
      color: $capital-red;
      text-transform: uppercase;
      letter-spacing: -0.41px;
    }
    .article-link-detail-wrapper {
      @include mQ(767px) {
        margin-top: 15px;
      }
    }
    .article-question {
      padding-bottom: 18px;
      padding-top: 28px;
      border-bottom: 1px solid;
      border-color: rgba(151, 151, 151, 0.2);
      display: block;
      @include mQ(767px) {
        padding-bottom: 4px;
        padding-top: 14px;
      }
      p {
        font-family: $pfhandbookpro-medium;
        font-size: 20px;
        line-height: 25px;
        color: $black;
      }
      &:last-child {
        border-bottom: none;
        padding-bottom: 0;
      }
    }
  }
}
