.full-width-change-email-wrapper {
    max-width: 380px;
    margin: 0 auto;
    padding-top: 55px;
    padding-bottom: 55px;
    @include for-phone { 
        padding-top: 11px;
        max-width: 100%;
    }
    .abc-form {
        &.abc-post-login-form {
            .otp-button {
                cursor: pointer;
                background-color: #c7222a;
                padding: 0 30px;
                float: right;
                text-align: center;
                color: #ffffff;
                border-radius: 2.9px;
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
                border: 0 none;
                white-space: nowrap;
                &.disabled {
                    background-color: #6d7278;
                    cursor: not-allowed;
                }
                &.otp-oneid-button {
                    width: 30%;
                }
            }
            .otp-text {
                flex: 1;
                display: inline-block;
                float: left;
                border-right: 0 !important;
                border-right-style: none !important;
                border-top-right-radius: 0 !important;
                border-bottom-right-radius: 0 !important;
                &.otp-oneid-text {
                    width: 70%;
                }
            }
            .btn {
                &__overlay {
                    &.btn__otp-counter {
                        font-weight: bold;
                        color: #6d7278;
                    }
                    &.btn__otp-counter-cta {
                        font-weight: bold;
                        color: #c7222a;
                    }
                }
            }
            .form-otp-label {
                &.error {
                    position: relative;
                    background-color: transparent !important;
                    border: transparent !important;
                    padding-left: 28px;
                    &:before {
                        content: "\f06a";
                        width: 20px;
                        height: 20px;
                        border-radius: 50%;
                        position: absolute;
                        left: 0px;
                        top: 0;
                        color: #F7A500;
                        font-size: 23px;
                        text-align: center;
                        font-family: FontAwesome;
                    }
                }
            }
            .cta__flat-with-arrow {
                position: relative;
                margin-top: -10px;
                &:after {
                    content: $icon-right-arrow;
                    top: 50%;
                    transform: translate(-0%, -50%);
                    font-family: "font-icons";
                    color: #c7222a;
                    font-style: normal;
                    font-variant: normal;
                    font-weight: normal;
                    line-height: 1;
                    text-transform: none;
                    position: absolute;
                    font-size: $button-primary-icon-size;
                    vertical-align: baseline;
                    right: -25px;
                }
            }
        }
    }
    .change-email-form {
        .notify-number {
            font-size: $font12;
            line-height: normal;
            color: #000;
            a {
                font-size: $font12;
            }
        }
        .mobile-number {
            color: #6d7278;
            font-size: $font14;
            opacity: 0.5;
        }
        .notify-txt{
            font-size: $font12;
            font-family: $pfhandbookpro-regular; 
            margin: 15px 0;
            background: #f1f3f6;
            padding: 8px;
            line-height: normal;
            a {
                font-size: $font12;
                color: #6d7278;
                &:focus, &:active, &:hover {
                    border: none;
                }
            }
        }
        .form-checkbox-wrapper {
            input[type="checkbox"]:checked + label {
                &::after {
                    top: 14px;
                }
            }
            label {
                &::before {
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
        .disabled.form-checkbox-wrapper {
            pointer-events: none;
            cursor: not-allowed;
            label::before {
                background: #f1f3f6;
                border: 0;
                
            }
        } 
    }
    .abc-post-login-form {
        & > .form-row {
            & > .form-group {
                .form-input-wrapper{
                    display: flex;
                }
            }
        }
    }
}
