//bg-nav
.bg-nav {
    $self: &;
    position: relative;
    padding: 0 0 0 28.8rem;
    @include for-tablet {
        padding: 0;
        margin: 0 -1.5rem;
    }
    &__title {
        display: none;
        @include for-tablet {
            display: block;
            font-size: $font22;
            line-height: $line-height-1;
            font-family: $pfhandbookpro-thin;
            padding: 0 15px;
        }
    }
    &__nav-link {
        font-size: $font20;
        line-height: $line-height-22;
        color: $bgnav-link-color;
        font-family: $pfhandbookpro-regular;
        background: $bgnav-bg-color;
        padding: 1.4rem 1.5rem;
        border-radius: 0.4rem;
        transition: all 0.3s ease-in;
        display: block;
        &:hover,
        &--active {
            background: $bgnav-link-bg-color;
            color: $bgnav-link-color-active;
        }
    }
    &__nav-item {
        display: block;
        margin-bottom: 1.6rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__cont-item {
        padding: 4rem 0;
        &:first-child {
            padding-top: 0;
        }
        .info-banner__title-wrap {
            margin: 0 1.5rem;
        }
        @include for-tablet {
            padding: 2rem 1.5rem;
        }
    }
    &__nav {
        position: absolute;
        left: 0;
        padding-left: 0;
        top: 0.8rem;
        transition: top 0.4s ease-out;
        max-width: 25.6rem;
        &--sticky {
            position: fixed;
            margin-top: 4rem;
            top: 3.2rem;
        }
        @include for-tablet {
            position: relative;
            display: block;
            left: auto;
            top: auto;
            margin: 3rem 0;
            padding: 0 1.5rem;
            max-width: 100%;
        }
    }
    &--dropdown {
        position: relative;
        @include for-tablet {
            margin: 0 0 2rem;
        }
    }
    &__dropdown {
        width: 100%;
        border: 1px solid $filter-drop-border;
        max-width: 288px;
        margin-right: 3.2rem;
        position: relative;
        padding: 1.2rem 1.8rem;
        border-radius: 4px;
        cursor: pointer;
        @include for-tablet {
            max-width: 100%;
            margin: 0 0 2rem 0;
        }
    }
    &__selected {
        font-size: $font18;
        line-height: $line-height-1;
        &--placeholder {
            color: $filter-placeholder;
            text-transform: capitalize;
        }
    }
    &__drop-icon {
        font-size: $font14;
        line-height: $line-height-18;
        color: $filter-drop-icon;
        position: absolute;
        right: 1rem;
        top: 1.2rem;
    }
    &__droplist {
        background: $filter-drop-list;
        border: 1px solid $filter-drop-border;
        box-shadow: 0 2px 4px 0 rgba($filter-drop-shadow, 0.25);
        position: absolute;
        top: 45px;
        left: 1.5rem;
        right: 1.5rem;
        width: auto;
        z-index: 1;
        max-height: 22rem;
        overflow: hidden;
        display: none;
        // .bg-nav__nav-item {
        //     margin-bottom: 0;
        // }
        .mCSB_inside>.mCSB_container {
            margin-right: 0;
        }
        .bg-nav__nav-item {
            font-size: $font16;
            line-height: $line-height-20;
            background: transparent;
            color: $filter-drop-selected;
            border-bottom: 1px solid $filter-drop-border;
            width: 100%;
            margin-bottom: 0;
            &--active {
                .tabs__anchor {
                    background: transparent;
                    color: $filter-drop-selected;
                    &:hover {
                        color: $filter-drop-selected;
                    }
                }
            }
        }
        .bg-nav__nav-link {
            font-size: $font20;
            line-height: $line-height-1-1;
            font-family: $pfhandbookpro-regular;
            padding: 1rem 1.6rem;
            border-radius: 2px;
            transition: all 0.5s ease;
            display: block;
            background: transparent;
            color: $filter-drop-selected;
            display: block;
            @include for-tablet {
                display: inline-block;
                text-align: left;
                width: 100%;
            }
            &--active,
            &:hover {
                // background: $filter-link-bg-active;
                color: $filter-drop-selected;
                background: $filter-drop-item-active;
            }
            &:active,
            &:focus-within {
                color: $filter-drop-selected;
                background: $filter-drop-item-active;
            }
        }
    }
    &__cont-wrap {
        @include for-tablet {
            padding-left: 0;
        }
    }
    &__info-banner {
        @include for-tablet {
            margin: 0 -1.5rem;
        }
    }
    &__info-banner-icon {
        @include for-tablet {
            font-size: 7rem;
        }
    }
    &__lead-form {
        @include for-tablet {
            margin: 0 -1.5rem;
            width: auto;
            padding: 2rem 1.5rem;
        }
    }
    &__lead-form-wrap {
        @include for-tablet {
            width: 90% !important;
        }
    }
    &__tips {
        @include for-tablet {
            margin: 0 -1.5rem;
        }
    }
    .cat-pro-banner {
        //max-width: 100%;
    }
    .media-tile-list {
        max-width: 73.6rem;
    }

    .wb-info-card1-bg {
        background: transparent;
        &:before, &:after {
            display: none;
        }
    }
    
    .light-grey-full {
        &:before, &:after {
            display: none;
            @include for-tablet() { 
                display: block;
            }
        }
    }
    .accordian--v2 {
        max-width: 100%;
    }
    .video-blog-wrap--v2 {
        padding: 3rem 45px;
    }
    .wb-lead-from-bg {
        padding: 3rem 5rem;
        &:before, &:after {
            display: none;
            @include for-tablet() { 
                display: block;
            }
        }
    }

    .lead-form__container {
        display: block;
    }
    .lead-form__title-wrap {
        margin-bottom: 2.5rem;
    }
    .lead-form__title {
        @include mQ(1200px) {
            font-size: 3rem;
        }
    }
    .lead-form__wrap {
        width: 60%;
        @include for-tablet() { 
            width: 90% !important;
        }
    }

    .benefits-list {
        max-width: inherit;
    }
    @include for-phone() { 
        .benefits {
            padding-left: 0;
            display: flex;
            flex-flow: row wrap;
            align-items: center;
        }
        .benefits__icon {
            position: relative;
            top: auto;
            left: auto;
            font-size: 5rem;
            max-width: 5rem;
            width: 30%;
            margin-right: 1.5rem;
            margin-bottom: 1.5rem;
        }
    }
}