//Account Statement Modal
.qsmodal {
  &__acSuccessHeader {
    padding-bottom: 18px;
    h5 {
      display: block;
      width: 100%;
      text-align: center;
      padding-top: 20px;
    }
    span {
      padding-bottom: 20px;
    }
    p {
      font-size: $font16;
      line-height: 1.5;
      color: $success-message;
    }
  }
}

//Account Statement
.acSuccess {
  margin-top: 0;
  &__headline {
    font-size: $font34;
    line-height: 0.59;
  }
}
