.like-component {
  display: inline-block;
  @include for-phone {
    margin-right: 7px;
  }
  .increment {
    cursor: pointer;
    display: flex;
    align-items: center;
    .icon {
      font-size: 1.4rem;
      color: #000000;
      line-height: 16px;
      margin: 0;
      &.icon-downvote {
        color: $capital-red;
      }
      &:before {
        top: 1px;
        position: relative;
      }
      @include mQ(1200px) {
        font-size: 1.2rem;
      }
      @include mQ(1020px) {
        font-size: 1rem;
      }
    }
  }
  .count-wrapper {
    display: flex;
    font-size: 1.4rem;
    color: #000000;
    margin-left: 5px;
    line-height: 16px;
    letter-spacing: -0.32px;
    span {
      margin-left: 2px;
    }
    @include mQ(1200px) {
      font-size: 1.2rem;
    }
    @include mQ(1020px) {
      font-size: 1rem;
      margin-left: 2px;
    }
  }
}
