.abc-website {
header {
  position: relative;
}
}
header {
  position: absolute;
  @include for-tablet {
      position: relative;
  }
  left: 0;
  right: 0;
  z-index: 999;
  * {
      color: $white;
  }
  a {
      &:hover,
      &:focus {
          color: $white;
          border-bottom: 1px solid $white;
          padding-bottom: 2px;
      }
  }
  // .iconLink {
  //     @include mQ(1020px) {
  //         display: block !important;
  //     }
  //     &:hover, &:active, &:focus {
  //         border-bottom: none;
  //     }
  // }
  .container-fluid {
      @include mQ(1020px) {
          margin-left: 0;
          margin-right: 0;
          padding-left: 0;
          padding-right: 0;
          .outerpadding {
              .row {
                  margin-right: 0px;
                  margin-left: 0px;
                  .col-xs-12 {
                      padding-left: 0;
                      padding-right: 0;
                      .mobile-menu-wrapper {
                          display: block;
                          position: absolute;
                          top: 28px;
                          right: 46px;
                          @include mQ(767px) {
