  }
}

//Top Global Nav Starts
.top-navigation {
  padding-right: 47px;
  @include mQ(1199px) {
      padding-right: 33px;
  }
  ul {
      display: flex;
      flex-wrap: nowrap;
      justify-content: flex-end;
      list-style: none;
      margin-bottom: 0;
      padding-left: 0;
      li {
          margin: 0 12px 0 9px;
          font-size: $font14;
          position: relative;
          padding-bottom: 6px;
          padding-top: 10px;
          &::before {
              display: none;
          }
          a {
              font-size: $font18;
              line-height: 16px;
              padding-bottom: 0;
          }
          &:after {
              content: "";
              height: 13px;
              border-right: 2px solid $white;
              transform: translateY(-50%);
              top: 50%;
              position: absolute;
              margin-top: 3px;
              margin-left: 10px;
              right: -12px;
          }
          &:last-child {
              margin-right: 0;
              &:after {
                  display: none;
              }
          }
      }
  }
  @include mQ(1020px) {
      display: none !important;
  }
}

//Top Global Nav Ends
.top-header-wrapper {
  @include clearfix;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-left: 47px;
  padding-right: 20px;
  padding-top: 23px;
  padding-bottom: 13px;
  @include mQ(1199px) {
      padding-left: 33px;
      padding-right: 33px;
  }
  @include mQ(1020px) {
      display: block;
      padding-top: 14px;
      padding-bottom: 3px;
      padding-left: 21px;
  position:relative;
  }
  .language {
      visibility: hidden;
      @include for-phone {
          visibility: visible;
      }
      @include for-tablet {
          visibility: visible;
      }
  }
}

.logo {
