.header-wrapper {
  padding-left: 108px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 47px;
  padding-top: 15px;
  padding-bottom: 15px;
  @include mQ(1200px) {
      padding-left: 108px;
  }
  @include mQ(1199px) {
      padding-right: 33px;
      padding-left: 94px;
  }
  @include mQ(1020px) {
      padding-right: 42px;
      padding-left: 7px;
      padding-top: 9px;
      padding-bottom: 9px;
      position: relative;
  }
  @include mQ(767px) {
      padding-right: 16px;
      padding-top: 4px;
      padding-bottom: 4px;
  }
  .header-title {
      font-family: $pfhandbookpro-bold;
      padding-left: 14px;
      a {
          font-family: $pfhandbookpro-bold;
          &:hover,
          &:active,
          &:focus {
              border-bottom: none;
              padding-bottom: 0;
          }
      }
  }
  .search-wrapper {
      flex: 1;
      justify-content: flex-end;
      display: flex;
      position: relative;
      input[type="text"] {
          width: 356px;
          height: 40px;
          line-height: 40px;
          background-color: transparent;
          border: 1px solid $white;
          border-radius: 2px;
          //margin-right:40px;
          margin-right: 0px;
          padding: 0px 44px 0px 14px;
          font-family: $pfhandbookpro-regular;
          &::placeholder {
              color: $white;
          }
          @include mQ(1020px) {
              height: 37px;
              width: 232px;
          }
      }
      .search-button {
          position: absolute;
          font-size: 1.8rem;
          //right: 40px;
          right: 0px;
          top: 0px;
          margin: 0;
          &:before {
              position: absolute;
              top: 11px;
              right: 17px;
          }
          input[type="submit"],
          .search-submit {
              position: relative;
              background-color: transparent;
              border: none;
              width: 40px;
              height: 40px;
              display: block;
          }
          @include mQ(1020px) {
              font-size: 1.5rem;
              &:before {
                  top: 11px;
                  right: 21px;
              }
              input[type="submit"],
              .search-submit {
                  height: 37px;
              }
          }
      }
      @include mQ(767px) {
          display: none;
          position: absolute;
          top: 100%;
          z-index: 99;
          background-color: $capital-red;
          left: 0;
          right: 0;
          width: 100%;
          input[type="text"] {
              width: calc(100% - 42px);
              margin: 11px 21px 12px 21px;
          }
          .search-button {
              right: 21px;
              &:before {
                  top: 22px;
                  right: 14px;
              }
          }
      }
  }
  .subscribe-wrapper {
      //display: none;
      text-transform: uppercase;
      padding-left: 40px;
      @include mQ(767px) {
          padding-left: 20px;
          text-transform: none;
          a {
              font-family: $pfhandbookpro-bold;
              font-size: 1.6rem;
          }
      }
  }
}

