.hamburger {
  font: inherit;
  display: inline-block;
  overflow: visible;
  margin: 0;
  cursor: pointer;
  transition-timing-function: linear;
  transition-duration: 0.15s;
  transition-property: opacity, filter;
  text-transform: none;
  color: inherit;
  border: 0;
  background-color: transparent;
  .hamburger-box {
      position: relative;
      display: inline-block;
      width: 24px;
      height: 18px;
  }
  &.hamburger--elastic {
      .hamburger-inner {
          top: 2px;
          transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
          transition-duration: 0.275s;
          &:before {
              top: 8px;
              transition: opacity 0.125s ease 0.275s;
          }
          &:after {
              top: 16px;
              transition: transform 0.275s cubic-bezier(0.68, -0.55, 0.265, 1.55);
          }
      }
      &.is-active {
          .hamburger-inner {
              transition-delay: 75ms;
              transform: translate3d(0, 8px, 0) rotate(135deg);
              &:before {
                  transition-delay: 0s;
                  opacity: 0;
              }
              &:after {
                  transition-delay: 75ms;
                  transform: translate3d(0, -16px, 0) rotate(-270deg);
              }
          }
      }
  }
  .hamburger-inner,
  .hamburger-inner:after,
  .hamburger-inner:before {
      position: absolute;
      width: 24px;
      height: 2px;
      transition-timing-function: ease;
      transition-duration: 0.15s;
      transition-property: transform;
      border-radius: 1px;
      background-color: $white;
  }
  .hamburger-inner {
      &:before {
          top: -10px;
      }
      &::after {
          bottom: -10px;
      }
      &:after,
      &:before {
          display: block;
          content: "";
      }
  }
}

#mobi-navigation,
#mobi-navigation-primary {
  display: none;
}

@include mQ(1020px) {
#mobi-navigation,
#mobi-navigation-primary {
  display: block;
  height: 100%;
  width: 412px;
  background-color: $white;
  z-index: 999;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateX(412px);
  transition: transform 0.6s;

  @include mQ(767px) {
    width: 100%;
    transform: translateX(100%);
  }

  &.active {
    transform: translateX(0px);
    transition: transform 0.6s;
    overflow-y: scroll;
  }

  .menu-content {
    margin: 19px 30px 0;

    * {
      color: $top-menu-mobile-color;
    }
  }

  &.main-nav-active {
    background-color: $white;
    padding-top: 0px;

    .utility-links {
      display: none;
    }


    .menu-content-wrapper {
      height: auto;
    }

    .mobi-top-navigation,
    .mobi-footer-links {
      display: none;
    }

    @include mQ(767px) {
      .menu-header {
        padding-bottom: 5px;

        .utility-links-global-nav {
          display: none !important;
        }

        .utility-links {
          display: none;
        }
      }
    }
  }
}

