//article-detail-info
.article-detail-info {
    $this: &;
    &__container {
        width: 73.4rem;
        margin: 3rem auto;
        @include for-tablet {
            width: 80%;
            margin: 1.5rem auto;
        }
        @include for-phone {
            width: 100%;
            margin: 1.5rem auto;
        }
        &--share {
            margin: 0 auto;
            @include for-tablet {
                width: 80%;
                margin: 0rem auto;
            }
            @include for-phone {
                width: 100%;
                margin: 0rem auto;
            }
        }
    }
    &__title-wrap {
        padding-bottom: 2.4rem;
        margin-bottom: 1.8rem;
        border-bottom: 1px solid $article-border-color;
    }
    &__meta {
        font-size: $font18;
        line-height: $line-height-20;
        color: $article-base-color;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 0;
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-14;
        }
    }
    &__meta-info {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        padding-left: 0;
        #{$this}__item {
            margin-right: 6rem;
            @include for-tablet {
                margin-right: 3.5rem;
            }
            @include for-phone {
                margin-right: 1.7rem;
            }
            &:first-child {
                max-width: 300px;
                @include for-tablet {
                    max-width: 225px;
                }
                @include for-phone {
                    max-width: 180px;
                    margin-right: auto;
                }
                @media (max-width: 425px) {
                    max-width: 110px;
                }
            }
            &:last-child {
                margin-left: auto;
                margin-right: 0;
                @include for-phone {
                    margin-left: 0;
                }
            }
            .media-tile__likes {
                position: relative;
                bottom: auto;
                right: auto;
                .media-tile__like-icon {
                    font-size: 1.5rem;
                    margin: 4px;
                }
            }
        }
    }
    &__share-wrap {
        padding-left: 0;
        margin-left: -2rem;
        @include for-phone {
            margin-left: -1rem;
        }
    }
    &__share-item {
        display: inline-block;
        margin-right: 3.2rem;
        padding: 2rem;
        transition: all 0.3s ease-in;
        border-radius: 50%;
        @include for-tablet {
            padding: 1.5rem;
            margin-right: 2rem;
        }
        @include for-phone {
            padding: 1rem;
            margin-right: 1.5rem;
        }
        &:last-child {
            margin-right: 0;
            padding: 2rem;
            @include for-tablet {
                padding: 1.5rem;
            }
            @include for-phone {
                padding: 1rem;
            }
        }
        &:hover {
            background: $article-pen-bg;
            #{$this}__icon {
                color: $article-theme-primary-color;
            }
        }
        &:only-child,&:first-child {
            .share-menu__menu {
                @include mQ(991px) {
                    margin-left: 82px;
                }
            }
        }
    }
    &__icon {
        font-size: $font28;
        color: $article-theme-primary-color !important;
        @include for-phone {
            font-size: $font24;
        }
    }
    &__img-banner {
        width: 100%;
        height: 56rem;
        overflow: hidden;
        margin: 6rem 0;
        @include for-tablet {
            width: auto;
            height: 40rem;
            margin: 4rem -1.5rem;
        }
        @include for-phone {
            width: auto;
            height: auto;
            margin: 4rem -1.5rem;
        }
        &--first-child {
            margin-top: 2.4rem;
        }
    }
    &__img {
        width: 100%;
    }
    &__p {
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $article-text-color;
        margin-bottom: 3rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 2rem;
        }
    }
    &__highlighted {
        font-size: $font32;
        line-height: $line-height-34;
        color: $article-text-color;
        font-family: $pfhandbookpro-regular;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-22;
        }
        &--seperator {
            padding: 1.5rem 0;
            border-top: 1px solid $article-border-separator;
            border-bottom: 1px solid $article-border-separator;
            @include for-phone {
                padding: 0.8rem 0;
            }
        }
    }
}