//Main Navigation starts
nav,
.top-menu {
  width: 100%;
  > ul {
    display: flex;
    padding-left: 45px;
    margin-bottom: 14px;
    @include mQ(1199px, 1020px) {
      padding-left: 25px;
    }
    > li > span:first-child {
      font-size: 1.8rem;
      line-height: 20px;
      color: $top-menu-color;
      @include mQ(1280px, 1201px) {
        font-size: 1.6rem;
        line-height: 20px;
      }
      @include mQ(1200px, 1021px) {
        font-size: 1.6rem;
        line-height: 20px;
      }
    }
    li {
      span.icon-arrow {
        &:hover {
          padding-bottom: 7px;
        }
      }
    }
    li {
      padding-bottom: 0px;
      padding-top: 0px;
      padding-left: 15px;
      padding-right: 15px;
      position: relative;
      a,
      span {
        padding-bottom: 7px;
        padding-top: 6px;
        display: inline-block;
        font-family: $pfhandbookpro-regular;
        float: left;
        &:hover,
        &:focus,
        &:active {
          border-bottom: 1px solid $transparent;
          padding-bottom: 0;
          @include mQ(1020px) {
            border-bottom: none;
          }
        }
        @include mQ(1200px) {
          font-size: 1.6rem;
        }
      }

      ul {
        margin-left: 0;
        visibility: hidden;
        max-height: 0;
        height: auto;
        position: absolute;
        left: 0;
        opacity: 0;
        width: 100%;

        li {
          &:hover,
          &.active {
            a {
              background-color: $top-menu-link-hover-bg-color;
            }
          }
          a {
            font-size: 1.6rem;
            width: 100%;
          }
        }
      }
      &::before {
        display: none;
      }
      &:hover {
        background-color: $top-menu-link-hover-bg-color;
        a,
        span {
          color: $top-menu-hover-color;
          border-bottom: none;
        }
        ul {
          visibility: visible;
          opacity: 1;
          max-height: 500px;
          transition: max-height 0.8s linear 0.2s;
          top: 100%;
          left: 0;
          min-width: 262px;
          padding-top: 0;
          padding-left: 0;
          z-index: 9099;
          background-color: $top-menu-dropdown-bg;
          @include box-shadow(
            0px,
            6px,
            12px,
            rgba($top-menu-dropdown-box-shadow, 0.18)
          );
          overflow: hidden;
        }
        @include mQ(1020px) {
          background-color: $transparent;
        }
      }
      &.active {
        color: $top-menu-active-color;
        background-color: $top-menu-link-hover-bg-color;
        a {
          color: $top-menu-active-color;
          border-bottom: none;
        }
        .icon-arrow {
          color: $top-menu-icon-hover-color;
        }
      }
      &.has-children {
        .icon-arrow {
          color: $top-menu-icon-color;
          font-size: 1rem;
          cursor: pointer;
          margin-left: 10px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          margin-top: 2px;
          margin-bottom: 0;
          @include mQ(1200px) {
            margin-left: 6px;
          }
          @include mQ(1020px) {
            display: none;
          }
        }
        &:hover {
          background-color: $top-menu-link-hover-bg-color;
          a {
            color: $top-menu-dropdown-link;
            border-bottom: none;
          }
          .icon-arrow {
            transition: 0.4s;
            color: $top-menu-icon-hover-color;
            &::before {
              transform: rotate(180deg);
              display: inline-block;
            }
          }

          li {
            padding-left: 0;
            padding-right: 0;
            margin-left: 0;
            margin-right: 0;
            a {
              border-bottom: $transparent;
              padding-bottom: 7px;
              padding-top: 9px;
              padding-left: 15px;
              padding-right: 15px;
              width: 100%;
            }
          }
          @include mQ(1020px) {
            background-color: $transparent;
          }
        }
      }
    }
  }
}

nav {
  ul {
    padding-left: 0;
    margin-left: 73px;
    @include mQ(1271px) {
      margin-left: 81px;
    }
    @include mQ(1199px) {
      margin-left: 93px;
    }
    li {
      margin-left: 34px;
      margin-right: 14px;
      padding-right: 35px;
      cursor: pointer;
      @include mQ(1271px) {
        margin-left: 26px;
        margin-right: 0px;
        padding-right: 30px;
      }
      @include mQ(1199px) {
        margin-left: 0px;
      }
    }
  }
  @include mQ(1020px) {
    display: none !important;
  }
}
.top-menu {
  flex: 1;
  margin-right: 50px;
 
  @include mQ(1199px, 1020px) {
    margin-right: 15px;
  }
  @include mQ(1020px) {
    display: none;
  }
  > ul {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 14px;
    li {
      margin: 0;
      text-transform: uppercase;
      padding-left: 7px;
      padding-right: 35px;
      cursor: pointer;
      @include mQ(1199px, 1020px) {
        padding-left: 3px;
        padding-right: 30px;
      }
      @include mQ(1020px) {
        padding-left: 0px;
        padding-right: 10px;
      }
      > a {
        /*cursor: not-allowed;
                  pointer-events: none;*/
        @include mQ(1020px) {
          padding-bottom: 6px;
          padding-top: 5px;
          font-size: 1.4rem;
        }
        @include mQ(767px) {
          font-size: 1.2rem;
        }
      }
      .icon-arrow {
        margin-left: 10px !important;
        @include mQ(1200px) {
          margin-top: 0px !important;
        }
      }
      ul {
        li {
          margin: 0;
          text-transform: none;
        }
        @include mQ(1020px) {
          display: none !important;
        }
      }
    }
    @include mQ(1020px) {
      justify-content: flex-start;
      padding-left: 0px;
    }
  }
}

//Mobile Wrapper
.mobi-nav-wrapper {
  padding-bottom: 0px;
  ul {
    padding-left: 0;
    li {
      position: relative;
      &::before {
        display: none;
      }
      &.level1 {
        padding: 8px 0 0 0;
        margin-bottom: 32px;
        clear: both;
        float: left;
        width: 100%;
        &:last-child {
          margin-bottom: 22px;
        }
      }
      &.level1::after {
        content: "";
        width: 8px;
        height: 100%;
        position: absolute;
        left: -30px;
        top: 0;
        display: block;
      }
      a,
      span {
        font-family: $pfhandbookpro-bold;
        font-size: 2.8rem;
        line-height: 32px;
        text-transform: uppercase;
      }
      ul {
        // column-count: 2;
        padding-top: 3px;
        width: 100%;
        float: left;
        li {
          padding: 10px 0 0 0;
          display: inline-block;
          width: 50%;
          float: left;
          a,
          span {
            font-family: $pfhandbookpro-regular;
            font-size: 1.6rem;
            line-height: 20px;
            text-transform: none;
            color: $top-menu-mobile-color;
            &:hover {
              padding-bottom: 0px;
            }
          }
        }
      }
    }
  }
  .icon-arrow {
    display: none;
  }

  .red-link {
    &.level1::after {
      background-color: $top-menu-advicing;
    }
    > a,
    span {
      color: $top-menu-advicing !important;
    }
  }

  .pink-link {
    &.level1::after {
      background-color: $top-menu-protecting;
    }
    > a,
    span {
      color: $top-menu-protecting !important;
    }
  }

  .green-link {
    &.level1::after {
      background-color: $top-menu-investing;
    }
    > a,
    span {
      color: $top-menu-investing !important;
    }
  }

  .yellow-link {
    &.level1::after {
      background-color: $top-menu-financing;
    }
    > a,
    span {
      color: $top-menu-financing !important;
    }
  }
}
//Main Navigation Ends
