/*
 * Portfolio Global Style
 *
 */

 .dashboard {
    &__portfolio {
        &-summary-card.dashboard__summary-card {
            padding-right: 0;
            min-height: 650px;
            @include fontScaling(min-height,65.0);
            @include for-phone {
                border-radius: 8px;
                box-shadow: 0 10px 32px -14px rgba(0, 0, 0, 0.22);
                background-color: #ffffff;
                border-left: none !important;
                min-height: auto;
            }
            // @include for-tablet {
            //     margin-top: -60px;
            // }
            &.dashboard__portfolio-summary-card-by-scheme {
                .dashboard__portfolio-scheme-wrapper {
                    max-height: 600px;
                    @include fontScaling(max-height,60.0);
                    overflow-y: auto;
                    @include scrollbars(5px, rgba(#d8d8d8, 0.5));

                    @include for-phone {
                        max-height: 100%;
                        overflow-y: visible;
                    }
                }
            }

            .dashboard__portfolio-inner-content-wrapper {
                max-height: 600px;
                @include fontScaling(max-height,60.0);
                overflow-y: auto;
                @include scrollbars(5px, rgba(#d8d8d8, 0.5));
                
                @include for-phone {
                    max-height: 100%;
                    overflow-y: visible;
                }
            }
        }
        &-main-wrapper {
            width: 100%;
            
            @include for-phone {
                display: flex;
                flex-direction: column;
               padding-top: 0;
            }
            .component-basic-portfolio {
                max-width: 100%;
                display: flex;
                flex-direction: column;  
                @include for-phone {
                    padding-left: 7px;
                    padding-right: 7px;
                }
                
                @include mQ(575px) {
                    padding-left: 7px;
                    padding-right: 7px;
                } 
                
                &.component-basic-portfolio-by-scheme {
                    margin-bottom: 27px;
                    @include fontScaling(margin-bottom,2.7);

                    .horizontal-listsummary-row {
                        ul {
                            li {
                                padding-left: 10px;
                                @include fontScaling(padding-left,0.5);
                                &:first-child {
                                    padding-left: 20px;
                                    @include fontScaling(padding-left,2.0);
                                }
                                p:after {
                                    margin-left: 12px;
                                    @include fontScaling(margin-left,1.2);
                                    @include for-phone {
                                        margin-left: 7px;
                                        top: 13px;
                                    } 
                                    @include mQ(575px) {
                                        top: 7px; 
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        &-mobile-dropdown {
            display: none;
        }
        &-cta-wrapper {
            display: flex;
            margin-left: 20px;
            @include fontScaling(margin-left,2.0);
            margin-top: 10px;
            @include fontScaling(margin-top,1.0);
            @media (max-width: 575px) { 
                display: flex;
                margin-left: 0;
            }
        }
        &-scheme-wrapper {
            padding-left: 20px;
            @include fontScaling(padding-left,2.0);
            padding-right: 15px;
            @include fontScaling(padding-right,1.5);
            @include for-phone {
                padding-left: 33px;
                padding-right: 30px;
            }
            @include mQ(575px) { 
                padding-left: 7px;
                padding-right: 7px;
            }
        }
        &-scheme {
            &--heading {
                font-family: $pfhandbookpro-bold;
                font-size: $font18;
                @include fontScaling(font-size,1.8);
                color: $black;
                margin-bottom: 8px;
                @include fontScaling(margin-bottom,0.8);
                @include for-phone {
                    font-size: $font22;
                    margin-bottom: 10px;
                }
                @include mQ(575px) {
                    font-size: 1.4rem;
                    margin-bottom: 5px;
                }
            }
            &--detail-title {
                font-family: $pfhandbookpro-regular;
                font-size: $font20;
                @include fontScaling(font-size,2.0);
                color: $black;
                display: flex;
                margin-bottom: 15px;
                @include fontScaling(margin-bottom,1.5);
                @include for-phone {
                    font-size: $font23;
                    margin-bottom: 20px;
                }
                @include mQ(575px) {
                    display: flex;
                    font-size: $font14;
                    margin-bottom: 10px;
                }
                .listsummary-switcher {
                    font-family: $pfhandbookpro-light;
                    font-size: $font18;
                    @include fontScaling(font-size,1.8);
                    color: rgba($black, 0.16);
                    margin-left: 10px;
                    @include fontScaling(margin-left,1.0);
                    @include for-phone {
                        font-size: $font22;
                    }
                    @include mQ(575px) {
                        margin-left: 0px;
                        font-size: $font14;
                    }
                    &.active {
                        font-family: $pfhandbookpro-regular;
                        font-size: $font18;
                        @include fontScaling(font-size,1.8);
                        color: $investing-pallete-06;
                        @include for-phone {
                            font-size: $font22;
                        }
                        @include mQ(575px) {
                            font-size: $font14;
                        } 
                    }

                    .listsummary-switcher-head {
                        display: flex;
                        align-items: center;
                        .listsummary-switcher {
                            margin-left: 9px;
                            @include fontScaling(margin-left,0.9);
                            margin-right: 8px;
                            @include fontScaling(margin-right,0.8);
                            &:first-child {
                                margin-left: 0;
                            }
                            @include mQ(575px) { 
                                margin-left: 4px;
                                margin-right: 4px;
                            }
                        }
                        @include mQ(575px) {
                            margin-top: 0px;
                            margin-left: 15px;
                        }
                    }
                }
            }
        }
    }
    

    // Download CTA style
    &__download-cta {
        margin-right: 30px;
        @include fontScaling(margin-right,3.0);
        @media (max-width: 575px) {
            margin-right: 10px;
            margin-bottom: 20px;
        }
        a {
            border: solid 1px #a1ac7b;
            @include fontScaling(border-width,0.1);
            border-radius: 2px;
            @include fontScaling(border-radius,0.2);
            color: #a1ac7b;
            font-family: $pfhandbookpro-bold;
            font-size: $font16;
            @include fontScaling(font-size,1.6);
            text-transform: uppercase;
            padding: 12px 58px 10px 15px;
            @include fontScaling(padding-top,1.2);
            @include fontScaling(padding-right,5.8);
            @include fontScaling(padding-bottom,1.0);
            @include fontScaling(padding-left,1.5);
            display: inline-block;
            position: relative;
            @include for-phone {
                font-size: $font23;
                padding: 23px 15px 19px 15px;
                &:after {
                    display: none;
                }
            }
            @media (max-width: 575px) {
                font-size: $font14;
                padding: 9px;
            } 
            &:after {
                content: $icon-left-arrow-mob;
                font-family: "font-icons";
                font-style: normal;
                font-variant: normal;
                font-weight: normal;
                line-height: 1;
                text-transform: none;
                position: absolute;
                font-size: $font18;
                @include fontScaling(font-size,1.8);
                transform: translateY(-50%) rotate(180deg);
                top: 50%;
                right: 17px;
                @include fontScaling(right,1.7);
                margin-top: 2px;
                @include fontScaling(margin-top,0.2);
            }
            &:hover {
                color: $white;
                background-color: #a1ac7b;
            }
        }
    }


    // Dark Dropdown style
    &__dark-dropdown {
        position: relative;
        @include for-phone {
            width: 150px;
        }
        @include mQ(575px) {
            width: 117px;
        } 
        .selected {
            border-radius: 2px;
            @include fontScaling(border-radius,0.2);
            background-color: $investing-shade;
            min-width: 144px;
            @include fontScaling(min-width,14.4);
            padding: 13px 46px 13px 26px;
            @include fontScaling(padding-top,1.3);
            @include fontScaling(padding-right,4.6);
            @include fontScaling(padding-bottom,1.3);
            @include fontScaling(padding-left,2.6);
            border: none;
            color: $white;
            font-family: $pfhandbookpro-bold;
            font-size: $font16;
            @include fontScaling(font-size,1.6);
            text-transform: uppercase;
            line-height: 1;
            display: block;
            position: relative;
            cursor: pointer;
            &:after {
                content: $icon-arrow;
                font-family: "font-icons";
                font-style: normal;
                font-variant: normal;
                font-weight: normal;
                line-height: 1;
                text-transform: none;
                position: absolute;
                font-size: $font12;
                @include fontScaling(font-size,1.2);
                top: 16px;
                @include fontScaling(top,1.6);
                right: 17px;
                @include fontScaling(right,1.7);
            }
            &.active {
                &:after {
                    transform: rotate(180deg);
                }
            }

            @include for-phone {
                font-size: $font23;
                padding: 0px;
                height: 64px;
                min-width: 150px;
                padding: 20px 46px 19px 15px;
                &:after {
                    margin-top: 0px;
                    top: 26px;
                }
            }

            @include mQ(575px) {
                font-size: 1.4rem;
                height: 40px;
                min-width: 117px;
                padding: 13px 36px 9px 23px;
                &:after {
                    top: 14px;
                }
            }
        }
        .select-dropdown {
            overflow-y: auto;
            overflow-x: hidden;
            max-height: 200px;
            @include fontScaling(max-height,20.0);
            -webkit-overflow-scrolling: touch;
            padding-left: 0px;
            border: 1px solid #d0d0d0;
            @include fontScaling(border-width,0.1);
            border-top: 0 none;
            z-index: 10;
            position: absolute;
            background-color: #fff;
            width: 100%;
            display: none;
            &.active {
                display: block;
            }
            li {
                box-shadow: inset 0 -1px 0 0 #f1f3f6;
                font-size: $font18;
                @include fontScaling(font-size,1.8);
                font-family: $pfhandbookpro-regular;
                color: #303030;
                padding: 12px 16px !important;
                @include fontScaling(padding-top,1.2,!important);
                @include fontScaling(padding-right,1.6,!important);
                @include fontScaling(padding-bottom,1.2,!important);
                @include fontScaling(padding-left,1.6,!important);
                cursor: pointer;
                &:before {
                    display: none;
                }
                &:hover, &:active {
                    background-color: #f1f3f6;
                }
            }
        }
    }

    // Light Dropdown style
    &__light-dropdown {
        position: relative;
        @include for-phone {
            width: 150px;
        }
        @include mQ(575px) {
            width: 117px;
        } 
        .selected {
            border-radius: 2px;
            @include fontScaling(border-radius,0.2);
            border: solid 1px #a1ac7b;
            @include fontScaling(border-width,0.1);
            background-color: $white;
            min-width: 108px;
            @include fontScaling(min-width,10.8);
            padding: 13px;
            @include fontScaling(padding,1.3);
            color: #a1ac7b;
            font-family: $pfhandbookpro-bold;
            font-size: $font16;
            @include fontScaling(font-size,1.6);
            text-transform: uppercase;
            line-height: 1;
            display: block;
            position: relative;
            cursor: pointer;
            &:after {
                content: $icon-arrow;
                font-family: "font-icons";
                font-style: normal;
                font-variant: normal;
                font-weight: normal;
                line-height: 1;
                text-transform: none;
                position: absolute;
                font-size: $font12;
                @include fontScaling(font-size,1.2);
                top: 14px;
                @include fontScaling(top,1.4);
                right: 10px;
                @include fontScaling(right,1.0);
                color: #a1ac7b; 
            }
            &.active { 
                &:after {
                    transform: rotate(180deg);
                }
            }

            @include for-phone {
                font-size: $font23;
                padding: 0px;
                height: 64px;
                min-width: 150px;
                padding: 20px 46px 19px 15px;
                &:after {
                    margin-top: 0px;
                    top: 26px;
                }
            }
            @include mQ(575px) { 
                font-size: 1.4rem;
                height: 40px;
                min-width: 117px;
                padding: 13px 36px 9px 17px;
                &:after {
                    top: 14px;
                }
            }
        }
        .select-dropdown {
            overflow-y: auto;
            overflow-x: hidden;
            max-height: 200px;
            @include fontScaling(max-height,20.0);
            -webkit-overflow-scrolling: touch;
            padding-left: 0px;
            box-shadow: 0 5px 11px -4px rgba(0, 0, 0, 0.27);
            @media screen and (min-width: $custom-screen-max-width) {
                box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.1rem) -0.4rem rgba(0, 0, 0, 0.27);
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                box-shadow: 0 0.5vw 1.1vw -0.4vw rgba(0, 0, 0, 0.27);
            }
            border-top: 0 none;
            z-index: 10;
            position: absolute;
            background-color: #fff;
            width: 100%;
            display: none;
            &.active {
                display: block;
            }
            li {
                box-shadow: inset 0 -1px 0 0 #f1f3f6;
                @media screen and (min-width: $custom-screen-max-width) {
                    border-width: 0 calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.1rem)) calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0)) calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0));
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    border-width: 0 -0.1vw 0 0;
                }
                font-size: $font12;
                @include fontScaling(font-size,1.2);
                font-family: $pfhandbookpro-regular;
                color: #303030;
                padding: 5px 12px !important;
                @include fontScaling(padding-top,0.5,!important);
                @include fontScaling(padding-right,1.2,!important);
                @include fontScaling(padding-bottom,0.5,!important);
                @include fontScaling(padding-left,1.2,!important);
                cursor: pointer;
                &:before {
                    display: none;
                }
                &:hover, &:active {
                    background-color: #f1f3f6;
                }
                &:nth-child(even){
                    background-color: #f6f6f6;
                }
            }
        }
    }
}

// Basic List Summary Details
.horizontal-listsummary-row {
    ul{
        list-style:none;
        padding:0px;
        display: flex;
        margin: 0px;
        width:100%;
        @include for-phone {
            display: inline-block;  
        }
        li {
            flex-basis: 100%;
            padding: 15px 0px 13px 20px;
            @include fontScaling(padding-top,1.5);
            @include fontScaling(padding-bottom,1.3);
            @include fontScaling(padding-left,2.0);
            color: $brand-black !important;
            display: grid;
            @include for-phone{
                width: 50%;
                display: block;
                float: left;
                padding-left: 26px !important;
                padding-top: 15px;
                padding-bottom: 21px;
            }
            @include mQ(575px) {
                padding-left: 3px !important;
                padding-top: 15px;
                padding-bottom: 9px;
                padding-right: 3px;
            }
            span { 
                font-size: $font14;
                @include fontScaling(font-size,1.4);
                font-family: $pfhandbookpro-light;
                line-height: 1.15;
                @include for-phone {
                    font-size: $font19;
                }
                @include mQ(575px) {
                    font-size: 1.2rem;
                }
            }
            .big-size {
                span {
                    font-size: $font18;
                    @include fontScaling(font-size,1.8);
                    @media (max-width: 1040px) {
                        font-size: $font16;
                    }
                    @include for-phone {
                        font-size: $font19;
                    }
                    @include mQ(575px) { 
                        font-size: $font12;
                    }
                }
            }
            p, p > a {
                margin-bottom: 0px;
                font-size: $font26;
                @include fontScaling(font-size,2.6);
                color: $brand-black;
                font-family: $pfhandbookpro-medium;
                line-height: 1.3;
                position: relative;
                @include for-phone {
                    font-size: 2.9rem;
                }
                @include mQ(575px) {
                    font-size: 1.8rem;
                }
                &.positive, &.negative {
                    &:after {
                        content:$icon-down-arrow-selected;
                        -moz-osx-font-smoothing: grayscale;
                        -webkit-font-smoothing: antialiased;
                        font-family: "font-icons";
                        font-style: normal;
                        font-variant: normal;
                        font-weight: normal;
                        line-height: 1;
                        speak: none;
                        position: absolute;
                        top: 10px;
                        @include fontScaling(top,1.0);
                        margin-left: 7px;
                        @include fontScaling(margin-left,0.7);
                        font-size: $font13;
                        @include fontScaling(font-size,1.3);
                        @include mQ(575px) {
                            top: 8px;
                            font-size: 0.9rem;
                        }
                    }
                }
                &.positive{
                    &:after {
                        color: $light-green;
                        transform: rotate(180deg);
                        @include mQ(575px) {
                            top: 8px;
                            font-size: 0.9rem;
                        } 
                    }
                }
                &.negative {
                    &:after{
                        color: $capital-red;
                    }
                } 

                &:hover, &:focus {
                    border: none;
                }
            }

            p > a {
                @include themify($themes) {
                    color: themed("recommProductTitle");
                  }
                text-decoration: none;
            }
            .listsummary-switcher-head{
                display: inline-flex;
                justify-content: center;
                > * {
                    display: flex;
                    justify-content: center;
                    align-self: center;
                    margin-right: 8px;
                    @include fontScaling(margin-right,0.8);
                }
                > span{
                    opacity: 0.16;
                    &.active{
                        color: $investing-pallete-06;
                        opacity: 1;
                    }
                }
            }
            .listsummary-switcher-content{
                .listsummary-switcher-data.active{
                    display: block !important;
                }
                .listsummary-switcher-data {
                    &.positive, &.negative {
                        p {
                            position: relative;
                            &:after {
                                content:$icon-down-arrow-selected;
                                -moz-osx-font-smoothing: grayscale;
                                -webkit-font-smoothing: antialiased;
                                font-family: "font-icons";
                                font-style: normal;
                                font-variant: normal;
                                font-weight: normal;
                                line-height: 1;
                                speak: none;
                                position: absolute;
                                top: 10px;
                                @include fontScaling(top,1.0);
                                margin-left: 7px;
                                @include fontScaling(margin-left,0.7);
                                font-size: $font13;
                                @include fontScaling(font-size,1.3);
                            }
                        }
                    }
                    &.positive{
                        p {
                            &:after {
                                color: $light-green;
                                transform: rotate(180deg);
                            }
                        }
                    }
                    &.negative {
                        p {
                            &:after{
                                color: $capital-red;
                            }
                        }
                    }
                } 
            }
        }
    }

    &.small-size {
        ul {
            li {
                padding-top: 5px;
                @include fontScaling(padding-top,0.5);
                padding-bottom: 0px;
                &:first-child {
                    padding-left: 0;
                }
                @include for-phone{
                    width: 50%;
                    display: block;
                    float: left;
                    padding-left: 0px !important;
                    padding-top: 15px;
                    padding-bottom: 21px;
                }
                @include mQ(575px) {
                    padding-top: 0px;
                    padding-bottom: 19px;
                }
                span {
                    font-size: $font12;
                    @include fontScaling(font-size,1.2);
                    margin-bottom: 3px;
                    @include fontScaling(margin-bottom,0.3);
                    @include for-phone {
                        font-size: $font19;
                    }
                    @include mQ(575px) {
                        font-size: $font12;
                    }
                }
                p {
                    font-size: $font18;
                    @include fontScaling(font-size,1.8);
                    @include for-phone {
                        font-size: 2.9rem;
                    }
                    @include mQ(575px) {
                        font-size: $font18;
                    } 
                    &:after {
                        font-size: $font9 !important;
                        @include fontScaling(font-size,0.9,!important);
                        top: 8px !important;
                        @include fontScaling(top,0.8,!important);
                        @include for-phone {
                            font-size: $font13 !important;
                            top: 14px !important;
                        }
                        @include mQ(575px) {
                            font-size: 0.9rem !important;
                            top: 8px !important;
                        }  
                    }
                }
            }
        }
    }
}
.my-advisor-details-wrapper {
    .horizontal-listsummary-row {
        ul {
            li {
                span {
                    @include mQ(575px) {  
                        font-size: 1.4rem;
                        line-height: 1;
                    }
                }
                padding: 15px 0px 15px 20px;
                @include fontScaling(padding-top,1.5);
                @include fontScaling(padding-bottom,1.5);
                @include fontScaling(padding-left,2.0);
                @include for-phone {
                    clear: none !important;
                }
                @include mQ(575px) {  
                    padding: 10px 0px 10px 20px !important;
                }
                &:first-child {
                    padding-left: 36px;
                    @include fontScaling(padding-left,3.6);
                } 
                p {
                    font-size: 2rem;
                    margin-top: 5px;
                    @include fontScaling(margin-top,0.5);
                    @include fontScaling(font-size,2.0);
                    @include mQ(767px,576px) {
                        font-size: 2.9rem;
                    }
                    @include mQ(575px) {  
                        margin-top: 0px;
                        font-size: $font15;
                    } 
                }
                p > a {
                    font-size: 2rem;
                    text-decoration: underline;
                    @include fontScaling(font-size,2.0);
                    @include mQ(575px) {  
                        font-size: $font15;
                    } 
                    @include mQ(767px,576px) {
                        font-size: 2.9rem;
                    }
                }
                .ad-whatsapp img {
                    width: 25px;
                    @include fontScaling(width,2.5);
                    height: 25px;
                    @include fontScaling(height,2.5);
                    margin-top: -7px;
                    @include fontScaling(margin-top,-0.7);
                    margin-bottom: 0;
                    margin-left: 5px;
                    @include fontScaling(margin-left,0.5);
                    visibility: hidden;
                    @include mQ(767px) {
                        visibility: visible;
                    }
                    @include mQ(575px) { 
                        width: 20px;
                        height: 20px;
                    }
                }
                .icon-ad-contact{
                    visibility: hidden;
                    @include mQ(767px) {
                        visibility: visible;
                    }
                    &:before {
                        position: absolute;
                        top: -2px;
                        @include fontScaling(top,-0.2);
                        @include mQ(767px,576px) {  
                            top: 0px;
                            font-size: 2.6rem;
                        } 
                        @include mQ(575px) {
                            top: 4px;
                        }
                    }
                }
                &:nth-child(2){
                    @include mQ(767px) {
                        clear: both !important;
                    }
                } 
            }
        }
    }
}
.orange-border {
    border-bottom: solid 1px $brand-support-yellow;
    @include fontScaling(border-bottom,0.1);
    padding-bottom: 23px;
    @include fontScaling(padding-bottom,2.3);
    margin-bottom: 16px;
    @include fontScaling(margin-bottom,1.6);
    @include for-phone { 
        padding-bottom: 40px;
        margin-bottom: 46px;
    }
    @include mQ(575px) { 
        padding-bottom: 5px;
        margin-bottom: 30px;
    } 
}