.info-banner {
  $self: &;
  &__wrap {
    vertical-align: middle;
    padding: 2.9rem 0;
    position: relative;
    display: flex;
    align-items: center;
    @include for-phone {
      display: block;
      margin: 0 -15px;
      padding: 1rem 1.6rem;
      padding-bottom: 8rem;
      overflow: hidden;
    }
  }
  &__title {
    font-size: $font44;
    line-height: $line-height-46;
    font-family: $pfhandbookpro-regular;
    color: $ib-title;
    @include for-md-desktop {
      font-size: $font34;
      line-height: $line-height-36;
    }
    @include for-phone {
      font-size: $font32;
      line-height: $line-height-34;
    }
  }
  &__icon {
    font-size: $font110;
    color: $ib-icon;
    display: inline-block;
    max-width: 11rem;
    @include for-md-desktop {
      font-size: $font80;
    }
    @include for-phone {
      font-size: $font80;
      position: absolute;
      left: 1.5rem;
      bottom: -1.8rem;
      max-width: 8rem;
    }
    &--img {
      @include for-phone {
        bottom: 0;
      }
    }
  }
  &__desc {
    font-size: $font24;
    line-height: $line-height-26;
    font-family: $pfhandbookpro-thin;
    color: $ib-content;
    margin-bottom: 0;
    @include for-md-desktop {
      font-size: $font20;
      line-height: $line-height-22;
    }
    @include for-phone {
      font-size: $font18;
      line-height: $line-height-20;
    }
  }
  &__title-wrap {
    margin-left: 3.2rem;
    @include for-phone {
      margin-left: 0;
    }
  }
  &__action-wrap {
    margin-left: auto;
    display: inline-block;
    @include for-phone {
      position: absolute;
      right: 1.5rem;
      bottom: 2.1rem;
    }
  }
  &--typ-no-icon {
    height: 16rem;
    @include for-phone {
      margin: 0 -1.5rem;
    }
    #{$self}__wrap {
      padding: 2.9rem 0;
      @include for-phone {
        padding: 2rem 0 5.4rem 0;
      }
    }
    #{$self}__title-wrap {
      margin-left: 0;
    }
    #{$self}__action-wrap {
      @include for-phone {
        right: 2rem;
        bottom: 1rem;
      }
    }
  }
}

/*** --- Buying Guide Component --- ***/
.bg-nav__cont-item {
  .light-grey-full {
    &:before, &:after {
      display: none;
      @include mQ($screen-sm) {
        display: block;
      }
    }
  }
  .info-banner__wrap {
    padding-left: 48px;
    padding-right: 48px;
    @include mQ($screen-md) {
        padding-left: 40px;
        padding-right: 40px;
    }
    @include mQ($screen-sm) {
        padding-left: 0px;
        padding-right: 0px;
    }
  }
  @include mQ($screen-sm) {
    .info-banner__icon {
      bottom: -4.2rem;
    }
  }
}