.brief-history-wrap {
    padding: 3rem 0;
    .title-wrap__title {
        text-align: center;
    }
}

.history-card-list {
    @include for-tablet {
        margin: 0 -15px;
    }
    .slick-slide {
        margin: 0 1.6rem;

        & > div {
            height: 100%;
        }
    }
    .slick-list {
        margin: 0 -1.6px;
        @include for-tablet {
            padding: 0 1.5rem 0;
            margin: 0 -15px;
        }
    }
}

.history-years-list {
    max-width: 54.5rem;
    margin: 0 auto;
    margin-bottom: 1.5rem;
    width: 100%;
    .slick-list {
        width: 100%;
    }
    @include mQ(1024px, 768px){
        .slick-slide {
            width: 80px !important; 
        }
    }
}

@include mQ(1024px) {
    .slick-nav--history {
        display: none;
    }
}

.history-card {
    border-radius: 2px;
    box-shadow: 0 0 14px 0 rgba($history-card-shadow, 0.24);
    border: solid 1px $history-card-border;
    background-color: $history-card-bg;
    padding: 1.2rem 2.4rem 8rem 2.4rem;
    min-height: 20rem;
    position: relative;
    height: 100%;
    @include for-phone {
        min-height: 15rem;
    }
    &__brief {
        font-size: $font24;
        line-height: $line-height-26;
        color: $history-card-brief;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__date {
        font-size: $font22;
        line-height: $line-height-24;
        color: $history-card-date;
        margin-bottom: 0.6rem;
        display: block;
        text-align: right;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-16;
        }
    }
    &__icon {
        font-size: $font80;
        position: absolute;
        right: 2.4rem;
        bottom: 0;
        margin: 0;
        max-width: 8rem;
        @include for-phone {
            font-size: $font60;
            max-width: 6rem;
            bottom: 0;
        }
    }
}