.accbg {
    background: #ECDDC0 0% 0% no-repeat padding-box;
    border: 1px solid #ECDDC0;
    padding: 30px;
    width: 90%;
    margin: auto !important;
}
.accbtn {
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 3px;
    font-family: 20px/28px $pfhandbookpro-regular;
    opacity: 1;
    color: #000;
    border: 0;
    width: 166px;
    
    display: block;
    height: 52px;
}
.card {
    background: #FFFFFF 0% 0% no-repeat padding-box;
    padding: 10px;
    margin: 10px;
}
.titlehead-acc {
    text-align: left;
    font-family: $pfhandbookpro-bold;
    letter-spacing: 0px;
    color: #695934;
    font-size: $font18;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    vertical-align: 1rem;
}
.helptext_danger
{
font-family: $pfhandbookpro-regular;
letter-spacing: 0px;
color: #8B151B;
font-weight: normal;
font-style: normal;
font-stretch: normal;
line-height: normal;
letter-spacing: normal;
}
.helptext
{
font-family: $pfhandbookpro-regular;
letter-spacing: 0px; 
font-size: $font16;
font-weight: normal;
font-style: normal;
font-stretch: normal;
line-height: normal;
letter-spacing: normal;
color: gray;
}
.docs
{
    font-family: $pfhandbookpro-regular;
    color: black !important;
    font-size: $font18;
    font-weight: normal;
font-style: normal;
font-stretch: normal;
line-height: normal;
letter-spacing: normal;
vertical-align: 1rem;
padding-left: 0rem;
}
.docs li{
    line-height:1.70;

    }
