.rate-us {
    &__modal {
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        max-width: 64rem;
        max-height: 57rem;
        background: $rate-us-modal;
        padding: 2.4rem 2.4rem 0 2.4rem;
        z-index: 10003;
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease-in;
        overflow-y: auto;
        @include for-phone {
            left: 1rem;
            right: 1rem;
            transform: translate(0%, -50%);
            width: auto;
            height: auto;
            padding: 1.5rem 2.4rem;
        }
        &--active {
            visibility: visible;
            opacity: 1;
        }
        &--thankyou {
            max-width: 38.8rem;
            min-height: 18.6rem;
            padding: 2.4rem;
            height: auto;
            @include for-phone {
                left: 4rem;
                right: 4rem;
            }
        }
    }
    &__overlay {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        background: rgba($rate-us-overlay, 0.6);
        z-index: 10002;
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-1;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__desc {
        font-size: $font24;
        line-height: $line-height-26;
        color: $rate-us-text;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 3rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 4rem;
        }
        &--bold {
            font-family: $pfhandbookpro-regular;
        }
    }
    &__close {
        position: absolute;
        right: 2.4rem;
        top: 2.8rem;
        @include for-phone {
            right: 1.6rem;
            top: 1.6rem;
        }
    }
    &__close-icon {
        font-size: $font16;
        color: $rate-us-close;
    }
    .exp-rating {
        &__title {
            font-size: $font24;
            line-height: $line-height-26;
            color: $rate-us-text;
            text-align: center;
            margin-bottom: 2.4rem;
            @include for-phone {
                font-size: $font18;
                line-height: $line-height-20;
            }
        }
    }
    .noUi-horizontal {
        height: 0.5rem;
        background: $rate-us-slider-bg;
        border: none;
        &:before,
        &:after {
            content: "";
            width: 1.2rem;
            height: 1.2rem;
            display: inline-block;
            border-radius: 50%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }
        &:before {
            left: -0.6rem;
            background: $protecting-pallete-01;
        }
        &:after {
            right: -0.6rem;
            background: $rate-us-slider-bg;
        }
        .noUi-connect {
            background: $protecting-pallete-01;
            background: -moz-linear-gradient( left, $protecting-pallete-01 0%, $protecting-pallete-02 23%, $protecting-pallete-03 49%, $protecting-pallete-04 73%, $protecting-pallete-05 100%);
            background: -webkit-gradient( left top, right top, color-stop(0%, $protecting-pallete-01), color-stop(23%, $protecting-pallete-02), color-stop(49%, $protecting-pallete-03), color-stop(73%, $protecting-pallete-04), color-stop(100%, $protecting-pallete-05));
            background: -webkit-linear-gradient( left, $protecting-pallete-01 0%, $protecting-pallete-02 23%, $protecting-pallete-03 49%, $protecting-pallete-04 73%, $protecting-pallete-05 100%);
            background: -o-linear-gradient( left, $protecting-pallete-01 0%, $protecting-pallete-02 23%, $protecting-pallete-03 49%, $protecting-pallete-04 73%, $protecting-pallete-05 100%);
            background: -ms-linear-gradient( left, $protecting-pallete-01 0%, $protecting-pallete-02 23%, $protecting-pallete-03 49%, $protecting-pallete-04 73%, $protecting-pallete-05 100%);
            background: linear-gradient( to right, $protecting-pallete-01 0%, $protecting-pallete-02 23%, $protecting-pallete-03 49%, $protecting-pallete-04 73%, $protecting-pallete-05 100%);
            filter: "progid:DXImageTransform.Microsoft.gradient( startColorstr=" $protecting-pallete-01 ", endColorstr=" $protecting-pallete-05 ", GradientType=1 )";
        }
        .noUi-handle {
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
            border: none;
            box-shadow: 0 0 14px 0 rgba(106, 71, 66, 0.24);
            right: -1rem;
            top: -0.8rem;
            cursor: pointer;
            &:before,
            &:after {
                content: none;
            }
        }
        .noUi-marker-horizontal {
            &.noUi-marker-large {
                display: none;
            }
            &.noUi-marker-sub {
                height: 0.8rem;
                background: $brand-dark-grey;
            }
        }
        .noUi-value-horizontal {
            &.noUi-value-large {
                color: $brand-dark-grey;
                font-size: $font24;
                line-height: $line-height-26;
                top: -0.7rem;
            }
            &.noUi-value-sub {
                display: none;
            }
        }
        .noUi-tooltip {
            bottom: -144%;
            background: $rate-us-row-bg;
            border: 0;
            font-size: $font24;
            line-height: $line-height-24;
            z-index: -1;
        }
    }
}

.abc-form {
    $this: &;
    .form-group {
        .form-check {
            &--smiley {
                display: inline-block;
                vertical-align: middle;
                width: 19%;
                text-align: center;
                padding-left: 0;
                font-size: $font24;
                line-height: $line-height-26;
                color: $rate-us-radio;
                margin-bottom: 4rem;
                @include for-phone {
                    font-size: $font16;
                    line-height: $line-height-18;
                }
            }
            .radio {
                &.form-check-label--smiley {
                    width: 4.2rem;
                    height: 4.2rem;
                    border: 0;
                    box-shadow: none;
                    display: block;
                    position: relative;
                    margin: 0 auto 1.2rem;
                    transition: transform 0.3s ease-in;
                    @include for-phone {
                        width: 3rem;
                        height: 3rem;
                    }
                }
                &.form-check-label--worst {
                    background: url(../images/weary-face-emoji.png);
                    background-size: cover;
                }
                &.form-check-label--poor {
                    background: url(../images/sad-face-emoji.png);
                    background-size: cover;
                }
                &.form-check-label--avg {
                    background: url(../images/neutral-face-emoji.png);
                    background-size: cover;
                }
                &.form-check-label--good {
                    background: url(../images/relieved-emoji.png);
                    background-size: cover;
                }
                &.form-check-label--excel {
                    background: url(../images/heart-eyes-emoji.png);
                    background-size: cover;
                }
            }
            input {
                &:checked {
                    &~.radio.form-check-label--smiley {
                        background-color: transparent;
                        border: 0;
                        box-shadow: none;
                        transform: scale(1.33);
                        @include for-phone {
                            transform: scale(1.5);
                        }
                    }
                    &~.form-check__text {
                        color: $rate-us-radio-active;
                    }
                }
            }
        }
    }
    .form-row {
        &--range-wrap {
            background: $rate-us-row-bg;
            padding: 1.5rem 7rem 5rem;
            margin: 0 -2.4rem;
            @include for-phone {
                padding: 1.5rem 3rem 4rem;
            }
        }
    }
    &--rating {
        .btn-wrap {
            padding-top: 2.4rem;
            padding-bottom: 2.4rem;
        }
        .on-page-editor & {
            .form-group {
                .scLooseFrameZone.scEnabledChrome {
                    display: inline-block;
                    width: 19%;
                    text-align: center;
                    vertical-align: middle;
                    .form-check--smiley {
                        width: 100%;
                    }
                }
            }
        }
    }
}