.dashboard__summary-card{
    box-shadow: 2px 6px 12px 0 rgba(0, 0, 0, 0.08);
    @media screen and (min-width: $custom-screen-max-width) {
        box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.2rem) 0 rgba(0, 0, 0, 0.08);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        box-shadow: 0.2vw 0.6vw 1.2vw 0 rgba(0, 0, 0, 0.08);
    }

    background-color:$brand-white;
    border-radius: 2px;
    @include fontScaling(border-radius,0.2); 
    // display: flex;
    // flex-wrap: wrap;
    padding: 6px 16px 6px 0px;
    @include fontScaling(padding-top,0.6);
    @include fontScaling(padding-right,1.6);
    @include fontScaling(padding-bottom,0.6);
    margin-bottom: 16px;
    @include fontScaling(margin-bottom,1.6);
    @include themify($themes) {
        border-left: themed('contentCardLeftBorderColor');  
    }
    &__left {
        width: 59.8%;
        float: left;
        padding-right: 5px;
        @include fontScaling(padding-right,0.5);
        @include for-phone{
            width: 100%;
            padding: 0px;
            border:none !important;
            border-bottom:1px solid #dee2e6 !important;
        }
    }
    &__right{
        width: 40.2%;
        float: left;
        padding-left: 23px;
        @include fontScaling(padding-left,2.3);
        @include for-phone{
            width: 100%;
            padding: 0px;
        }
    }
}

.dashboard__summary-card-with-flex {
    display: flex;
    flex-wrap: wrap;
}

.horizontal-listsummary{
    .horizontal-listsummary__container{
        @include for-phone {
            border-right: 0px !important;  
        }
        .horizontal-listsummary-row{
            ul{
                list-style:none;
                padding:0px;
                display: flex;
                margin: 0px;
                width:100%;
                @include for-tablet {
                    display: inline-block;  
                }
                li {
                    flex-basis: 100%;
                    padding: 25px 26px 13px 22px;
                    @include fontScaling(padding-top,2.5);
                    @include fontScaling(padding-right,2.6);
                    @include fontScaling(padding-bottom,1.3);
                    @include fontScaling(padding-left,2.2);
                    color: $brand-black !important;
                    padding-right: 0px;
                    display: grid;
                    @include for-tablet{
                        width: 50%;
                        display: block;
                        float: left;
                        padding-left: 26px !important;
                    }
                    span {
                        font-size: $font14;
                        @include fontScaling(font-size,1.4);
                        font-family: $pfhandbookpro-light;
                        line-height: 1.15;
                    }
                    p {
                        margin-bottom: 0px;
                        font-size: $font26;
                        @include fontScaling(font-size,2.6);
                        color: $brand-black;
                        font-family: $pfhandbookpro-medium;
                        line-height: 1.3;
                        position: relative;
                        .decimal {
                            font-size: $font16;
                            @include fontScaling(font-size,1.6);
                            font-family: $pfhandbookpro-medium;
                        } 
                        &.positive{
                            &::after {
                                content:$icon-down-arrow-selected;
                                -moz-osx-font-smoothing: grayscale;
                                -webkit-font-smoothing: antialiased;
                                font-family: "font-icons";
                                font-style: normal;
                                font-variant: normal;
                                font-weight: normal;
                                line-height: 1;
                                speak: none;
                                position: absolute;
                                color: $light-green;
                                transform: rotate(180deg);
                                top: 15px;
                                @include fontScaling(top,1.5);
                                margin-left: 12px;
                                @include fontScaling(margin-left,1.2);
                                font-size: 13px;
                                @include fontScaling(font-size,1.3);

                                @include for-phone {
                                    top: 14px;
                                    margin-left: 6px;
                                }
                                @include mQ(575px) {
                                    top: 8px;
                                    font-size: 1.0rem;
                                }  
                            }
                        }
                        &.negative {
                            &::after{
                                content:$icon-down-arrow-selected;
                                -moz-osx-font-smoothing: grayscale;
                                -webkit-font-smoothing: antialiased;
                                font-family: "font-icons";
                                font-style: normal;
                                font-variant: normal;
                                font-weight: normal;
                                line-height: 1;
                                speak: none;
                                position: absolute;
                                color: $capital-red;
                                top: 12px;
                                @include fontScaling(top,1.2);
                                margin-left: 12px;
                                @include fontScaling(margin-left,1.2);
                                font-size: 13px;
                                @include fontScaling(font-size,1.3);

                                @include for-phone { 
                                    top: 12px;
                                    margin-left: 6px;
                                } 
                                @include mQ(575px) {
                                    top: 7px;
                                    font-size: 1.0rem;
                                } 
                            }
                        }
                    }
                    .listsummary-switcher-head{
                        display: inline-flex;
                        justify-content: center;
                        > * {
                            display: flex;
                            justify-content: center;
                            align-self: center;
                            margin-right: 8px;
                            @include fontScaling(margin-right,0.8);
                        }
                        > span{
                            opacity: 0.16;
                            &.active{
                                color: $investing-pallete-06;
                                opacity: 1;
                            }
                        }
                    }
                    .listsummary-switcher-content{
                        .listsummary-switcher-data.active{
                            display: block !important;
                        }
                        .listsummary-switcher-data{
                            &.positive{
                                p:after {
                                    content: "";
                                    width: 0;
                                    height: 0;
                                    border-style: solid;
                                    border-width: 0 7px 8px 7px;
                                    @media screen and (min-width: $custom-screen-max-width) {
                                        border-width: 0 calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem)) calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem)) calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem));
                                    }
                                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                        border-width: 0 0.7vw 0.8vw 0.7vw;
                                    }
                                    border-color: transparent transparent $investing-pallete-06 transparent;
                                    position: absolute;
                                    top: 15px;
                                    @include fontScaling(top,1.5);
                                    margin-left: 24px;
                                    @include fontScaling(margin-left,2.4);
                                }
                            }
                            &.negative {
                                p:after{
                                    content: "";
                                    width: 0;
                                    height: 0;
                                    border-style: solid;
                                    border-width: 8px 7px 0 7px;
                                    @media screen and (min-width: $custom-screen-max-width) {
                                        border-width: 0.8rem calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem)) calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0)) calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem));
                                    }
                                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                        border-width: 0.8vw 0.7vw 0 0.7vw;
                                    }
                                    border-color: $capital-red transparent transparent transparent;
                                    position: absolute;
                                    top: 15px;
                                    @include fontScaling(top,1.5);
                                    margin-left: 24px;
                                    @include fontScaling(margin-left,2.4);
                                }
                            }
                        } 
                    }
                }
            }
        }
    }
}


.switch {
    position: relative;
    display: inline-block;
    width: 24px;
    @include fontScaling(width,2.4);
    height: 12px;
    @include fontScaling(height,1.2);
    margin: 0px;
    input { 
        opacity: 0;
        width: 0;
        height: 0;
    }
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #fff;
        -webkit-transition: .4s;
        transition: .4s;
        &.round {
            border-radius: 5px;
            @include fontScaling(border-radius,0.5);
            border: solid 0.5px $investing-pallete-06;
            @include fontScaling(border-width,0.05);
            &:before {
                border-radius: 50%;
            }
        }
        &::before {
            position: absolute;
            content: "";
            height: 8px;
            @include fontScaling(height,0.8);
            width: 8px;
            @include fontScaling(width,0.8);
            left: 1px;
            @include fontScaling(left,0.1);
            bottom: 1px;
            @include fontScaling(bottom,0.1);
            background-color: $investing-pallete-06;
            -webkit-transition: .4s;
            transition: .4s;

            @include for-phone {
                left: 2px;
                bottom: 1px;
            } 
            @include mQ(575px) {
                left: 2px;
                bottom: 2px;
            }
        }
    }
    input[type=checkbox]:checked + .slider {
        background-color: $brand-white;
        &::before {
            -webkit-transform: translateX(18px);
            -ms-transform: translateX(18px);
            transform: translateX(11px);
            @media screen and (min-width: $custom-screen-max-width) {
                -webkit-transform: translateX(calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18}));
                -ms-transform: translateX(calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18}));
                transform: translateX(calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11}));
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                -webkit-transform: translateX(1.8vw);
                -ms-transform: translateX(1.8vw);
                transform: translateX(1.1vw);
            }
        }
    }
}
@include for-phone {
    .dashboard__bottom-wrapper[data-id='mutual-funds'], .dashboard__bottom-wrapper[data-id='mutual-fund']{
        .dashboard__summary-card {
            padding-left: 16px;
            padding-bottom: 16px;
            &.dashboard__portfolio-summary-card-by-scheme {
                padding-left: 0;
            }
        }
        .dashboard__portfolio-horizontal-summary {
            ul {
                li {
                    padding-top: 9px;
                    &:nth-child(2n + 1) {
                        clear: both;
                    }
                }
            }
        }
        .horizontal-listsummary {
            .horizontal-listsummary__container {
                .horizontal-listsummary-row {
                    ul {
                        li {
                            padding: 8px 26px 8px 0px !important;
                        }
                    }
                }
            }
        }
        .dashboard__summary-card__right {
            &.border-left { 
                border-left: none !important;
            }
        }

        .dashboard__summary-card__right {
            .link-list__list--column {
                li {
                    a {
                        height: 60px;
                        align-items: center;
                        display: flex;
                        padding-top: 10px !important;
                        padding-bottom: 10px !important;
                    }
                }
            }
        }
    }
}
@include mQ(575px) {
    .dashboard__bottom-wrapper[data-id='mutual-funds'], .dashboard__bottom-wrapper[data-id='mutual-fund']{
        .horizontal-listsummary-row {
            ul {
                li {
                    span {
                        font-size: 1.2rem !important;
                    }
                    p {
                        font-size: 1.8rem !important;
                    }
                }
            }
        }
        .component-basic-portfolio-by-scheme {
            .switch {
                .slider {
                    &:before { 
                        left: 3px;
                        bottom: 1px;
                    }
                }
            }
        }
        .dashboard__summary-card__right {
            .link-list__list--column {
                li {
                    a { 
                        height: 48px;
                        align-items: center;
                        display: flex;
                        padding-top: 10px !important;
                        padding-bottom: 10px !important;
                    } 
                }
            }
        }
    }
}

