.marginTop86 {
  margin-top: 74px !important;
}
.trending-wrapper {
  float: left;
  width: 100%;
  margin-bottom: 55px;
  @include mQ(991px) {
    margin-bottom: 20px;
    float: left;
    width: 100%;
  }
}
.trending-item {
  @include mQ(991px, 768px) {
    width: calc(50% - 10px);
    float: left;
    margin-right: 20px;
    &:nth-child(even) {
      margin-right: 0;
    }
  }
  .article {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    margin-bottom: 8px;
    border-left-width: 0px;
    padding: 0;
    height: 190px;
    @include mQ(767px) {
      height: 126px;
      font-size: 16px;
      line-height: normal;
    }
    .like-shares {
      @include for-phone() {
        display: block;
      }
      .icon {
        @include for-phone() {
          font-size: 1.8rem;
        }
      }
    }
  }
  &.red {
    .article-tag {
      background: $capital-red;
    }
  }
  &.pink {
    .article-tag {
      background: $pink;
    }
  }
  &.green {
    .article-tag {
      background: $green;
    }
  }
  &.yellow {
    .article-tag {
      background: $yellow;
    }
  }
  .article-tag {
    background: $capital-red;
    color: $white;
    font-family: $pfhandbookpro-bold;
    padding: 4px 5px 4px 5px;
    letter-spacing: -0.41px;
    border-radius: 2px;
    text-transform: uppercase;
    display: inline-block;

    @include mQ(1020px, 992px) {
      font-size: 1.2rem;
      padding: 0px 5px 0px 5px;
    }
  }

  .article-desc {
    h3 {
      //height: auto !important;
      margin-bottom: 5px;
      // /* autoprefixer: off */
      // -webkit-box-orient: vertical;
      // /* autoprefixer: on */
      // display: block;
      // display: -webkit-box;
      // -webkit-line-clamp: 2;
      // overflow: hidden;
      // text-overflow: ellipsis;
      // //max-width: 400px;
      // height: 17px * 1.39 * 2; /* Fallback for non-webkit */
      margin: 0 auto;
      font-size: 1.7rem;
      line-height: 1.2;

      @include mQ(1200px) {
        font-size: 1.7rem;
      }
      @include mQ(1020px, 992px) {
        margin-bottom: 0px;
      }
    }
  }
  .article-details {
    padding: 18px 17px;
    @include mQ(1020px) {
      padding: 10px;
    }
  }
  .article-desc {
    padding: 5px 5px 7px;

    @include mQ(1020px, 992px) {
      padding: 5px 5px 0px;
    }
    p {
      margin-bottom: 0;
      /* autoprefixer: off */
      -webkit-box-orient: vertical;
      /* autoprefixer: on */
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
      //max-width: 400px;
      height: 16px * 1.2 * 2; /* Fallback for non-webkit */
      margin: 0 auto;
      font-size: 1.6rem;
      line-height: 1.2;

      @include mQ(1200px) {
        font-size: 1.6rem;
        line-height: 20px;
      }
      @include mQ(1020px) {
        font-size: 1.4rem;
        line-height: 18px;
      }
      @include mQ(767px) {
        display: none;
      }
    }
  }
  .article-updates {
    padding: 0 5px;
    position: absolute;
    bottom: 18px;
  }
}

.trending-widget-banner-wrapper {
  .trending-wrapper {
    margin-bottom: 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
  margin-top: -15px;
  .count-wrapper,
  .count-wrapper > span,
  .count-wrapper .count {
    font-family: $pfhandbookpro-bold;
  }
  @include mQ(1020px) {
    margin-top: 15px;
    .trending-wrapper {
      margin-bottom: 0;
    }
    .article-tag {
      padding: 0px 5px 0px 5px;
      font-size: 1.2rem;
    }
  }
  .trending-item {
    max-width: 303px;
    float: left;
    margin-right: 30px;
    margin-bottom: 20px;
    width: calc(50% - 30px);
    @include mQ(767px) {
      max-width: 100%;
      width: calc(50% - 7.5px);
      margin-right: 15px;
    }
    &:nth-child(even) {
      margin-right: 0;
    }
    .article-desc h3 {
      font-size: 1.8rem;
      line-height: 1.39;
    }
    .article {
      border-left-width: 4px;
      //box-shadow: 0 6px 30px rgba(0, 0, 0, 0.06);
      box-shadow: 0 18px 36px 0 rgba(0, 0, 0, 0.1);
      display: block;
      position: relative;
      margin-bottom: 0px;
      height: auto;

      //margin-bottom: 20px;
      .blur-background {
        position: absolute;
        overflow: hidden;
        width: 100%;
        height: 100%;
        z-index: -1;
        &:before {
          content: "";
          width: 100%;
          height: 100%;
          // background-color: rgba(255, 255, 255, 0.56);
          // @include blur(5);
          -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
          -webkit-box-shadow: 0 32px 64px 0 rgba(0, 0, 0, 0.1),
            0 6px 30px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 32px 64px 0 rgba(0, 0, 0, 0.1),
            0 6px 30px 0 rgba(0, 0, 0, 0.06);
          background-color: rgba(255, 255, 255, 0.53);
          position: absolute;
          left: 0;
          top: 0;
          z-index: -1;
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }
      }
    }
    .article-details {
      //     .article-updates{
      //         position: absolute;
      //          bottom: 18px;
      //    }
      @include mQ(767px) {
        padding: 5px;
        .article-desc {
          padding: 5px 5px 0px;
        }
        .like-shares {
          margin-top: 0;
          .icon {
            //font-size: 1rem;
            font-size: 1.8rem;
          }
        }
        .count-wrapper {
          font-size: 1rem;
          margin-left: 3px;
        }
      }
    }
  }
  .article-updates {
    padding: 0 5px;
    position: relative;
    bottom: 0;
  }
}

.top-margin {
  .block-heading {
    margin-top: 150px;
    @include mQ(1200px) {
      margin-top: 142px;
      margin-bottom: 7px;
    }
    @include mQ(1020px) {
      margin-top: 96px;
      margin-bottom: 0px;
    }
  }
}
//height auto for trending article
.two-columns .trending-item .article-desc h3 {
  height: auto !important;
  font-size: 2rem;
  line-height: 1.25;
  margin-bottom: 5px;
  @include mQ(767px) {
    font-size: 16px;
    line-height: normal;
  }
}
@media only screen and (max-width: 800px) {
  .trending-widget-banner-wrapper .article-tag {
    font-size: 1.2rem !important;
  }
  .trending-widget-banner-wrapper .trending-item .article-desc h3 {
    font-size: 14px !important;
    line-height: 1.16 !important;
  }
  .trending-item .article-details {
    //padding: 10px 5px;
    padding: 15px;
  }
  // .trending-widget-banner-wrapper .trending-item {
  //   width: calc(54% - 30px) !important;
  // }
}
