//info-text
.info-text{
    padding: 2rem 0;
    &__title{
        font-size: $font44;
        line-height: $line-height-46;
        font-family: $pfhandbookpro-regular;
        color: $it-text-color;
        margin-bottom: 1rem;
        @include for-phone{
            font-size: $font32;
            line-height: $line-height-34;
        }
    }
    &__p{
        font-size: $font24;
        line-height: $line-height-26;
        color: $it-text-color;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 2rem;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 1rem;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }
    &__desc{
        max-width: 73.6rem;
    }
}