//our-funds
.our-funds {
    $this: &;
    &__page-title {
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $of-text-clr;
        margin-bottom: 1.5rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 0.5rem;
        }
    }
    &__title {
        font-size: $font44;
        line-height: $line-height-46;
        font-family: $pfhandbookpro-regular;
        color: $of-text-clr;
        text-transform: capitalize;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
        }
        &--bottom-space {
            margin-bottom: 1.5rem;
        }
    }
    &__desc {
        #{$this}__p {
            margin-bottom: 5rem;
            @include for-phone {
                margin-bottom: 2.5rem;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    &__p {
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $of-text-clr;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__title-wrap {
        margin-bottom: 2.5rem;
        @include for-phone {
            margin-bottom: 1.5rem;
        }
    }
    &__head {
        margin: 0 8rem;
        margin-bottom: 2rem;
        border-bottom: 1px solid $of-border-color;
        @include for-tablet {
            margin: 0;
        }
        @include for-phone {
            margin: 0;
            margin-bottom: 1.2rem;
        }
    }
    &__container {
        max-width: 73.6rem;
        margin: 0 auto;
        &--left {
            margin: 0;
        }
    }
    &__social-wrap {
        margin-bottom: 3.5rem;
        @include for-phone {
            margin-bottom: 3rem;
        }
    }
    &__social-wrap,
    &__info-wrap {
        padding: 0;
    }
    &__social-item {
        display: inline-block;
        margin-right: 2rem;
        vertical-align: middle;
        &:last-child {
            margin-right: 0;
        }
    }
    &__social-link {
        font-size: $font24;
        max-width: 2.4rem;
        max-height: 2.4rem;
        display: inline-block;
        @include for-phone {
            font-size: $font22;
            max-width: 2.2rem;
            max-height: 2.2rem;
        }
    }
    &__social-img {
        width: 100%;
    }
    &__info-title {
        font-size: $font44;
        line-height: $line-height-46;
        font-family: $pfhandbookpro-regular;
        color: $of-text-clr;
        margin-bottom: 0.8rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
            margin-bottom: 0.3rem;
        }
    }
    &__info-subtitle {
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $of-text-clr;
        margin-bottom: 2.2rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 1.3rem;
        }
    }
    &__info-item {
        margin-bottom: 8rem;
        @include for-phone {
            margin-bottom: 3.7rem;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__form {
        margin-top: 4rem;
        @include for-phone {
            margin-top: 2rem;
        }
        .form-group--download {
            @include for-phone {
                width: 100%;
            }
        }
    }
    &__infography {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @include for-tablet {
            display: block;
            align-items: initial;
            justify-content: initial;
        }
        #{$this}__title-wrap {
            max-width: 73.6rem;
            @include for-md-desktop {
                max-width: 53rem;
            }
        }
    }
    &__img-wrap {
        max-width: 35.2rem;
        display: block;
        @include for-tablet {
            width: 80%;
            margin: 0 auto;
        }
    }
    &__form-group {
        margin-bottom: 2rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__row {
        margin-top: 4rem;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        @include for-tablet {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-top: 2rem;
        }
        @include for-phone {
            display: block;
            justify-content: initial;
            align-items: initial;
        }
        #{$this}__form {
            margin-top: 0;
        }
    }
    &__row-item {
        width: 50%;
        @include for-phone {
            width: 100%;
            margin-bottom: 2rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}