/*
 * Portfolio Scheme/Folio Style
 *
 */


.component-scheme {
    border-radius: 2.6px;
    box-shadow: 2px 2px 7px 0 rgba(0, 0, 0, 0.08);
    background-color: $white;
    margin-bottom: 20px;
    padding: 14px 13px 10px 10px;
    width: 100%;
    float: left;

    @media screen and (min-width: $custom-screen-max-width) {
        border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.6px);
        margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font13}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
        box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2px) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2px) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7px) 0 rgba(0, 0, 0, 0.08);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        border-radius: 0.26vw;
        margin-bottom: 2.0vw;
        padding: 1.4vw 1.3vw 1.0vw 1.0vw;
        box-shadow: 0.2vw 0.2vw 0.7vw 0 rgba(0, 0, 0, 0.08);
    }


    @include for-phone {
        display: flex;
        flex-direction: column;
        padding: 14px 24px 0px 24px;
    }
    @include mQ(575px) {
        padding: 14px 17px 0px 15px;
    }
    .dashboard {
        &__portfolio {
            &-scheme {

                &--left-wrapper{
                    float: left;
                    width: 60%;
                    @include for-phone {
                        width: 100%;
                    }
                }
                &--right-wrapper{
                    float: right;
                    @include for-phone {
                        float: left;
                        margin-top: 10px;
                        margin-bottom: 20px;
                        order: 3;
                    }
                    @include mQ(575px) {
                        margin-top: 0px;
                    } 
                }
                &--title{
                    font-family: $pfhandbookpro-regular;
                    font-size: $font18;
                    line-height: 1.11;
                    color: $investing-shade;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.8vw;
                    }

                    @include for-phone {
                        font-size: $font23;
                        line-height: 1.39;
                    }
                    @include mQ(575px) {
                        font-size: $font14;
                        line-height: 1.43;
                    } 
                }
            }
            
            &--dropdown {
                &-wrapper {
                    margin-bottom: -3px;

                    @media screen and (min-width: $custom-screen-max-width) {
                        margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.3rem);
                        margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        margin-bottom: -0.3vw;
                        margin-top: 0.8vw;
                    }

                    @include for-phone {
                        order: 4;
                        margin-bottom: 20px;
                        margin-top: 8px;

                        @media screen and (min-width: $custom-screen-max-width) {
                            margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.0rem);
                            margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.8rem);
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            margin-bottom: 2.0vw;
                            margin-top: 0.8vw;
                        }
                    }
                    @include mQ(575px) { 
                        margin-bottom: 10px;
                    }
                    > a {
                        font-family: $pfhandbookpro-bold;
                        font-size: $font12;
                        color: $investing-shade;
                        position: relative;

                        @media screen and (min-width: $custom-screen-max-width) {
                            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            font-size: 1.2vw;
                        }

                        &:after {
                            content:$icon-arrow;
                            -moz-osx-font-smoothing: grayscale;
                            -webkit-font-smoothing: antialiased;
                            font-family: "font-icons";
                            font-style: normal;
                            font-variant: normal;
                            font-weight: normal;
                            line-height: 1;
                            speak: none;
                            position: absolute;
                            top: 3px;
                            margin-left: 10px;
                            font-size: $font10;

                            @media screen and (min-width: $custom-screen-max-width) {
                                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
                                top: 3px;
                                margin-left: 10px;
                            }
                            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                font-size: 1.0vw;
                                top: 0.3vw;
                                margin-left: 1.0vw;
                            }


                        }
                        &.active {
                            &:after {
                                transform: rotate(180deg);
                            }
                        }
                        &:hover, &:focus {
                            border: none;
                        }

                        @include for-phone { 
                            font-size: $font19;
                            &:after {
                                top: 4px;
                                margin-left: 13px;
                                font-size: $font14;
                            }
                        }

                        @media (max-width: 575px) {
                            font-size: $font15;
                            &:after {
                                font-size: $font12;
                            }
                        }
                    }
                }
                &-content {
                    background-color: #f7f8f5;
                    border-radius: 2.6px;
                    float: left;
                    width: calc(100% + 23px);
                    margin-left: -10px;
                    padding-left: 10px;
                    padding-right: 13px;
                    margin-bottom: -10px;
                    padding-bottom: 16px;
                    padding-top: 10px;
                    margin-top: 5px;
                    display: none;

                    @media screen and (min-width: $custom-screen-max-width) {
                        border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.26rem);
                        width: calc(100% + calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.3rem));
                        margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.0rem);
                        padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.0rem);
                        padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.3rem);
                        margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -1.0rem);
                        padding-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.6rem);
                        padding-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.0rem);
                        margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        border-radius: 0.26vw;
                        width: calc(100% + 2.3vw);
                        margin-left: -1.0vw;
                        padding-left: 1.0vw;
                        padding-right: 1.3vw;
                        margin-bottom: -1.0vw;
                        padding-bottom: 1.6vw;
                        padding-top: 1.0vw;
                        margin-top: 0.5vw;
                    }

                    @include for-phone {
                        width: calc(100% + 48px);
                        margin-left: -24px;
                        margin-bottom: -24px;
                        padding-left: 24px;
                        padding-right: 24px;
                        padding-top: 24px;
                        padding-bottom: 24px;
                    }
                    @include mQ(575px) {
                        width: calc(100% + 34px);
                        margin-left: -17px;
                        margin-bottom: -10px;
                        padding-left: 17px;
                        padding-right: 17px;
                        padding-top: 20px;
                        padding-bottom: 20px;
                    }
                    &.active {
                        display: block;
                    }
                } 
                &-content + .dashboard__portfolio--dropdown-content {
                    margin-top: 20px;
                    @media screen and (min-width: $custom-screen-max-width) {
                        margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        margin-top: 2.0rem;
                    }
                    @include for-phone { 
                        margin-top: 34px;
                    }
                    @include mQ(575px) { 
                        margin-top: 20px;
                    } 
                }
                &-title {
                    font-family: $pfhandbookpro-medium;
                    font-size: $font18;
                    color: $black;
                    margin-bottom: 4px;
                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
                        margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.8vw;
                        margin-bottom: 0.4vw;
                    }
                }
                &-username {
                    font-family: $pfhandbookpro-light;
                    font-size: $font12;
                    color: $black;
                    margin-left: 5px;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                        margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.2vw;
                        margin-left: 0.5vw;
                    }
                }
                &-left-section {
                    float: left;
                    width: 70%;
                    @include mQ(575px) {
                        width: 100%;
                    }
                }
                &-right-section {
                    float: right;
                    @include for-phone {
                        float: left;
                        margin-top: 5px;
                    }
                }
            }
        }
    }
}

.tag-wrapper {
    margin-top: 7px;

    @media screen and (min-width: $custom-screen-max-width) {
        margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font7});
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        margin-top: 0.7vw;
    }

    @include for-phone {
        margin-top: 20px;
    }
    @include mQ(575px) {
        margin-top: 13px;
    }
    ul {
        padding-left: 0;
        display: flex;
        flex-wrap: wrap;
        li {
            border-radius: 10px;
            background-color: rgba(#a1ac7b, 0.2);
            font-family: $pfhandbookpro-regular;
            font-size: $font12;
            color: #565f66 !important;
            text-align: center;
            padding: 3px 11px !important;
            margin-right: 10px;
            margin-bottom: 5px;

            @media screen and (min-width: $custom-screen-max-width) {
                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.3rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font11}) !important;
                margin-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
                margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5});
                border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                font-size: 1.2vw;
                padding: 0.3vw 1.1vw !important;
                margin-right: 1.0vw;
                margin-bottom: 0.5vw;
                border-radius: 1.0vw;
            }

            @include for-phone {
                font-size: $font19;
                border-radius: 15px;
                padding: 6px 20px !important;
            }
            @media (max-width: 575px){
                text-align: left;
                font-size: $font12;
                border-radius: 10px;
                padding: 3px 11px !important;
            }
            &:before {
                display: none;
            }
        }
    }
}