//sme-scheme
.sme-scheme{
    &__p{
        font-size: $font24;
        line-height: $line-height-26;
        color: $ss-text-color;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 3rem;
        &:last-child{
            margin-bottom: 0;
        }
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
            margin-bottom: 1.5rem;
        }
    }
    &__desc{
        margin-bottom: 4rem;
    }
    &__info-wrap{
        padding: 0;
    }
    &__info-item{
        margin-bottom: 8rem;
        @include for-phone{
            margin-bottom: 4rem;   
        }
        &:last-child{
            margin-bottom: 0;
        }
        &:first-child{
            width: 60%;
            @include for-tablet{
                width: 100%;
            }   
            @include for-phone{
                width: 100%;
            }
        }
        
    }
    &__form{
        width: 65%;
        @include for-phone{
            width: 100%;   
        }
    }
    &__tips{
        padding: 5rem 5rem 1rem 5rem;
        @include for-tablet{
            margin: 0 -1.5rem 8rem;
            padding: 2rem 1.5rem; 
        }        
        @include for-phone{
            margin: 0 -1.5rem 4rem;
            padding: 2rem 1.5rem; 
        }
    }
}