.group-solutions {
    margin-bottom: 3rem;
    &__desc {
        font-family: $pfhandbookpro-thin;
        font-size: $font24;
        line-height: $line-height-26;
        color: $grp-solutions-desc;
        max-width: 73.6rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__item {
        font-family: $pfhandbookpro-thin;
        font-size: $font24;
        line-height: $line-height-26;
        color: $grp-solutions-item !important;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__list {
        max-width: 73.6rem;
        list-style-type: disc;
    }
}