#lang-topmenu-mobile {
  display: none;

  @include for-tablet {
    display: block;
  }
}

#lang-mobile-dropdown {
  position: absolute;
  top: 20%;

  background: #ffffff5e;
  border-radius: 2rem;
  padding: 0px 1rem;

  @include for-tablet {
    right: 14rem;
    top: 28px;
  }
  @include for-phone {
    right: 10.5rem;
  }
}
.lang-childmenu-mobile-dropdown {
  &::after {
    position: absolute;
    @include for-phone {
      right: 4.5%;
    }
    right: 10%;
    top: -10px;
    width: 0;
    height: 0;
    content: "";
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid #ffffff;
  }
}
.language {
  $self: &;

  &__icon {
    display: inline-block;
    vertical-align: middle;
    margin-right: 1rem;
    height: 1.6rem;
    transform: translateY(-3px);
    width: 1.6rem;
    transition: all 0.3s ease;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: transparent;
    &--english {
      background-image: url("../images/lang-english.png");
    }
    &--hindi {
      background-image: url("../images/lang-hindi.png");
    }
    &--telegu {
      background-image: url("../images/lang-telegu.png");
    }
    &--tamil {
      background-image: url("../images/lang-tamil.png");
    }
    &--kannada {
      background-image: url("../images/lang-kannada.png");
    }
    &__mobile {
      margin-left: unset !important;
    }
    @include for-phone {
      margin-left: 1.2rem;
    }
  }
  &__title {
    font-size: $font18;
    line-height: $line-height-18;
    color: $ls-text-clr;
    text-transform: capitalize;
    display: inline-block;
    vertical-align: middle;
    transition: all 0.3s ease;
  }

  &__selected {
    color: $white;
    @include for-phone {
      display: none;
    }
  }

  &__item {
    display: block;
    padding: 1rem 0.5rem;
    font-weight: 600 !important;

    &__selected {
      @include for-tablet {
        display: inline;
      }

      @include for-phone {
        display: inline-grid;
      }
    }
  }
}

.top-black {
  .language {
    &__icon {
      color: $ls-icon-clr !important;
    }
    .selectize-dropdown {
      .active {
        color: $white !important;
      }
    }
  }
}
