.accordian {
  $self: &;

  &__title {
    font-size: $font24;
    line-height: $line-height-26;
    color: $acc-title;
    font-family: $pfhandbookpro-regular;
    padding: 1.6rem 6.4rem 1.6rem 1.6rem;
    background: $acc-trigger-panel;
    position: relative;
    border-bottom: 1px solid transparent;
    cursor: pointer;
    transition: all 0.3s ease;

    #{$self}__icon {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 1.6rem;
    }

    @include for-phone {
      font-size: $font20;
      line-height: $line-height-22;
      padding: 2rem 6.4rem 2rem 1.6rem;
    }

    &:hover {
      color: $acc-title-hover;
    }
  }

  &__icon {
    font-size: $font20;
    color: $acc-icon;
    transition: all 0.3s ease;
  }

  &__desc {
    font-size: $font24;
    line-height: $line-height-26;
    color: $acc-content;
    font-family: $pfhandbookpro-thin;
    margin-bottom: 0;

    @include for-phone {
      font-size: $font18;
      line-height: $line-height-20;
    }
  }

  &__list {
    list-style-type: disc;
  }

  &__list-item {
    font-size: $font24;
    line-height: $line-height-26;
    color: $acc-content;
    font-family: $pfhandbookpro-thin;
    margin-bottom: 0;

    @include for-phone {
      font-size: $font18;
      line-height: $line-height-20;
    }
  }

  &__info {
    padding: 2rem 1.6rem;
    display: none;
  }

  &__item {
    border: 1px solid $acc-border;

    &--active {
      #{$self}__title {
        border-color: $acc-border;
      }
    }
  }

  &__wrap {
    padding-left: 0;
    border-radius: 2px;
    overflow: hidden;

    &--v1 {
      .accordian__item {
        display: none;

        &:nth-child(-n + 4) {
          display: block;
        }
      }
    }
  }

  &__view-all,
  &__view-less {
    padding-left: 0;
    margin-top: 2rem;
  }

  &--v2 {
    max-width: 92.8rem;
    margin: 0 auto;

    #{$self}__title {
      background: $acc-trigger-panel-2;
    }

    #{$self}__icon {
      font-size: $font14;
    }

    #{$self}__item {
      border: none;
      border-bottom: 1px solid $acc-trigger-panel-active;

      &:first-child {
        border-top: 1px solid $acc-trigger-panel-active;
      }

      &--active {
        #{$self}__title {
          background: $acc-trigger-panel-active;
          border-color: $acc-trigger-panel-active;
        }

        #{$self}__icon {
          transform: translateY(-70%) rotate(180deg);
        }
      }
    }

    #{$self}__wrap {
      border-radius: 0;

      @include for-phone {
        margin: 0 -15px;
        width: auto;
      }
    }

    .title-wrap {
      text-align: center;

      @include for-phone {
        text-align: left;
      }
    }
  }

  &--v3 {
    #{$self}__title {
      &:hover {
        color: $acc-title;
      }

      &--icon {
        font-size: $font20;
        transition: all 0.3s ease;
      }
    }

    #{$self}__item {
      margin: 8px 0;
      border: none;
    }

    #{$self}__item {
      &--active {
        #{$self}__title {
          background: $acc-title-active-bg;
          color: $acc-title-active;
        }
      }
    }
  }

  &--left {
    margin: 0;
  }

  &--contact {
    margin: 0;
    max-width: none;

    .title-wrap {
      text-align: left;
    }

    #{$self}__item {
      &:first-child {
        border-top: none;

        @include for-phone {
          border-top: 1px solid $acc-trigger-panel-active;
        }
      }
    }
  }
}

// on-page-editor

.on-page-editor {
    .accordian {
        &__wrap {
            & > div {
                width: calc(50% - 1.6rem);
                margin-bottom: 3.2rem;
            }
        }
        &__item {
            width: 100%;
        }
        &--v2 {
            .accordian__wrap {
                & > div {
                    width: 100%;
                    margin-bottom: 0;
                }
            }
        }
        &__wrap--v1 {
            & > div {
                width: 100%;
                margin-bottom: 0;
            }
        }
    }
}