//tax-list
.tax-list {
    @include for-phone {
        background: $itl-bg-clr;
        padding: 2rem 1.5rem;
        margin: 0 -1.5rem;
    }
    &__title {
        font-size: $font24;
        line-height: $line-height-26;
        color: $itl-text-clr;
        font-family: $pfhandbookpro-regular;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__value {
        font-size: $font32;
        line-height: $line-height-34;
        font-family: $pfhandbookpro-bold;
        color: $itl-highlight-clr;
        position: relative;
        padding-left: 1.5rem;
        margin-bottom: 0;
        .icon {
            font-size: $font12;
            color: $itl-highlight-clr;
            position: absolute;
            top: -4px;
            left: 0;
        }
    }
    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1.2rem 0;
        border-bottom: 1px solid $itl-border-clr;
        &:last-child {
            border-bottom: none;
        }
    }
}