//link-distribution
.link-distribution {
    &__head {
        padding: 1.7rem 4rem;
        border: 1px solid $ld-border-clr;
        box-shadow: 0 0 14px 0 rgba(106, 71, 66, 0.24);
        border-radius: 0.2rem;
        background: $ld-grad-top-clr;
        background: -moz-linear-gradient(top, $ld-grad-top-clr 0%, $ld-grad-bottom-clr 100%);
        background: -webkit-gradient(left top, left bottom, color-stop(0%, $ld-grad-top-clr), color-stop(100%, $ld-grad-bottom-clr));
        background: -webkit-linear-gradient(top, $ld-grad-top-clr 0%, $ld-grad-bottom-clr 100%);
        background: -o-linear-gradient(top, $ld-grad-top-clr 0%, $ld-grad-bottom-clr 100%);
        background: -ms-linear-gradient(top, $ld-grad-top-clr 0%, $ld-grad-bottom-clr 100%);
        background: linear-gradient(to bottom, $ld-grad-top-clr 0%, $ld-grad-bottom-clr 100%);
        filter: "progid:DXImageTransform.Microsoft.gradient( startColorstr="$ld-white-clr", endColorstr="$ld-icon-color", GradientType=0 )";
        display: flex;
        align-items: center;
        @include for-phone {
            padding: 1.7rem 2rem;
        }
    }
    &__icon-wrap {
        width: 6.4rem;
        height: 6.4rem;
        margin-right: 3.6rem;
        @include for-phone {
            margin-right: 2.5rem;
        }
    }
    &__icon {
        font-size: $font64;
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $ld-black-clr;
        font-family: $pfhandbookpro-regular;
        text-transform: capitalize;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__link-list {
        padding: 4rem 0 6rem;
        @include for-phone {
            padding: 1.4rem 0 3.4rem;
        }
        .three-col-list {
            &.link-list__container {
                ul {
                    li {
                        a {
                            @include for-phone {
                                font-size: $font16;
                                line-height: $line-height-22;
                                padding: 1.2rem 2rem 1.2rem 0;
                            }
                            &:after {
                                @include for-phone {
                                    font-size: $font16;
                                }
                            }
                        }
                    }
                }
            }
        }
        .link-list__list {
            li {
                a {
                    &:after {
                        top: 2.5rem;
                    }
                }
            }
        }
    }
}