.historical-nav-tabs {
    width: 100%;
    .tabs__anchor--v2 {
        min-width: 27rem;
        text-align: center;
        @include for-phone {
            min-width: 50vw;
        }
    }
    .tabs__links--v3 {
        max-width: 54.4rem;
        margin: 0 auto;
        @include for-tablet {
            max-width: 100%;
        }
    }
    .tabs__anchor--v3 {
        padding: 1rem 3.2rem;
    }
    .abc-form {
        max-width: 54rem;
        margin: 4rem auto 0;
        @include for-tablet {
            margin: 2rem auto 0;
        }
    }
    .table {
        max-width: 54rem;
        margin: 4rem auto 0;
        height: 211px;
        overflow-y: auto;
        @media (max-width: 767px){
            height: 199px;
        }
        &__head,
        &__body {
            .table__heading,
            .table__data {
                &:first-child {
                    @include for-phone {
                        width: 50%;
                    }
                }
            }
        }
    }
    .graph-list {
        .slick-slide {
            margin: 0 0.8rem;
        }
        @include for-tablet {
            margin: 0 -15px 0;
            padding-left: 15px;
            width: auto;
            max-width: none;
        }
    }
}