.blog-detail {
  .img-right {
    float: right;
    margin-left: 30px;
  }
  .img-left {
    float: left;
    margin-right: 30px;
  }

  p {
    margin-bottom: 50px;
  }

  .author-tag {
    font-size: 1.8rem;
    line-height: 16px;
    font-family: $pfhandbookpro-bold;
    color: $capital-red;
    margin-bottom: 13px;
  }
}

@include mQ($screen-sm) {
  .blog-detail {
    margin-bottom: 20px;

    p {
      margin-bottom: 20px;
    }

    .author-tag {
      font-size: 1.6rem;
      line-height: 16px;
    }
  }
}

@include mQ($screen-xs) {
  .blog-detail {
    .img-right,
    .img-left {
      float: none;
      margin-left: 0;
    }
  }
}
