//nav-calc
.nav-calc{
    $this:&;
    padding: 3rem;
    @include for-tablet{
        padding: 3rem 1.5rem;
    }
    @include for-phone{
        margin: 0 -1.5rem;
        padding: 2rem 1.5rem;
    }
    &__item-wrap{
        padding: 0;
        margin: 0;
    }
    &__label{
        font-size: $font20;
        line-height: $line-height-22;
        color: $nc-label-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 0.5rem;
        display: block;
        @include for-phone{
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__value{
        font-size: $font18;
        line-height: $line-height-20;
        color: $nc-count-clr;
        font-family: $pfhandbookpro-thin;
        position: relative;
        display: inline-block;
        @include for-phone{
            font-size: $font16;
            line-height: $line-height-18;
        }
        #{$this}__sup{
            position: absolute;
            top: 0.6rem;
            right: -1.7rem;
        }
    }
    &__count{
        font-size: $font44;
        line-height: $line-height-46;
        color: $nc-count-clr;
        font-family: $pfhandbookpro-bold;
        display: block;
        @include for-phone{
            font-size: $font32;
            line-height: $line-height-34;
        }
    }
    &__sup{
        font-size: $font20;
        line-height: $line-height-22;
        color: $nc-count-clr;
        font-family: $pfhandbookpro-regular;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__item{
        display: inline-block;
        vertical-align: top;
        margin-right: 10rem;
        @include for-tablet{
            margin-right: 4rem;
        }
        &:last-child{
            margin-right: 0;
        }
        
    }
}