.content-block {
  &--center {
    width: 66.6667%;
    margin: 0 auto;
    @include for-tablet {
      width: 100%;
    }
  }
  * {
    font-family: $pfencoresanspro-reg;
  }
  &--snippet {
    * {
      font-size: $font18;
      line-height: $line-height-24;
    }
    ol,
    ul,
    li {
      list-style: none;
      margin: 0;
      padding: 0;
      border: 0;
      outline: 0;
    }
    ul {
      list-style: none;
      margin-bottom: 28px;
      padding-left: 0;
      li {
        &:before {
          content: "";
          display: none;
          color: $black !important;
        }
      }
    }
    * {
      a {
        all: unset;
        color: $capital-red !important;
        & * {
          all: unset;
        }
        &:hover {
          text-decoration: underline;
          cursor: pointer;
        }
      }
      color: $black !important;
    }
    p {
      margin-bottom: 16px;
    }
  }
  &--left-img {
    float: left;
    margin: 0 10px 0 0;
    @include for-phone() {
      float: none;
      margin: 0 0 10px 0;
    }
  }
  &--right-img {
    float: right;
    margin: 0 0 10px 0;
    @include for-phone() {
      float: none;
      margin: 0 0 10px 0;
    }
  }
}

.ticklistitem {
  padding-left: 0;
  li {
    position: relative;
    padding-left: 40px;
    margin: 8px 0;
    padding-top: 0;
    padding-bottom: 0;
    &:before {
      content: "";
      border: solid #C7222A;
      border-width: 0 2px 2px 0;
      display: block !important;
      position: absolute;
      left: 22px;
      top: 1px;
      margin-top: 0;
      transform: rotate(45deg);
      width: 10px;
      height: 17px;
      }
  }
}