.start-journey {
    padding: 3rem 1.5rem;
    @include for-phone {
        padding: 3rem 0;
    }
    .qust-paging {
        text-align: center;
        font-size: $font24;
        line-height: $line-height-26;
        padding-top: 1.8rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-22;
            padding-top: 1rem;
        }
    }
}

.qust-series {
    margin-bottom: 0;
    width: calc(100% + 30px);
    padding: 0 15px;
    margin-left: -15px;
    overflow: hidden;
    &__card {
        background: $qust-card-bg;
        border-radius: 2px;
        border: solid 1px $qust-card-border;
        padding: 2.4rem;
        @include for-phone {
            padding: 2rem 1.6rem;
        }
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-1;
        margin-bottom: 2rem;
        @include for-phone {
            font-size: $font24;
        }
    }
    .abc-form--qust {
        .btn-wrap {
            text-align: right;
            padding-top: 2.4rem;
            @include for-phone {
                padding-top: 1.2rem;
            }
        }
    }
}

.qust-series {
    .slick-slide {
        &.slick-active {
            box-shadow: 0 0 14px 0 rgba($qust-card-shadow, 0.24);
        }
    }
}