//author
.author{
    &__title{
        font-size: $font20;
        line-height: $line-height-22;
        font-family: $pfhandbookpro-regular;
        color:$author-text-color;
        text-transform: capitalize;
        margin-bottom: 2rem;
    }
    &__info{
        display: block;
    }
    &__img-wrap,&__info-wrap{
        display: inline-block;
        vertical-align: middle;
    }
    &__img-wrap{
        width: 6.4rem;
        height: 6.4rem;
        overflow: hidden;
        margin-right: 2rem;
        @include for-phone{
            width: 5rem;
            height: 5rem;
            margin-right: 1.2rem;
        }
    }
    &__img{
        width: 100%;
    }
    &__name{
        font-size: $font20;
        line-height: $line-height-22;
        font-family: $pfhandbookpro-regular;
        color:$author-text-color;
        text-transform: capitalize;
        display: block;
        @include for-phone{
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__designation{
        font-size: $font18;
        line-height: $line-height-20;
        font-family: $pfhandbookpro-regular;
        color:$author-text-color;
        display: block;
        @include for-phone{
            font-size: $font12;
            line-height: $line-height-14;
        }
    }
}