.author-component {
  margin-bottom: 20px;
  float: left;
  width: 100%;
  border-radius: 3.4px;
  -webkit-box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.13);
  box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.13);
  background-color: $white;
  padding: 6px;
  &.top-margin {
    margin-top: 75px;
  }
}

.author-item {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 24px;
  &:last-child {
    margin-bottom: 0;
  }
}

.author-image {
  img {
    max-width: 100px;
    margin-bottom: 0;
  }
}

.author-title {
  font-family: $pfhandbookpro-bold;
  font-size: 2rem;
  line-height: 23px;
  color: $black;
}

.author-designation {
  font-size: 1.4rem;
  line-height: 20px;
  color: $black;
}

.author-content-wrapper {
  padding-left: 20px;
  padding-right: 60px;
  @include mQ(1200px) {
    padding-right: 30px;
  }
}

.author-content {
  font-size: 1.2rem;
  line-height: 18px;
  color: $black;
  display: none;
}

.author-click {
  cursor: pointer;
  width: 16px;
  height: 16px;
  border: 1px solid $black;
  border-radius: 100%;
  display: block;
  position: absolute;
  right: 37px;
  top: 50%;
  transform: translateY(-50%);
  &:after {
    content: "+";
    position: absolute;
    top: -5px;
    left: 1.5px;
    font-size: 2.1rem;
    @include mQ(1200px) {
      left: 2px;
    }
  }
  &.active {
    &:after {
      content: "-";
      left: 3px;
      @include mQ(1200px) {
        left: 4px;
      }
    }
  }
}
