.image {
  position: relative;
  img {
    margin-bottom: 0;
  }
  &__title {
    position: absolute;
    top: 16px;
    left: 16px;
    float: left;
    opacity: 0.9;
    width: 65%;
    color: $image-tile-brand-title-color;
    padding: 8px;
    font-size: $font32;
    line-height: $line-height-32;
    @include for-tablet {
      font-size: $font24;
      line-height: $line-height-24;
    }

    &--link {
      padding: 0;
      span {
        display: block;
      }
      .brand-name {
        margin: 8px;
        @include for-tablet {
          font-size: $font24;
          line-height: $line-height-24;
        }
        @include for-phone {
          margin: 16px;
          font-size: $font28;
          line-height: $line-height-32;
        }
      }
      .click {
        font-size: $font18;
        line-height: $line-height-24;
        margin: 16px 16px 8px 8px;
        position: relative;
        text-transform: uppercase;
        @include for-phone {
          font-size: $font20;
          line-height: $line-height-24;
          margin: 24px 16px 16px 16px;
        }
        &::after {
          content: $icon-right-arrow;
          font-family: "font-icons";
          color: $image-tile-click-icon-color;
          font-style: normal;
          line-height: 1;
          font-size: $font12;
          position: absolute;
          right: 0;
          top: 50%;
          -ms-transform: translateY(-50%);
          transform: translateY(-50%);
        }
      }
    }
  }
  &__link-container {
    a {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      &:hover {
        border: none;
        text-decoration: none;
      }
    }
  }
}
