//calc-result
.calc-result {
    $this: &;
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $cr-black-clr;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
            margin-bottom: 1.2rem;
        }
    }
    &__highlight-count {
        font-size: $font44;
        line-height: $line-height-46;
        color: $cr-light-brown-clr;
        font-family: $pfhandbookpro-bold;
        position: relative;
        display: block;
        padding-left: 2rem;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-34;
        }
        #{$this}__rupee {
            position: absolute;
            top: -0.3rem;
            left: 0;
        }
    }
    &__rupee {
        font-size: $font20;
        color: $cr-light-brown-clr;
        @include for-phone {
            font-size: $font14;
        }
    }
    &__p {
        font-size: $font24;
        line-height: $line-height-26;
        color: $cr-black-clr;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__strong {
        font-size: $font24;
        line-height: $line-height-26;
        color: $cr-black-clr;
        font-family: $pfhandbookpro-regular;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
        .icon {
            font-size: $font14;
            color: $cr-black-clr;
            @include for-phone {
                font-size: $font10;
            }
        }
    }
    &__info-wrap {
        border: 1px solid $cr-border-clr;
        display: flex;
        margin-bottom: 2rem;
        @include for-phone {
            display: block;
            margin: 0 -1.5rem;
            margin-bottom: 1.8rem;
            min-height: auto;
        }
    }
    &__lhs {
        max-width: 38.3rem;
        padding: 2rem 4rem 2rem 2rem;
        @include for-md-desktop {
            max-width: 33.3rem;
            padding: 2rem;
        }
        @include for-tablet {
            max-width: 29.3rem;
            padding: 2rem 2rem 2rem 2rem;
        }
        @include for-phone {
            max-width: inherit;
            padding: 1.5rem;
        }
    }
    &__rhs {
        width: 100%;
        display: flex;
        align-items: center;
        padding: 2rem 2rem 2rem 4rem;
        @include for-md-desktop {
            padding: 2rem;
        }
        @include for-tablet {
            padding: 2rem;
            display: block;
        }
        @include for-phone {
            padding: 1.5rem;
            display: flex;
        }
        &--retirement {
            @include for-phone {
                padding-bottom: 10rem;
                position: relative;
            }
            #{$this}__input-list {
                @include for-tablet {
                    left: 2rem;
                    right: 2rem;
                }
                @include for-phone {
                    position: absolute;
                    left: 1.5rem;
                    right: 1.5rem;
                    bottom: 0;
                }
            }
        }
    }
  &__graph-wrap {
    width: 29.3rem;
    margin-right: 1.5rem;
    @include for-tablet {
      margin: 0 auto 2rem;
    }
    @include for-phone {
      width: 16.6rem;
      margin-right: 2rem;
    }
  }
  &__graph-info {
    @include for-phone {
      width: 80%;
    }
  }
  &__action-wrap {
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include for-phone {
      flex-flow: row wrap;
    }
  }
  &__act-btn {
    margin-left: 4rem;
    &:first-child {
      margin-left: 0;
    }
    .button-link {
      padding: 0;
    }
  }
  &__row {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &:first-child {
      margin-right: 4rem;
      @include for-phone {
        margin-right: 0;
        margin-bottom: 2rem;
      }
    }
    &:last-child {
      #{$this}__act-btn {
        @include for-phone {
          margin-left: 6rem;
        }
        @include for-small-phone {
          margin-left: 2.5rem;
        }
      }
    }
  }
  &__icon {
    font-size: $font16;
  }
  &__cont {
    padding: 2rem 0 3rem 0;
    border-bottom: 1px solid $cr-border-light-clr;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }
    @include for-phone {
      padding: 1.5rem 0 0.5rem 0;
    }
  }
  &__input-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
  &__input-list-item {
    width: 45%;
    margin-bottom: 0.8rem;
    &--full {
      width: 100%;
      @include for-phone {
        width: 30%;
      }
    }
    @include for-phone {
      width: 30%;
      margin-bottom: 1.5rem;
    }
  }
  &__input-label {
    font-size: $font24;
    line-height: $line-height-26;
    color: $cr-black-clr;
    font-family: $pfhandbookpro-thin;
    margin-bottom: 1rem;
    @include for-phone {
      font-size: $font18;
      line-height: $line-height-20;
    }
  }
  &__input-value {
    font-size: $font32;
    line-height: $line-height-34;
    color: $cr-black-clr;
    font-family: $pfhandbookpro-regular;
    @include for-phone {
      font-size: $font22;
      line-height: $line-height-24;
    }
    &--text {
      position: relative;
      padding-left: 2rem;
      .icon {
        font-size: $font16;
        position: absolute;
        left: 0;
        @include for-small-phone {
          font-size: $font13;
        }
      }
    }
  }
  &__input {
    background: transparent;
    border: none;
    height: 4rem;
    // border-bottom: 1px solid $cr-border-clr;
  }
  &__input-group {
    position: relative;
    max-width: 17rem;
    #{$this}__input {
      padding-left: 2rem;
      width: 100%;
      @include for-small-phone {
        padding-left: 1.5rem;
      }
    }
    #{$this}__input-addon {
      font-size: $font16;
      position: absolute;
      bottom: 10%;
      left: 0;
      @include for-phone {
        font-size: $font13;
        bottom: 25%;
      }
    }
    #{$this}__input-addon-text {
      font-size: $font16;
      position: absolute;
      bottom: 25%;
      right: 25%;
      text-transform: capitalize;
    	color: $cr-black-clr;
    	font-family: $pfhandbookpro-thin;

      @include for-phone {
        font-size: $font13;
        bottom: 40%;
      }
    }
    &--sm-input {
      #{$this}__input {
        max-width: 10rem;
        @include for-phone {
          max-width: 6rem;
        }
      }
    }
  }
    &__progress-wrap {
        margin-top: 2rem;
    }
    &--income {
        position: relative;
        #{$this}__rhs,
        #{$this}__lhs {
            width: 100%;
        }
        #{$this}__info-wrap {
            @include for-tablet {
                display: block;
            }
        }
        #{$this}__rhs {
            border-left: 1px solid $cr-border-clr;
            @include for-phone {
                padding-bottom: 0;
            }
        }
        #{$this}__lhs {
            @include for-tablet {
                max-width: initial;
                width: 100%;
            }
        }
        .banner-ad-wrap {
            position: absolute;
            top: 0;
            right: 0;
            @include for-md-desktop {
                display: none;
            }
        }
        #{$this}__parent-cont {
            width: 75%;
            @include for-md-desktop {
                width: 100%;
            }
        }
        #{$this}__tabs-link {
            padding: 1.5rem 7.6rem;
            @include for-phone {
                padding: 1.5rem 3.5rem;
            }
        }
        .tabs__content {
            @include for-phone {
                padding-bottom: 0;
            }
        }
        .tabs__links--v3 {
            @include for-phone {
                margin-bottom: 2rem;
                justify-content: space-around;
            }
        }
    }
}