.hero-container {
    padding: 0 24px;

    .hero-banner {
        display: flex;
        align-items: flex-end;
    }

    .hero-text-block {
        text-align: center;

        span {
            font-weight: bold;
            letter-spacing: 5px;
        }
    }
}
.hero-card {
  width: 75%;
  background: $grey-002;
  position: relative;
  top: -50px;
  display: flex;
  margin: -35px auto;
  box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.16);

  &__bg {
    background: $financing-pellete-01;
    display: flex;
    margin: auto;
  }

  &__item {
    justify-content: space-evenly;
    align-items: center;
    padding: 10px;

    &:last-child {
      border-right: none;
    }

    &--wrapper {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;

      .hero-card__item--value {
        font-family: $pfhandbookpro-bold;
        font-size: $font50;
        color: $capital-red;
        line-height: 1.2;
      }

      .hero-card__item--detail {
        text-transform: uppercase;
        font-family: $pfhandbookpro-bold;
      }
    }

    &--text {


      p {
        padding: 10px;
        margin: 30px;
        letter-spacing: 2px;
        color: $black;
        font-size: $font20;
        font-weight: bold;
      }
    }
  }
}




.hfl {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
  margin: auto !important;
  width: 90% !important;
  align-items: flex-start;


  @include for-tablet {
    width: 100% !important;
  }

  @include for-phone {
    width: 100% !important;
  }

  .tabs {



    &__content--active {
      border: 1px solid $financing-pellete-01;
      padding: 15px;
      box-shadow: 0 2px 4px 0 rgba($financing-pellete-01, 0.50);
    }

    &__contents--vertical {
      max-width: 860px;
    }

    &__anchor {
      &--v3 {
        color: $financing-shade;
        background-color: $grey-002;

        &:hover,
        &:focus,
        &:active {
          color: $financing-shade;
        }

        .txt {
          font-size: $font15;
          display: flex;
          margin-top: -13px;
          padding-left: 35px;
        }

        .icon {
          display: inline;
          font-size: $font30;
          vertical-align: middle;
        }

        .icon-next {
          display: inline;
          font-size: $font12;
          float: right;
          vertical-align: middle;
        }
      }
    }

    &__link {
      &--active {
        .tabs__anchor--v3 {
          background-color: $financing-pellete-01;
          color: $financing-shade;
        }
      }
    }

    &__links {
      &--droplist {
        background: $financing-shade;
        border: 1px solid $financing-shade;
        box-shadow: 0 2px 4px 0 rgba($financing-shade, 0.25);
        min-height: 360px;

        .tabs__link {
          color: $financing-shade;
          border-bottom: 1px solid $financing-shade;

          &--active {
            .tabs__anchor {
              background: transparent;
              color: $financing-shade;

              &:hover {
                color: $financing-shade;
              }
            }
          }
        }

        .tabs__anchor {
          background: transparent;
          color: $financing-shade;

          &--active,
          &:hover {
            color: $financing-shade;
            background: $filter-drop-item-active;
          }

          &:active,
          &:focus-within {
            color: $financing-shade;
            background: $filter-drop-item-active;
          }
        }
      }
    }

    @include for-tablet {
      &--vertical {
        width: 100% !important;
      }
    }

    @include for-phone {
      &--vertical {
        width: 100% !important;
      }
    }
  }
}
