//assets-allocation
.assets-allocation {
    &__wrap {
        padding: 0;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @include for-tablet {
            flex-flow: row wrap;
        }
        @include for-phone {
            display: block;
            align-items: initial;
            justify-content: initial;
        }
    }
    &__item-name {
        font-size: $font24;
        line-height: $line-height-26;
        color: $aa-label-clr;
        display: block;
        margin-bottom: 0.5rem;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-22;
            margin-bottom: 1rem;
        }
    }
    &__item {
        @include for-md-desktop {
            width: 32%;
        }
        @include for-tablet {
            width: 48%;
            margin-bottom: 2rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
        @include for-phone {
            margin-bottom: 2rem;
            width: 100%;
        }
        &:last-child {
            @include for-phone {
                margin-bottom: 0;
            }
        }
    }
}