.share-component {
  display: inline-block;
  margin-left: 18px;
  position: relative;
  .share-wrapper {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    .icon {
      font-size: 1.4rem;
      color: #000000;
      line-height: 1;
      margin: 0;
      &:before {
        top: 1px;
        position: relative;
      }
      @include mQ(1200px) {
        font-size: 1.2rem;
      }
      @include mQ(1020px) {
        font-size: 1rem;
      }
    }
  }
  .count-wrapper {
    display: flex;
    font-size: 1.4rem;
    color: #000000;
    margin: 0 5px;
    line-height: 16px;
    letter-spacing: -0.32px;
    span {
      margin-left: 2px;
    }
    @include mQ(1200px) {
      font-size: 1.2rem;
    }
    @include mQ(1020px) {
      font-size: 1rem;
      margin-left: 2px;
    }
  }
  a {
    font-family: $pfhandbookpro-light;
    border-bottom: 1px solid transparent !important;
  }
  
  .share-content {
    
    position: absolute;
    width: 200px;
    right: 0px;
    top: 20px;
    display: none;
    @include mQ(1200px) {
      width: 200px;
      right: -75px;
      top: 25px;
    }
  }
  .st-btn {
    //margin-bottom: 10px;
    display: inline-block !important;
    height: 28px !important;
    margin-right: 5px !important;
    padding: 0 6px !important;
    border-radius: 0px !important;
    &:last-child {
      margin-right: 0px !important;
    }
    > img {
      top: 6px !important;
    }
    @include mQ(1200px) {
      height: 30px !important;
      line-height: 20px !important;
      margin-right: 2px !important;
      padding: 0 5px !important;

      > img {
        height: 30px !important;
        width: 25px !important;
        top: 0px !important;
      }
    }
  }
  .st-total {
    display: none !important;
  }
}
