.like-shares {
  .share-component {
    margin-left: 8px;
    @include mQ(1200px) {
      margin-left: 5px;
    }
    @include mQ(767px) {
      margin-left: 0px;
    }
  }
  @include mQ(1020px) {
    margin-top: 5px;
  }
}

.trending-widget-banner-wrapper .share-component .share-content {
  @include for-phone() {
    right: -29px !important;
    z-index: 9;
  }
}

.trending-item .share-component .share-content {
  @include for-phone() {
    right: -45px;
    z-index: 9;
  }
}

.share-component .share-content-inline{
	position:absolute;
	width:200px;
	left:-15px;
	top:-4px;
	display:none
}
@media screen and (max-width:1200px){
	.share-component .share-content-inline{
		width:200px;
		left:0!important;
		top:-9px
	}
}