.text-tile {
  display: flex;
  flex-wrap: wrap;
  // margin-right: -15px;
  // margin-left: -15px;
  @include for-tablet {
    margin: 0;
  }

  &__container {
    padding: 32px 24px;
    background: $text-tile-background;
    height: 280px;
    position: relative;
    &--hasLink {
      &:hover .text-tile__title {
        color: $text-tile-title-hover;
      }
      &:hover .text-tile__title--link {
        &:after {
          color: $text-tile-icon-hover;
        }
      }
      &:hover .text-tile__icon {
        color: $text-tile-icon-hover;
      }
      &:hover .text-tile__link-text {
        color: $text-tile-link-text-hover;
      }
      @include for-tablet {
        &:hover .text-tile__title {
          color: $text-tile-title;
        }
        &:hover .text-tile__title--link {
          &:after {
            color: $text-tile-icon;
          }
        }
        &:hover .text-tile__icon {
          color: $text-tile-icon;
        }
        &:hover .text-tile__link-text {
          color: $text-tile-link-text;
        }
      }
    }
  }

  &__item {
    position: relative;
    width: 100%;
    // padding-right: 15px;
    // padding-left: 15px;
    height: 100%;
    @include for-phone {
      padding: 0;
    }
  }
  &__title {
    font-size: $font32;
    line-height: $line-height-36;
  }
  &__desc {
    color: $text-tile-desc;
    font-size: $font18;
    line-height: $line-height-24;
    font-family: $pfencoresanspro-reg;
    padding: 16px 0 30px;
  }
  &__link {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: block;
    &:hover,
    &:active,
    &:active,
    &:focus {
      border: none;
    }
  }
  &__icon {
    font-size: $text-tile-icon-size;
    color: $text-tile-icon;
    position: absolute;
    right: 24px;
    bottom: 23px;
  }
  &__link-text {
    position: absolute;
    bottom: 29px;
    right: 52px;
    font-size: $font22;
    line-height: $line-height-25;
    $link-text-color: $text-tile-link-text;
  }

  &--container {
    width: 100%;
    // padding-right: 15px;
    // padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    max-width: 1400px;
    position: relative;
    // padding: 40px 0;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;
      width: 100vw;
      background: $text-tile-container-background;
    }
    &--invert {
      &::before {
        background: $text-tile-container-background-invert;
      }
      .text-tile__container {
        background: $text-tile-container-background;
      }
    }
  }
  &--center {
    .owl-stage {
      margin: 0 auto;
    }
  }
  //Owl Carousel
  .owl-dots .owl-dot span {
    background: $text-tile-owl-carousel;
    width: 40px;
    height: 4px;
    margin: 0;
    border-radius: 0;
  }
  .owl-dots .owl-dot {
    &:focus {
      outline: none;
    }
  }
  .owl-dots .owl-dot.active span,
  .owl-dots .owl-dot:hover span {
    background: $text-tile-owl-carousel-active;
  }
}

//space between components

.outerpadding {
  .text-tile:nth-child(n + 1) {
    margin-bottom: 30px;
  }
  .text-tile:last-child {
    margin-bottom: 0;
  }
}
