.calc-stepper {
    position: relative;
    margin: 1.6rem auto 3rem;
    height: 6rem;
    padding: 0 10rem;
    @include for-phone {
        padding: 0;
        background-color: transparent !important;
    }
    &:after {
        content: "";
        background: rgba($calc-stepper-bg, 1);
        background: linear-gradient( 90deg, rgba($calc-stepper-bg, 1) 0%, rgba($calc-stepper-bg, 0) 45%, rgba($calc-stepper-bg, 0) 55%, rgba($calc-stepper-bg, 1) 100%);
        position: absolute;
        left: 10rem;
        right: 10rem;
        top: 0;
        bottom: 0;
        z-index: 2;
        pointer-events: none;
        @include for-phone {
            left: 0;
            right: 0;
            background: transparent;
        }
    }
    .swiper-wrapper {
        height: auto;
    }
    .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 4rem;
        height: 6rem;
        font-size: $font30;
        line-height: $line-height-60;
        text-align: center;
        color: $calc-stepper-clr;
        cursor: grab;
        @include for-phone {
            font-size: $font24;
        }
        &-active {
            color: $calc-stepper-active;
            font-family: $pfhandbookpro-bold;
            font-size: $font44;
            @include for-phone {
                font-size: $font32;
            }
        }
    }
    &__frame {
        width: 4rem;
        height: 4.5rem;
        border-bottom: 1px solid $calc-stepper-underline;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        cursor: text;
    }
    &__label {
        position: absolute;
        white-space: nowrap;
        text-align: center;
        left: 50%;
        bottom: -3.8rem;
        transform: translateX(-50%);
        font-size: $font18;
        line-height: $line-height-20;
        color: $calc-stepper-lbl;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__input {
        position: absolute;
        font-size: $font30;
        width: 4rem;
        left: 5rem;
        line-height: $line-height-50;
        transform: translate(-50%, -50%);
        top: 50%;
        text-align: center;
        z-index: 2;
        opacity: 0;
    }
}