.breadcrumb {
    margin-bottom: 22px;
    padding: 0;
    background-color: transparent;
    a {
        font-size: 1.9rem;
        line-height: 16px;
        color: $black;
        border-bottom-color: $black;
        margin-left: 4px;

        &:first-child {
            margin-left: 0;
        }
        &:hover, &:active, &:focus {
            border-bottom-color: $black;
            color:$black;
        }
    }
    span {
        font-family: $pfhandbookpro-bold;
        font-size: 1.9rem;
        line-height: 16px;
        color: $black;
        border-bottom: 1px solid transparent;
        margin-right: 0px;
        margin-left: 4px;
    }

    @include mQ(1020px){
        margin-bottom: 12px;
    }
    @include mQ(767px){
        margin-bottom: 14px;
    }
}