.qsform {
  &__tlSuccess {
    margin-top: 0;
  }
  &__lsDetailsContainer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 0 20px;
  }
  &__lsLabel {
    font-size: $font16;
    line-height: $line-height-26;
    font-family: $pfhandbookpro-bold;
  }
  &__lsDownload {
    font-size: $font14;
    line-height: $line-height-20;
    &:hover,
    &:focus,
    &:active {
      border: none;
      text-decoration: none;
    }
    @include for-phone {
      font-size: $font20;
    }
  }
}
//Modal
.qsmodal {
  &__lsHeader {
    margin-bottom: 0;
    padding-bottom: 10px;
  }
}
