.dashboard__main-content-wrapper {
  counter-reset: section;
}

.mac {
  .listsummary-main-wrapper {
    .listsummary-head {

      .listsummary-head-title,
      .listsummary-head-title-static {
        .listsummary-head-title_arrow {
          span {
            &:before {
              line-height: 16px;
              @media screen and (min-width: $custom-screen-max-width) {
                line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
              }
              @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                line-height: 1.6vw;
              }
            }
          }
        }
      }
    }
  }
}

.listsummary-main-wrapper {
  box-shadow: 2px 6px 12px 0 rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
  border-radius: 2px;
  display: flow-root;
  margin-bottom: 16px;

  @include fontScaling(border-radius,0.2);
  @include fontScaling(margin-bottom,1.6);

  @media screen and (min-width: $custom-screen-max-width) {
      box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.2rem) 0 rgba(0, 0, 0, 0.08);
  }
  @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      box-shadow: 0.2vw 0.6vw 1.2vw 0 rgba(0, 0, 0, 0.08);
  }

  .listsummary-head {
    .listsummary-head-title {
      display: flex;
      align-items: center;
      width: 100%;
      font-family: $pfhandbookpro-medium;
      color: $brand-black;
      margin: 0px;
      padding: 6px 12px 6px 17px;
      cursor: pointer;
      min-height: 43px;

      @media screen and (min-width: $custom-screen-max-width) {
        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.7rem);
        min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.3rem);
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        padding: 0.6vw 1.2vw 0.6vw 1.7vw;
        min-height: 4.3vw;
      }

      @include mQ(767px, 576px) {
        padding: 18px 12px 17px 17px;
      }

      .listsummary-head-title_arrow {
        margin-left: auto;
      }

      .listsummary-head-title_left {
        font-size: 1.8rem;
        line-height: 20px;
        width: calc(50% - 10px);

        @include fontScaling(font-size,1.8);
        @include fontScaling(line-height,2.0);

        @media screen and (min-width: $custom-screen-max-width) {  
          width: calc(50% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}));
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          width: calc(50% - 1.0vw);
        }

        @include mQ(575px) {
          margin-right: 20px;
        }

        p {
          @include mQ(767px, 576px) {
            font-size: 2.4rem;
          }

          &::before {
            counter-increment: section;
            content: counter(section, decimal-leading-zero) " ";
          }
        }

        @include mQ(767px, 576px) {
          font-size: 2.4rem;
        }

        @include mQ(575px) {
          font-size: 1.5rem;
        }
      }

      .listsummary-head-title_right {
        width: calc(50% - 10px);
        text-align: right;
        padding-right: 15px;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;

        @media screen and (min-width: $custom-screen-max-width) {  
          width: calc(50% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}));
          padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15});
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          width: calc(50% - 1.0vw);
          padding-right: 1.5vw;
        }

        @include mQ(767px, 576px) {
          .btn-orange {
            margin-top: -12px;
            margin-bottom: -11px;
          }
        }

        p {
          span {
            color: $raven-gray;

            &.active {
              color: $brand-capital-red;
            }
          }

          @include mQ(575px) {
            white-space: nowrap;
          }
        }

        .hide-mob {
          @include mQ(767px) {
            display: none !important;
          }
        }

      }

      .listsummary-head-title_arrow {
        span {
          cursor: pointer;
          display: block;
          width: 20px;
          height: 20px;
          line-height: 20px;
          text-align: center;
          background-color: $brand-white;
          float: left;
          border-radius: 50%;
          font-size: $font20;
          margin: 0px;

          @media screen and (min-width: $custom-screen-max-width) {
              font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
              width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
              height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
              line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
          }
          @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
              font-size: 2.0vw;
              width: 2.0vw;
              height: 2.0vw;
              line-height: 2.0vw;
          }

          &:before {
            line-height: 11px;
            font-size: 24px;

            @include fontScaling(font-size,2.4);
            @include fontScaling(line-height,1.1);

            @include for-phone {
              line-height: 15px;
            }

            @include themify($themes) {
              color: themed('textColorForLeftMainTitle');
            }
          }

        }
      }

      p {
        margin: 0px;
        color: $brand-black;
      }
    }

    .listsummary-head-title-static {
      display: flex;
      align-items: center;
      width: 100%;
      font-family: $pfhandbookpro-medium;
      color: $brand-black;
      margin: 0px;
      padding: 6px 12px 6px 17px;
      cursor: pointer;
      min-height: 36px;

      @media screen and (min-width: $custom-screen-max-width) {
        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font6}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font6}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font17});
        min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.6rem);
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        padding: 0.6vw 1.2vw 0.6vw 1.7vw;
        min-height: 3.6vw;
      } 

      .listsummary-head-title_left {
        width: calc(50% - 10px);

        @media screen and (min-width: $custom-screen-max-width) {
          width: calc(50% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}));
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
          line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          width: calc(50% - 1.0vw);
          font-size: 1.8vw;
          line-height: 2.0vw;
        } 

        @include mQ(575px) {
          margin-right: 40px;
        }

        p {
          &::before {
            counter-increment: section;
            content: counter(section, decimal-leading-zero) " ";
          }
        }

        @include mQ(767px, 576px) {
          font-size: 2.4rem;
        }

        @include mQ(575px) {
          font-size: 1.5rem;
        }
      }

      .listsummary-head-title_right {
        width: calc(50% + 10px);
        text-align: right;
        padding-right: 0px;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;

        @media screen and (min-width: $custom-screen-max-width) {
          width: calc(50% + calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}));
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          width: calc(50% + 1.0vw);
        } 

        p {
          span {
            color: $raven-gray;

            &.active {
              color: $brand-capital-red;
            }
          }

          @include mQ(575px) {
            white-space: nowrap;
          }
        }

        .hide-mob {
          @include mQ(767px) {
            display: none !important;
          }
        }
      }

      .listsummary-head-title_arrow {
        display: none;
      }

      p {
        margin: 0px;
        color: $brand-black;
      }

      &.listsummary-head-title-static-custom {
        @include mQ(767px, 576px) {
          padding: 26px 12px 26px 17px;
        }
      }
    }

    .listsummary-head-content {
      font-family: $pfhandbookpro-medium;
      color: #000;
      margin: 0px;

      @include for-phone {
        border: none;
      }

      ul {
        list-style: none;
        padding: 0px;
        display: -ms-flexbox;
        display: flex;
        width: 100%;
        margin: 0px;
        flex-wrap: wrap;
        
        @include for-phone {
          display: inline-block;
        }

        li {
          -ms-flex-preferred-size: 100%;
          flex-basis: 100%;
          padding: 12px 26px 12px 26px;
          color: $brand-black !important;
          padding-right: 0px;
          max-width: 33%;

          @include fontScaling(padding-top,1.2);
          @include fontScaling(padding-right,2.6);
          @include fontScaling(padding-bottom,1.2);
          @include fontScaling(padding-left,2.6);

          
          @include for-phone {
            width: 50%;
            display: block;
            float: left;
            padding-left: 26px !important;
          }

          span {
            font-size: $font14;
            font-family: $pfhandbookpro-light;
            line-height: 1.15;

            @include fontScaling(font-size,1.4);

            @include mQ(767px, 576px) {
              font-size: 2.0rem;
            }

            @include mQ(575px) {
              font-size: 1.3rem;
            }
          }

          p {
            margin-bottom: 0px;
            font-size: $font26;
            color: $brand-black;
            font-family: $pfhandbookpro-medium;
            line-height: 1.3;
            word-break: break-word;
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
              word-break: break-all;
            }

            @include fontScaling(font-size,2.6);

            @include mQ(767px, 576px) {
              font-size: 3.0rem;
            }

            @include mQ(575px) {
              font-size: 1.9rem !important;
            }
          }
          span.pascal-case {
            margin-bottom: 0px;
            font-size: $font26 !important;
            color: $brand-black;
            font-family: $pfhandbookpro-medium !important;
            line-height: 1.3 !important;
            word-break: break-word;
            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
              word-break: break-all;
            }

            @include fontScaling(font-size,2.6,!important);

            @include mQ(767px, 576px) {
              font-size: 3.0rem;
            }

            @include mQ(575px) {
              font-size: 1.9rem !important;
            }
          }
        }

        li:nth-child(2) {
          text-align: left;
          padding-left: 70px;
          @include fontScaling(padding-left,7.0);
        }

        li:last-child {
          text-align: left;
          padding-left: 100px;
          @include fontScaling(padding-left,10.0);
        }
      }

      .no-wrap-list {
        ul {
          list-style: none;
          padding: 0px;
          display: -ms-flexbox;
          display: flex;
          width: 100%;
          margin: 0px;
          justify-content: space-between;

          @include for-phone {
            display: inline-block;
          }

          li {
            flex-basis: 33.33333333%;
            padding: 16px 26px 16px 26px;
            color: $brand-black !important;
            flex-wrap: nowrap;

            @media screen and (min-width: $custom-screen-max-width) {
              padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font23}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font23});
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
              padding: 1.6vw 2.3vw 1.6vw 2.3vw;
            }

            &:nth-child(2n+1) {
              @include for-phone {
                clear: both;
              }
            } 

            @include for-phone {
              width: 50%;
              max-width: 50%;
              display: block;
              float: left;
              padding: 10px 8px 10px 19px !important;

              @include fontScaling(padding-top,1.0,!important);
              @include fontScaling(padding-right,0.8,!important);
              @include fontScaling(padding-bottom,1.0,!important);
              @include fontScaling(padding-left,1.9,!important);

              

            }

            span {
              font-size: $font14;
              font-family: $pfhandbookpro-light;
              line-height: 1.15;

              @media screen and (min-width: $custom-screen-max-width){
                  font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
              }
              @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                font-size: 1.4vw;
              }
              // @include mQmin($xxl) {
              //     font-size: 17px;
              // }
            }

            p {
              margin-bottom: 0px;
              font-size: $font26;
              color: $brand-black;
              font-family: $pfhandbookpro-medium;
              line-height: 1.3;

              @media screen and (min-width: $custom-screen-max-width) {
                  font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font26});
              }
              @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                font-size: 2.6vw;
              }
              // @include mQmin($xxl) {
              //     font-size: 22px;
              // }

            }
          }

          li:nth-child(2) {
            text-align: left;
            //padding-left: 70px;
          }

          li:last-child {
            text-align: left;
            //padding-left: 100px;
          }
        }
      }

      .listsummary__inner-content {
        margin-bottom: 35px;

        @media screen and (min-width: $custom-screen-max-width) {
          margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.5rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          margin-bottom: 3.5vw;
        }


        p {
          margin: 35px 30px 6px 31px;
          font-family: "pfhandbookpro-regular", sans-serif;
          font-size: 1.2rem;
          line-height: 1.25;
          color: $black;

          @media screen and (min-width: $custom-screen-max-width) {
              font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
              margin: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.5rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.0rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font6}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.1rem);
          }
          @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
              font-size: 1.2vw;
              margin: 3.5vw 3.0vw 0.6vw 3.1vw;
          }

          @include mQ(767px, 576px) {
            font-size: 2.4rem;
          }
        }

        a {
          position: relative;
          display: inline;
          font-size: 1.4rem;
          line-height: 1.04;
          font-family: "pfhandbookpro-bold", sans-serif;
          color: #da9089;

          @media screen and (min-width: $custom-screen-max-width) {
              font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
          }
          @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
              font-size: 1.4vw;
          }

          @include mQ(767px, 576px) {
            font-size: 2.4rem;
          }

          &:hover {
            border-bottom: 0px;
          }

          &:after {
            content: $icon-right-arrow;
            font-size: 1.7rem;
            color: #da9089;
            font-family: "font-icons";
            font-style: normal;
            font-variant: normal;
            font-weight: normal;
            line-height: 1;
            text-transform: none;
            position: absolute;
            right: -25px;
            top: 0px;

            @media screen and (min-width: $custom-screen-max-width) {
                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font17});
                right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -2.5rem);
            }
            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                font-size: 1.7vw;
                right: -2.5vw;
            }

            @include mQ(767px, 576px) {
              top: 6px;
            }
          }
        }
      }
    }

  }

  &.active {
    .listsummary-head {
      .listsummary-head-content {
        display: none;
      }

      .listsummary-head-title,
      .listsummary-head-title-static {
        .listsummary-head-title_arrow {
          span {
            &::before {
              content: $icon-collapse;
            }
          }
        }
      }
    }

    .listsummary-content {
      display: flex !important;
      width: 100%;
      padding: 18px 15px 22px 15px;
      flex-wrap: wrap;

      @include fontScaling(padding-top,1.8);
      @include fontScaling(padding-right,1.5);
      @include fontScaling(padding-bottom,2.2);
      @include fontScaling(padding-left,1.5);
      
      &.padding-low {
        padding: 10px 15px 22px 15px;
        @include fontScaling(padding-top,1.0);
        @include fontScaling(padding-right,1.5);
        @include fontScaling(padding-bottom,2.2);
        @include fontScaling(padding-left,1.5);

      }
    }
  }

  &:last-child {
    margin-bottom: 0px;
  }

  .btn-orange {
    background-color: #f7a500;
    padding: 4px 23px 4px;
    font-size: 14.4px;
    font-family: "pfhandbookpro-bold", sans-serif;
    border-bottom: none;
    margin-left: 30px;
    white-space: nowrap;

    @media screen and (min-width: $custom-screen-max-width) {
        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.3rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem);
        margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.0rem);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        font-size: 1.4vw;
        padding: 0.4vw 2.3vw 0.4vw;
        margin-left: 3.0vw;
    }

    &:hover,
    &:active,
    &:focus {
      color: $black;
    }

    @include mQ(767px, 576px) {
      font-size: 2.2rem;
      padding: 8px 23px 8px;
    }

    @include mQ(575px) {
      font-size: 1.4rem;
      padding: 5px 23px 5px;
    }
  }
}

.listsummary__col-row {
  margin-right: -8px;
  margin-left: -8px;
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;

  @include fontScaling(margin-right,-0.8);
  @include fontScaling(margin-left,-0.8);
  @include fontScaling(margin-top,0.5);

  @include mQ(767px, 576px) {
    margin-top: 45px;
  }

  .listsummary__col {
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
    padding-left: 8px;
    padding-right: 8px;

    @include fontScaling(padding-right,0.8);
    @include fontScaling(padding-left,0.8);

    @include mQ(767px) {
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 50px;
    }

    @include mQ(575px) {
      margin-bottom: 20px;
    }

    .listsummary-main-wrapper {
      .listsummary-head {
        .listsummary-head-title-static {
          .listsummary-head-title_left {
            width: 100%;
          }

          @include mQ(767px, 576px) {
            padding: 26px 12px 26px 17px;
          }
        }

        &-content {
          .listsummary__inner-content {
            margin-bottom: 0px;

            p {
              margin-top: 23px;
              margin-bottom: 0px;

              @include fontScaling(margin-top,2.3);

              @include mQ(767px, 576px) {
                margin-top: 6px;
                margin-bottom: -23px;
              }

              @include mQ(767px) {
                text-align: left;
                margin-left: 20px;
              }
            }
          }

          .LOBQuickLinks {
            ul {
              li {
                @include mQ(767px) {
                  padding: 10px 0 10px !important;
                }
              }
            }
          }
        }
      }
    }
  }
}

.title-tag {
  background-color: rgba(247, 165, 0, 0.5);
  padding: 2px 14px 4px 12px;
  border-radius: 11px;
  margin-bottom: 5px;
  display: inline-block;
  @include fontScaling(padding-top,0.2);
  @include fontScaling(padding-right,1.4);
  @include fontScaling(padding-bottom,0.4);
  @include fontScaling(padding-left,1.2);
  @include fontScaling(border-radius,1.1);
  @include fontScaling(margin-bottom,0.5);
  
}

.listsummary-content {
  .listsummary-content_left {
    width: 58.19%;
    float: left;
    padding-right: 5px;

    @include fontScaling(padding-right,0.5);

    &:after {
      @include for-phone {
        width: calc(100% - 12px);
        height: 1px;
        display: block;
        content: '';
        margin: auto;
        border-bottom: 1px solid #dee2e6;
      }
    }

    @include for-phone {
      width: 100%;
      padding: 0px;
      border: none !important;

    }
  }

  .listsummary-content_right {
    width: 41.81%;
    float: left;
    padding-left: 23px;
    display: flex;
    @include fontScaling(padding-left,2.3);
    align-items: center;
    
    .LOBQuickLinks,
    .lOBQuickLinks {
      width: 100%;

      .link-list__list--column {
        li {
          a {
            @include for-phone {
              word-break: break-word;
              min-height: 80px;
              display: flex;
              align-items: center;
              padding: 17.5px 40px 17.5px 20px !important;
            }
            @include mQ(575px) {
              min-height: 55px;
              padding: 9px 40px 9px 10px !important;
            } 
          }
        }
      }
    }

    @include for-phone {
      width: 100%;
      padding: 0px;
    }

    

  }
}
.dashboard__inner-content-wrapper[data-id='mi-view-policies'] {
  .dashboard__portfolio-inner-content-wrapper.active {
    @include for-phone {
      margin-top: 20px;
    }
  }
}
.un-numbered-list {
  .listsummary-head-title_left {
    p:before {
      content: none !important;
    }
  }
}

div[data-id="hi-my-services"] {
  .link-list__list--column {
    li {
      display: flex;
    }
  }
}

.dashboard__bottom-inner-wrapper .listsummary-main-wrapper {

  // Color Theme
  @include themify($themes) {
    border-left: themed('contentCardLeftBorderColor');
  }
}

.dashboard__bottom-inner-wrapper .listsummary-main-wrapper .listsummary-head .listsummary-head-title,
.dashboard__bottom-inner-wrapper .listsummary-main-wrapper .listsummary-head .listsummary-head-title-static {

  // Color Theme
  @include themify($themes) {
    background-color: themed('contentCardWithBackgroundOpacity');
  }
}