  cursor: pointer; }

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  border-radius: 4px;
  background-color: #ffffff;
  border: solid 2px #cdcece; }

.checkmark:focus {
  border-radius: 4px;
  box-shadow: 0 0 6px 0 #cdcece;
  background-color: #ffffff;
  border: solid 2px #cdcece;
  outline: none; }

.container-new input:checked ~ .checkmark {
  border-radius: 4px;
  box-shadow: 0 0 6px 0 #cdcece;
  background-color: #ffffff;
  border: solid 2px #cdcece;
  outline: none; }

.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

.container-new input:checked ~ .checkmark:after {
  display: block; }

.container-new .checkmark:after {
  left: 7px;
