.pillarcolumn {
  &__heading {
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    text-align: center;
    color: $card-heading;
    margin: 3px 0;
    margin-bottom: 3px;

    h1 {
      font-family: $pfhandbookpro-regular;
      font-size: $font30;
      font-weight: bold;
      line-height: normal;

      @include mQ(1200px, 799px) {
        margin: 20px 0 28px 0;
      }

      @include mQ(800px, 360px) {
        font-size: $font24;
        margin: 12px 0 13px 0;
      }

      @include mQ(800px, 360px) {
        font-size: $font24;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
      }
    }
  }

  &__container {
    width: 100%;
    display: flex;

    .scLooseFrameZone {
      width: 100%;

      .pillarcolumn__box {
        width: 100%;
      }
    }
  }

  &__box {
    background: $box-background;
    width: 24.2%;
    margin-right: 13px;

    &:last-child {
      margin-right: 0;
    }

    @include mQ(601px, 320px) {
      width: 100%;
      margin-right: 0;
      display: none;
      background: none;
    }
  }

  &__title {
    width: 100%;
    padding-top: 6px;
    padding-bottom: 6px;
    display: block;

    h4 {
      font-size: $font26;
      color: $box-main-title;
      text-align: center;
      font-family: $pfhandbookpro-bold;

      @include mQ(1399px, 1200px) {
        font-size: $font20;
        line-height: $line-height-20;
      }

      @include mQ(1199px, 750px) {
        font-size: $font15;
        line-height: $line-height-20;
      }

      @include mQ(749px, 601px) {
        font-size: $font11;
        line-height: $line-height-14;
      }
    }

    @include mQ(601px, 320px) {
      display: none;
      padding: 0;
    }
  }

  &__card {
    width: 92%;
    height: 200px;
    background: $card-background;
    margin: 8px auto;
    padding: 14px 0 0;
    box-shadow: 1px -2px 8px 0 rgba($black, 0.08);
    position: relative;

    @include mQ(1399px, 1200px) {
      height: 168px;
      padding-top: 5px;
      width: 95%;
    }

    @include mQ(1199px, 750px) {
      height: 117px;
      padding-top: 8px;
      width: 96%;
    }

    @include mQ(749px, 602px) {
      height: 86px;
      padding-top: 7px;
      width: 94%;
    }

    @include mQ(601px, 320px) {
      height: 220px;
      margin-top: 17px;
    }

    .icon-link {
      margin-right: 18px;
      margin-top: 16px;
      text-align: right;
      float: right;
      display: flex;
      color: $card-arrow;

      @include mQ(1399px, 1200px) {
        margin-right: 15px;
      }

      @include mQ(1199px, 750px) {
        margin-right: 9px;
        width: 14px;
        height: 9px;
        margin-top: 10px;
      }

      @include mQ(749px, 602px) {
        margin-right: 8px;
        margin-top: 8px;
        width: 10%;
      }

      @include mQ(601px, 320px) {
        margin-right: 20px;
        margin-top: 21px;
      }
    }

    .icon {
      font-size: $font15;
      margin: 0;
      line-height: normal;

      @include mQ(1199px, 750px) {
        font-size: $font9;
      }

      @include mQ(749px, 602px) {
        font-size: $font9;
      }
    }

    p {
      font-size: $font16;
      font-family: $pfhandbookpro-regular;
      color: $card-desc;
      line-height: $line-height-20;
      margin-left: 20px;
      margin-top: 9px;
      margin-right: 16px;
      margin-bottom: 0;

      @include mQ(1399px, 1200px) {
        font-size: $font15;
        line-height: $line-height-17;
        margin-left: 14px;
        margin-top: 7px;
      }

      @include mQ(1199px, 750px) {
        font-size: $font10;
        line-height: $line-height-12;
        margin-left: 8px;
        margin-top: 4px;
      }

      @include mQ(749px, 602px) {
        font-size: $font7;
        line-height: $line-height-9;
        margin-left: 7px;
        margin-top: 3px;
      }

      @include mQ(799px, 750px) {
        margin-right: 6px;
      }

      @include mQ(601px, 320px) {
        margin-right: 6px;
        font-size: $font18;
      }

      @include mQ(599px, 360px) {
        margin-top: 13px;
      }
    }
  }

  &__card-title {
    font-family: $pfhandbookpro-medium;
    // font-size: 19px;
    // line-height: 1;
    // font-weight: 500;
    letter-spacing: normal;
    padding: 5px 10px 5px 20px;
    display: inline-block;
    width: 230px;
    text-transform: uppercase;

    @include mQ(1399px, 1200px) {
      padding: 3px 9px 4px 14px;
      width: 81%;
    }

    @include mQ(1199px, 750px) {
      padding-left: 8px;
      height: 28px;
      padding-top: 2px;
      width: 84%;
    }

    @include mQ(749px, 602px) {
      padding-left: 6px;
      height: 28px;
      width: 78%;
    }

    @include mQ(601px, 320px) {
      font-size: 20px;
      padding-left: 20px;
      width: 250px;
    }

    span {
      display: block;
      color: $card-title-1;
      font-size: $font20;
      line-height: $line-height-20;

      @include mQ(1399px, 1200px) {
        font-size: $font17;
      }

      @include mQ(1199px, 750px) {
        font-size: $font11;
        line-height: $line-height-12;
      }

      @include mQ(749px, 602px) {
        font-size: $font8;
        line-height: $line-height-10;
      }

      @include mQ(601px, 320px) {
        font-size: $font18;
        line-height: $line-height-22;
      }

      &:last-child {
        color: $card-title-2;
        line-height: 1;

        @include mQ(1399px, 1200px) {
          font-size: $font15;
        }

        @include mQ(1199px, 750px) {
          font-size: $font8;
        }

        @include mQ(749px, 602px) {
          font-size: $font6;
        }
      }
    }
  }

  &__list {
    ul {
      padding: 0;

      li {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 0 23px;
        margin-top: 24px;
        line-height: $line-height-24;

        &::before {
          content: " ";
          all: unset;
          display: none;
        }

        @include mQ(1399px, 1200px) {
          margin-top: 14px;
          padding-left: 20px;
        }

        @include mQ(1199px, 750px) {
          margin-top: 5px;
          padding-left: 10px;
        }

        @include mQ(749px, 602px) {
          margin-top: 0;
          padding-left: 7px;
        }

        a {
          color: $card-qs-link;

          @include mQ(1399px, 1200px) {
            font-size: $font15;
          }

          @include mQ(1199px, 750px) {
            font-size: $font10;
          }

          @include mQ(749px, 602px) {
            font-size: $font8;
            line-height: $line-height-17;
          }
        }

        .icon {
          color: $card-qs-arrow;
        }
      }
    }
  }
  //Mobile Start
  &__tabcontainer {
    width: 100vw;
    position: relative;
    left: calc(-1 * (100vw - 100%) / 2);
    display: none;

    ul {
      width: 100%;
      padding-left: 0;
      display: flex;

      li {
        width: 25%;
        display: inline-block;
        padding: 0;

        &::before {
          content: "";
          all: unset;
          display: none;
        }

        a {
          font-family: $pfhandbookpro-regular;
          font-size: $font16;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: $line-height-1;
          letter-spacing: normal;
          text-align: center;
          color: $card-tab-link;
          padding: 10px 15px;
          display: block;
          position: relative;

          &.active {
            color: $card-tab-link-active;
          }
        }
      }
    }

    @include mQ(601px) {
      display: block;
    }
  }
  //Protecting
  &__protecting {
    .pillarcolumn,
    &.pillarcolumn {
      &__title {
        background: $protecting;
        border: 1px solid $protecting;
      }

      &__card {
        border-left: 3.6px solid $protecting;

        .btn {
          background: $protecting;
          color: $card-btn-hover;
          border: 1px solid $protecting;
          text-transform: uppercase;
          // border: 1px solid $protecting;
          // color: $protecting;
          // &:hover {
          //   background: $protecting;
          //   color: $card-btn-hover;
          // }
        }
      }

      &__card-title {
        background: rgba($protecting, 0.2);
      }

      &__qslink {
        &:hover {
          color: $protecting;
          text-decoration: underline;
        }
      }

      &__item {
        border-bottom: 5px solid $protecting;

        .active {
          background: $protecting;
          padding-bottom: 11px;
        }
      }
    }
  }
  //Investing
  &__investing {
    .pillarcolumn,
    &.pillarcolumn {
      &__title {
        background: $investing;
        border: 1px solid $investing;
      }

      &__card {
        border-left: 3.6px solid $investing;

        .btn {
          text-transform: uppercase;
          background: $investing;
          color: $card-btn-hover;
          border: 1px solid $investing;
          // color: $investing;
          // &:hover {
          //   background: $investing;
          //   color: $card-btn-hover;
          // }
        }
      }

      &__card-title {
        background: rgba($investing, 0.2);
      }

      &__qslink {
        &:hover {
          color: $investing;
          text-decoration: underline;
        }
      }

      &__item {
        border-bottom: 5px solid $investing;

        .active {
          background: $investing;
          padding-bottom: 11px;
        }
      }
    }
  }
  //Financing
  &__financing {
    .pillarcolumn,
    &.pillarcolumn {
      &__title {
        background: $financing;
        border: 1px solid $financing;
      }

      &__card {
        border-left: 3.6px solid $financing;

        .btn {
          background: $financing;
          color: $card-btn-hover;
          border: 1px solid $financing;
          text-transform: uppercase;
          // color: $financing;
          // &:hover {
          //   background: $financing;
          //   color: $card-btn-hover;
          // }
        }
      }

      &__card-title {
        background: rgba($financing, 0.2);
      }

      &__qslink {
        &:hover {
          color: $financing;
          text-decoration: underline;
        }
      }

      &__item {
        border-bottom: 5px solid $financing;

        .active {
          background: $financing;
          padding-bottom: 11px;
        }
      }
    }
  }
  //Quick Services
  &__qs {
    .pillarcolumn,
    &.pillarcolumn {
      &__title {
        background: $capital-red;
        border: 1px solid $capital-red;
      }

      &__item {
        border-bottom: 5px solid $capital-red;

        .active {
          background: $capital-red;
          padding-bottom: 11px;
        }
      }
      //   &__card {
      //     border-left: 3.6px solid $capital-red;
      //   }
      //   &__card-title {
      //     background: rgba($capital-red, 0.2);
      //   }
    }
  }

  a {
    &:visited,
    &:hover,
    &:active,
    &:focus {
      border: none;
    }
  }
  //Button
  .btn {
    width: 70%;
    height: 40px;
    border-radius: 2px;
    background-color: $card-btn-bg;
    font-family: $pfhandbookpro-bold;
    font-size: $font16;
    line-height: $line-height-28;
    text-align: center;
    padding: 4px 0;
    margin: 13px 0 0 20px;
    display: block;

    @include mQ(1399px, 1200px) {
      font-size: $font13;
      margin-left: 14px;
      height: 30px;
      padding-top: 1px;
      padding-bottom: 1px;
    }

    @include mQ(1199px, 750px) {
      font-size: $font8;
      margin-left: 8px;
      height: 20px;
      margin-top: 10px;
      padding: 1px 0;
      line-height: 17px;
    }

    @include mQ(749px, 602px) {
      font-size: $font6;
      margin-top: 3px;
      margin-left: 20px;
      width: 54%;
      height: 15px;
      line-height: $line-height-1;
    }

    @include mQ(601px, 320px) {
      font-size: $font16;
      margin-left: 20px;
      width: 207px;
    }

    @include mQ(599px, 20px) {
      margin-top: 19px;
    }
  }
}
