.tips {
    $self: &;
    &__list {
        padding-left: 5rem;
        counter-reset: section;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        @include for-phone {
            display: block;
            flex-flow: inherit;
            justify-content: inherit;
            padding-left: 2rem;
        }
    }
    &__item {
        padding-left: 1rem;
        position: relative;
        width: 45%;
        margin-bottom: 5rem;
        @include for-phone {
            width: 100%;
            padding-bottom: 2rem;
            margin-bottom: 2rem;
            border-bottom: 1px solid $tips-border;
            &:last-child {
                border-bottom: none;
            }
        }
        &:before {
            counter-increment: section;
            content: counter(section, decimal-leading-zero);
            font-size: $font44;
            line-height: $line-height-46;
            font-family: $pfhandbookpro-regular;
            color: $tips-count;
            display: block;
            position: absolute;
            left: 0;
            top: -3px;
            font-weight: 400;
            @include for-phone {
                font-size: $font22;
                line-height: $line-height-24;
                top: 0;
            }
        }
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $tips-title;
        font-family: $pfhandbookpro-regular;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-22;
        }
    }
    &__info {
        font-size: $font24;
        line-height: $line-height-26;
        color: $tips-content;
        font-family: $pfhandbookpro-thin;
        margin-bottom: 1.5rem;
        &:last-child {
            margin-bottom: 0;
        }
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__list-item{
        margin-bottom: 4rem;
        &:last-child{
            margin-bottom: 0;
        }
        #{$self}__title{
            margin-bottom: 2rem;
        }
    }
    &--typ-full {
        #{$self}__item {
            width: 100%;
            margin-bottom: 4rem;
            &:last-child {
                @include for-phone {
                    margin-bottom: 2rem;
                }
            }
            @include for-phone {
                border-bottom: none;
                padding-bottom: 0;
                margin-bottom: 2rem;
            }
        }
        #{$self}__title-wrap {
            margin-bottom: 2.5rem;
        }
    }
    &--no-count {
        #{$self}__item {
            padding-left: 0;
            &:before {
                content: none;
            }
        }
        #{$self}__list {
            padding-left: 0;
        }
    }
}