//download-file
.download-file {
    $this:&;
    padding: 3rem 0;
    @include for-tablet {
        background: transparent;
    }
    &__container {
        max-width: 1200px;
        margin: 0 auto;
        @include for-md-desktop {
            width: 100%;
            padding: 0 10rem;
        }
        @include for-tablet {
            width: 100%;
            padding: 0 1.5rem;
        }
    }
    &__title-wrap {
        text-align: center;
    }
    &__count {
        font-size: $font14;
        line-height: $line-height-16;
        color: $df-total-text;
        font-family: $pfhandbookpro-regular;
        display: block;
        margin-bottom: 1.5rem;
    }
    &__item {
        border-bottom: 1px solid $df-border-color;
        display: none;
        &:nth-child(-n + 10) {
            display: block;
        }
        @include for-tablet {
            margin: 0 -15px;
        }
    }
    &__list {
        padding: 0;
        &--typ2 {
            .download-file__item {
                display: none;
                &:nth-child(-n + 5) {
                    display: block;
                }
            }
        }
    }
    &__view-action {
        margin-top: 2rem;
        text-align: center;
        @include for-tablet {
            text-align: left;
        }
    }
    &--v2{
        padding: 4rem 0;
        @include for-tablet{
            padding: 0 0 2rem 0;
        }
        #{$this}__list{
            display: flex;
            flex-flow: row wrap;
            margin: 0 -1.5rem;
            @include for-tablet{
                display: block;
                flex-flow: initial;
            }
        }
        #{$this}__item{
            width: 33.33%;
            padding: 0 1.5rem;
            @include for-tablet{
                width: auto;
                padding: 0;
                margin: 0;
            }
        }
        @include for-md-desktop{
            .download-band__action-wrap{
                .download-band__act-btn{
                    margin-left: 1rem;
                }
            }
        }
    }
}

