.dashboard__right-list-wrapper {
    box-shadow: 2px 6px 12px 0 rgba(0, 0, 0, 0.08);
    background-color: $brand-white;
    border-radius: 2px;
    @media screen and (min-width: $custom-screen-max-width) {
        box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.2rem) 0 rgba(0, 0, 0, 0.08);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        box-shadow: 0.2vw 0.6vw 1.2vw 0 rgba(0, 0, 0, 0.08);
    }
    @include fontScaling(border-radius,0.2);
    
    .dashboard__right-list-title{
        font-family: $pfhandbookpro-medium;
        color: $brand-black;
        margin: 0px;
        padding: 7px 20px 6px 17px;
        @include fontScaling(padding-top,0.7);
        @include fontScaling(padding-left,2.0);
        @include fontScaling(padding-bottom,0.6);
        @include fontScaling(padding-left,1.7);
        p{
            color: $brand-black;
            margin: 0px;
                
        }
        // @include mQmin($xxl){
        //     padding: 7px 12px 6px 21px; 
        // }
    }
    .dashboard__right-list-content{
        font-family: $pfhandbookpro-light;
        ul{
            list-style:none;
            padding:0px;
            display: flex;
            align-items: flex-start;
            margin: 0px;
            width:100%;
            @include for-phone {
                display: inline-block;  
            }
            li {
                flex-basis: 100%;
                padding: 16px 26px 16px 26px;
                color: $brand-black !important;
                padding-right: 0px;
                @include fontScaling(padding-top,1.6);
                @include fontScaling(padding-bottom,1.6);
                @include fontScaling(padding-left,2.6);
                
                // @include mQmin($xxl){
                //     padding-left: 28px;
                //     padding-bottom: 22px;
                //     padding-top: 22px;
                // }
                
                @include for-phone{
                    width: 50%;
                    display: block;
                    float: left;
                    padding-left: 26px !important;
                }
                span {
                    font-size: $font14;
                    @include fontScaling(font-size,1.4);
                    // @include mQmin($xxl){
                    //     font-size: 17px;
                    // }
                    font-family: $pfhandbookpro-light;
                    line-height: 1.15;
                    @include mQ(767px) {
                        font-size: $font18;
                    }
                    @include mQ(575px) {
                        font-size: 1.3rem;
                    }
                }
                p {
                    margin-bottom: 0px;
                    font-size: $font26;
                    @include fontScaling(font-size,2.6);
                    // @include mQmin($xxl){
                    //     font-size: 22px;
                    //     padding-top: 7px;
                    // }
                    color: $brand-black;
                    font-family: $pfhandbookpro-regular;
                    line-height: 1.3;
                    @include mQ(767px,576px) {
                        font-size: $font30;
                    }
                }

                &.policy-cta {
                    span, p {
                        color: #F7A500;
                    }
                    a {
                        color: #F7A500;
                        font-family: $pfhandbookpro-bold;
                        font-size: $font14;
                        @include fontScaling(font-size,1.4);
                        line-height: 1.15;
                        display: inline-flex;
                        text-transform: uppercase;
                        margin-top: 5px;
                        @include fontScaling(margin-top,0.5);
                        text-decoration: underline;
                        position: relative;
                        padding-right: 20px;
                        @include fontScaling(padding-right,2.0);
                        margin-right: 34px;
                        @include fontScaling(margin-right,3.4);
                        &:after{
                            content: $icon-right-arrow;
                            display: block !important;
                            -moz-osx-font-smoothing: grayscale;
                            -webkit-font-smoothing: antialiased;
                            font-family: "font-icons";
                            font-style: normal;
                            font-variant: normal;
                            font-weight: normal;
                            line-height: 1;
                            speak: none;
                            text-transform: none;
                            font-size: $font10;
                            @include fontScaling(font-size,1.0);
                            line-height: 1;
                            right: 0px;
                            top: 4px;
                            @include fontScaling(top,0.4);
                            position: absolute;
                        }
                        &:hover, &:focus{
                            border: none;
                        }
                    }
                }
                
            }
            li:nth-child(2){
                text-align: left;
                padding-left: 70px;
                @include fontScaling(padding-left,7.0);
                // @include mQmin($xxl){
                //     padding-left: 37px;
                // }
            }
            li:last-child{
                text-align: left;
                padding-left: 100px;
                @include fontScaling(padding-left,10.0);
                // @include mQmin($xxl){
                //     padding-left: 103px !important;
                // }
            }
        }
        &__2{
            ul{
                li{
                    padding-left: 26px !important;
                    @include fontScaling(padding-left,2.6);
                    // @include mQmin($xxl){
                    //     padding-left: 28px !important;
                    //     padding-bottom: 22px;
                    //     padding-top: 22px;
                    // }
                    flex-basis: 33.33333333%;
                    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
                        flex-basis: auto;
                    }

                    &.width50 {
                        flex-basis: 50%;
                        @include for-phone {
                            flex-basis: 100%;
                            width: 100%;
                        }
                    }
                }
            }
        }
        &-column3 {
            ul {
                flex-wrap: wrap;
            }
            @include for-phone {
                ul {
                    li {
                        &:nth-child(2n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
    .dashboard__right-content-wrapper {
        padding: 10px 16px;
        @include fontScaling(padding-top,1.0);
        @include fontScaling(padding-right,1.6);
        @include fontScaling(padding-bottom,1.0);
        @include fontScaling(padding-left,1.6);
        @include mQ(767px) {
            .abc-form.abc-post-login-form {
                .form-group__label {
                    font-size: 1.8rem;
                }
            }
        }
        
        @include mQ(575px) {
            .abc-form.abc-post-login-form {
                .form-group__label {
                    font-size: 1.2rem;
                }
                .selectize-input {
                    font-size: 1.2rem;
                    height: 40px;
                    //padding-top: 0px;
                    line-height: 24px;
                    // &:after {
                    //     font-size: 0.8rem !important;
                    //     right: 5px !important;
                    //     width: 8px !important;
                    //     height: 8px !important;
                    // }
                }
            }
        }

    }
    .dashboard__right-content-title {
        width: 100%;
        display: block;
    }
    .dashboard__right-content-select {
        width: 100%;
    }
    .dashboard__right-protect-now-content-title {
        color: #da9089;
        font-size: 2.4rem;
        font-family: "pfhandbookpro-bold", sans-serif;
        padding-left: 17px;
        @include fontScaling(padding-left,1.7);
        @include fontScaling(font-size,2.4);
        margin-bottom: 0px;
        @include mQ(767px) { 
            font-size: 3.6rem;
            margin-bottom: 8px;
        }
        @include mQ(575px) { 
            font-size: 2.3rem;
            margin-bottom: 8px;
        }
    }
    .link-list__list--column {
        &-with-orange-btn {
            a { 
                @include themify($themes) {
                    border-left: themed('contentCardLeftBorderColor') !important;  
                }                
                border-radius: 2.6px !important;
                @include fontScaling(border-radius,0.26,!important);
                &:after {
                    display: none;
                }

                p {
                    padding-right: 70px;
                    line-height: 17px;
                    @include fontScaling(padding-right,7.0,!important);
                    @include fontScaling(line-height,1.7,!important);

                }
            }
            .link-list-btn-orange {
                background-color: #f7a500;
                padding: 6px 21px 6px 4px;
                @include fontScaling(padding-top,0.6);
                @include fontScaling(padding-right,2.1);
                @include fontScaling(padding-bottom,0.6);
                @include fontScaling(padding-left,0.4);
                font-size: 9.6px !important;
                @include fontScaling(font-size,0.96,!important);
                line-height: 1;
                color: #000000;
                width: auto !important;
                display: inline-block !important;
                float: right;
                top: 50%;
                right: 10px;
                @include fontScaling(right,1.0);
                position: absolute;
                transform: translateY(-50%);
                font-family: "pfhandbookpro-bold", sans-serif;
                &:after {
                    content: "";
                    font-family: "font-icons";
                    right: 5px;
                    @include fontScaling(right,0.5);
                    font-size: 1rem;
                    @include fontScaling(font-size,1.0);
                    color: #000000;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    margin-top: -1px;
                    @include fontScaling(margin-top,-0.1);
                }
            }
            .link-list__static-list {
                a {
                    p {
                        padding-right: 50px !important;
                        @include fontScaling(padding-right,5.0,!important);    
                        @include mQ(767px) { 
                            font-size: 3rem !important;
                            line-height: 30px !important;
                            padding-right: 150px !important; 
                        }
                        @include mQ(575px) { 
                            padding-right: 80px !important;
                        }
                    }
                    .link-list-btn-orange {
                        top: 8px;
                        @include fontScaling(top,0.8);
                        transform: translateY(0px);
                        @include mQ(767px) {  
                            top: 24px;
                            right: 40px;
                        }
                        @include mQ(575px) {
                            top: 16px;
                            right: 10px;
                        }
                    }
                }
                
            }
            li {
                @include mQ(767px) {
                    flex-basis: 100% !important;
                    &:nth-last-of-type(odd) {
                        margin-right: 0 !important;
                    }
                    a {
                        min-height: auto !important;
                        max-height: none !important;
                    }
                    p {
                        font-size: 2.7rem !important;
                    }
                    .link-list-btn-orange {
                        font-size: 1.6rem !important;
                        padding: 9px 21px 8px 10px;
                        width: 111px !important;
                        &:after {
                            font-size: 1.7rem;
                            right: 10px;
                        }
                    }
                }
                @include mQ(575px) {
                    flex-basis: 100% !important;
                    &:nth-last-of-type(odd) {
                        margin-right: 0 !important;
                    }
                    
                    p {
                        font-size: 1.7rem !important;
                    }
                    .link-list-btn-orange {
                        font-size: 1.1rem !important;
                        padding: 7px 21px 6px 10px;
                        width: 69px !important;
                        &:after {
                            font-size: 1.0rem;
                            right: 6px;
                        }
                    }
                }
            }
        }
    }

    .abc-form {
        &.abc-post-login-form {
            .form-group__label {
                font-size: 1.8rem;
                @include fontScaling(font-size,1.8);                
            }            
            @include mQ(575px) {
                &.pf-my-docs-soa-form,
                &.pf-my-docs-interest-certificate-form {
                    .form-group__label {
                        font-size: $font11;
                    }
                    .radio-button-collection {
                        .radio-input-wrapper {
                            label {
                                font-size: 10px;
                                padding-left: 18px;
                                padding-right: 18px;
                                &::before {
                                    width: 10px;
                                    height: 10px;
                                    top: 5px;
                                }
                            }
                            input[type="radio"]:checked + label::after {
                                width: 6px;
                                height: 6px;
                                top: 7px;
                                left: 2px;
                            }
                        }
                    }
                    .form-group__placeholder {
                        height: 28px;
                        font-size: $font11;
                    }
                    .selectize-input {
                        height: 28px;
                        padding: 2px 8px;
                        input {                            
                            font-size: $font11;
                        }
                    }
                    .dashboard__cta {
                        a, 
                        button, 
                        input {
                            font-size: 10px;
                            line-height: 10px;
                            padding: 8px;
                        }
                    }
                }
            }
            @include mQ(767px,576px) {
                &.pf-my-docs-soa-form,
                &.pf-my-docs-interest-certificate-form {                    
                    .form-group__placeholder {
                        font-size: $font20;
                        padding: 0.375rem 1.8rem;
                        height: 44px;
                    }                    
                }
            }
        }
    }
    .dashboard__content-horizontal-tab-main-wrapper {
        margin: 0 auto 6px auto;
        @include fontScaling(margin-bottom,0.6);
        width: calc(100% - 36px);
        padding-top: 13px;
        @media screen and (min-width: $custom-screen-max-width) {
            width: calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.6rem));
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            width: calc(100% - 3.6vw);
        }
        @include fontScaling(padding-top,1.3);

        @include for-phone {
            margin: 0 24px 20px;
        }
        @include mQ(575px) {
            margin: 0 24px 13px;
        }
    }

}
.active-progress-bar-wrapper {
    margin-bottom: 20px;
    @include fontScaling(margin-bottom,2.0);
    @include mQ(767px) {
        display: flex;
        flex-direction: column;
        margin-bottom: 0px;
    }
    p {
        float: left;
        width: 50%;
        font-family: 'pfhandbookpro-medium', sans-serif;
        color: $black;
        margin-bottom: 8px;
        @include fontScaling(margin-bottom,0.8);
        @include mQ(767px) {
            font-size: 2.4rem;
            margin-bottom: 20px;
            width: 100%;
        }
        @include mQ(575px) {
            font-size: 1.5rem;
            margin-bottom: 10px;
            width: 100%;
        }
    }
    a {
        float: right;
        width: 50%;
        text-align: right;
        color: #7f3a34;
        font-size: 1.4rem;
        font-family: 'pfhandbookpro-bold', sans-serif;
        position: relative;
        padding-right: 30px;
        @include fontScaling(font-size,1.4);
        @include fontScaling(padding-right,3.0);
        &:hover, &:focus {
            border: none;
        }
        &:after {
            content: $icon-right-arrow-thin;
            top: 50%;
            transform: translate(-50%, -50%);
            font-family: "font-icons";
            color: #7f3a34;
            font-style: normal;
            font-variant: normal;
            font-weight: bold;
            line-height: 1;
            text-transform: none;
            position: absolute;
            font-size: 1.2rem;
            vertical-align: baseline;
            right: 0;
            @include fontScaling(font-size,1.2);
        }
        @include mQ(767px) { 
            display: inline-block;
            order: 3;
            margin-top: 10px;
            width: auto;
            text-align: left;
            float: left;
            font-size: 2.4rem;
            margin-bottom: 20px;
            &:after { 
                font-size: 1.8rem;
                position: relative;
                right: -20px;
            }

        }
        @include mQ(575px) {
            font-size: 1.5rem;
            margin-bottom: 10px;
            &:after {
                font-size: 1.1rem;
                right: -15px;
            }
        }
    }
    .active-progress-bar {
        width: 100%;
        clear: both;
    }
    progress[value] {
        appearance: none;
        border: none;
        height: 10px;
        @include fontScaling(height,1.0);
        @include mQ(767px) {
            height: 19px;
        }
        @include mQ(575px) {
            height: 12px;
        }
    }
    progress[value]::-webkit-progress-bar {
        background-color: #c2c7cb;
        border-radius: 8.5px;
        @include fontScaling(border-radius,0.85);
    }
    progress[value]::-webkit-progress-value {
        position: relative;
        border-radius: 8.5px 0 0 8.5px;
        @include fontScaling(border-top-left-radius,0.85);
        @include fontScaling(border-bottom-left-radius,0.85);
        background: #f7a500;
    }
}
