.dashboard__right-list-wrapper {
.abc-form.abc-post-login-form { 
    &.dropdown-form {
        .form-group__datepicker-container {
            @include fontScaling(max-width,27.6);
            .form-calendar-icon {  
                @include fontScaling(font-size,2.0);
                @include fontScaling(right,0.3);
            } 
        }
    }
    .form-group__placeholder {
        @include fontScaling(font-size,2.0);
        @include fontScaling(height,5.0);
        @include fontScaling(padding-top,0.375);
        @include fontScaling(padding-right,1.8);
        @include fontScaling(padding-bottom,0.375);
        @include fontScaling(padding-left,1.8);
    }
    .radio-input-wrapper {
        label {
            @include fontScaling(padding-left,3.0);
            @include fontScaling(padding-right,3.0);
            @include fontScaling(font-size,1.8);
            @include fontScaling(line-height,2.0); 
            @include fontScaling(margin-bottom,0.5); 
            
            &:before {
                @include fontScaling(width,1.6); 
                @include fontScaling(height,1.6); 
                @include fontScaling(top,0.2); 
                @include fontScaling(border-width,0.1); 
            }
        }
        input[type="radio"]:checked + label::after {
            @include fontScaling(width,1.0); 
            @include fontScaling(height,1.0); 
            @include fontScaling(top,0.5);
            @include fontScaling(left,0.3);
        }
    }
    &.pipe-style {

        .selectize-input {
            &:after {
                @include fontScaling(height,4.4,!important);
                @include fontScaling(right,0.5,!important);
            }
            &.dropdown-active {
                &:after {
                    @include fontScaling(height,4.4,!important);
                    @include fontScaling(right,0.5,!important);
                }
            }
        }
    }

    .selectize-input {
        // font-size: 1.8rem !important;
        // border-radius: 4px;
        // height: 44px;
        // padding: 8px;
        @include fontScaling(font-size,1.8,!important); 
        @include fontScaling(border-radius,0.4); 
        @include fontScaling(height,4.4); 
        @include fontScaling(padding,0.8); 
        &:after {
            // width: 12px !important;
            // height: 12px !important;
            // font-size: 1.2rem !important;
            // right: 10px !important;
            // margin-top: -0.3px;

            @include fontScaling(font-size,1.2,!important);
            @include fontScaling(width,1.2,!important);
            @include fontScaling(height,1.2,!important);
            @include fontScaling(right,1.0,!important);
            @include fontScaling(margin-top,-0.3);
        }
        &.dropdown-active {
            &:after {
                // margin-top: 0 !important;
                // width: 12px !important;
                // height: 12px !important;
                // font-size: 1.2rem !important;
                // right: 10px !important;
                // margin-top: -0.3px;

                @include fontScaling(font-size,1.2,!important);
                @include fontScaling(width,1.2,!important);
                @include fontScaling(height,1.2,!important);
                @include fontScaling(right,1.0,!important);
                @include fontScaling(margin-top,-0.3);
            }
        }
    }
    .selectize-input input {
        @include fontScaling(font-size,1.8,!important); 
    }
    .selectize-dropdown {
        .selectize-dropdown-content {
            //max-height: 200px;
