//insurance-filter
.insurance-filter {
    &__link {
        font-size: $font18;
        line-height: $line-height-20;
        color: $if-disable-link;
        text-transform: uppercase;
        transition: all 0.3s ease-in;
        // &:hover{
        //     color: $if-link-hover;
        // }
        &--active,
        &:active,
        &:focus {
            color: $if-link-active;
        }
        @include for-phone {
            letter-spacing: 5px;
        }
    }
    &__cont-wrap {
        display: flex;
        justify-content: space-between;
    }
    &__cont {
        padding: 0;
        max-width: 73.6rem;
    }
    &__filter-wrap {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
        margin-bottom: 4rem;
        padding: 0;
        @include for-phone {
            margin-bottom: 2rem;
        }
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        color: $if-text-color;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-22;
        }
    }
    &__desc {
        font-size: $font24;
        line-height: $line-height-26;
        color: $if-text-color;
        font-family: $pfhandbookpro-thin;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__item {
        margin-bottom: 4rem;
        display: none;
        &:last-child {
            margin-bottom: 0;
        }
        &--active {
            display: block;
        }
    }
    &__filter-item {
        @include for-phone {
            margin-right: 2.5rem;
            white-space: nowrap;
        }
    }
}