#modal-popup-dialog {
    .modal-body {
        padding: 20px;
    }
    .error-msg {
        color: $capital-red;
        font-size: 1.6rem;
        margin-bottom: 10px;
    }
    label {
        font-family: $pfhandbookpro-medium;
        font-size: 1.6rem;
        line-height: 20px;
        display: block;
        width: 50%;
    }
    form {
        > div {
            margin-top: 15px;
        }
    }
    input[type="text"], input[type="email"], input[type="password"] {
        width: 100%;
        margin-bottom: 10px;
        opacity: 0.7;
        font-family: $pfhandbookpro-regular;
        font-size: 1.6rem;
        text-align: left;
        color: $black;
        padding: 6px 10px;
        border-radius: 4px;
        background-color: #ffffff;
        border: solid 1px #cdcece;
    }
    input[type="submit"] {
        font-family: $pfhandbookpro-bold;
        font-size: 2.0rem;
        line-height: 24px;
        text-align: center;
        border-radius: 2px;
        padding: 9px 22px;
        outline: none;
        background-color: $white;
        border: solid 1px $capital-red;
        color: $capital-red;
        text-transform: uppercase;
        display: inline-block;
        margin-top:10px;
        &:hover,
        &:active,
        &:focus {
            background-color: $capital-red;
            color: $white;
            text-decoration: none;
        }
    }
}