.profiles {
    &__list {
        display: flex;
        flex-wrap: wrap;
        padding-left: 0;
        margin-bottom: 0;
    }
    &__item {
        width: 33%;
        margin-top: 8rem;
        &:first-child,
        &:nth-child(2),
        &:nth-child(3) {
            margin-top: 0;
        }
        @include for-phone {
            width: 100%;
            margin-top: 2rem;
            display: flex;
            align-items: center;
            &:nth-child(2),
            &:nth-child(3) {
                margin-top: 2rem;
            }
        }
        &--modal {
            width: 100%;
            display: flex;
            align-items: center;
            margin-bottom: 0;
        }
        &--accord {
            width: 100%;
            display: flex;
            align-items: center;
            margin-top: 4rem;
            &:nth-child(2),
            &:nth-child(3) {
                margin-top: 4rem;
            }
            @include for-phone {
                flex-wrap: wrap;
                margin-top: 0;
                margin-top: 3rem;
                &:nth-child(2),
                &:nth-child(3) {
                    margin-top: 3rem;
                }
            }
        }
    }
    &__img-wrap {
        max-width: 18rem;
        margin-bottom: 3rem;
        @include for-phone {
            max-width: 12.5rem;
            margin: 0 1.6rem 0 0;
        }
        &--modal {
            max-width: 16rem;
            margin: 0 2.4rem 2.4rem 0;
            @include for-phone {
                margin: 0 1.6rem 2rem 0;
            }
        }
    }
    &__name {
        font-size: $font32;
        line-height: $line-height-1;
        color: $profile-name;
        &--accord {
            width: 50%;
            @include for-phone {
                width: 100%;
            }
        }
        @include for-phone {
            font-size: $font22;
        }
    }
    &__designation {
        font-family: $pfhandbookpro-thin;
        font-size: $font24;
        line-height: $line-height-1;
        margin-bottom: 1rem;
        color: $profile-designation;
        min-height: 4.8rem;
        padding-right: 30px;
        @include for-phone {
            min-height: 3.6rem;
            margin-bottom: 1.6rem;
            padding-right: 0;
        }
        &--accord {
            margin-bottom: 0;
        }
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
    &__button {
        padding-left: 0;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-18;
        }
    }
    &__about {
        max-height: 336px;
        padding-bottom: 2.4rem;
    }
    .on-page-editor & {
        &__list {
            & > div {
                width: 33%;
                margin-top: 8rem;
                &:nth-child(1),           
                &:nth-child(2),           
                &:nth-child(3) {
                    margin-top: 0;
                }           
            }            
        }
        &__item {
            width: 100%;
        }
    }
}

.profiles-overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba($profile-overlay, 0.6);
    display: none;
    z-index: 10002;
}

.profiles-modal {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    display: none;
    max-width: 640px;
    max-height: 544px;
    border-radius: 2px;
    background: $profile-modal-bg;
    padding: 2.4rem 1rem 0 2.4rem;
    z-index: 10003;
    @include for-phone {
        left: 0;
        bottom: 0;
        transform: none;
        top: unset;
        max-width: 100%;
    }
    &__close {
        position: absolute;
        top: 2.7rem;
        right: 2.4rem;
    }
    &__close-icon {
        font-size: $font15;
        color: $profile-modal-close;
    }
}

.our-people-wrap {
    width: 100%;
}

.people-list {
    @include for-tablet {
        margin: 0 -15px 4rem;
        padding-left: 15px;
        width: auto;
    }
    .slick-slide {
        margin: 0 0.8rem;
    }
    .slick-list {
        margin: 0 -1.6px;
    }
}