.close {
  font-size: 3.5rem;
  padding: 0 !important;
  position: absolute;
  right: 28px;
  top: 17px;
  z-index: 99;
}
.modal-header {
  border-bottom: none;
  position: relative;
}
.form-subscribe {
  position: relative;
  margin: 5px 5px 20px 5px;
  #frm-loading {
    background: url("../images/loading.gif") no-repeat center center;
    position: absolute;
    top: 0;
    left: -1px;
    height: 100%;
    width: 100%;
    z-index: 9999999;
    background-color: #fff;
  }
  label {
    font-family: $pfhandbookpro-medium;
    font-size: 1.6rem;
    line-height: 20px;
    display: block;
    width: 50%;
    & + label {
      font-family: $pfhandbookpro-regular;
    }
  }
  form {
    > div {
      margin-top: 15px;
    }
  }
  input[type="text"],
  input[type="email"],
  input[type="password"] {
    width: 100%;
    margin-bottom: 10px;
    opacity: 0.7;
    font-family: $pfhandbookpro-regular;
    font-size: 1.6rem;
    text-align: left;
    color: $black;
    padding: 6px 10px;
    border-radius: 4px;
    background-color: #ffffff;
    border: solid 1px #cdcece;
    max-width: 300px;
  }
  input[type="radio"],
  input[type="checkbox"] {
    margin-right: 10px;
  }
  input[type="submit"] {
    font-family: $pfhandbookpro-bold;
    font-size: 2rem;
    line-height: 24px;
    text-align: center;
    border-radius: 2px;
    padding: 9px 22px;
    outline: none;
    background-color: $white;
    border: solid 1px $capital-red;
    color: $capital-red;
    text-transform: uppercase;
    display: inline-block;
    margin-top: 10px;
    &:hover,
    &:active,
    &:focus {
      background-color: $capital-red;
      color: $white;
      text-decoration: none;
    }
  }
}

.field-validation-error {
  color: $capital-red;
  font-size: 1.4rem;
  margin-bottom: 10px;
  float: left;
  width: 100%;
}

.validation-summary-errors {
  ul {
    padding-left: 0;
    li {
      color: $capital-red !important;
      font-size: 1.4rem;
      margin-bottom: 10px;
      &:before {
        display: none;
      }
    }
  }
}

.field-validation-valid {
}
