.state {
    &__icon {
        font-size: $font80;
        line-height: $line-height-80;
        display: block;
        @include for-phone {
            font-size: $font50;
            line-height: $line-height-50;
        }
    }
    &__img {
        max-width: 8rem;
        margin-bottom: 2rem;
        @include for-phone {
            max-width: 5rem;
        }
    }
    &__value {
        font-size: $font44;
        line-height: $line-height-44;
        font-weight: 600;
        display: block;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-32;
        }
    }
    &__desc {
        font-size: $font20;
        display: block;
        @include for-phone {
            font-size: $font16;
            line-height: $line-height-18;
        }
    }
}

.value {
    &__unit {
        display: inline-block;
        vertical-align: top;
        font-size: $font24;
        line-height: $line-height-24;
    }
}

.state-list {
    display: flex;
    padding: 0;
    flex-wrap: wrap;
    &__item {
        width: 25%;
        padding-right: 5rem;
        @include for-tablet {
            text-align: center;
            padding-right: 0;
        }
        @include for-phone {
            width: 50%;
            margin-bottom: 2rem;
        }
        &--xs-center {
            @include for-tablet {
                text-align: left;
            }
        }
    }
    .on-page-editor & {
        .scLooseFrameZone.scEnabledChrome {
            width: 25%;
            .state-list__item {
                width: 100%;
            }
        }
    }
}