.summaryListItemWrapper {
  display: flex;
  flex-direction: column;
  position: relative;

  @include mQ(575px) {
    &.pt-5 {
      padding-top: 1rem !important;
    }
  }
}

@include for-phone {
  .content__policy-summary-main-wrapper .summaryListItemWrapper.summary-list-item ul li {
    padding: 6px 0px 8px 0px !important;
  }

  .dashboard__summary-card .content__policy-summary-main-wrapper .summary-list-item ul li {
    padding: 6px 6px 8px 0px !important;
   
  }
}

.summary-list-item-content,
.loan-eligibility-content {
  order: 3;

  @include for-phone {
    order: 2;
  }
}

.loan-eligibility-content {

  span {
    background-color: #f8e9e8;
    margin: 10px 0 25px;
    @include fontScaling(margin-top,1.0);
    @include fontScaling(margin-bottom,2.5);
    padding: 4px 5px 4px 4px;
    @include fontScaling(padding-top,0.4);
    @include fontScaling(padding-right,0.5);
    @include fontScaling(padding-bottom,0.4);
    @include fontScaling(padding-left,0.4);
    border-radius: 15px;
    @include fontScaling(border-radius,1.5);
    height: 34px;
    @include fontScaling(height,3.4);
    font-size: $font24;
    @include fontScaling(font-size,2.4);
    font-family: "pfhandbookpro-medium", sans-serif;
    display: flex;
    align-items: center;
    width: auto;
    justify-content: center;
    text-align: center;

    @include for-phone {
      margin: 10px 0 25px;
      padding: 14px 23px 14px 24px;
      border-radius: 42px;
      height: auto;
      line-height: 1.3;
    }

    @include mQ(575px) {
      margin: 10px 0 12px;
      padding: 9px 26px 9px 26px;
      border-radius: 26px;
      font-size: 1.5rem;
      line-height: 1.2;
    }

  }
}

.summary-list-item-cta {
  position: absolute;
  right: 18px;
  @include fontScaling(right,1.8);
  top: 9px;
  @include fontScaling(top,0.9);

  @include for-phone {
    position: relative;
    right: 0;
    top: 0;
    order: 3;
    text-align: right;
    margin-top: 7px;
    margin-bottom: 10px;
  }

  .btn-orange {
    background-color: #f7a500;
    padding: 4px 23px 4px;
    @include fontScaling(padding-top,0.4);
    @include fontScaling(padding-right,2.3);
    @include fontScaling(padding-bottom,0.4);
    @include fontScaling(padding-left,2.3);
    font-size: 14.4px;
    @include fontScaling(font-size,1.44);
    font-family: "pfhandbookpro-bold", sans-serif;
    border-bottom: none;
    margin-left: 30px;
    @include fontScaling(margin-left,3.0);
    white-space: nowrap;
    height: 30px;
    @include fontScaling(height,3.0);
    width: 120px;
    @include fontScaling(width,12.0);

    &:hover,
    &:active,
    &:focus {
      color: $black;
    }

    @include for-phone {
      height: auto;
      width: auto;
    }

    @include mQ(767px, 576px) {
      font-size: 2.2rem;
      padding: 8px 35px 8px;
    }

    @include mQ(575px) {
      font-size: 1.4rem;
      padding: 5px 23px 5px;
    }
  }
}

.summary-list-item-title-small {
  font-size: 1.4rem;
  color: #000;
  font-family: "pfhandbookpro-light", sans-serif;

  @include mQ(575px) {
    font-size: 0.9rem;
  }
}