//msg
.msg{
    text-align: center;
    &__icon{
        font-size: 6rem;
        display: inline-block;
        margin-bottom: 0.8rem;
    }
    &__img{
        max-width: 6rem;
        display: inline-block;
        margin-bottom: 0.8rem;
    }
    &__title{
        font-size: $font32;
        line-height: $line-height-34;
        color: $msg-text-color;
        font-family: $pfhandbookpro-regular;
        margin-bottom: 1rem;
        @include for-phone{
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__desc{
        font-size: $font24;
        line-height: $line-height-26;
        color: $msg-text-color;
        font-family: $pfhandbookpro-thin;
        @include for-phone{
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
}