.gender-switch-wrap {
    max-width: 45rem;
    margin: 1.6rem 0;
    &__title {
        font-size: $font32;
        line-height: $line-height-1;
        margin-bottom: 1.2rem;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
}

.gender-switch {
    height: 5rem;
    border-radius: 5.2rem;
    background-color: $gender-switch-bg;
    display: flex;
    position: relative;
    &__group {
        width: 33.33%;
        position: relative;
        z-index: 2;
    }
    &__label {
        font-family: $pfhandbookpro-bold;
        font-size: $font20;
        line-height: $line-height-50;
        border-radius: 5.2rem;
        color: $gender-switch-lbl;
        text-align: center;
        display: block;
        margin: 0;
        @include for-phone {
            font-size: $font18;
        }
    }
    &__input {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
        &:checked {
            &+.gender-switch__label {
                color: $gender-switch-lbl-active;
                .gender-switch__icon {
                    display: inline-block;
                }
            }
        }
    }
    &__indicator {
        box-shadow: 0 0 14px 0 rgba($gender-switch-shadow, 0.24);
        background: $gender-switch-indicator;
        position: absolute;
        top: 0;
        left: 0;
        width: 33%;
        height: 100%;
        z-index: 0;
        border-radius: 50px;
        transition: transform 0.4s ease-out;
    }
    &__icon {
        font-size: $font30;
        color: $gender-switch-icon;
        vertical-align: middle;
        margin: 0 2rem 0 0;
        display: none;
        @include for-phone {
            margin: 0 1.2rem 0 0;
        }
    }
}