.LOBQuickLinks,.lOBQuickLinks{
    &.padding-x {
        padding-left: 14px;
        padding-right: 14px;

        @include fontScaling(padding-left,1.4);
        @include fontScaling(padding-right,1.4);
    }
    ul.link-list__list--column{
        padding: 5px 0px;
        @include fontScaling(padding-top,0.5);
        @include fontScaling(padding-bottom,0.5);
        @include for-tablet{
            display: block; 
        }
        @include for-phone{
            padding-top: 15px;
            padding-bottom: 0px;
        }
        li{
            width: 100%;
            max-width: 100%;
            a{
                border-radius: 2px;
                box-shadow: 2px 6px 12px 0 rgba(0, 0, 0, 0.08);
                margin-bottom: 10px;
                font-size: $font14;
                line-height: 1.4;
                padding: 17.5px 20px;
                border: solid 0.5px #da9089;

                @include fontScaling(border-radius,0.2);
                @include fontScaling(margin-bottom,1.0);
                @include fontScaling(font-size,1.4);
                @include fontScaling(padding-top,1.75);
                @include fontScaling(padding-right,2.0);
                @include fontScaling(padding-bottom,1.75);
                @include fontScaling(padding-left,2.0);
                @include fontScaling(border-width,0.05);

                @media screen and (min-width: $custom-screen-max-width) {
                    box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.2rem) 0 rgba(0, 0, 0, 0.08);
                }
                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                    box-shadow: 0.2vw 0.6vw 1.2vw 0 rgba(0, 0, 0, 0.08);
                }



                @include themify($themes) {
                    border-color: themed('contentCardWithBackgroundOpacity');  
                }
                @include mQ(767px,576px) {
                    font-size: 2.0rem;
                    line-height: 1.17;
                    margin-bottom: 26px; 
                    box-shadow: 2px 2px 7px 0 rgba(0, 0, 0, 0.08);
                    &::after { 
                        font-size: $font18;
                     }
                }
                @include mQ(575px) {
                    font-size: 1.3rem;
                    margin-bottom: 10px;
                }
                &::after{
                    //right: 16px;
                    right: 9px;
                    font-size: $font16;

                    @media screen and (min-width: $custom-screen-max-width) {
                        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
                        right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                        margin-top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.3rem);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        font-size: 1.6vw;
                        //right: 1.2vw;
                        right: 9px;
                        margin-top: -0.3vw;
                    }  

                    @include themify($themes) {
                        color: themed('backgroundColor');  
                    }
                    @include for-phone{
                        top:50% !important;
                    }
                    @include mQ(575px) {
                        right: 10px;
                        font-size: 1.2rem;
                    }
                }
                // &:hover{                    
                //     &::after{
                //         right: 16px;
                //         font-size: $font16;
                //     }                    
                // }
               
                &.cta-link{
                    text-transform: uppercase;
                    font-family: $pfhandbookpro-bold;
                    @include themify($themes) {
                        color: themed('backgroundColor');  
                    }                    
                }
            }
            &:nth-last-child(1) {
                &:first-child {
                    a {
                        padding: 84px 40px 84px 20px;
                        @include fontScaling(padding-top,8.4);
                        @include fontScaling(padding-right,4.0);
                        @include fontScaling(padding-bottom,8.4);
                        @include fontScaling(padding-left,2.0);
                        &::after{
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                    ~ {
                        li {
                            a {
                                padding: 84px 40px 84px 20px;
                                @include fontScaling(padding-top,8.4);
                                @include fontScaling(padding-right,4.0);
                                @include fontScaling(padding-bottom,8.4);
                                @include fontScaling(padding-left,2.0);
                                &::after {
                                    top: 50%;
                                    transform: translateY(-50%);
                                }
                            }
                        }
                    }
                }
            }
            &:nth-last-child(2) {
                &:first-child {
                    a {
                        //padding: 34px 40px 34px 20px;
                    }
                    ~ {
                        li {
                            a {
                                //padding: 34px 40px 34px 20px;
                            }
                        }
                    }
                }
            }
            &:nth-last-child(3) {
                &:first-child {
                    a {
                        padding: 17.5px 20px;
                        padding-right: 40px;

                        @include fontScaling(padding-top,1.75);
                        @include fontScaling(padding-right,4.0);
                        @include fontScaling(padding-bottom,1.75);
                        @include fontScaling(padding-left,2.0);
                    }
                    ~ {
                        li {
                            a {
                                padding: 17.5px 20px;
                                padding-right: 40px;

                                @include fontScaling(padding-top,1.75);
                                @include fontScaling(padding-right,4.0);
                                @include fontScaling(padding-bottom,1.75);
                                @include fontScaling(padding-left,2.0);
                            }
                        }
                    }
                }
            }
            &:nth-last-child(4) {
                &:first-child {
                    a {
                        padding: 10px 40px 10px 20px;

                        @include fontScaling(padding-top,1.0);
                        @include fontScaling(padding-right,4.0);
                        @include fontScaling(padding-bottom,1.0);
                        @include fontScaling(padding-left,2.0);


                        @include for-phone {
                            padding: 10px 35px 10px 16px;
                        }
                        @include mQ(575px) {
                            padding: 10px 25px 10px 10px;
                        }
                        &::after{
                            top: 50%;
                        }
                    }
                    ~ {
                        li {
                            a {
                                padding: 10px 40px 10px 20px;

                                @include fontScaling(padding-top,1.0);
                                @include fontScaling(padding-right,4.0);
                                @include fontScaling(padding-bottom,1.0);
                                @include fontScaling(padding-left,2.0);

                                &::after{
                                    top: 50%;
                                }
                            }
                            
                        }
                    }
                }
            }
            @include for-phone{
                    width: calc(50% - 13px);
                    float: left;
                    &:nth-child(2n+2) {
                        margin-left: 26px;
                    }
            }
            @include mQ(575px) {
                width: calc(50% - 3px);
                &:nth-child(2n+2) {
                    margin-left: 6px;
                }
            }
            // @media (max-width: 575px) {
            //     width: 100%;
            //     float: left;
            //     margin-right: 0px !important;
            // }
            // &:nth-last-of-type(odd) {
            //     @include for-phone{
            //     margin-right: 14px;
            //     }
            // }
            &::before{
                content: none;
            }

            &.link-list__list-download {
                a {
                    border-radius: 2.6px;
                    box-shadow: 2px 2px 7px 0 rgba(0, 0, 0, 0.08);

                    @include fontScaling(border-radius,0.26);
                    @media screen and (min-width: $custom-screen-max-width) {
                        box-shadow: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem) 0 rgba(0, 0, 0, 0.08);
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        box-shadow: 0.2vw 0.2vw 0.7vw 0 rgba(0, 0, 0, 0.08);
                    }

                    &:after {
                        content: $icon-square-download;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
            }
            &.link-list__mobile-inline-description {
                a{
                    p{
                        span {
                            @media (max-width: 767px) {
                                font-size: $font27;
                                width: auto;
                                display: inline;
                                font-family: "pfhandbookpro-regular", sans-serif;
                                &::before {
                                    content: " ";
                                }
                            }
                            @media (max-width: 575px) {
                                font-size: $font16;
                            }
                        }
                    }
                }
            }
            &.life-insurance-li {
                a {
                    padding: 17.5px 20px !important;
                    @include fontScaling(padding-top,1.75,!important);
                    @include fontScaling(padding-right,2.0,!important);
                    @include fontScaling(padding-bottom,1.75,!important);
                    @include fontScaling(padding-left,2.0,!important);
                }
            }
        }
        li:last-child{
            a{
                margin-bottom: 0px;
            }
        }
        
    }
    ul.no-stretch{
        li{
            a{
                padding: 17.5px 30px 17.5px 20px !important;

                @include fontScaling(padding-top,1.75,!important);
                @include fontScaling(padding-right,3.0,!important);
                @include fontScaling(padding-bottom,1.75,!important);
                @include fontScaling(padding-left,2.0,!important);

                @include mQ(575px) {
                    padding: 9px 34px 9px 10px !important;
                    word-break: break-word;
                    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                        word-break: break-all;
                    }
                }
            }
        }
    }
    .dashboard__right-link-list-content-title {
        font-family: "pfhandbookpro-bold", sans-serif;
        font-size: $font16;
        line-height: 19px;
        padding: 20px 10px 0 10px;

        @media screen and (min-width: $custom-screen-max-width) {
            font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
            line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font19});
            padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15}) 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15});
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            font-size: 1.6vw;
            line-height: 1.9vw;
            padding: 2.0vw 1.5vw 0 1.5vw;
        }


        @include mQ(767px) { 
            font-size: $font27;
            line-height: 31px;
        }
        @include mQ(575px) { 
            font-size: $font17;
            line-height: 19px;
        }
    }
}
.dashboard__right-my-services-content-wrapper {
    ul.link-list__list--column{
        li {
            @include mQ(767px) {
                width: calc(50% - 3px);
                &:nth-child(2n+2) {
                    margin-left: 0;
                }
            }
        }
    }
}
div[data-id=insurance-advisory] {
    .two-col-list {
        ul.link-list__list--column{ 
            li {
                @include mQ(767px) {
                    width: calc(50% - 3px);
                    &:nth-child(2n+2) {
                        margin-left: 6px;
                    }
                }
            }
            &.link-list__list--column-with-orange-btn {
                li {
                    @include mQ(767px) {
                        &:nth-child(2n+2) {
                            margin-left: 0px;
                        }
                    }
                }
            }
        }
    }
}
.no-stretch{
    ul{
        li{
            a{
                padding: 17.5px 20px !important;

                @include fontScaling(padding-top,1.75,!important);
                @include fontScaling(padding-right,2.0,!important);
                @include fontScaling(padding-bottom,1.75,!important);
                @include fontScaling(padding-left,2.0,!important);
            }
        }
    }
}

.margin-left {
    margin-left: 16px; 
    @include fontScaling(margin-left,1.6);
    @include for-phone{
        margin-left: 0;
    }
}

.dashboard__portfolio-content-wrapper.padding-bottom {
    padding-bottom: 96px;
    @include fontScaling(padding-bottom,9.6);
}
.dashboard__right-my-services-content-wrapper {
    overflow: hidden;
    padding: 16px 14px;

    @include fontScaling(padding-top,1.6);
    @include fontScaling(padding-right,1.4);
    @include fontScaling(padding-bottom,1.6);
    @include fontScaling(padding-left,1.4);
   
    .listsummary-main-wrapper {
        display: inline-block;
        margin: 4px 8px 12px 8px;
        width: calc(50% - 19px);

        @include fontScaling(margin-top,0.4);
        @include fontScaling(margin-right,0.8);
        @include fontScaling(margin-bottom,1.2);
        @include fontScaling(margin-left,0.8);

        @media screen and (min-width: $custom-screen-max-width) {
            width: calc(50% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.9rem));
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
            width: calc(50% - 1.9vw);
        }

        @include mQ(767px) { 
            width: calc(100% - 19px);
            margin: 4px 4px 16px 4px;
        }
        @include mQ(575px) { 
            width: calc(100% - 11px);
            margin: 4px 4px 9px 4px;
        }
        .listsummary-head .listsummary-head-title-static {
            padding: 8px 12px 8px 10px;

            @include fontScaling(padding-top,0.8);
            @include fontScaling(padding-right,1.2);
            @include fontScaling(padding-bottom,0.8);
            @include fontScaling(padding-left,1.0);

        }
    }   
    .LOBQuickLinks,.lOBQuickLinks{
        ul.link-list__list--column{
            padding: 12px 0;
            @include fontScaling(padding-top,1.2);
            @include fontScaling(padding-bottom,1.2);
            @include mQ(767px) { 
                padding: 18px 0;
            }
            @include mQ(575px) { 
                padding: 11px 0;
            }
            li {
                padding: 4px 12px;
                padding-left: 14px !important;
                max-width: 100%;

                @include fontScaling(padding-top,0.4);
                @include fontScaling(padding-right,1.2);
                @include fontScaling(padding-bottom,0.4);
                @include fontScaling(padding-left,1.4,!important);


                @include mQ(767px) { 
                    width: 100%;
                    padding: 6px 20px;
                    padding-left: 20px !important;
                }
                @include mQ(575px) { 
                    padding: 4px 12px;
                    padding-left: 12px !important;
                }
                a {
                    margin-bottom: 0;
                    padding: 12px 16px !important;

                    @include fontScaling(padding-top,1.2,!important);
                    @include fontScaling(padding-right,1.6,!important);
                    @include fontScaling(padding-bottom,1.2,!important);
                    @include fontScaling(padding-left,1.6,!important);

                    .title {
                        width: calc(100% - 25px);
                        word-break: break-word;
                        @media screen and (min-width: $custom-screen-max-width) {
                            width: calc(100% - calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.5rem));
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            width: calc(100% - 2.5vw);
                        }
                        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                            word-break: break-all;
                        }
                    }
                    @include mQ(767px) { 
                        padding: 19px 16px !important;                        
                    }
                    @include mQ(575px) { 
                        padding: 12px 16px !important;
                    }
                }
            }
        }
    }
}
.LOBQuickLinks_row3_wrapper{
    .LOBQuickLinks{
        ul{
            &.link-list__list--column{
                width: 100%;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-wrap: wrap;
                flex-wrap: wrap;
                padding: 4px 0px;
                @include fontScaling(padding-top,0.4);
                @include fontScaling(padding-bottom,0.4);
                &.reduce-p-lr {
                    li {
                        padding: 5px 10px !important;
                        @include fontScaling(padding-top,0.5,!important);
                        @include fontScaling(padding-right,1.0,!important);
                        @include fontScaling(padding-bottom,0.5,!important);
                        @include fontScaling(padding-left,1.0,!important);
                        @include for-phone{
                            padding: 5px 0 !important;
                        }
                    }
                    
                }
                li{
                    
                    width: 33.333333%;
                    padding: 5px 15px !important;

                    @media screen and (min-width: $custom-screen-max-width) {
                        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15}) !important;
                    }
                    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                        padding: 0.5vw 1.5vw !important;
                    }
                    
                    @include for-phone{
                        width: 100%;
                        margin-left: 0;
                    }
                    a{
                        margin-bottom:0px;
                        padding: 5px 20px 5px 10px !important;
                        position: relative;
                        min-height: 54px;

                        @media screen and (min-width: $custom-screen-max-width) {
                            padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}) !important;
                            min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 5.4rem);
                        }
                        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                            //padding: 0.5vw 2.0vw 0.5vw 1.0vw !important;
                            padding: 5px 20px 5px 7px !important;
                            min-height: 5.4vw;
                        }

                        p{
                            position: absolute;
                            top: 50%;
                            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                                /* IE10+ CSS styles go here */
                                top: 32%;
                            }
                            -moz-transform: translateY(-50%);
                            -webkit-transform: translateY(-50%);
                            font-size: $font14;
                            line-height: 1.4;
                            margin: 0px;
                            padding-right: 33px;
                            color: $grey-003;
                            margin-right: 40px;
                            word-break: break-word;
                            @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                                word-break: break-all;
                            }
                            
                            @include mQ(1100px,992px) {
                                margin-right: 10px;
                            }
                            @media screen and (min-width: $custom-screen-max-width) {
                                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.4rem);
                                padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.2rem);
                                margin-right: 0;
                            }
                            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                font-size: 1.4vw;
                                padding-right: 2.2vw;  
                                margin-right: 0;  
                            }

                            
                            sup.ovr-sup {
                                font-size: 1.6rem;
                                top: -2px;

                                @media screen and (min-width: $custom-screen-max-width) {
                                    font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.6rem);
                                    top: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.2rem);
                                }
                                @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                    font-size: 1.6vw;
                                    top: -0.2vw;    
                                }
                            }
                        }
                        span{
                            width: 100%;
                            display:block;
                            font-size: 12px;
                            @media screen and (min-width: $custom-screen-max-width) {
                                font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
                            }
                            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                font-size: 1.2vw;
                            }
                        }
                        sup {
                            font-size: 60%;
                        }
                    }
                    &::before{
                        content:none;
                    }

                    &.link-list__static-list {                       
                        a {
                            border: 0 none;
                            @include themify($themes) {
                                border-left: themed('contentCardLeftBorderColor') !important;
                            }
                            border-radius: 2.6px !important;
                            //max-height: 103px;
                            min-height: 84px;
                            padding: 7px 20px 5px 10px !important;

                            @media screen and (min-width: $custom-screen-max-width) {
                                padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font7}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font5}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}) !important;
                                border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.6px) !important;
                                min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 8.4rem);
                            }
                            @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
                                padding: 0.7vw 2.0vw 0.5vw 1.0vw !important;
                                border-radius: 0.26vw !important;
                                min-height: 8.4vw;
                            }

                            @include mQ(767px) { 
                                max-height: none;
                                padding: 25px 16px 25px 16px !important;
                            }
                            @include mQ(575px) {
                                padding: 16px 10px 16px 10px !important;
                            }
                            p {
                                position: relative;
                                top: 0;
                                transform: translateY(0);
                                font-size: 1.4rem;
                                line-height: 19px;
                                font-family: "pfhandbookpro-bold", sans-serif;
                                padding-right: 5px;

                                @include fontScaling(font-size,1.4);
                                @include fontScaling(line-height,1.9);
                                @include fontScaling(padding-right,0.5);


                                @include mQ(767px) { 
                                    padding-right: 65px;
                                    line-height: 30px !important;
                                }
                                @include mQ(575px) { 
                                    padding-right: 30px;
                                    line-height: 19px !important;
                                }
                                span {
                                    line-height: 1.33;
                                    opacity: 0.7;
                                    margin-top: 2px;
                                    color: $brand-black;
                                    text-overflow: ellipsis;
                                    overflow: hidden;
                                    //max-height: 45px;

                                    @include fontScaling(margin-top,0.2);


                                    @include mQ(767px) { 
                                        line-height: normal;
                                        opacity: 0.5;
                                        font-size: 2.4rem;
                                        margin-top: 5px;
                                        max-height: none;
                                    }
