.pagination {
  display: block;
  text-align: center;
  ul {
    padding-left: 0;
    margin-top: 0px;
    display: inline-block;
  }
  li,
  .page-dots {
    margin: 0 0.2em;
    display: inline-block;
    min-width: auto;

    &:before {
      display: none;
    }
    a {
      border-bottom: none !important;
    }
    a,
    span {
      font-family: $pfencoresanspro-medium;
      font-size: 1.6rem;
      letter-spacing: 0.7px;
      text-align: center;
      color: darken(desaturate(adjust-hue($capital-red, -357), 70.82), 10.39);
      padding: 8px 12px;
      vertical-align: middle;
      width: 38px;
      height: 38px;
      border-radius: 2px;
      background-color: lighten(
        desaturate(adjust-hue($capital-red, -357), 70.82),
        50.78
      );
      border: solid 1px transparent !important;
    }
    &.active,
    &:hover {
      a,
      span {
        border: solid 1px $capital-red !important;
        color: $capital-red;
      }
    }
  }

  .page-dots {
    &.active,
    &:hover {
      a,
      span {
        border: solid 1px transparent !important;
        color: darken(desaturate(adjust-hue($capital-red, -357), 70.82), 10.39);
      }
    }
  }

  .button {
    a {
      position: relative;
    }
    &.active,
    &:hover {
      a {
        border: solid 1px transparent !important;
        color: $white;
      }
    }
  }

  .prev,
  .next {
    > span {
      position: relative;
      display: inline-block;
      cursor: pointer;
      &:after {
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        content: $icon-pagination-arrow;
        font-family: "font-icons";
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
        position: absolute;
        top: 11px;
        left: 10px;
        color: $white;
      }
    }
    &.active,
    &:hover {
      span {
        color: $white;
        border: solid 1px transparent !important;
      }
    }
  }
  .page-item.first,
  .page-item.last {
    > span {
      position: relative;
      background-color: $capital-red;
      width: 38px;
      height: 38px;
      border-radius: 2px;
      display: inline-block;
      cursor: pointer;
      &:after,
      &:before {
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        content: $icon-pagination-arrow;
        font-family: "font-icons";
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
        position: absolute;
        color: $white;
      }
      &:after {
        top: 11px;
        right: 5px;
      }
      &:before {
        top: 11px;
        left: 3px;
      }
    }
    &.disabled {
      > span {
        pointer-events: none;
        cursor: not-allowed;
        background-color: darken(
          desaturate(adjust-hue($capital-red, -160), 67.68),
          1.96
        );
      }
    }
  }
  .page-item.last {
    > span {
      &:after,
      &:before {
        transform: rotateY(180deg);
      }
      &:after {
        right: 3px;
      }
      &:before {
        left: 5px;
      }
    }
  }
  .prev {
    //display: none;
    > span {
      width: 38px;
      height: 38px;
      border-radius: 2px;
      color: $white;
      background-color: $capital-red;
    }
    &.disabled {
      > span {
        pointer-events: none;
        cursor: not-allowed;
        background-color: darken(
          desaturate(adjust-hue($capital-red, -160), 67.68),
          1.96
        );
      }
    }
  }

  .next {
    > span {
      width: 38px;
      height: 38px;
      border-radius: 2px;
      color: $white;
      background-color: $capital-red;
      &:after {
        transform: rotateY(180deg);
      }
    }
    &.disabled {
      > span {
        pointer-events: none;
        cursor: not-allowed;
        background-color: darken(
          desaturate(adjust-hue($capital-red, -160), 67.68),
          1.96
        );
      }
    }
  }
}
