.filter {
    $self: &;
    @include for-phone {
        margin-right: -15px;
    }
    &__item {
        display: block;
        margin-bottom: 1.6rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__wrap {
        padding-left: 0;
    }
    .slick-slide {
        margin-right: 1.5rem;
    }
}

.faq-buy-guide {
    &__list {
        margin-bottom: 4rem;
        @include for-phone {
            margin-bottom: 2rem;
        }
        .tabs__link--v3 {
            margin-right: 1rem;
        }
    }
}

.faq {
    .tabs {
        &__anchor {
            &--v3 {
                color: $filter-link;
                background-color: $filter-link-bg;
                &:hover,
                &:focus,
                &:active {
                    color: $filter-link-hover;
                }
            }
        }
        &__link {
            &--active {
                .tabs__anchor--v3 {
                    background-color: $filter-link-bg-active;
                    color: $filter-link-active;
                }
            }
        }
        &__links {
            &--droplist {
                background: $filter-drop-list;
                border: 1px solid $filter-drop-border;
                box-shadow: 0 2px 4px 0 rgba($filter-drop-shadow, 0.25);
                .tabs__link {
                    color: $filter-drop-selected;
                    border-bottom: 1px solid $filter-drop-border;
                    &--active {
                        .tabs__anchor {
                            background: transparent;
                            color: $filter-drop-selected;
                            &:hover {
                                color: $filter-drop-selected;
                            }
                        }
                    }
                }
                .tabs__anchor {
                    background: transparent;
                    color: $filter-drop-selected;
                    &--active,
                    &:hover {
                        color: $filter-drop-selected;
                        background: $filter-drop-item-active;
                    }
                    &:active,
                    &:focus-within {
                        color: $filter-drop-selected;
                        background: $filter-drop-item-active;
                    }
                }
            }
        }
    }
}