/*
 * Dashboard Global Style
 *
 */

.dashboard {
  &__main-wrapper {
    margin-top: 24px;
    border-radius: 3px;
    box-shadow: 0 6px 20px -9px rgba(0, 0, 0, 0.22);
    background-color: $white;

    // @include mQmin($xxl){
    //   margin-top: 30px;
    // }
    @include mQ(991px) {
      margin-left: -15px;
      margin-right: -15px;
      box-shadow: none;
    }

    @include mQ(575px) {
      margin-top: 15px;
    }
  }

  &__main-title {
    width: 22.187%;
    line-height: 1.07;
    letter-spacing: normal;
    font-family: $pfhandbookpro-medium;
    font-size: $font14;
    padding: 7px 14px 0px 14px;

    
    @media screen and (min-width: $custom-screen-max-width) {
        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
        padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14}) 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      font-size: 1.4vw;
      padding: 0.7vw 1.4vw 0 1.4vw;
    }
    // @include mQmin($xxl){
    //     font-size: 17px;
    //     padding: 7px 17px 0 17px;
    // }

    // Color Theme
    @include themifySelf($themes) {
      background-color: themed("backgroundColorWithOpacityForLeftVertical");
      color: themed("textColorForLeftMainTitle");
    }

    @include mQ(991px) {
      display: block;
      order: 1;
      width: 100%;
      padding: 10px 16px;
      font-size: $font18;
    }

    @include mQ(575px) {
      font-size: $font11;
      padding: 6px 16px;
    }
  }

  // Top Wrapper
  &__top-wrapper {
    display: flex;

    @include mQ(991px) {
      flex-direction: column;
    }
  }

  // Bottom Wrapper
  &__bottom-wrapper {
    display: none;

    &.active {
      display: block;
    }

    // Color Theme
    @include themifySelf($themes) {
      .dashboard__left-inner-wrapper {
        background-color: themed("backgroundColorWithOpacityForLeftVertical");

        @include mQ(991px) {
          background-color: transparent;
          position: relative;
        } 
      }
    }
  }

  &__bottom-inner-wrapper {
    display: flex;

    @include mQ(991px) {
      flex-wrap: wrap;
    }
  }

  // Bottom Wrapper - Left and Right Sections
  // Left Section
  &__left-inner-wrapper {
    width: 22.187%;
    padding: 0px;

    @include mQ(991px) {
      width: 100%;
    }
  }

  &__right-inner-wrapper {
    width: 77.814%;
    padding: 15px 15px 20px 18px;

    @media screen and (min-width: $custom-screen-max-width){
      padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      padding: 1.5vw 1.5vw 2.0vw 1.8vw;
    }
    // @include mQmin($xxl){
    //   padding: 16px 20px 20px 18px;
    // }

    @include mQ(991px) {
      width: 100%;
      position: relative;
      //z-index: 1; issue in mobile popup hidden inside backdrop
    }
  }

  &__inner-content-wrapper {
    display: none;

    &.active {
      display: block;
    }

    .normal-link {
      padding-right: 20px;
      padding-bottom: 30px;
      padding-top: 20px;

      a {
        font-family: $pfhandbookpro-regular;
        font-size: $font14;
        color: $black;
        display: inline-block;
        border-bottom: 1px solid transparent;

        &:hover {
          border-color: $black;
        }
      }

    }

    .dashboard__cta {
      margin-bottom: 10px;
      @include fontScaling(margin-bottom,1.0);
      @include fontScaling(line-height,2.0);

      @include mQ(575px) {
        margin-bottom: 20px;
      }

      a,
      button {
        padding: 12px 21px;
        border-radius: 2px;
        @include fontScaling(padding-top,1.2);
        @include fontScaling(padding-right,2.1);
        @include fontScaling(padding-bottom,1.2);
        @include fontScaling(padding-left,2.1);
        @include fontScaling(border-radius,0.2);

        @include mQ(575px) {
          font-size: $font10;
          line-height: 12px;
          padding: 8px;
        }
      }
    }
  }

  // &__main-content-wrapper {
  //   @include mQ(991px) {
  //     .reduce-top-margin {
  //       margin-top: -55px;
  //     }
  //   }
  //   @include mQ(575px){
  //     .reduce-mb {
  //       margin-top: -30px;
  //     }
  //   }
  // }

  // Static content
  &__static-content-wrapper {
    display: flex;
  }

  &__static--left-content-wrapper {
    width: 22.187%;
    padding: 14px;

    @media screen and (min-width: $custom-screen-max-width) {
      padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      padding: 1.4vw;
    }

    // Color Theme
    @include themifySelf($themes) {
      background-color: themed("backgroundColorWithOpacityForLeftVertical");
    }

    @include mQ(991px) {
      display: none;
    }
  }

  &__static--right-content-wrapper {
    width: 77.814%;
    padding: 20px 0px 31px 0;

    @media screen and (min-width: $custom-screen-max-width) {
      padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.0rem) 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 3.1rem) 0;
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      padding: 2.0vw 0 3.1vw 0;
    }

    @include mQ(991px) {
      width: 100%;
      padding: 20px 0px 21px 0;
    }
  }

  // General Styling

  // Button
  &__cta {
    display: inline-block;
    margin-top: auto;

    a,
    button {
      font-family: $pfhandbookpro-bold;
      font-size: $font16;
      text-align: center;
      color: $white;
      padding: 7px 40px 8px;
      border-radius: 1.6px;
      display: inline-block;
      text-decoration: none;
      border: none;

      // Color Theme
      @include themify($themes) {
        background-color: themed("backgroundColor");
      }

      &:hover,
      &:focus {
        color: $white;
        border-bottom: none;
      }
    }
  }

  // Table
  &__table-wrapper {
    overflow-x: auto;
    margin-top: 10px;
    margin-bottom: 26px;

    &::-webkit-scrollbar {
      height: 5px;
    }

    &::-webkit-scrollbar-track {
      background: transparentize($grey-004, 0.75);
    }

    &::-webkit-scrollbar-thumb {
      background: $grey-004;
    }

    @include mQ(575px) {
      margin-bottom: 12px;
    }

    table {
      border-right: 1px solid $grey-006;
      min-width: 690px;
      width: 100%;

      @include mQ(575px) {
        min-width: 440px;
      }

      tr {
        th {
          background-color: $grey-004;
          font-family: $pfhandbookpro-bold;
          font-size: $font14;
          padding: 14px 10px;
          color: $white;

          @include mQ(575px) {
            font-size: $font9;
            line-height: 11px;
            padding: 9px 7px;
          }

          &.center {
            text-align: center;
          }

          &:first-child {
            padding-left: 20px;

            @include mQ(575px) {
              padding-left: 14px;
            }
          }

          &:last-child {
            padding-right: 20px;

            @include mQ(575px) {
              padding-right: 14px;
            }
          }
        }

        td {
          font-family: $pfhandbookpro-regular;
          font-size: $font14;
          line-height: 18px;
          padding: 26px 10px;
          color: $grey-005;
          border-bottom: 1px solid $grey-006;
          vertical-align: top;

          @include mQ(575px) {
            font-size: $font9;
            line-height: 11px;
            padding: 16px 7px;
          }

          &.center {
            text-align: center;
          }

          &:first-child {
            padding-left: 20px;

            @include mQ(575px) {
              padding-left: 14px;
            }
          }

          &:last-child {
            padding-right: 20px;

            @include mQ(575px) {
              padding-right: 14px;
            }
          }
        }
      }
    }
  }

  // No data copy
  &__no-data-copy {
    height: 256px;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    border: 1px solid $grey-006;
    margin-top: 12px;
    margin-bottom: 20px;

    @include mQ(767px) {
      border: 0 none;
    }

    @include mQ(575px) {
      height: 175px;
    }

    p {
      font-family: $pfhandbookpro-bold;
      font-size: $font18;
      line-height: 20px;
      color: $black;
      letter-spacing: .2px;

      @include mQ(575px) {
        font-size: $font11;
        line-height: 13px;
        margin-bottom: 8px;
      }
    }

    .dashboard__cta {
      margin-top: 0;
    }
  }

  &__invest-now-content-wrapper,
  &__my-services-content-wrapper,
  &__right-protect-now-content-wrapper {
    @include mQ(575px) {
      min-height: auto;
    }
  }
  
  &__portfolio-inner-content-wrapper, &__right-copy-wrapper {
    clear: both;
  } 
  &__table-wrapper {
    .dashboard__table-v2 {
      tr {
        th, td {
          text-align: center;
          &:first-child {
            text-align: left;
          }
        }
        
        td {
          padding: 10px 25px;
          border-bottom:none;
        }
      }
      tbody {
        tr {
          display: none;
          &.visible { 
            display: table-row;
          }
        }
      }
    }
  }
}
.formatMaxWidth {
  max-width: 286px !important;
}
.hr {
  width: 100%;
  border-top: 2px solid rgba(151, 151, 151, 0.2) !important;
  height: 1px;
  margin: 12px 0px 15px 0px;
  @include fontScaling(height,0.1);
  @include fontScaling(margin-top,1.2);
  @include fontScaling(margin-bottom,1.5);
  @include fontScaling(border-top-width,0.2);

}
.tr-view-more {
  font-size: $font16;
  font-family: $pfhandbookpro-bold;
  text-decoration: underline;
  border: none;
  margin-left: 20px;
  text-transform: uppercase;
  display: inline-block;
  @include themify($themes) { 
    color: themed("backgroundColor");
  }
  &:hover, &:focus, &:active {
    border: none !important;
