  }
}
// Breadcrumbs Style
.breadcrumbs {
  font-family: $pfhandbookpro-regular;
  font-size: $font14;
  color: $raven-gray;
  @include fontScaling(font-size,1.4);

  span {
    padding-left: 2px;
    padding-right: 2px;
    line-height: 20px;
    @include fontScaling(padding-left,0.2);
    @include fontScaling(padding-right,0.2);
    @include fontScaling(line-height,2.0);

    &:last-child {
      padding-left: 0px;
      color: $brand-capital-red;
    }
  }

  a {
    font-family: $pfhandbookpro-regular;
    font-size: $font14;
    color: $raven-gray;
    border-color: $raven-gray;
    @include fontScaling(font-size,1.4);

    &:hover {
      border-bottom: none;
    }
  }

  &-component {
    margin-bottom: 0px;
  }

  @include mQ(767px) {
    font-size: 1.8rem;

    a {
      font-size: 1.8rem;
    }
  }

  @include mQ(575px) {
    font-size: 1.1rem;

    a {
      font-size: 1.1rem;
    }
  }
}

// ToolTip Style
.tooltip-custom {
  cursor: pointer;
  position: relative;
  z-index: 9;
  color: $protecting-shade;
  width: 12px;
  height: 12px;
  display: inline-block;

  span {
    display: none;
  }

  &:hover {
    span {
      display: block;
      position: absolute;
      font-size: 10px;
      line-height: 14px;
      top: 100%;
      width: 82px;
      left: 80%;
      transform: translateX(-50%);
      text-align: center;
      border-radius: 0.3px;
      box-shadow: 4px 4px 16px 0 rgba(0, 0, 0, 0.08);
      background-color: $white;
      color: $black;
      z-index: 99;
      font-family: $pfhandbookpro-medium;
      padding: 3px;
      margin-top: 5px;
    }
  }

  &:before {
    content: "";
    border: 1px solid $protecting-shade;
    border-radius: 50%;
    position: absolute;
    left: 0;
    width: 12px;
    height: 12px;
    top: 0;
  }

  &:after {
    content: "i";
    font-style: normal;
    font-size: $font10;
    width: 12px;
    height: 12px;
    position: absolute;
    top: -4px;
    left: 0;
    margin-left: 6px;
  }

  &-component {}
}

.dropdown-form {
  padding-left: 24px;
  padding-right: 24px;

  @include mQ(575px) {
    padding-left: 12px;
    padding-right: 12px;
  }

  .dashboard__right-content-select {
   

    &.full-width {
      max-width: none;
    }
    @include mQ(575px) {
      max-width: 100%;
    }
  }
  .form-group__datepicker-container {
    max-width: 276px;
    position: relative;
    .form-calendar-icon {
      position: absolute;
      right: 3px;
      top: 50%;
      margin: 0;
      display: block;
      transform: translateY(-50%);
      font-size: 2rem;
      cursor: pointer;
    }
  } 

  .selectize-control.single {
    .selectize-input:after {
      color: #565f66 !important;
    }
  }


  .dashboard__cta {
    display: inline-block;
    margin-top: auto;
    margin-bottom: 10px;
    
    @include fontScaling(margin-bottom,1.0);

    @include mQ(575px) {
      margin-bottom: 20px;
    }

    a,
    button,
    input {
      font-family: $pfhandbookpro-bold;
      font-size: $font20;
      text-align: center;
      color: $white;
      padding: 12px 16.5px 12px 16.5px;
      border-radius: 2px;
      display: inline-block;
      text-decoration: none;
      border: none;
      min-width: 161px;

      @include fontScaling(font-size,2.0);
      @include fontScaling(padding-top,1.2);
      @include fontScaling(padding-right,1.65);
      @include fontScaling(padding-bottom,1.2);
      @include fontScaling(padding-left,1.65);
      @include fontScaling(border-radius,0.2);
      @include fontScaling(min-width,16.1);

      // Color Theme
      @include themify($themes) {
        background-color: themed("backgroundColor");
      }

      @include mQ(575px) {
        padding: 10px;
        min-width: auto;
        font-size: 1.8rem;
      }

      &:hover,
      &:focus {
        color: $white;
        border-bottom: none;
      }
    }

    &+.dashboard__cta-v2 {
      margin-left: 30px;
      @include fontScaling(margin-left,3.0);

      @include mQ(575px) {
        margin-left: 10px;
      }
    }

    &.dashboard__cta-v2 {

      a,
      button,
      input {
        background-color: $white;
        border-width: 1px;
        border-style: solid;
        padding: 11px 16.5px 11px 16.5px;

        @include fontScaling(padding-top,1.1);
        @include fontScaling(padding-right,1.65);
        @include fontScaling(padding-bottom,1.1);
        @include fontScaling(padding-left,1.65);
        @include fontScaling(border-width,0.1);

        // Color Theme
        @include themify($themes) {
          color: themed("backgroundColor");
          border-color: themed("backgroundColor");
        }

        @include mQ(575px) {
          padding: 10px;
        }

        &:hover,
        &:focus {
          @include themify($themes) {
            color: themed("backgroundColor");
            border-color: themed("backgroundColor");
          }
        }
      }
    }
  }
}

.small-info-text {
  font-size: 1.2rem;
  line-height: 1.5;
  margin-right: 25px;
}
.pl-clearfix {
  clear: both;
}
.abc-form.abc-post-login-form  {
  .account-statement-sms-main-wrapper {
    width: 100%;
    background-color: rgba(161, 172, 123, 0.1);
    padding: 12px 19px 8px 19px;
  
    .account-statement-sms-checkbox {
      cursor: pointer;
      label {
        font-size: 1.4rem;
        color: #3e593c;
        &:before {
          width: 15px;
          height: 15px;
          top: 4px;
        }
        
      }
      input[type="checkbox"]:checked + label:after {
        left: 5px;
        top: 5px;
        width: 5px;
        height: 10px;
        border-width: 0 1px 1px 0 !important;
        border: solid #3e593c;
      } 
    }
    .download-save__cta {
      min-width: 160px;
    }
    .account-statement-sms__title {
      margin-bottom: 8px;
      color: rgba(0,0,0,0.7);
    }
    .inline-radio {
      margin-top: 14px;
      margin-bottom: 22px;
      .form-check {
        margin-right: 24px;
        font-size: 1.8rem;
        color: #000000;
        padding-left: 25px;
        padding-top: 0;
        @include mQ(767px) { 
          margin-right: 15px;
        } 
        .radio {
          top: 3px;
          left: 0;
          height: 15px;
          width: 15px;
          border: 1px solid #3e593c;
          &:after {
            top: 2px;
            left: 2px;
            width: 9px;
            height: 9px;
          }
        }
        &.disabled {
          opacity: 0.5;
        }
        .disabled {          
          & + .radio {
            opacity: 0.5;
          }
        }
        input:checked ~ .radio {
          background-color: #ffffff;
          border: 1px solid #3e593c;
          box-shadow: none;
          &:after {
            background-color: #3e593c;
          }
        } 
      }
    }
    .account-statement-sms-content-top {
      display: flex;
      padding-top: 6px;
      padding-bottom: 4px;
      .float-50 {
        width: 50%;
      }
      @include mQ(767px) {
        display: block;
        padding-bottom: 14px;
        .float-50 {
          width: 100%;
        }
      }
    }
    .account-statement-sms-content-bottom {
      padding-bottom: 8px;
    }
    
  }
  .select + span.error {
    width: 100%;
  }
  .form-group__datepicker-container + span.error {
    width: 100%;
  }
}
.abc-form {
