// Modal Style
body.modal-open {
  padding-right: 0 !important;
} 
.modal-custom-component {
  .modal-title {
    font-family: $pfhandbookpro-regular;
    font-size: $font24;
    line-height: 1.17;
    color: $black;
  }

  .modal-content {
    border: none;
  }

  .modal-header {
    padding: 13px 20px 0 20px;

    .close {
      width: 40px;
      height: 40px;
      border-radius: 0px 2px 0 0;
      background-color: #5a5a5a;
      opacity: 1;
      margin: 0;
      right: 0;
      top: 0;
      text-shadow: none;

      span {
        color: $white;
        font-size: 45px;
        font-weight: normal;
        text-shadow: none;
        opacity: 1;
        position: absolute;
        right: 8px;
        top: -6px;
      }
    }
  }

  .modal-body {
    font-family: $pfhandbookpro-regular;
    font-size: $font16;
    line-height: 1.25;
    color: $brand-dark-grey;
    padding: 4px 20px;

    ul {
      li {
        margin-left: 5px;
        position: relative;
        padding-bottom: 3px;

        &:before {
          width: 0.6em;
          margin-left: -0.6em;
          font-size: 34px;
          line-height: 0;
          position: absolute;
          top: 16px;
        }
      }
    }
  }

  .modal-footer {
    justify-content: flex-start;
    border-top: none;
    padding: 26px 20px 26px 20px;
  }

  .modal-btn {
    background-color: $brand-capital-red;
    font-family: $pfhandbookpro-bold;
    font-size: 20px;
    line-height: 1;
    text-align: center;
    color: $white;
    padding: 12px 44px;
    border: none;
    margin: 0;
  }

  .modal {
    z-index: 99991;
  }

  .modal-dialog {
    @media (min-width: 576px) {
      max-width: 460px;
    }
  }
}

.modal-backdrop {

  //z-index: 9999 !important;
  &.show {
    opacity: 0.6 !important;
  }
}


// Heightlight button
.cta-bg {
  background-color: #f7a500;
  font-size: 1.8rem;
  & > p {
    color: $brand-black !important; 
    font-size: 1.8rem !important;
  }
  &::after {
    color: $brand-black !important; 
  }
}

// Added by Padma
.tooltipBackground {
  background-color: #F7A500;
  color: $brand-black;
  width: 14px;
  height: 14px;
  text-align: center;
  display: inline-block;
  margin-left: 8px;
  font-size: 1.2rem;
