/*
 * 1269 - NBO component
 *
 */

 .dashboard {

  &__nbo-cards-wrapper {
    &.change-position {
      position: static;
      height: auto !important;
      .cta-label {
        max-width: none;
      }
      .dashboard__nbo--button.dashboard__cta,
      .foot-notes.dashboard__nbo--initial {
        //display: none;
      }
      .dashboard__nbo--initial.initial-bg {
        //border-radius: 2px;
        @include fontScaling(border-radius,0.2);
      } 
      .dashboard__nbo--initial.initial-bg,
      .dashboard__nbo--initial.initial-bg + .txt-mobile {
        display: none;
      }
    
      .curve-lbl {
        //display: none;
      }
      
      .dashboard__nbo-info-icon .btn-box {
       // text-align: left;
        .btn-orange {
          display: inline-flex;
        }
      }
      .dashboard__nbo-card-item {
        //border: 1px solid $brand-capital-red;
      }
      @include mQ(767px){
        .dashboard__nbo-card-item-wrapper {
          padding: 0 15px;
        }
        .dashboard__nbo-recommended {
          display: none;
        }
        .dashboard__nbo-card-item {
          margin: 0 0 20px;
        }
        .dashboard__nbo-card-item {
          max-width: none;
        }
        .dashboard__nbo-card-item-wrapper {
          flex-wrap: wrap;
        }
        .dashboard__nbo--content, .dashboard__nbo--button, .dashboard__nbo--initial.foot-notes {
          display: block !important;
        }
      }
      @include mQ(575px) {
        .cta-label {
          font-size: $font20;
        }
        .dashboard__nbo--content, .dashboard__nbo--button {
          display: block;
        }
        .dashboard__nbo--initial.foot-notes {
          display: block !important;
        }
        .dashboard__nbo-info-icon .desc {
          min-height: auto;
        }
      }
      @include mQ(360px) {
          .cta-label {
            font-size: $font13;
          }
          .dashboard__nbo--button.dashboard__cta {
            display: inline-flex;
          }
          .dashboard__nbo--initial.initial-bg,
          .dashboard__nbo--initial.initial-bg + .txt-mobile {
            display: none;
          }
          .curve-lbl {
            font-size: $font16;
          }
          .curve-lbl, .dashboard__nbo--content {
            display: block !important;
          }
          .dashboard__nbo--initial.btn-orange {
            min-width: 66px;
            min-height: auto;
          }
          .foot-notes.dashboard__nbo--initial {
            font-size: $font14;
            display: block !important;
          }
          .dashboard__nbo-card-item {
            font-size: $font13;
            border: 0;
            &:first-of-type {
              margin-top: 20px;
            }
          }
          .dashboard__nbo-info-icon .btn-box {
            text-align: right;
          }
          .dashboard__nbo--label .btn-mini {
            font-size: $font10;
          }
      }
    }
  }

  @include for-phone {
    // &__bottom-wrapper {
    //     margin-bottom: 145px;
    // }
    &__nbo-cards-wrapper {
      position: fixed;
      bottom: 0;
      width: 100%;
      z-index: 99;
      //background-color: $white;      
    }
  }
  &__nbo-cards-innerwrapper {
    padding-left: 5px;
    padding-right: 5px;

    @media screen and (min-width: $custom-screen-max-width) {
      padding-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem);
      padding-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      padding-left: 0.5vw;
      padding-right: 0.5vw;
    }

    @include for-phone {
      margin-top: 17px;
      position: relative;
      &.active {
        box-shadow: 3px -5px 13px 0 rgba(0, 0, 0, 0.08);
        background-color: $white;
      }
    }
    @include mQ(360px){
      margin-top: 24px;
    }
  }

  &__nbo-card-item {
    &-wrapper {
      display: flex;

      @include for-phone {
        overflow-x: auto;
        padding-bottom: 10px;
      }
      @include mQ(360px){
        padding-top: 10px;
      }
     
    }
    width: 50%;
    margin-left: 13px;
    margin-right: 13px;
    border-radius: 2px;
    box-shadow: 0 6px 20px -9px rgba(0, 0, 0, 0.22);
    background-color: $white;
    display: flex;
    flex-direction: column;

    @media screen and (min-width: $custom-screen-max-width) {
      margin-left: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.3rem);
      margin-right: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 1.3rem);
      border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem);
      box-shadow: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.6rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.0rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * -0.9rem) rgba(0, 0, 0, 0.22);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
      margin-left: 1.3vw;
      margin-right: 1.3vw;
      border-radius: 0.2vw;
      box-shadow: 0 0.6vw 2.0vw -0.9vw rgba(0, 0, 0, 0.22);
    }

    @include mQ(991px) {
      margin-left: 10px;
      margin-right: 10px;
      border: none;
    }
    @include for-phone {
      width: 100%;
      padding: 0px;
      margin-top: 30px;
      border-radius: 3px;
      max-width: 220px;
    }
    @include mQ(360px){
      margin-top: 10px;
    }
  }
  &__nbo-info-icon {
    position: relative;
   
    .abolute-link {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      z-index: 3;
      border: 0;
    }
    // &:after {
    //   @include for-phone {
    //     content: $icon-nbo-info;
    //     position: absolute;
    //     top: 0;
    //     right: 0;
    //     display: block;
    //     font-size: 73px;
    //     color: $brand-capital-red;
    //     opacity: 0.1;
    //     -moz-osx-font-smoothing: grayscale;
    //     -webkit-font-smoothing: antialiased;
    //     font-family: "font-icons";
    //     font-style: normal;
    //     font-variant: normal;
    //     font-weight: normal;
    //     line-height: 1;
    //     speak: none;
    //     text-transform: none;
    //     font-size: 43px;
    //     width: 43px;
    //     height: 43px;
    //   }
    // }
    .btn-orange {
      background-color: #f7a500;
      min-width: 78px;
      font-size: $font14;
      color: $black;
      display: inline-flex;
      min-height: 26px;
      align-items: center;
      justify-content: center;
      font-family: $pfhandbookpro-bold;
      padding: 0 2px;

      @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
          min-width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 7.8rem);
          min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.6rem);
          padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem);
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 1.4vw;
          min-width: 7.8vw;
          min-height: 2.6vw;
          padding: 0 0.2vw;
      }

      &:hover {
        border: 0;
      }
      @include mQ(991px) {
        // min-width: 140px;
        // min-height: 34px;
        font-size: 1.6rem;

      }
      @include mQ(360px) {
        display: none;
      }
    }
    .btn-box {
      text-align: right;
      margin-bottom: 10px;
      padding: 0 15px 0 10px;

      @media screen and (min-width: $custom-screen-max-width) {
        margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
        padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15}) 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          margin-bottom: 1.0vw;
          padding: 0 1.5vw 0 1.0vw;
      }

      // @include mQ(991px) {
      //   text-align: left;
      // }

      @include for-phone {
        padding: 0 10px;
      }

    }
    .foot-notes {
      font-size: $font16;
      padding: 0 10px;
      font-family: $pfhandbookpro-medium;

      @media screen and (min-width: $custom-screen-max-width) {
        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
        padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          padding: 0 1.0vw;
          font-size: 1.6vw;
      }

      @include themify($themes) {
        color: themed("textColorForLeftMainTitle");
      }
      @include for-phone {
        display: flex !important;
        font-size: $font10;
        line-height: normal;
        border-radius: 0;
        padding: 4px;
      }
      @include mQ(360px) {
        display: flex;
        font-size: $font9;
        padding: 4px 2px;
      }
    }
    .desc {
      margin: 5px 0 4px 0;
      max-height: 100px;
      height: 100%;

      @media screen and (min-width: $custom-screen-max-width) {
        margin: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.5rem) 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem) 0;
        max-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 10.0rem);
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        margin: 0.5vw 0 0.4vw 0;
        max-height: 10.0vw;
      }
      
    }
  }
  &__nbo {
    &-recommended {
      padding: 0 10px 10px 10px;
      font-size: $font18;
      font-family: $pfhandbookpro-bold;

      @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font18});
          padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 1.8vw;
          padding: 0 1.0vw 1.0vw 1.0vw;
      }

      @media only screen and (min-width: 768px) {
        pointer-events: none;
        cursor: default;
      }
      @include for-phone {
        display: block;
        border-radius: 3.2px;
        background-color: $brand-capital-red;
        height: 34px;
        padding: 7px 14px 6px 22px;
        color: $white;
        font-family: $pfhandbookpro-regular;
        font-size: $font23;
        letter-spacing: -0.26px;
        position: absolute;
        top: -17px;
        left: 16px;
        margin-left: -8px;
        z-index: 9;
      }
      @include mQ(575px) {
        font-size: $font23;
        padding: 0 10px;
        display: inline-flex;
        align-items: center;
      }
      @include mQ(360px) {
        font-size: $font14;
        padding: 0 10px;
        height: auto;
        display: inline-flex;
        align-items: center;
        top: -10px;
      }
    }
    &--label {
      font-size: $font16;
      line-height: 1.25;
      background-color: $brand-capital-red;
      color: $brand-white;
      margin-bottom: 7px;
      z-index: 1;
      position: relative;
      padding: 4px 15px 4px 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 40px;

      @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
          padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font15}) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
          margin-bottom: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.7rem);
          min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 4.0rem);
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 1.6vw;
          padding: 0.4vw 1.5vw 0.4vw 1.0vw;
          margin-bottom: 0.7vw;
          min-height: 4.0vw;
      } 


      @include for-phone {
        min-height: 24px;
        margin-bottom: 0;
        padding: 4px;
      }
      @include mQ(575px) {
        font-size: $font11;
        padding: 2px;
        height: 100%;
      }

      .dashboard__nbo--button {
        @include for-phone { 
          display: block;
        }
      }
      .cta-label {
        line-height: $font16;
        max-width: 150px;
        width: 100%;

        @media screen and (min-width: $custom-screen-max-width) {
          line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font14});
            max-width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 15.0rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          line-height: 1.6vw;
            max-width: 15.0vw;
        }

        // @include mQ(768px) {
        //   max-width: none;
        // }
        @include for-phone {
          max-width: none;
          font-size: $font14;
        }
        @include mQ(360px) {
          font-size: $font11;
          max-width: 86px;
          line-height: normal;
        }
      }

      .btn-mini {
        font-size: $font12;
        font-family: $pfhandbookpro-bold;
        padding: 0 4px;
        white-space: nowrap;
        min-width: 100px;
        line-height: 20px;

        @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
          padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.4rem);
          min-width: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 10.0rem);
          line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 2.0rem);
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 1.2vw;
          padding: 0 0.4vw;
          min-width: 10.0vw;
          line-height: 2.0vw;
        }

        
        // @include mQ(767px) {
        //   display: none;
        // }
        @include mQ(360px) {
          min-width: 60px;
          font-size: $font7;
          height: 13px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
    &--initial {
      &.curve-lbl {
        border-radius: 12px;
        color: $brand-black;

        @media screen and (min-width: $custom-screen-max-width) {
          border-radius: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font12});
        }
        @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          border-radius: 1.2vw;
        }

        // @include mQ(768px) {
        //   display: none;
        // }
        @include themify($themes) {
          background-color: themed("recommProductTitleBg");
        }
        // @include for-phone {
        //   display: none;
        // }
        @include mQ(360px) {
          display: block;
          font-size: $font11;
        }
      }
      font-size: $font20;
      padding: 2px 10px;
      line-height: 2.0rem;

      @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
          padding: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 0.2rem) calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
          line-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 2.0vw;
          padding: 0.2vw 1.0vw;
          line-height: 2.0vw;
      } 

      &.foot-notes {
        // @include mQ(991px) {
        //   display: none;
        // }
        // Color Theme
        @include themify($themes) {
          color: themed("recommProductTitle");
          background-color: themed("recommfoteNotesBg");
        }
      }
      @include for-phone {
        font-size: $font19;
        border-radius: 2.5px;
      }
      &.initial-bg {
        display: none;
        margin-right: 7px;
        width: 20px;
        height: 20px;
        font-size: $font12;
        align-items: center;
        justify-content: center;

        @include fontScaling(font-size,1.2);
        @include fontScaling(width,2.0);
        @include fontScaling(height,2.0);
        @include fontScaling(margin-right,0.7);

        // Color Theme
        color: $white;
        @include themify($themes) {
           background-color: themed('backgroundColor');
       }

        
        @include for-phone {
          display: none;
          width: 32px;
          height: 32px;
          align-items: center;
          justify-content: center;
          // Color Theme
          @include themify($themes) {
            color: themed("recommProductTitle");
            background-color: themed("recommfoteNotesBg");
          }
          @include mQ(360px) {
            display: none;
          }
        }
        + .txt-mobile {
          display: none;
          
          @include for-phone {
            display: none; 
            margin-left: 10px;
            font-size: $font24;
            font-family: $pfhandbookpro-medium;
          }
          @include mQ(575px) {
            margin-left: 0;
            font-size: $font20;
            white-space: normal;
          }
          @include mQ(360px) {
            display: none;
          }
        }
      }
      &.btn-orange { 
        // @include mQ(991px) {
        //      // Color Theme
        //      color: $white;
        //      @include themify($themes) {
        //         background-color: themed('backgroundColor');
        //     }
        // }
          
       
      }
    }
    &--title {
      font-family: $pfhandbookpro-medium;
      font-size: $font20;
      color: $black;
      display: flex;

      @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 2.0vw;
      }

      &-cta {
        font-family: $pfhandbookpro-medium;
        font-size: $font20;
        color: $black;
        display: flex;
        align-items: center;
        pointer-events: none;
        padding: 0 10px;

        @media screen and (min-width: $custom-screen-max-width) {
          font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font20});
          padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
      }
      @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
          font-size: 2.0vw;
          padding: 0 1.0vw;
      }


        &:hover,
        &:focus {
          color: $black;
          border-bottom: none;
        }

        @include for-phone {
          padding: 0;
          white-space: nowrap;
          pointer-events: auto;
          padding: 12px;
          display: flex;
          align-items: center;
        }
        @include mQ(360px) {
          padding: 4px 0 0 4px;
        }
      }
    }
    &--content {
      font-family: $pfhandbookpro-regular;
      font-size: $font16;
      color: $black;
      min-height: 78px;
      padding: 0 10px;

      @media screen and (min-width: $custom-screen-max-width) {
        font-size: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font16});
        padding: 0 calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * #{$font10});
        min-height: calc(#{$custom-max-width-number} / #{$custom-screen-width-number} * 7.8rem);
    }
    @media screen and (min-width: $custom-min-width) and (max-width: $custom-max-width) {
        font-size: 1.6vw;
        padding: 0 1.0vw;
        min-height: 7.8vw;
    }

      line-height: normal;
      @include for-phone {
        display: none;
      }
    }

    &--button {
      @include for-phone {
        display: none;
      }
      @include mQ(360px) {
        display: block;
      }
    }
  }
}

.nbo-modal {
  z-index: 99991;

  .modal-body {
    padding: 0;
  }
  .modal-header {
    padding: 0;
    .close { 
      margin: 0;
      font-size: 2.5rem;
      right: 3%;
      top: 15px; 
      background-color: #fff !important;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      opacity: 1;
      @include for-phone { 
        z-index: 99;
      }
      @include mQ(767px,576px) {  
        font-size: 3.9rem;
        width: 39px;
        height: 39px;
        right: -3%;
      }  
      @include mQ(360px) {   
        right: 2%;
        top: 0px; 
      }
      span {
        align-items: center;
        justify-content: center;
        width: 26px;
        height: 25px;
        display: flex;
        padding-bottom: 4px;
        font-weight: normal;

        @include mQ(767px,576px) { 
          font-size: 3.9rem;
          width: 40px;
          height: 39px;
        }
      } 

    }
  }
  .modal-content {
    background-color: transparent;
    padding: 0;
    border: none;
  }
  .dashboard__nbo-card-item {
    max-width: 90%;
    border:none;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    @include mQ(767px,576px) {
      max-width: 100%;
    }
  }
  .dashboard__nbo--label {
    margin-bottom: 6px;
    padding: 4px 10px 4px 10px;
    min-height: 38px;
    @include mQ(767px,576px) {
      min-height: 60px;
      margin-bottom: 11px;
      padding-left: 15px;
      padding-right: 15px;
    }
    .cta-label {
      @include mQ(767px,576px) {
        font-size: 2.4rem;
        max-width: 62%;
        line-height: 1;
      }
      font-size: 1.5rem;
      max-width: 60%;
    }
    .dashboard__cta {
      
      @include mQ(767px,576px) { 
        margin-right: 20px !important;
      }
      margin-right: 15px !important;
      .btn-mini {
        display: block;
        font-family: "pfhandbookpro-bold", sans-serif;
        font-size: 1.2rem;
        padding: 4px;
        display: inline-block;
        line-height: 1;
        height: 20px; 
        @include mQ(767px,576px) {  
          height: 30px;
          font-size: 1.8rem;
          padding: 5px 24px;
        }
      }
    }
  }
  .dashboard__nbo--title-cta {
    padding: 0 0 0 8px;
    @include mQ(767px,576px) {  
      padding: 0 15px 0 15px;
    }
  }
  .dashboard__nbo--initial {
    &.initial-bg {
      display: none;
      & + .txt-mobile {
        display: none;
      }
    }
    &.curve-lbl {
      display: block;
      padding: 0 8px 3px 7px;
      font-size: 1.9rem;
      @include mQ(767px,576px) { 
        padding: 8px 12px 8px 12px;
        font-size: 3.0rem;
        border-radius: 17.5px;
      }
    }
    &.foot-notes {
      display: block;
      padding: 4px 9px;
      @include mQ(767px,576px) {
        font-size: 1.6rem;
        line-height: 2.4rem;
      } 
    }
  }

  .dashboard__nbo--content {
    display: block;
    font-size: 1.5rem;
    @include mQ(767px,576px) { 
      font-size: 2.4rem;
      line-height: normal;
      padding: 0 15px;
    }
  }
  
  .dashboard__nbo-info-icon {
    .desc {
      @include mQ(767px,576px) { 
        margin: 8px 0 auto 0;
      }
    }
    .btn-box.dashboard__nbo--button {
      text-align: right;
      display: block;
      @include mQ(767px,576px) { 
        margin-top: 16px;
        margin-bottom: 16px;
      }
      .dashboard__nbo--initial{
        &.btn-orange{
          font-size: $font15;
          background-color: #f7a500;
          color: #2a2a2a;
          min-width: 88px;
          min-height: 30px;
          @include for-phone {
            display: inline-flex !important;
          }
          @include mQ(767px,576px) {  
            font-size: 2.4rem;
            min-width: 141px;
            min-height: 48px; 
          }
        }
      }
    }
  }


}

.shift-left {
  padding: 0;
  width: 0;
  position: relative;
  left: -100%;
  @include mQ(991px) {
    width: 100%;
  }
}