.download-listing {
  &__title {
    margin-bottom: 24px;
    @include for-phone {
      margin-bottom: 16px;
    }
  }
  &__list {
    padding-left: 0;
    margin-bottom: 0;
  }
  &__link {
    padding: 24px 0 8px 0;
    margin-right: 24px;
    @include for-phone {
      padding: 20px 0 8px 0;
      display: block;
    }
    width: auto;
    display: inline-block;
    word-wrap: break-word;
    color: $download-listing-link;
    font-size: $font24;
    @include for-phone {
      font-size: $font22;
    }
    line-height: $line-height-28;
    &:hover,
    &:focus,
    &:active {
      border: none;
    }
    &:hover {
      color: $download-listing-link-hover;
      .icon {
        color: $download-listing-link-icon-hover;
      }
    }
    &:active {
      color: $download-listing-link;
      .icon {
        color: $download-listing-link-icon;
      }
    }
    &:focus {
      color: $download-listing-link-icon;
      &:hover {
        color: $download-listing-link-hover;
      }
    }
    .icon {
      color: $download-listing-link-icon;
      margin-left: 8px;
      text-transform: none;
      font-size: $font16;
      display: inline-block;
      height: auto;
      line-height: normal;
    }
  }
  &__caption {
    color: $download-listing-caption;
    font-size: $font16;
    line-height: $line-height-24;

    @include for-phone {
      margin-left: 0;
    }
  }
  &__item {
    padding: 0 0 16px 0;
    border-bottom: 1px solid $download-listing-border;
    &:last-child {
      border: none;
    }
    &:before {
      all: unset;
      content: "";
    }
  }
  .modal-dialog {
    width: 66.67%;
    margin: 0 auto;
    position: fixed;
    top: 50%;
    left: 50%;
    max-width: 66.67%;
    transform: translate(-50%, -50%) !important;
    @include for-tablet {
      width: 100%;
      max-width: 100%;
      padding-right: 0 !important;
    }
  }
  .modal-header {
    padding: 40px 40px 0 40px;
    @include for-phone {
      padding: 32px 32px 0 16px;
      line-height: $line-height-32;
    }
    color: $download-listing-link;
    .close {
      font-family: $pfhandbookpro-thin;
      color: $black;
      font-size: $font44 !important;
      opacity: 1;
      &:hover {
        color: $black;
        opacity: 1;
      }
    }
  }
  .modal-title {
    font-weight: bold;
    line-height: $line-height-36;
    margin-bottom: 8px;
    -webkit-font-smoothing: antialiased;
    @include for-phone {
      font-weight: normal;
      line-height: $line-height-32;
    }
  }
  .modal-body {
    height: 380px;
    @include for-phone {
      height: 264px;
      padding: 0 0 16px 16px;
    }
    padding: 0 0 16px 40px;
    margin-bottom: 34px;
    overflow-y: scroll;
    &::-webkit-scrollbar-track {
      border-radius: 6px;
      background-color: $download-listing-scroll-bg;
      margin: 0 0 30px 0 !important;
    }
    &::-webkit-scrollbar {
      width: 8px;
      background-color: $download-listing-scroll-bg;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 6px;
      background-color: $light-grey;
    }
    .download-listing__list {
      padding-top: 0;
      margin-right: 24px;
    }
    .download-listing__item {
      padding: 0 0 12px 0;
    }
  }
  .modal-content {
    height: 500px;
    @include for-phone {
      height: 400px;
    }
  }
  .modal-dialog,
  .modal-content {
    border: none;
    border-radius: 0;
    float: left;
    width: 100%;
    padding-bottom: 0;
    padding-right: 8px;
  }
  .page-title {
    padding: 0 0 48px 0;
    @include for-phone {
      padding: 0 0 32px 0;
    }
  }
  .scLooseFrameZone {
    width: 100%;
  }
}
