.backtotop {
  position: fixed;
  right: 10px;
  bottom: 70px;
  z-index: 99;
  //z-index: 99999999999999;

  &__icon {
    cursor: pointer;
    text-align: center;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px;
    border-radius: 7px;
    background: $backtotop-bg;
    color: $backtoptop-icon;
    padding: 2px 8px;
    text-align: center;
    margin-top: 15px;
    font-size: 25px;
    border: none;
    font-size: $font18;
    display: block;
    &:before {
      line-height: 28px;
      font-size: $font16;
    }
    &.toPrev {
      transform: rotate(180deg);
    }
  }
  &__hide {
    opacity: 0;
    transition: opacity 0.3s ease-out, top 0.3s step-end;
    z-index: -9999;
    visibility: hidden;
  }
  &__show {
    transition: opacity 0.3s ease-out, top 0.3s step-start;
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }
}
