.abc-form {
  width: 100%;

  &.form-center {
    margin: 0 auto;
    width: 50%;

    @include for-phone {
      width: 90%;
    }
  }

  .form-group,
  .input-group {
    &.inline-radio {
      display: flex;
      justify-content: flex-start;

      .form-check {
        margin-right: 24px;
        font-size: $font18;
        color: $radio-label-color;
      }
    }

    .custom-file-label {
      cursor: pointer;
    }

    .custom-file-label::after {
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      content: "\EA4A";
      font-family: "font-icons";
      background-color: $upload-btn-bg;
      color: $upload-btn-color;
    }

    .info-text {
      padding: 2px 0px;
      opacity: 0.6;
    }

    &__label {
      opacity: 0.7;
      font-family: $pfhandbookpro-regular;
      font-size: $font16;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: $form-label;
      padding-bottom: 8px;

      &--buy-plan {
        font-size: $font32;
        color: $buy-plan-lbl;
        opacity: 1;

        @include for-phone {
          font-size: $font22;
        }
      }
    }

    &__placeholder {
      font-family: $pfhandbookpro-regular;
      font-size: $font18;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1;
      letter-spacing: normal;
      text-align: left;
      height: 44px;
      border-radius: 4px;
      background-color: $form-input-bg;
      border: 1px solid $form-input-border;
      color: $form-input;
      box-shadow: none;

      &:focus {
        box-shadow: 0 0 6px 0 $form-input-focus-shadow;
        border: 1px solid $form-input-focus-border;
      }

      &.datepicker {
        width: 100%;
      }

      &::-webkit-input-placeholder {
        /* Edge */
        opacity: 0.25;
        color: $black;
      }

      &:-ms-input-placeholder {
        /* Internet Explorer */
        opacity: 0.25;
        color: $black;
      }

      &::placeholder {
        opacity: 0.25;
        color: $black;
      }

      &.error {
        &::-webkit-input-placeholder {
          /* Edge */
          opacity: 1;
          color: $error-code;
        }

        &:-ms-input-placeholder {
          /* Internet Explorer */
          opacity: 1;
          color: $error-code;
        }

        &::placeholder {
          opacity: 1;
          color: $error-code;
        }
      }
    }

    &__datepicker-container {
      position: relative;
      display: block;
      width: 100%;

      img {
        position: absolute;
        top: 6px;
        right: 7px;
        margin-bottom: 0;
      }
    }

    &__disabled {
      cursor: not-allowed;
      background-color: rgba(236, 236, 236, 0.58) !important;
      border: solid 1px #f4f4f4 !important;
      box-shadow: none;
      color: #000000 !important;
    }

    &__xs {
      height: 25px;
      font-size: $font10;
    }

    &--inc-dec {
      border-bottom: 1px solid $increment-border;
      padding: 0;

      &.focused {
        border: 1px solid $increment-border;
      }
    }
    //captcha
    #captchaDivAccStmt {
      #SampleCaptcha_CaptchaDiv {
        display: flex;
      }
    }

    .BDC_CaptchaDiv {
      display: flex;
    }

    .error + .selectize-control .selectize-input {
      input {
        &::-webkit-input-placeholder {
          /* Edge */
          opacity: 1;
          color: $error-code;
        }

        &:-ms-input-placeholder {
          /* Internet Explorer */
          opacity: 1;
          color: $error-code;
        }

        &::placeholder {
          opacity: 1;
          color: $error-code;
        }
      }

      &:after {
        color: $error-code;
      }
    }

    .form-check {
      display: block;
      position: relative;
      padding-left: 35px;
      margin-bottom: 0px;
      padding-top: 3px;
      cursor: pointer;
      font-family: $pfhandbookpro-regular;
      font-size: $font18;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $form-label;
      user-select: none;

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        margin: 0;
        z-index: 1;

        &:checked ~ .checkmark {
          border-radius: 4px;
          box-shadow: 0 0 6px 0 $form-input-shadow;
          background-color: $form-input-bg;
          border: 2px solid $form-input-border;
          outline: none;
        }

        &:checked ~ .checkmark:after {
          display: block;
        }

        &:checked ~ .radio {
          background-color: $form-input-bg;
          border: 2px solid $form-input-border;
          box-shadow: 0 0 6px 0 $form-input-shadow;
        }

        &:checked ~ .radio:after {
          display: block;
        }
      }

      .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 24px;
        border-radius: 4px;
        background-color: $form-input-bg;
        border: 2px solid $form-input-border;

        &:focus {
          border-radius: 4px;
          box-shadow: 0 0 6px 0 $form-input-shadow;
          background-color: $form-input-bg;
          border: 2px solid $form-input-border;
          outline: none;
        }

        &:after {
          content: "";
          position: absolute;
          display: none;
          left: 7px;
          top: 3px;
          width: 6px;
          height: 12px;
          border-width: 0 2px 2px 0;
          transform: rotate(45deg);
        }
      }

      .radio {
        position: absolute;
        top: 0;
        left: 0;
        height: 24px;
        width: 24px;
        background-color: $form-input-bg;
        border-radius: 50%;
        border: 2px solid $form-input-border;

        &:after {
          content: "";
          position: absolute;
          display: none;
          top: 5px;
          left: 5px;
          width: 10px;
          height: 10px;
          border-radius: 50%;
        }
      }

      &.typ2 {
        font-size: $font14;
        line-height: $line-height-16;
        color: $form-input-bg;
        width: 100%;
        position: relative;
        padding-top: 0;

        .checkmark {
          background: transparent;
          border-color: $form-input-bg;

          &:after {
            border-color: $protecting;
          }
        }

        input {
          &:checked ~ .checkmark {
            background: $form-input-bg;
            border-color: $form-input-bg;
          }
        }

        .terms-content {
          width: 65%;
          display: block;

          @include for-phone {
            width: 100%;
          }
        }

        .button-brown {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 0;
          min-width: auto;
          padding: 9px 15px;
          z-index: 1;
          max-width: calc(35% - 20px);
          margin-left: 20px;
          height: auto;

          @include for-phone {
            position: relative;
            top: 0%;
            transform: translateY(0%);
            max-width: 100%;
            margin-left: 0px;
            margin-top: 20px;
          }

          &.disabled {
            cursor: no-drop;
            background: $form-input-border;
          }
        }
      }
    }
    //Error Message
    span.error,
    &.globalError {
      background: transparent !important;
      border: none !important;
      font-size: $font15;
      font-family: $pfencoresanspro-book;
      color: $capital-red;
      margin-bottom: 0;
    }
    //icon
    .icon-btn {
      height: 44px;
      border-radius: 4px;

      .icon {
        color: $form-button-group-icon;
        font-size: $font16;
        vertical-align: bottom;
      }

      &--inc-dec {
        padding: 0;

        .icon {
          color: $increment-icon;
        }
      }

      &__no-br-right {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
    }

    .hfl-icon-btn {
      background: $form-icon-btn-bg;
    }

    .hfl-btn {
      background: $financing-shade;
    }
    //Action Button
    .act-btn {
      height: 44px;
      border-radius: 4px;
      padding: 9px 30px;
      font-size: $font16;
      color: $form-button-group-action;

      &.disabled {
        background: $formbtn-disabled-bg;
        color: $formbtn-disabled-color;
      }
    }
  }

  &--card {
    border: 1px solid $form-card-border;
    box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.3);
    border-radius: 5px;

    &__header {
      padding: 8px 16px;

      &.header-with-bg {
        background: $form-card-header-bg;
      }
    }

    &__body {
      padding: 16px 24px;

      .btn-and-note-wrapper {
        display: flex;
        justify-content: space-between;

        .note-text {
          font-size: $font14;
          color: $form-card-note-color;

          &__head {
            color: $brand-black;
          }
        }
      }
    }
  }

  .amt-text {
    color: $amt-to-text;
    font-size: $font18;
  }
}

//Button Group
.input-group .form-control:last-child,
.input-group-addon:last-child,
.input-group-btn:first-child > .btn-group:not(:first-child) > .btn,
.input-group-btn:first-child > .btn:not(:first-child),
.input-group-btn:last-child > .btn,
.input-group-btn:last-child > .btn-group > .btn,
.input-group-btn:last-child > .dropdown-toggle {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

//FED CSS
.abc-form {
  .input-group {
    $self: &;

    &--v2 {
      background: $input-typ2-bg;
      border-radius: 4px;
      // overflow: hidden;
      .input-group-addon {
        padding: 1rem;
        font-size: $font18;
        line-height: $line-height-24;
        color: $addon-typ2-color;
        font-family: $pfhandbookpro-regular;
        position: relative;

        &:after {
          content: "";
          width: 0.1rem;
          top: 1rem;
          bottom: 1rem;
          right: 0;
          position: absolute;
          // background: $clr-placeholder;
          background: $addon-typ2-after;
        }

        &--file {
          padding: 1rem 1.6rem;

          .icon {
            font-size: $font14;
            color: $file-icon;
            margin: 0;
          }

          &:after {
            display: none;
          }
        }
      }

      .form-control {
        box-shadow: none;
        border: none;
        padding: 1rem;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;

        &::placeholder {
          // color: $clr-placeholder;
          color: $input-typ2-plcholder;
          font-size: $font18;
          line-height: $line-height-1;
          font-family: $pfhandbookpro-regular;
        }

        &.select {
          padding: 0;
        }
      }

      .selectize-control.single .selectize-input,
      .selectize-dropdown.single {
        border: none;
      }

      .selectize-dropdown-content {
        background: $form-input-bg;
        border-radius: 4px;
      }

      .input-group-btn {
        background: $input-grup-btn-bg;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;

        .btn {
          padding: 1rem 2rem;
          font-size: $font20;
          line-height: $line-height-24;
          color: $input-grup-btn-typ2;
          font-family: $pfhandbookpro-bold;
        }

        &--white {
          background: $form-input-bg;
          border-color: transparent;
        }
      }
    }

    &--border {
      border: 1px solid $form-input-border;

      .input-group-btn {
        padding: 0.5rem 1rem;
      }
    }

    &.disabled {
      .input-group-btn {
        background: $addon-typ2-disable;

        .btn {
          pointer-events: none;
          cursor: no-drop;
        }
      }
    }

    .input-group-btn {
      &__icon {
        display: block;
        width: 1.8rem;
        height: 1.8rem;
        border: 2px solid $input-grup-typ2-icon;
        border-radius: 50%;
        color: $input-grup-typ2-icon;
        font-size: $font18;
        line-height: $line-height-10;
        text-align: center;
        font-weight: 600;
      }

      .btn {
        &:hover,
        &:active,
        &:focus {
          .input-group-btn__icon {
            background: $input-grup-typ2-icon;
            color: $input-grup-btn-typ2;
          }
        }
      }
    }
  }
}

.abc-form {
  &__title {
    font-size: $font32;
    line-height: $line-height-1;
    margin-bottom: 2rem;
  }

  .form-control {
    &.input-small {
      font-size: $font16;
    }

    &--increment {
      border-radius: 0;
      border: 0;
      font-size: $font32;
      height: 4.4rem;
      text-align: center;
      opacity: 1;
      color: $form-control-increment;

      &:focus {
        box-shadow: none;
        border: 0;
      }
    }

    &--textarea {
      height: 18.6rem;

      @include for-phone {
        height: 8.8rem;
      }
    }

    &--file {
      opacity: 0;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
    }
  }

  &--feedback {
    .form-group {
      margin-bottom: 2rem;
      margin-right: 2rem;

      @include for-phone {
        margin-right: 0;
      }
    }

    .btn-wrap {
      text-align: right;
      padding-right: 2rem;

      @include for-phone {
        padding-right: 0.5rem;
      }
    }
  }

  .form-group {
    &__note {
      font-family: $pfencoresanspro-book;
      font-size: $font14;
      line-height: $line-height-16;
      color: $input-note-color;
      display: block;
      padding-top: 1rem;
    }

    &--download {
      width: 80%;
      max-width: 544px;
    }
  }

  .form-row {
    &--download {
      align-items: flex-end;
    }
  }

  &--download-filter {
    .form-group--download {
      width: auto;
      max-width: initial;
    }

    .row {
      width: 100%;
    }
  }

  &--download {
    .btn-wrap {
      display: inline-block;
      width: auto;
    }
  }
}

.form-sort {
  $self: &;

  input[type="checkbox"]:not(old) {
    width: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    display: none;

    & + label {
      border: 1px solid $form-input-border;
      padding: 1.1rem 3rem 1.1rem 1rem;
      border-radius: 4px;
      position: relative;
      min-width: 10rem;
      width: 100%;
      cursor: pointer;

      #{$self}__text {
        font-size: $font18;
        line-height: $line-height-20;
        text-transform: uppercase;
        color: $form-input-focus-border;

        &--unchecked {
          display: block;
        }

        &--checked {
          display: none;
        }
      }

      #{$self}__icon {
        font-size: $font16;
        color: $df-icon-color;
        position: absolute;
        top: 46%;
        transform: translateY(-50%);
        right: 1rem;
      }
    }

    &:checked {
      & + label {
        #{$self}__text {
          &--unchecked {
            display: none;
          }

          &--checked {
            display: block;
          }
        }
      }
    }
  }
}

.btn-right-align {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.photo-card-wrapper {
  padding: 0 12px;

  .photo-card {
    width: 100%;
    display: block;
    border: 1px solid $brand-light-grey;
    box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.3);
    border-radius: 5px;
    margin-bottom: 8px;
  }
}

.inline-radio-with-bg {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0px 8px 16px;
  background: $grey-002;

  .inline-radio__label {
    padding: 0 16px;
    margin: 0px;
  }

  .inline-radio {
    margin: 0px;
  }
}

.input-group {
  .input-group-prepend {
    .input-group-text--icon {
      margin: 0;
      font-size: 1.6rem;
    }
  }

  .form-control {
    &.input-small {
      font-size: $font16;
    }
  }
}

.otpmodal {
  .otp-modal-content {
    padding: 50px;
    margin: 0px;
   

    .otp-modal-header {
      padding: 0;
      margin: 0;
      text-align: center;
      line-height: 1.2;

      .otp-modal-title {
        padding: 8px 16px;
        margin: 0;
        text-align: center;
        display: block;
        font-family: $pfhandbookpro-bold;
        font-size: $font22;
        color: $otp-modal-primary-color;
      }

      .otp-modal-subtitle {
        padding: 8px 16px;
        margin: 0;
        display: block;
        font-size: $font18;
        position: relative;
        top: -15px;
      }
    }

    .modal-body {
      padding: 0;
      margin: 0;
      text-align: center;

      .digit-group {
        label {
          display: block;
          text-align: center;
          padding: 20px 0px 5px 10px;
          font-size: $font16;
          font-weight: bold;
          color: $black;
        }

        input {
          width: 40px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          font-size: $font24;
          margin: 0 2px;
          border: 1px solid $brand-light-grey;
          box-shadow: 0px 3px 6px rgba($color: #585757, $alpha: 0.3);
          border-radius: 3px;
        }
      }

      &--subtext {
       
        margin: 0 auto;
        text-align: center;
        background: $grey-002;
        padding: 4px 8px;
        font-size: $font16;

        &__link {
          color: $otp-modal-primary-color;
          font-size: $font16;
        }
      }
    }
  }
}
