//article-list
.article-list {
    $this: &;
    &__title-wrap {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin-bottom: 3rem;
        padding: 0 2rem 0 1.5rem;
        @include for-tablet {}
        @include for-phone {}
    }
    &__title {
        font-size: $font32;
        line-height: $line-height-34;
        font-family: $pfhandbookpro-regular;
        color: $article-text-color;
        text-transform: capitalize;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
    }
    &__slick-nav {
        display: none;
        @include for-tablet {
            display: none;
        }
        @include for-phone {
            display: none;
        }
    }
    &__list {
        background: $article-bg-color;
        .slick-nav {
            margin-top: 3.2rem;
        }
    }
    &__item-list {
        display: flex;
        justify-content: flex-start;
        padding: 0 1.5rem;                       
        #{$this}__item-tile {
            width: 100%;
            max-width: 40.4rem;
            margin-right: 3.2rem;
            &:last-child {
                margin-right: 0;
            }
        }
        .slick-list {
            margin: 0 -1.5rem;
            width: calc(100% + 3rem);
            .slick-track {
                margin-left: 0;
            }
        }
        .slick-slide {
            &>div {
                height: 100%;
            }
            #{$this}__item-tile {
                height: 100%;
                margin-bottom: 0;
            }
            .media-tile__wrapper--blog {
                min-height: 100%;
                height: 100%;
            }
        }
        @include for-tablet {
            .slick-dots {
                position: relative;
            }
        }
        &.slick-initialized {
            +.article-list__slick-nav {
                display: block;
                @include for-tablet {
                    display: none;
                }
                @include for-phone {
                    display: none;
                }
            }
        }
    }
    &__button {
        padding: 0;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &--bg-mobile {
        @include for-tablet {
            margin: 0 -1.5rem;
            padding: 1.5rem;
            background: $article-mbg-color;
        }
        @include for-phone {
            margin: 0 -1.5rem;
            padding: 1.5rem;
            background: $article-mbg-color;
        }
        #{$this}__list {
            background: transparent;
        }
    }
    .slick-dots {
        bottom: -2rem;
    }
    &__slick-nav--popular {
        @include for-tablet {
            display: none;
        }
    }
}