.illustration {
    $self: &;
    &__img-wrap {
        border-radius: 2px;
        border: 1px solid $illus-border;
        box-shadow: 0 0 14px 0 rgba($illus-box-shadow, 0.24);
        display: block;
        padding: 1.5rem;
        width: 100%;
        @include for-phone {
            padding: 0.4rem;
        }
    }
    &__btn {
        display: none;
        margin-top: 1rem;
        margin-left: auto;
        background: transparent;
        border: none;
        #{$self}__icon {
            font-size: $font20;
            color: $illus-link-color;
            display: inline-block;
            vertical-align: middle;
            margin-right: 1rem;
        }
        #{$self}__link {
            color: $illus-link-color;
            font-size: $font18;
            line-height: $line-height-20;
            font-family: $pfhandbookpro-bold;
            display: inline-block;
            vertical-align: middle;
            text-decoration: underline;
        }
        @include for-phone {
            display: block;
        }
    }
    &__overlay {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba($illus-overlay, 0.8);
        z-index: 10000;
        display: none;
    }
    &__close {
        position: absolute;
        right: 2rem;
        top: 2rem;
    }
    &__icon-close {
        font-size: $font20;
        color: $illus-close;
    }
    &__img-popup {
        display: none;
        background-position: center center;
        background-size: contain;
        background-repeat: no-repeat;
        width: auto;
        left: 1rem;
        right: 1rem;
        height: 12rem;
        position: fixed;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10001;
    }
    &--active {
        #{$self}__img-popup {
            display: block;
        }
        #{$self}__overlay {
            display: block;
        }
    }
}