.table {
    $self: &;
    max-width: 38rem;
    @include for-phone {
        width: 100%;
    }
    &__wrap {
        width: 100%;
        border: 1px solid $table-border-color;
    }
    &__head,
    &__body {
        #{$self}__heading,
        #{$self}__data {
            vertical-align: middle;
            text-align: center;
            width: 33.33%;
            font-size: $font18;
            line-height: $line-height-20;
            font-family: $pfhandbookpro-regular;
            color: $table-text;
            border-bottom: none;
            &:first-child,
            &:nth-child(2) {
                border-right: 1px solid $table-border-color;
            }
            @include for-phone {
                font-size: $font16;
                line-height: $line-height-18;
                width: auto;
                &:first-child {
                    width: 25%;
                }
            }
        }
    }
    &__row {
        border-bottom: 1px solid $table-border-color;
    }
    &--typ2 {
        max-width: 54rem;
        margin-top: 2.5rem;
        @include for-phone {
            margin-top: 2rem;
        }
        #{$self}__head,
        #{$self}__body {
            #{$self}__heading,
            #{$self}__data {
                vertical-align: top;
                text-align: left;
                width: 40%;
                font-size: $font18;
                line-height: $line-height-20;
                font-weight: 400;
                padding: 2rem 1.5rem;
                @include for-phone {
                    font-size: $font14;
                    line-height: $line-height-16;
                    padding: 1rem;
                }
            }
            #{$self}__heading {
                font-size: $font20;
                line-height: $line-height-22;
                @include for-phone {
                    font-size: $font16;
                    line-height: $line-height-18;
                }
            }
        }
    }
    &--typ-allocation {
        max-width: initial;
        margin-bottom: 0;
        #{$self}__wrap {
            height: 23.7rem;
        }
        #{$self}__head,
        #{$self}__body {
            #{$self}__heading {
                font-weight: 400;
                padding: 1.5rem;
            }
            #{$self}__data {
                padding: 1rem 1.5rem;
                &:last-child {
                    border-right: none;
                }
            }
            #{$self}__heading,
            #{$self}__data {
                width: 7.5rem;
                border-right: 1px solid $table-border-color;
                &:first-child {
                    text-align: left;
                    width: 22.4rem;
                }
            }
        }
    }
    &--typ-funds {
        max-width: initial;
        margin-bottom: 0;
        //#{$self}__wrap {
        //    height: 23.7rem;
        //}
        #{$self}__head,
        #{$self}__body {
            #{$self}__heading {
                font-weight: 400;
                padding: 1.5rem;
            }
            #{$self}__data {
                padding: 1.7rem 1.5rem; // 3477 - ie fix top bottom spacing 1 to 1.7
                &:last-child {
                    border-right: none;
                }
                &--filled {
                    //background-color: $aa-cell-highlight-clr;
                    position: relative;
                    span {
                        position: absolute;
                        background-color: $aa-cell-highlight-clr;
                    }
                    & + #{$self}__data{
                        &--filled{
                            border-left: 2px solid $aa-cell-highlight-clr;
                        }
                    }
                }
            }
            #{$self}__heading,
            #{$self}__data {
                width: 7.5rem;
                border-right: 1px solid $table-border-color;
                &:first-child {
                    text-align: left;
                    width: 9.9rem;
                    word-break: break-word;
                }
            }
        }
    }
    &--typ-no-border {
        #{$self}__wrap {
            width: 28.8rem;
        }
        #{$self}__head,
        #{$self}__body {
            #{$self}__heading {
                font-weight: 400;
                text-align: left;
                padding: 1.5rem;
                &:first-child {
                    border-right: none;
                }
            }
        }
    }
    &--income-tax {
        max-width: inherit;
        width: 100%;
        margin-bottom: 0;
        #{$self}__head,
        #{$self}__body {
            #{$self}__heading,
            #{$self}__data {
                text-align: left;
                width: auto;
                font-size: $font20;
                line-height: $line-height-22;
                font-family: $pfhandbookpro-regular;
                font-weight: 400;
                &:first-child {
                    width: 2rem;
                }
                @include for-phone {
                    font-size: $font16;
                    line-height: $line-height-18;
                    &:first-child {
                        width: auto;
                    }
                }
                .icon {
                    font-size: $font14;
                }
            }
        }
    }
}