.listsummary__container {
    width: 100%;
    .listsummary-row {
        ul{
            list-style:none;
            padding:0px;
            display: flex;
            margin: 0px;
            width:100%;
            @include for-tablet {
                display: inline-block;  
            }
            li{
                flex-basis: 100%;
                padding: 0px 15px 23px 10px;
                @include fontScaling(padding-right,1.5);
                @include fontScaling(padding-bottom,2.3);
                @include fontScaling(padding-left,0.6);
                color: $brand-black !important;
                padding-right: 5px;
                @include fontScaling(padding-right,0.5);
                max-width: 50%;
                word-break: break-word;
                @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
                    word-break: break-all;
                }
                
                @include mQ(575px) {
                    padding-bottom: 8px;
                }

                @include for-tablet{
                    width: 50%;
                    display: block;
                    float: left;
                    padding-left: 10px !important;
                    padding-bottom: 5px;
                }
                span{
                    font-size: $font14;
                    @include fontScaling(font-size,1.4);
                    font-family: $pfhandbookpro-light;
                    line-height: 1.15;
                    @include mQ(767px,576px) {
                        font-size: 2.0rem;
                    }
                    @include mQ(575px) {
                        font-size: 1.3rem;
                    }
                }
                p{
                    margin-bottom: 0px;
                    font-size: $font26;
                    @include fontScaling(font-size,2.6);
                    color: $brand-black;
                    font-family: $pfhandbookpro-medium;
                    line-height: 1.3;
                    @include mQ(767px,576px) {
                        font-size: 3.0rem;
                    }
                    @include mQ(575px) {
                        font-size: 1.9rem;
                    }
                }
                span.pascal-case {
                    margin-bottom: 0px;
                    font-size: $font26 !important;
                    @include fontScaling(font-size,2.6,!important);
                    color: $brand-black;
                    font-family: $pfhandbookpro-medium !important;
                    line-height: 1.3 !important;
                    @include mQ(767px,576px) {
                        font-size: 3.0rem;
                    }
                    @include mQ(575px) {
                        font-size: 1.9rem;
                    }
                }
                &.cta-link{
                    span{
                        @include themify($themes) {
                            color: themed('backgroundColor'); 
                        }
                    }
                    p{
                        @include themify($themes) {
                            color: themed('backgroundColor'); 
                        }
                    }
                    a{
                        @include themify($themes) {
                            color: themed('backgroundColor'); 
                        }
                        font-family: $pfhandbookpro-bold;
                        font-size: $font14;
                        @include fontScaling(font-size,1.4);
                        line-height: 1.15;
                        display: inline-flex;
                        text-transform: uppercase;
                        margin-top: 5px;
                        @include fontScaling(margin-top,0.5);
                        text-decoration: underline;
                        position: relative;
                        padding-right: 20px;
                        @include fontScaling(padding-right,2.0);
                        &:after{
                            content: $icon-right-arrow;
                            display: block !important;
                            -moz-osx-font-smoothing: grayscale;
                            -webkit-font-smoothing: antialiased;
                            font-family: "font-icons";
                            font-style: normal;
                            font-variant: normal;
                            font-weight: normal;
                            line-height: 1;
                            speak: none;
                            text-transform: none;
                            font-size: $font10;
                            @include fontScaling(font-size,1.0);
                            line-height: 1;
                            right: 0px;
                            top: 4px;
                            @include fontScaling(top,0.4);
                            position: absolute;
                        }
                        &:hover{
                            border: none;
                        }
                        &:focus{
                            border: none;
                        }
                    }
                }
            }
            
        }
        &.last{
            padding-bottom: 0px;
            li{
                padding-bottom: 0px;
                @include for-phone{
                    padding-bottom: 18px;
                }
            }
        }
    }
    .btn-tooltip {
        border: 1px solid #da9089 !important;
        @include fontScaling(border-width,0.1);
        color: $black !important;
        width: 20px;
        @include fontScaling(width,2.0);
        height: 20px;
        @include fontScaling(height,2.0);
        text-align: center;
        display: inline-block;
        margin-left: 8px;
        @include fontScaling(margin-left,0.8);
        margin-top: 0px;
        font-size: $font15;
        @include fontScaling(font-size,1.5);
        line-height: 19px;
        @include fontScaling(line-height,1.9);
        border-radius: 50%;
        cursor: pointer;
        position: relative;
        top: -3px;
        @include fontScaling(top,-0.3);            
        &:hover,
        &:focus,
        &:active {
            border-bottom: 0 none;
        }
    }    
}
.title-btn{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
   
    .btn-orange {
        font-size: $font14;
        @include fontScaling(font-size,1.4);
        font-family: $pfhandbookpro-bold;
        font-size: $font14;
        @include fontScaling(font-size,1.4);
       
        min-width: 120px;
        @include fontScaling(min-width,12.0);
        background: #f7a500;
        border-radius: 11px;
        @include fontScaling(border-radius,1.1);
        display: inline-flex;
        align-items: center;
        min-height: 30px;
        @include fontScaling(min-height,3.0);
        border-radius: 1px;
        @include fontScaling(border-radius,0.1);
        justify-content: center;
        color: $brand-black;
        margin-right: 10px;
        @include fontScaling(margin-right,1.0);
        // @include mQmin($xxl){
        //     min-width: 141px !important;
        //     border-radius: 3px;
        //     min-height: 36px !important;
        //     margin-right: 0px;
        //     font-size: 17px;
        // }
        &:hover, &:active, &:focus {
            text-decoration: none;
            border: 0;
        }
    }
}
.yellow-badge {
    background: rgba(247, 165, 0, 0.5);
    border-radius: 11px;
    @include fontScaling(border-radius,1.1);
    padding: 3px 8px;
    @include fontScaling(padding-top,0.3);
    @include fontScaling(padding-right,0.8);
    @include fontScaling(padding-bottom,0.3);
    @include fontScaling(padding-left,0.8);

}
.common-hr { 
    border-top: 1px solid rgba(151, 151, 151, 0.2);
    @include fontScaling(border-top-width,0.1);
    margin: 23px 0px;
    @include fontScaling(margin-top,2.3);
    @include fontScaling(margin-bottom,2.3);
} 