.calculator-link {
  border-radius: 4px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.13);
  clear: both;
  position: relative;
  @include mQ(991px) {
    margin-bottom: 20px;
  }
  .calculator-link-wrapper {
    padding: 20px 20px 20px 20px;
    .calculator-link-title {
      font-family: $pfhandbookpro-bold;
      color: $capital-red;
      text-transform: uppercase;
      letter-spacing: -0.41px;
      margin-bottom: 30px;
    }
    .calculator-link-detail-wrapper {
      padding-bottom: 20px;
      &:last-child {
        padding-bottom: 10px;
        @include mQ(767px) {
          padding-bottom: 0;
        }
      }
      .calculator-link-text {
        font-family: $pfhandbookpro-medium;
        color: $black;
        text-transform: uppercase;
        position: relative;
        &:after {
          content: $icon-right-arrow;
          -moz-osx-font-smoothing: grayscale;
          -webkit-font-smoothing: antialiased;
          font-family: "font-icons";
          font-style: normal;
          font-variant: normal;
          font-weight: normal;
          line-height: 1;
          speak: none;
          text-transform: none;
          font-size: 1rem;
          position: absolute;
          right: 10px;
          color: $capital-red;
          top: 50%;
          margin-top: -5px;
        }
      }
    }
  }
}

//Blog Migration
.two-columns{
  .component.calculator-link{
    margin-bottom: 12px;
  }
}