.lead-form {
    $self: &;
    background: $lead-bg-color;
    width: 100%;
    position: relative;
    @include for-tablet {
        padding: 0 15px;
    }
    &__container {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 12rem;
        padding-top: 10px;
        padding-bottom: 10px;
        margin: 0 auto;
        @include for-tablet {
            height: auto;
            flex-wrap: wrap;
            align-content: center;
            padding: 30px 0;
        }
    }
    &__title {
        font-size: $font44;
        color: $lead-title;
        line-height: $line-height-1;
        font-family: $pfhandbookpro-regular;
        @include for-md-desktop {
            font-size: $font36;
        }
        @include for-tablet {
            font-size: $font22;
        }
    }
    &__title-wrap {
        position: relative;
        width: 65%;
        margin-right: 40px;
        @include for-tablet {
            text-align: center;
            margin-bottom: 1.5rem;
            width: 100%;
            margin-right: 0px;
        }
    }
    &__wrap {
        width: 35%;
        @include for-tablet {
            width: 100%;
            margin: 0 auto;
        }
    }
    .abc-form {
        .form-group {
            margin-bottom: 0;
            position: relative;
            span.error {
                color: $lead-error;
                position: absolute;
                left: 0;
                top: 100%;
            }
        }
        input {
            &::-webkit-input-placeholder {
                /* Edge */
                opacity: 1;
                color: $lead-placeholder;
            }
            &:-ms-input-placeholder {
                /* Internet Explorer */
                opacity: 1;
                color: $lead-placeholder;
            }
            &::placeholder {
                opacity: 1;
                color: $lead-placeholder;
            }
        }
        &--lead,
        &--lead-gp {
            .form-check.typ2 {
                input {
                    width: 70%;
                    @include for-phone {
                        width: 100%;
                        height: calc(100% - 58px);
                    }
                }
            }
        }
    }
    &--typ2 {
        padding: 3rem 5rem;
        #{$self}__container {
            display: block;
        }
        #{$self}__title {
            @include for-md-desktop {
                font-size: $font30;
            }
        }
        #{$self}__title-wrap {
            margin-bottom: 2.5rem;
        }
        #{$self}__wrap {
            width: 60%;
        }
        #{$self}__info-wrap {
            display: block;
        }
        #{$self}__statement {
            padding: 1rem 0;
            border-right: none;
            border-bottom: 1px solid #ffffff;
            &:last-child {
                border-bottom: none;
            }
        }
    }
    &__thanks-msg {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        min-height: 12rem;
        padding: 18px 0;
        @include for-phone {
            height: auto;
        }
    }
    &__statement {
        font-size: $font24;
        line-height: $line-height-26;
        color: $lead-text;
        font-family: $pfhandbookpro-regular;
        @include for-md-desktop {
            font-size: $font18;
            line-height: $line-height-18;
        }
        @include for-phone {
            font-size: $font12;
            line-height: $line-height-14;
        }
        &--lg-text {
            font-size: $font44;
            line-height: $line-height-46;
            display: block;
            @include for-md-desktop {
                font-size: $font24;
                line-height: $line-height-24;
            }
            @include for-phone {
                font-size: $font22;
                line-height: $line-height-24;
            }
        }
    }
    &__icon {
        width: 7.6rem;
        height: 7.6rem;
        display: inline-block;
        border-radius: 50%;
        background: $lead-icon-bg;
        font-size: $font48;
        color: $lead-text;
        padding: 1.4rem;
        margin-right: 2.5rem;
    }
    &__icon-wrap {
        width: 7.6rem;
        height: 7.6rem;
        display: inline-block;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 2.5rem;
    }
    &__img {
        width: 100%;
    }
    &__info-wrap {
        display: flex;
        @include for-phone {
            display: block;
        }
    }
    &__statement {
        padding: 0 12rem;
        border-right: 1px solid $lead-text;
        margin-bottom: 0;
        @include for-lg-desktop {
            padding: 0 8rem;
        }
        @mixin for-md-desktop {
            padding: 0 4rem;
        }
        @include for-tablet {
            padding: 0 4rem;
        }
        @include for-phone {
            padding: 0.8rem 0;
            border-right: none;
            border-bottom: 1px solid $lead-text;
        }
        &:first-child {
            padding-left: 0;
        }
        &:last-child {
            border-right: none;
            @include for-phone {
                border-bottom: none;
            }
        }
    }
    .container-fluid {
        @include for-phone {
            padding: 0;
        }
    }    
}