.page-title {
  p {
    font-size: $font24;
    line-height: $line-height-28;
    color: $page-title;
    text-align: center;
    margin-bottom: 0;
    @include for-phone {
      text-align: left;
    }
  }
}



//HFL
.page-title-wrapper {
 /* padding-top: 3rem;*/
 margin:20px;

  .page-ts-title {
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: $font32;
    font-family: $pfhandbookpro-bold;
    line-height: 1.2;
    color: $page-title-color;

    @include mQ(767px) {
      font-size: $font22;
    }
  }

  .page-ts-subtitle {
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: $font18;
    line-height:33px;
    color: $page-subtitle-color;

    @include mQ(767px) {
      font-size: $font13;
    }
  }

  .success-head {
    font-family: $pfhandbookpro-regular;
    font-size: $font32;
    letter-spacing: 0px;
    color: #1F874C;
    text-align: center;
    font-weight: normal;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
  }

  .black {
    color: #000000;
  }
}
//HFL Ends