.cat-pro-banner {
    $self: &;
    //max-width: 760px;
    max-width: 58.5%;
    display: inline-block;
    @include for-tablet {
        max-width: 70%;
    }
    @include for-phone {
        margin-right: 0;
        max-width: 100%;
    }
    &__subtitle {
        font-size: $font32;
        line-height: $line-height-32;
        font-family: $pfhandbookpro-thin;
        &--bold {
            font-family: $pfhandbookpro-regular;
        }
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__title {
        font-size: $font44;
        line-height: $line-height-44;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font32;
            line-height: $line-height-32;
        }
    }
    &__desc {
        font-size: $font24;
        line-height: $line-height-26;
        font-family: $pfhandbookpro-thin;
        color: $cat-banner-desc;
        margin: 0;
        @include for-phone {
            font-size: $font18;
            line-height: $line-height-20;
        }
    }
    &__second-title {
        font-size: $font32;
        line-height: $line-height-32;
        margin-top: 3rem;
        @include for-phone {
            font-size: $font22;
            line-height: $line-height-24;
        }
        &--regular {
            font-family: $pfhandbookpro-thin;
            margin-bottom: 1rem;
        }
    }
    &__third-title {
        font-size: $font24;
        line-height: $line-height-26;
        margin-bottom: 1rem;
        @include for-phone {
            font-size: $font20;
            line-height: $line-height-22;
        }
    }
    &__btn-wrap {
        padding-top: 4rem;
        @include for-phone {
            padding-top: 1.5rem;
        }
    }
    &__info {
        padding-bottom: 5rem;
        margin-top: 5rem;
        border-bottom: 1px solid $about-border;
        @include for-phone {
            padding-bottom: 4rem;
            margin-top: 4rem;
        }
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            padding-bottom: 0;
            border: none;
        }
    }
    &--typ-details {
        #{$self}__desc {
            margin-bottom: 1rem;
            @include for-phone {
                margin-bottom: 1.5rem;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
        #{$self}__read-wrap {
            margin-top: 3rem;
        }
    }
    &--typ-dark {
        #{$self}__title,
        #{$self}__desc,
        #{$self}__second-title {
            color: $bgnav-text-color;
        }
    }
}

.category-product-banner {
    .read-less,
    .read-more {
        padding-left: 0;
        display: none;
        @include for-phone {
            display: block;
        }
    }
    .read-more {
        margin-top: 1.5rem;
    }
    .state-list {
        margin-top: 4rem;
    }
    .abc-form {
        &--download {
            margin-top: 3rem;
            .button-link--download {
                padding: 2rem;
                @include for-phone {
                    padding: 1.8rem 1.4rem;
                }
                .icon {
                    color: $pro-btn-icon;
                }
            }
        }
    }
    &--pro {
        padding-bottom: 4rem;
        border-bottom: 1px solid $pro-banner-border;
    }
    &__top {
    }
}

.banner-ad-wrap {
    max-width: 23%;
    //flex-shrink: 1;
    width: 100%;
    position: relative;
    display: inline-block;
    float: right;
    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
    @include for-tablet {
        max-width: 30%;
    }
    @include for-phone {
        display: none;
    }
}